var source='\n### \u041B\u0438\u0447\u043D\u044B\u0435 \u043C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u044F\n\n\u0412 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u043C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u044F \u043F\u043E\u0434\u0447\u0438\u043D\u044F\u044E\u0442\u0441\u044F \u043F\u0440\u0438\u0432\u044B\u0447\u043D\u043E\u0439 \u043C\u043D\u043E\u0433\u0438\u043C \u044F\u0437\u044B\u043A\u0430\u043C \u0441\u0445\u0435\u043C\u0435. \u0422\u0440\u0438 \u043B\u0438\u0446\u0430: \u043F\u0435\u0440\u0432\u043E\u0435, \u0432\u0442\u043E\u0440\u043E\u0435, \u0442\u0440\u0435\u0442\u044C\u0435 \u0438 \u0434\u0432\u0430 \u0447\u0438\u0441\u043B\u0430: \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0438 \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435. \u0421\u0442\u043E\u0438\u0442 \u0442\u043E\u043B\u044C\u043A\u043E \u0437\u0430\u043C\u0435\u0442\u0438\u0442\u044C, \u0447\u0442\u043E \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u043D\u0435\u0442 \u043F\u043E\u043D\u044F\u0442\u0438\u044F \u0440\u043E\u0434\u0430. \u041C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u044F \u043E\u043D, \u043E\u043D\u0430, \u043E\u043D\u043E \u0437\u0434\u0435\u0441\u044C \u043F\u0440\u0435\u0434\u0441\u0442\u0430\u0432\u043B\u0435\u043D\u043E \u0442\u043E\u043B\u044C\u043A\u043E \u043E\u0434\u043D\u0438\u043C, \u0438 \u043E \u043A\u043E\u043C \u043A\u043E\u043D\u043A\u0440\u0435\u0442\u043D\u043E \u0438\u0434\u0435\u0442 \u0440\u0435\u0447\u044C, \u0431\u0443\u0434\u0435\u0442 \u043F\u043E\u043D\u044F\u0442\u043D\u043E \u0443\u0436\u0435 \u0438\u0437 \u043A\u043E\u043D\u0442\u0435\u043A\u0441\u0442\u0430.\n\n<table>\n<tr>\n<th>\n\u041B\u0438\u0446\u043E\n</th>\n<th>\n\u0415\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E\n</th>\n<th>\n\u041C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E\n</th>\n</tr>\n<tr>\n<td>1</td>\n<td>\u042F Mina (ma)</td>\n<td>\u041C\u042B  Meie (me)</td>\n</tr>\n<tr>\n<td>2</td>\n<td>\u0422\u042B  Sina (sa)</td>\n<td>\u0412\u042B  Teie (te)</td>\n</tr>\n<tr>\n<td>3</td>\n<td>\n\u041E\u041D<br>\n\u041E\u041D\u0410 Tema (ta)<br>\n\u041E\u041D\u041E\n</td>\n<td>\n\u041E\u041D\u0418  Nemad (nad)\n</td>\n</tr>\n</table>\n\n\u041A\u0430\u043A \u0412\u044B \u043C\u043E\u0433\u043B\u0438 \u0437\u0430\u043C\u0435\u0442\u0438\u0442\u044C, \u0434\u043B\u044F \u0432\u0441\u0435\u0445 \u044D\u0442\u0438\u0445 \u043C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u0439 \u0438\u043C\u0435\u0435\u0442\u0441\u044F \u043F\u043E\u043B\u043D\u0430\u044F \u0438 \u043A\u043E\u0440\u043E\u0442\u043A\u0430\u044F, \u0443\u043A\u0430\u0437\u0430\u043D\u043D\u0430\u044F \u0432 \u0441\u043A\u043E\u0431\u043A\u0430\u0445,  \u0444\u043E\u0440\u043C\u0430. \u0412 \u043F\u0440\u0438\u043D\u0446\u0438\u043F\u0435 \u043E\u0431\u0435 \u0444\u043E\u0440\u043C\u044B \u0432 \u0440\u0430\u0432\u043D\u043E\u0439 \u043C\u0435\u0440\u0435 \u0447\u0430\u0441\u0442\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u044E\u0442\u0441\u044F, \u043D\u043E \u0435\u0441\u043B\u0438 \u0412\u044B \u0445\u043E\u0442\u0438\u0442\u0435 \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0435 \u043F\u043E\u0434\u0447\u0435\u0440\u043A\u043D\u0443\u0442\u044C \u0438\u043C\u0435\u043D\u043D\u043E \u043C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u0435, \u0441\u0434\u0435\u043B\u0430\u0432 \u043D\u0430 \u043D\u0435\u043C \u0443\u0434\u0430\u0440\u0435\u043D\u0438\u0435, \u0442\u043E \u0432 \u0442\u0430\u043A\u043E\u043C \u0441\u043B\u0443\u0447\u0430\u0435 \u0441\u043B\u0435\u0434\u0443\u0435\u0442 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C \u0435\u0433\u043E \u043F\u043E\u043B\u043D\u0443\u044E \u0444\u043E\u0440\u043C\u0443.\n\u041C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u0435 2\u0433\u043E \u043B\u0438\u0446\u0430 \u2014 \xAB\u0412\u044B\xBB, \xABTeie\xBB \u2014 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u0434\u043B\u044F \u0432\u044B\u0440\u0430\u0436\u0435\u043D\u0438\u044F, \u043A\u0430\u043A \u043D\u0435\u0441\u043A\u043E\u043B\u044C\u043A\u0438\u0445 \u0434\u0435\u0439\u0441\u0442\u0432\u0443\u044E\u0449\u0438\u0439 \u043B\u0438\u0446, \u0442\u0430\u043A \u0438 \u043E\u0434\u043D\u043E\u0433\u043E, \u0435\u0441\u043B\u0438 \u043C\u044B \u043A \u043D\u0435\u043C\u0443 \u043E\u0431\u0440\u0430\u0449\u0430\u0435\u043C\u0441\u044F \u0432 \u0443\u0432\u0430\u0436\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0439 \u0444\u043E\u0440\u043C\u0435.\n\n### Verbid\n### \u0413\u043B\u0430\u0433\u043E\u043B\u044B\n\n\u0412 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435, \u0432 \u043E\u0442\u043B\u0438\u0447\u0438\u0435 \u043E\u0442 \u0440\u0443\u0441\u0441\u043A\u043E\u0433\u043E 4 \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u0443 \u0433\u043B\u0430\u0433\u043E\u043B\u0430. \u041F\u0440\u0438 \u044D\u0442\u043E\u043C 3 \u0438\u0437 \u043D\u0438\u0445 \u044F\u0432\u043B\u044F\u044E\u0442\u0441\u044F \u0440\u0430\u0437\u043B\u0438\u0447\u043D\u044B\u043C\u0438 \u0432\u0438\u0434\u0430\u043C\u0438 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438 (\u043F\u0440\u043E\u0441\u0442\u043E\u0435 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0439, \u043F\u043E\u043B\u043D\u043E\u0435 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0438 \u0434\u0430\u0432\u043D\u043E \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0432\u0440\u0435\u043C\u044F), \u043F\u043B\u044E\u0441 \u0438\u043C\u0435\u0435\u0442\u0441\u044F \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0435 \u0432\u0440\u0435\u043C\u044F. \u0411\u0443\u0434\u0443\u0449\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u043A\u0430\u043A \u0442\u0430\u043A\u043E\u0433\u043E \u0432 \u044F\u0437\u044B\u043A\u0435 \u043D\u0435\u0442. \u0412\u043C\u0435\u0441\u0442\u043E \u044D\u0442\u043E\u0433\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0435 \u0441 \u0434\u043E\u043F\u043E\u043B\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u043C\u0438 \u0441\u043B\u043E\u0432\u0430\u043C\u0438, \u0443\u043A\u0430\u0437\u044B\u0432\u0430\u044E\u0449\u0438\u043C\u0438 \u043D\u0430 \u0442\u043E, \u0447\u0442\u043E \u0441\u043E\u0431\u044B\u0442\u0438\u0435 \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0434\u0438\u0442 \u043D\u0435 \u0441\u0435\u0439\u0447\u0430\u0441, \u0430 \u0437\u0430\u0432\u0442\u0440\u0430, \u043F\u043E\u0441\u043B\u0435\u0437\u0430\u0432\u0442\u0440\u0430, \u043D\u0430 \u0441\u043B\u0435\u0434\u0443\u044E\u0449\u0435\u0439 \u043D\u0435\u0434\u0435\u043B\u0435 \u0438 \u0442.\u043F. .\n\u041B\u043E\u0433\u0438\u0447\u043D\u043E, \u0447\u0442\u043E \u043A\u043E\u0433\u0434\u0430 \u043D\u0430\u043C \u043D\u0443\u0436\u043D\u043E \u043F\u0435\u0440\u0435\u0432\u0435\u0441\u0442\u0438 \u043D\u0435\u0437\u043D\u0430\u043A\u043E\u043C\u043E\u0435 \u0441\u043B\u043E\u0432\u043E, \u0432 \u043D\u0430\u0448\u0435\u043C \u0441\u043B\u0443\u0447\u0430\u0435 \u0433\u043B\u0430\u0433\u043E\u043B, \u043C\u044B \u0441\u043C\u043E\u0442\u0440\u0438\u043C \u0432 \u0441\u043B\u043E\u0432\u0430\u0440\u0435. \u0414\u043E\u043F\u0443\u0441\u0442\u0438\u043C \u043F\u0440\u0438 \u0438\u0437\u0443\u0447\u0435\u043D\u0438\u0438 \u0430\u043D\u0433\u043B\u0438\u0439\u0441\u043A\u043E\u0433\u043E, \u043C\u044B \u043D\u0435 \u0437\u043D\u0430\u0435\u043C \u0441\u043B\u043E\u0432\u043E \u0438\u043C\u0435\u0442\u044C, \u0441\u043C\u043E\u0442\u0440\u0438\u043C \u0432 \u0441\u043B\u043E\u0432\u0430\u0440\u0435, \u0433\u0434\u0435 \u0431\u0443\u0434\u0435\u0442 \u0443\u043A\u0430\u0437\u0430\u043D\u043E, \u0447\u0442\u043E \u0438\u043C\u0435\u0442\u044C \u044D\u0442\u043E \xABto have\xBB. \u0415\u0441\u043B\u0438 \u0436\u0435 \u043C\u044B \u0437\u0430\u0433\u043B\u044F\u043D\u0435\u043C \u0432 \u0441\u043B\u043E\u0432\u0430\u0440\u044C \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430, \u043A\u043E\u043D\u0435\u0447\u043D\u043E \u0435\u0441\u043B\u0438 \u044D\u0442\u043E \u0445\u043E\u0440\u043E\u0448\u0438\u0439 \u0441\u043B\u043E\u0432\u0430\u0440\u044C (\u043E\u0431 \u044D\u0442\u043E\u043C \u043F\u043E\u0433\u043E\u0432\u043E\u0440\u0438\u043C \u043D\u0438\u0436\u0435), \u043D\u0430 \u0441\u043B\u043E\u0432\u043E \u0438\u043C\u0435\u0442\u044C \u0443 \u043D\u0430\u0441 \u0431\u0443\u0434\u0435\u0442 \u0443\u043A\u0430\u0437\u0430\u043D\u043E 2, \u0430 \u0442\u043E \u0438 3 \u0444\u043E\u0440\u043C\u044B \u0441\u043B\u043E\u0432\u0430. \u0422\u0430\u043A \u0447\u0442\u043E \u044D\u0442\u043E \u0437\u0430 \u043C\u0430\u0433\u0438\u0447\u0435\u0441\u043A\u0438\u0435 \u0444\u043E\u0440\u043C\u044B.\n\u0412\u0441\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u044B \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430 \u0441\u043B\u0435\u0434\u0443\u0435\u0442 \u0443\u0447\u0438\u0442\u044C \u0432 3\u0445 \u0444\u043E\u0440\u043C\u0430\u0445:  -MA-\u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432, -DA-\u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432, \u0438 \u0444\u043E\u0440\u043C\u0430 3\u0433\u043E \u043B\u0438\u0446\u0430 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438. \u041A\u0430\u0436\u0434\u0430\u044F \u0438\u0437 \u043D\u0438\u0445 \u043D\u0430\u043C \u043F\u0440\u0438\u0433\u043E\u0434\u0438\u0442\u0441\u044F \u0432 \u0431\u0443\u0434\u0443\u0449\u0435\u043C \u0434\u043B\u044F \u0441\u043B\u043E\u0432\u043E\u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F, \u043F\u043E\u044D\u0442\u043E\u043C\u0443 \u0443\u0436\u0435 \u0441 \u043F\u0435\u0440\u0432\u044B\u0445 \u0443\u0440\u043E\u043A\u043E\u0432 \u0437\u0430\u043F\u043E\u043C\u0438\u043D\u0430\u0435\u043C \u0433\u043B\u0430\u0433\u043E\u043B\u044B \u0438\u043C\u0435\u043D\u043D\u043E \u0442\u0430\u043A.  \n-MA \u2014 \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432 \u043B\u0435\u0433\u043A\u043E \u0440\u0430\u0441\u043F\u043E\u0437\u043D\u0430\u0442\u044C, \u043E\u043D \u0432\u0441\u0435\u0433\u0434\u0430 \u0437\u0430\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u043D\u0430 \u041C\u0410\n-DA \u2014 \u043C\u043E\u0436\u0435\u0442 \u0437\u0430\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0442\u044C\u0441\u044F \u043D\u0430 \u2013DA, -TA \u0438 \u2013A\nNB!! \u041E\u0431\u0435 \u0444\u043E\u0440\u043C\u044B \u043D\u0430 \u0440\u0443\u0441\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u044F\u0442\u0441\u044F \u043E\u0434\u0438\u043D\u0430\u043A\u043E\u0432\u043E, \u0438 \u044F\u0432\u043B\u044F\u044E\u0442\u0441\u044F \u0434\u043B\u044F \u043D\u0430\u0441 \u0440\u0430\u0432\u043D\u043E\u0441\u0438\u043B\u044C\u043D\u044B\u043C\u0438 2\u043C\u044F \u043D\u0430\u0447\u0430\u043B\u044C\u043D\u044B\u043C\u0438 \u0444\u043E\u0440\u043C\u0430\u043C\u0438.\n-\u0444\u043E\u0440\u043C\u0430 \u0442\u0440\u0435\u0442\u044C\u0435\u0433\u043E \u043B\u0438\u0446\u0430 \u0435\u0434. \u0447\u0438\u0441\u043B\u0430 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u043F\u043E\u043A\u0430\u0437\u044B\u0432\u0430\u0435\u0442 \u043D\u0430\u043C, \u043A\u0430\u043A \u0431\u0443\u0434\u0435\u0442 \u0432\u044B\u0433\u043B\u044F\u0434\u0435\u0442\u044C \u0433\u043B\u0430\u0433\u043E\u043B \u043F\u0440\u0438 \u0438\u0437\u043C\u0435\u043D\u0435\u043D\u0438\u0438 \u0435\u0433\u043E \u043F\u043E \u043B\u0438\u0446\u0430\u043C \u0438 \u0447\u0438\u0441\u043B\u0430\u043C \u0432 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u043C \u0432\u0440\u0435\u043C\u0435\u043D\u0438, \u0442.\u0435 \u0432\u043C\u0435\u0441\u0442\u043E \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u044F -b, \u043A\u043E\u0442\u043E\u0440\u043E\u0435 \u044F\u0432\u043B\u044F\u0435\u0442\u0441\u044F \u043F\u0440\u0438\u0437\u043D\u0430\u043A\u043E\u043C \u0438\u043C\u0435\u043D\u043D\u043E\u0433\u043E \u044D\u0442\u043E\u0433\u043E 3\u0433\u043E \u043B\u0438\u0446\u0430, \u043F\u043E\u0434\u0441\u0442\u0430\u0432\u043B\u044F\u0435\u043C \u043D\u0443\u0436\u043D\u043E\u0435 \u043D\u0430\u043C.\n\n#### \u041F\u0440\u0438\u043C\u0435\u0440 / N\xE4ide:\n\n\u0413\u043B\u0430\u0433\u043E\u043B \u043B\u044E\u0431\u0438\u0442\u044C:  armastaMA, armastaDA, armastaB \u2014 \u043B\u044E\u0431\u0438\u0442\u044C, \u043B\u044E\u0431\u0438\u0442\u044C, \u043E\u043D/\u043E\u043D\u0430 \u043B\u044E\u0431\u0438\u0442.\n\n### \u0413\u043B\u0430\u0433\u043E\u043B \u0411\u042B\u0422\u042C\n### OLEMA-OLLA-ON\n\n\u042D\u0442\u043E \u0433\u043B\u0430\u0432\u043D\u043E\u0435 \u043E\u0442\u043B\u0438\u0447\u0438\u0435 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430 \u043E\u0442 \u0440\u0443\u0441\u0441\u043A\u043E\u0433\u043E. \u041F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0435 \u0431\u0435\u0437 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u043D\u0435 \u043C\u043E\u0436\u0435\u0442 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u043E\u0432\u0430\u0442\u044C, \u043F\u043E\u044D\u0442\u043E\u043C\u0443 \u0435\u0441\u043B\u0438 \u043C\u044B \u043D\u0435 \u0443\u043F\u043E\u0442\u0440\u0435\u0431\u043B\u044F\u0435\u043C \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0438 \u043E\u0431\u044B\u0447\u043D\u044B\u0439 \u0433\u043B\u0430\u0433\u043E\u043B, \u0442\u043E \u043D\u0443\u0436\u043D\u043E \u043F\u043E\u0441\u0442\u0430\u0432\u0438\u0442\u044C  \u0411\u042B\u0422\u042C-\u0415\u0421\u0422\u042C. \u0410\u043D\u0430\u043B\u043E\u0433\u0438\u044F \u0441 \u0430\u043D\u0433\u043B\u0438\u0439\u0441\u043A\u0438\u043C  \xABTO BE\xBB. \u041D\u043E \u043D\u0435 \u0442\u043E\u043B\u044C\u043A\u043E \u0432 \u044D\u0442\u043E\u043C \u0437\u0430\u043A\u043B\u044E\u0447\u0430\u0435\u0442\u0441\u044F \u043E\u0441\u043E\u0431\u0435\u043D\u043D\u043E\u0433\u043E \u044D\u0442\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430. \u0413\u043B\u0430\u0433\u043E\u043B OLEMA  \u044F\u0432\u043B\u044F\u0435\u0442\u0441\u044F \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u0435\u043C, \u0438\u0437-\u0437\u0430 \u0442\u043E\u0433\u043E \u0447\u0442\u043E \u0435\u0433\u043E \u0441\u043F\u0440\u044F\u0436\u0435\u043D\u0438\u0435, \u0438\u0437\u043C\u0435\u043D\u0435\u043D\u0438\u0435 \u043F\u043E \u043B\u0438\u0446\u0430\u043C \u0438 \u0447\u0438\u0441\u043B\u0430\u043C, \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0434\u0438\u0442 \u043D\u0435 \u0442\u0430\u043A, \u043A\u0430\u043A \u0443 \u0432\u0441\u0435\u0445 \u043E\u0441\u0442\u0430\u043B\u044C\u043D\u044B\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430. \u0418\u0441\u043A\u043B\u044E\u0447\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0439 \u0437\u0434\u0435\u0441\u044C \u0432\u044B\u0441\u0442\u0443\u043F\u0438\u0442 \u0444\u043E\u0440\u043C\u0430 3\u0433\u043E \u043B\u0438\u0446\u0430 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0438 \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430,  \u0438 \u0442\u0430\u043A \u043A\u0430\u043A \u044D\u0442\u043E\u0442 \u0433\u043B\u0430\u0433\u043E\u043B \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u044B\u0439 \u0432 \u0441\u0432\u043E\u0435\u043C \u0440\u043E\u0434\u0435, \u0440\u0430\u0441\u0441\u043C\u043E\u0442\u0440\u0438\u043C \u0435\u0433\u043E \u0441\u043F\u0440\u044F\u0436\u0435\u043D\u0438\u0435 \u0432 \u043F\u0435\u0440\u0432\u0443\u044E \u043E\u0447\u0435\u0440\u0435\u0434\u044C:\n\n<table>\n<tr>\n<th>\u041B\u0438\u0446\u043E</th><th>\u0415\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E<br>Ainsus</th><th>\u041C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E<br>Mitmus</th>\n</tr>\n<tr>\n<td>1</td><td>Mina oleN inimene<br>\u042F \u0435\u0441\u0442\u044C \u0447\u0435\u043B\u043E\u0432\u0435\u043A</td><td>Meie oleME inimesed<br>\u041C\u044B \u0435\u0441\u0442\u044C \u043B\u044E\u0434\u0438</td>\n</tr>\n<tr>\n<td>2</td><td>Sina oleD inimene<br>\u0422\u044B \u0435\u0441\u0442\u044C \u0447\u0435\u043B\u043E\u0432\u0435\u043A</td><td>Teie oleTE inimesed<br>\u0412\u044B \u0435\u0441\u0442\u044C \u043B\u044E\u0434\u0438</td>\n</tr>\n<tr>\n<td>3</td><td>Tema ON inimene<br>\u041E\u043D/\u041E\u043D\u0430 \u0435\u0441\u0442\u044C \u0447\u0435\u043B\u043E\u0432\u0435\u043A</td><td>Nad ON inimesed<br>\u041E\u043D\u0438 \u0435\u0441\u0442\u044C \u043B\u044E\u0434\u0438</td>\n</tr>\n</table>\n\n\u041A\u043E\u043D\u0435\u0447\u043D\u043E, \u043D\u0430 \u0440\u0443\u0441\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u043C\u044B \u043D\u0435 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u043C \xAB\u043C\u044B \u0435\u0441\u0442\u044C \u043B\u044E\u0434\u0438\xBB, \u0441\u043A\u0430\u0436\u0435\u043C \u043F\u0440\u043E\u0441\u0442\u043E \xAB\u043C\u044B \u043B\u044E\u0434\u0438\xBB, \u043D\u043E \u043F\u043E\u043A\u0430, \u0447\u0442\u043E\u0431\u044B \u043F\u0440\u0438\u0432\u044B\u043A\u043D\u0443\u0442\u044C \u0438 \u043D\u0435 \u0437\u0430\u0431\u044B\u0432\u0430\u0442\u044C \u0441\u0442\u0430\u0432\u0438\u0442\u044C \u0433\u043B\u0430\u0433\u043E\u043B OLEMA  \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0438,  \u0440\u0435\u043A\u043E\u043C\u0435\u043D\u0434\u0443\u044E \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u044C  \u0438\u043C\u0435\u043D\u043D\u043E \u0432 \u0442\u0430\u043A\u043E\u0439 \u043C\u0430\u043D\u0435\u0440\u0435.\n\u0413\u043B\u0430\u0433\u043E\u043B \xAB\u0431\u044B\u0442\u044C\xBB \u0412\u0430\u043C \u043F\u043E\u043C\u043E\u0436\u0435\u0442 \u0443\u0436\u0435 \u0441\u0444\u043E\u0440\u043C\u0438\u0440\u043E\u0432\u0430\u0442\u044C \u0441\u0432\u043E\u0438 \u043F\u0435\u0440\u0432\u044B\u0435 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F \u043D\u0430 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435. \u0422\u0435\u043F\u0435\u0440\u044C \u0412\u044B \u0441\u043C\u043E\u0436\u0435\u0442\u0435 \u043D\u0435 \u0442\u043E\u043B\u044C\u043A\u043E \u043F\u043E\u043F\u0440\u0438\u0432\u0435\u0442\u0441\u0442\u0432\u043E\u0432\u0430\u0442\u044C \u0441\u043E\u0431\u0435\u0441\u0435\u0434\u043D\u0438\u043A\u0430, \u043D\u043E \u0438 \u043F\u0440\u0435\u0434\u0441\u0442\u0430\u0432\u0438\u0442\u044C\u0441\u044F.\n\n##### \u041F\u0440\u0438\u043C\u0435\u0440 / N\xE4ide:\n\nTere! Mina olen Aleksandra. Mina olen \xF5petaja. \u0417\u0434\u0440\u0430\u0432\u0441\u0442\u0432\u0443\u0439\u0442\u0435! \u042F \u0410\u043B\u0435\u043A\u0441\u0430\u043D\u0434\u0440\u0430. \u042F \u044F\u0432\u043B\u044F\u044E\u0441\u044C \u043F\u0440\u0435\u043F\u043E\u0434\u0430\u0432\u0430\u0442\u0435\u043B\u0435\u043C.\nTema on \xF5pilane, aga nemad on \xFCli\xF5pilased. \u041E\u043D \u0443\u0447\u0435\u043D\u0438\u043A, \u0430 \u043E\u043D\u0438 \u0441\u0442\u0443\u0434\u0435\u043D\u0442\u044B.\n\n### \u041E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 OLEMA\n\n\u0421\u0440\u0430\u0437\u0443 \u0441\u043A\u0430\u0436\u0443, \u0447\u0442\u043E \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442\u0441\u044F \u0434\u043B\u044F \u0432\u0441\u0435\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u043E\u0434\u0438\u043D\u0430\u043A\u043E\u0432\u044B\u043C \u043E\u0431\u0440\u0430\u0437\u043E\u043C. \u041D\u043E \u0442\u0430\u043A \u043A\u0430\u043A \u043C\u044B \u0441 \u0412\u0430\u043C\u0438 \u0432\u0437\u044F\u043B\u0438, \u043F\u043E\u043A\u0430 \u0441\u0447\u0438\u0442\u0430\u0439 \u0442\u043E\u043B\u044C\u043A\u043E \u0433\u043B\u0430\u0433\u043E\u043B \xAB\u0431\u044B\u0442\u044C\xBB, \u0442\u043E \u0440\u0430\u0441\u0441\u043C\u043E\u0442\u0440\u0438\u043C \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u0435 \u043E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u044F \u0438\u043C\u0435\u043D\u043D\u043E \u043D\u0430 \u0435\u0433\u043E \u043F\u0440\u0438\u043C\u0435\u0440\u0435. \u0427\u0442\u043E \u0436\u0435 \u043D\u0430\u043C \u0434\u043B\u044F \u044D\u0442\u043E\u0433\u043E \u043F\u043E\u0442\u0440\u0435\u0431\u0443\u0435\u0442\u0441\u044F:\n\u0411\u0435\u0440\u0435\u043C \u0433\u043B\u0430\u0433\u043E\u043B \u0432 \u043F\u0435\u0440\u0432\u043E\u043C \u043B\u0438\u0446\u0435 \u0435\u0434.\u0447, \u0443\u0431\u0438\u0440\u0430\u0435\u043C \u043F\u0440\u0438\u0441\u0443\u0449\u0435\u0435 \u0435\u043C\u0443 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 \u2013N. \u041F\u043E\u043B\u0443\u0447\u0430\u0435\u043C \u0442\u0430\u043A \u0441\u043A\u0430\u0437\u0430\u0442\u044C \u043E\u0441\u043D\u043E\u0432\u0443 \u043F\u0435\u0440\u0432\u043E\u0433\u043E \u043B\u0438\u0446\u0430. \u041F\u0435\u0440\u0435\u0434 \u043F\u043E\u043B\u0443\u0447\u0438\u0432\u0448\u0438\u043C\u0441\u044F \u0441\u043B\u043E\u0432\u043E\u043C \u043F\u0438\u0448\u0435\u043C \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0443\u044E \u0447\u0430\u0441\u0442\u0438\u0446\u0443 EI \u2014 \xAB\u043D\u0435, \u043D\u0435\u0442\xBB, \u0438 \u044D\u0442\u043E \u0431\u0443\u0434\u0435\u0442 \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430 \u0433\u043B\u0430\u0433\u043E\u043B\u0430, \u0434\u0435\u0439\u0441\u0442\u0432\u0443\u044E\u0449\u0430\u044F \u0434\u043B\u044F \u0432\u0441\u0435\u0445 \u043B\u0438\u0446 \u0438 \u0447\u0438\u0441\u0435\u043B.\n\n##### \u041F\u0440\u0438\u043C\u0435\u0440 / N\xE4ide:\n\nOLEMA-OLLA-ON, \u0444\u043E\u0440\u043C\u0430 \u043F\u0435\u0440\u0432\u043E\u0433\u043E \u043B\u0438\u0446\u0430 \xAB\u044F \u0435\u0441\u0442\u044C\xBB \u2014 oleN, \u0443\u0431\u0438\u0440\u0430\u0435\u043C \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 \u0438 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0435\u043C \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0443\u044E \u0447\u0430\u0441\u0442\u0438\u0446\u0443 \xABei\xBB.\n\n<table>\n    <tr>\n        <td>Mina</td>\n        <td rowspan="6">+ ei ole</td>\n    </tr>\n    <tr>\n        <td>Sina</td>\n    </tr>\n    <tr>\n        <td>Tema</td>\n    </tr>\n    <tr>\n        <td>Meie</td>\n    </tr>\n    <tr>\n        <td>Teie</td>\n    </tr>\n    <tr>\n        <td>Nemad</td>\n    </tr>\n</table>\n\nNB! \u042F \u0443\u0436\u0435 \u0433\u043E\u0432\u043E\u0440\u0438\u043B\u0430, \u0447\u0442\u043E \u0433\u043B\u0430\u0433\u043E\u043B \u0431\u044B\u0442\u044C \u044F\u0432\u043B\u044F\u0435\u0442\u0441\u044F \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u0435\u043C. \u0422\u0430\u043A \u0432\u043E\u0442, \u0438 \u0432 \u0444\u043E\u0440\u043C\u0435 \u043E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u044F \u043E\u043D \u0442\u043E\u0436\u0435 \u0443\u0441\u043F\u0435\u043B \u043E\u0442\u043B\u0438\u0447\u0438\u0442\u044C\u0441\u044F. \u042D\u0442\u043E \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u044B\u0439 \u0433\u043B\u0430\u0433\u043E\u043B \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430, \u0443 \u043A\u043E\u0442\u043E\u0440\u043E\u0433\u043E \u0435\u0441\u0442\u044C 2 \u0444\u043E\u0440\u043C\u044B \u043E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u044F. \u0423\u0436\u0435 \u0438\u0437\u0432\u0435\u0441\u0442\u043D\u0430\u044F \u043D\u0430\u043C \u0444\u043E\u0440\u043C\u0430 ei ole \u0438\u043C\u0435\u0435\u0442 \u043A\u043E\u0440\u043E\u0442\u043A\u0443\u044E \u0444\u043E\u0440\u043C\u0443 pole \u0440\u0430\u0432\u043D\u043E\u0437\u043D\u0430\u0447\u043D\u0443\u044E \u0435\u0439.\n\n##### \u041F\u0440\u0438\u043C\u0435\u0440 / N\xE4ide:\n\nMina ei ole naine, mina olen mees. \u042F \u043D\u0435 \u0436\u0435\u043D\u0449\u0438\u043D\u0430, \u044F \u043C\u0443\u0436\u0447\u0438\u043D\u0430.\nTa pole eestlane, ta on venelane. \u041E\u043D \u043D\u0435 \u044D\u0441\u0442\u043E\u043D\u0435\u0446, \u043E\u043D \u0440\u0443\u0441\u0441\u043A\u0438\u0439.\n\n### \u0423\u043A\u0430\u0437\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u043C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u0435 \xABSEE\xBB\n\n\u0427\u0442\u043E\u0431\u044B \u043D\u0435\u043C\u043D\u043E\u0433\u043E \u0440\u0430\u0441\u0448\u0438\u0440\u0438\u0442\u044C \u043D\u0430\u0448 \u0441\u043B\u043E\u0432\u0430\u0440\u043D\u044B\u0439 \u0437\u0430\u043F\u0430\u0441, \u0438 \u043D\u0430\u0447\u0430\u0442\u044C \u0433\u043E\u0432\u043E\u0440\u0438\u0442\u044C \u0443\u0436\u0435 \u0441 \u043F\u0435\u0440\u0432\u044B\u0445 \u0443\u0440\u043E\u043A\u043E\u0432, \u0432\u044B\u0443\u0447\u0438\u043C \u0435\u0449\u0435 \u043E\u0434\u043D\u043E \u043E\u0447\u0435\u043D\u044C \u0438\u043D\u0442\u0435\u0440\u0435\u0441\u043D\u043E\u0435 \u043C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u0435. See \u2014 \u044D\u0442\u043E \u0443\u043A\u0430\u0437\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u043C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u0435, \u043D\u0430 \u0440\u0443\u0441\u0441\u043A\u0438\u0439 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u0441\u044F \u043A\u0430\u043A \xAB\u044D\u0442\u043E, \u044D\u0442\u043E\u0442, \u044D\u0442\u0430, \u0442\u043E, \u0442\u043E\u0442, \u0442\u0430\xBB. \u0418\u043D\u0442\u0435\u0440\u0435\u0441\u043D\u043E \u043E\u043D\u043E \u0442\u0435\u043C, \u0447\u0442\u043E \u0440\u043E\u0434 \u0438 \u043C\u0435\u0441\u0442\u043E\u043F\u043E\u043B\u043E\u0436\u0435\u043D\u0438\u0435 \u0443\u043A\u0430\u0437\u0430\u043D\u043D\u043E\u0433\u043E \u043F\u0440\u0435\u0434\u043C\u0435\u0442\u0430 \u0431\u0443\u0434\u0435\u0442 \u043F\u043E\u043D\u044F\u0442\u043D\u043E \u0443\u0436\u0435 \u0438\u0437 \u043A\u043E\u043D\u0442\u0435\u043A\u0441\u0442\u0430. \n\n##### \u041F\u0440\u0438\u043C\u0435\u0440 / N\xE4ide:\n\nSee on hea \xF5pilane. \u042D\u0442\u043E \u0445\u043E\u0440\u043E\u0448\u0438\u0439 \u0443\u0447\u0435\u043D\u0438\u043A.\nSee t\xFCdruk on ilus. \u042D\u0442\u0430 \u0434\u0435\u0432\u043E\u0447\u043A\u0430 \u043A\u0440\u0430\u0441\u0438\u0432\u0430\u044F.\nSee kaunis naine ei ole vana. \u042D\u0442\u0430 \u043F\u0440\u0435\u043A\u0440\u0430\u0441\u043D\u0430\u044F \u0436\u0435\u043D\u0449\u0438\u043D\u0430 \u043D\u0435 \u0441\u0442\u0430\u0440\u0430\u044F.\nSee on noor poiss. \u042D\u0442\u043E \u043C\u043E\u043B\u043E\u0434\u043E\u0439 \u043F\u0430\u0440\u0435\u043D\u044C.\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('lesson',{number:2}),source:source});}));

