var source='\n### \u0421\u043F\u0440\u044F\u0436\u0435\u043D\u0438\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438\n### Tegus\xF5nade p\xF6\xF6ramine olevikus\n\n\u041D\u0430 \u043D\u0430\u0448\u0435\u043C \u0432\u0442\u043E\u0440\u043E\u043C \u0437\u0430\u043D\u044F\u0442\u0438\u0438 \u043C\u044B \u043F\u0440\u043E\u0434\u043E\u043B\u0436\u0438\u043C \u0437\u043D\u0430\u043A\u043E\u043C\u0438\u0442\u044C\u0441\u044F \u0441 \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C\u0438 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430. \u0414\u0430, \u043C\u044B \u043F\u043E\u043A\u0430 \u043D\u0435 \u043F\u0435\u0440\u0435\u043A\u043B\u044E\u0447\u0430\u0435\u043C\u0441\u044F \u043D\u0430 \u0434\u0440\u0443\u0433\u0438\u0435 \u0447\u0430\u0441\u0442\u0438 \u0440\u0435\u0447\u0438, \u0442\u0430\u043A \u043A\u0430\u043A \u0441\u043D\u0430\u0447\u0430\u043B\u0430 \u043D\u0430\u043C \u043D\u0443\u0436\u043D\u043E \u0434\u043E \u043A\u043E\u043D\u0446\u0430 \u0440\u0430\u0437\u043E\u0431\u0440\u0430\u0442\u044C\u0441\u044F \u0441 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u043C, \u0432\u0435\u0434\u044C \u0438\u043C\u0435\u043D\u043D\u043E \u043D\u0430 \u043D\u0435\u043C \u043B\u0435\u0436\u0438\u0442 \u043F\u0440\u043E\u0446\u0435\u043D\u0442\u043E\u0432 80 \u0443\u0441\u043F\u0435\u0445\u0430 \u0441\u043A\u0430\u0437\u0430\u043D\u043D\u043E\u0433\u043E \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F! \u0418 \u0435\u0441\u043B\u0438 \u043C\u044B \u043F\u0440\u0430\u0432\u0438\u043B\u044C\u043D\u043E \u0437\u0430\u0434\u0430\u0434\u0438\u043C \u0442\u043E\u043D \u0433\u043B\u0430\u0433\u043E\u043B\u0443 \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0435 \u2014 \u044F \u0432\u043E\u0437\u044C\u043C\u0443, \u0438\u043B\u0438 \u0434\u0430\u0439\u0442\u0435 \u043C\u043D\u0435, \u044F \u043D\u0435 \u0445\u043E\u0447\u0443 \u0438 \u0442.\u0434., \u0430  \u0434\u0430\u043B\u044C\u0448\u0435 \u043C\u044B \u043C\u043E\u0436\u0435\u043C \u0445\u043E\u0442\u044F \u043D\u0430 \u043F\u0430\u043B\u044C\u0446\u0430\u0445 \u043F\u043E\u0441\u0442\u0430\u0440\u0430\u0442\u044C\u0441\u044F \u043E\u0431\u044A\u044F\u0441\u043D\u044F\u0442\u044C, \u043F\u0440\u043E \u043A\u0430\u043A\u043E\u0439 \u043F\u0440\u0435\u0434\u043C\u0435\u0442 \u0438\u043B\u0438 \u043F\u0440\u043E \u043A\u043E\u0433\u043E \u0438\u0434\u0435\u0442 \u0440\u0435\u0447\u044C. \u0422\u0430\u043A \u0447\u0442\u043E \u043F\u0440\u043E\u0434\u043E\u043B\u0436\u0430\u0435\u043C \u0440\u0430\u0431\u043E\u0442\u0430\u0442\u044C \u0441 \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C\u0438 \u0441\u0435\u0433\u043E\u0434\u043D\u044F!\n\n\u041D\u0435 \u0443\u0441\u0442\u0430\u043D\u0443 \u043D\u0430\u043F\u043E\u043C\u0438\u043D\u0430\u0442\u044C, \u0447\u0442\u043E \u0432\u0441\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u044B \u043C\u044B \u0431\u0443\u0434\u0435\u043C \u0443\u0447\u0438\u0442\u044C \u0432 3\u0445 \u0444\u043E\u0440\u043C\u0430\u0445: -MA \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432, -DA \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432 \u0438 \u0444\u043E\u0440\u043C\u0443 3\u0433\u043E \u043B\u0438\u0446\u0430 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438 (\u0442\u043E \u0435\u0441\u0442\u044C \u043E\u043D/\u043E\u043D\u0430 \u0447\u0442\u043E-\u0442\u043E \u0434\u0435\u043B\u0430\u0435\u0442). -MA \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432 \u0432\u0441\u0435\u0433\u0434\u0430 \u0437\u0430\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u043D\u0430 \u041C\u0410, -DA  \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432 \u043C\u043E\u0436\u0435\u0442 \u0437\u0430\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0442\u044C\u0441\u044F \u043D\u0430 -DA, -TA \u0438 -A. \u041E\u0431\u0435 \u0444\u043E\u0440\u043C\u044B \u043D\u0430 \u0440\u0443\u0441\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u044F\u0442\u0441\u044F \u043E\u0434\u0438\u043D\u0430\u043A\u043E\u0432\u043E.  \u0410 \u0432\u043E\u0442 \u043F\u0440\u043E 3\u0435 \u043B\u0438\u0446\u043E, \u0434\u0430 \u0438 \u0432 \u043F\u0440\u0438\u043D\u0446\u0438\u043F\u0435 \u043F\u0440\u043E \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0435 \u0432\u0440\u0435\u043C\u044F, \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u0441\u0442\u043E\u0438\u0442 \u043F\u043E\u0433\u043E\u0432\u043E\u0440\u0438\u0442\u044C \u043E\u0442\u0434\u0435\u043B\u044C\u043D\u043E.\n\u041F\u0435\u0440\u0432\u044B\u0439 \u0443\u0440\u043E\u043A \u043C\u044B \u043F\u043E\u0441\u0432\u0435\u0442\u0438\u043B\u0438 \u0433\u043B\u0430\u0433\u043E\u043B\u0443 OLEMA, \u0442\u0430\u043A \u043A\u0430\u043A \u043E\u043D \u0438\u0441\u043A\u043B\u044E\u0447\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u043C \u043E\u0431\u0440\u0430\u0437\u043E\u043C \u0441\u043F\u0440\u044F\u0433\u0430\u0435\u0442\u0441\u044F \u0432 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u043C \u0432\u0440\u0435\u043C\u0435\u043D\u0438. \u0422\u043E\u0447\u043D\u0435\u0435 \u0441\u043A\u0430\u0437\u0430\u0442\u044C, \u043E\u0441\u043E\u0431\u0435\u043D\u043D\u044B\u043C\u0438 \u0431\u0443\u0434\u0443\u0442 \u043A\u0430\u043A \u0440\u0430\u0437 \u0444\u043E\u0440\u043C\u044B 3\u0433\u043E \u043B\u0438\u0446\u0430. \u0412\u0441\u043F\u043E\u043C\u043D\u0438\u043C: Mina oleN, Sina oleD, Tema ON, Meie oleME, Teie oleTE, Nad ON\n\u0412\u0441\u0435 \u0436\u0435 \u043E\u0441\u0442\u0430\u043B\u044C\u043D\u044B\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u044B \u0438\u0437\u043C\u0435\u043D\u044F\u044E\u0442\u0441\u044F \u043F\u043E \u043E\u0434\u043D\u043E\u0439 \u0438 \u0442\u043E\u0439 \u0436\u0435 \u0441\u0445\u0435\u043C\u0435. \u041D\u043E \u043F\u043E\u043C\u043D\u0438\u043C, \u043F\u043E\u043C\u043E\u0436\u0435\u0442 \u043D\u0430\u043C \u0432 \u044D\u0442\u043E\u043C 3\u044F \u0444\u043E\u0440\u043C\u0430 \u0433\u043B\u0430\u0433\u043E\u043B, \u043A\u043E\u0442\u043E\u0440\u0443\u044E \u043C\u044B \u0443\u0447\u0438\u043C. \u0423\u0431\u0438\u0440\u0430\u0435\u043C \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -b  \u0438 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0435\u043C \u043D\u0443\u0436\u043D\u043E\u0435 \u043D\u0430\u043C, \u0432 \u0437\u0430\u0432\u0438\u0441\u0438\u043C\u043E\u0441\u0442\u0438 \u043E\u0442 \u043B\u0438\u0446\u0430 \u0438 \u0447\u0438\u0441\u043B\u0430. \u0414\u043B\u044F \u043F\u0440\u0438\u043C\u0435\u0440\u0430 \u0432\u043E\u0437\u044C\u043C\u0435\u043C \u0433\u043B\u0430\u0433\u043E\u043B \u0433\u043E\u0432\u043E\u0440\u0438\u0442\u044C: R\xC4\xC4KIMA-R\xC4\xC4KIDA-R\xC4\xC4GIB\n\n<table>\n<tr>\n<th>\u041B\u0438\u0446\u043E</th><th>\u0415\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E<br>Ainsus</th><th>\u041C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E<br>Mitmus</th>\n</tr>\n<tr>\n<td>1</td><td>Mina r\xE4\xE4giN<br>\u042F \u0433\u043E\u0432\u043E\u0440\u044E</td><td>Meie r\xE4\xE4giME<br>\u041C\u044B \u0433\u043E\u0432\u043E\u0440\u0438\u043C</td>\n</tr>\n<tr>\n<td>2</td><td>Sina r\xE4\xE4giD<br>\u0422\u044B \u0433\u043E\u0432\u043E\u0440\u0438\u0448\u044C</td><td>Teie r\xE4\xE4giTE<br>\u0412\u044B \u0433\u043E\u0432\u043E\u0440\u0438\u0442\u0435</td>\n</tr>\n<tr>\n<td>3</td><td>Tema r\xE4\xE4giB<br>\u041E\u043D/\u041E\u043D\u0430 \u0433\u043E\u0432\u043E\u0440\u0438\u0442</td><td>Nad r\xE4\xE4giVAD<br>\u041E\u043D\u0438 \u0433\u043E\u0432\u043E\u0440\u044F\u0442</td>\n</tr>\n</table>\n\n\u041E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430 \u0433\u043B\u0430\u0433\u043E\u043B\u0430.\nOleviku eitav vorm.\n\u041E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442\u0441\u044F \u0434\u043B\u044F \u0432\u0441\u0435\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u043E\u0434\u0438\u043D\u0430\u043A\u043E\u0432\u044B\u043C \u043E\u0431\u0440\u0430\u0437\u043E\u043C. \n\u0411\u0435\u0440\u0435\u043C \u0433\u043B\u0430\u0433\u043E\u043B \u0432 \u043F\u0435\u0440\u0432\u043E\u043C \u043B\u0438\u0446\u0435 \u0435\u0434.\u0447. \u0438 \u0443\u0431\u0438\u0440\u0430\u0435\u043C \u043F\u0440\u0438\u0441\u0443\u0449\u0435\u0435 \u0435\u043C\u0443 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 \u2013N. \u041F\u043E\u043B\u0443\u0447\u0430\u0435\u043C \u0442\u0430\u043A \u0441\u043A\u0430\u0437\u0430\u0442\u044C \u043E\u0441\u043D\u043E\u0432\u0443 \u043F\u0435\u0440\u0432\u043E\u0433\u043E \u043B\u0438\u0446\u0430. \u041F\u0435\u0440\u0435\u0434 \u043F\u043E\u043B\u0443\u0447\u0438\u0432\u0448\u0438\u043C\u0441\u044F \u0441\u043B\u043E\u0432\u043E\u043C \u043F\u0438\u0448\u0435\u043C \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0443\u044E \u0447\u0430\u0441\u0442\u0438\u0446\u0443 EI, \u0438 \u044D\u0442\u043E \u0431\u0443\u0434\u0435\u0442 \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430 \u0433\u043B\u0430\u0433\u043E\u043B\u0430, \u0434\u0435\u0439\u0441\u0442\u0432\u0443\u044E\u0449\u0430\u044F \u0434\u043B\u044F \u0432\u0441\u0435\u0445 \u043B\u0438\u0446 \u0438 \u0447\u0438\u0441\u0435\u043B.\n\u0424\u043E\u0440\u043C\u0430 \u043F\u0435\u0440\u0432\u043E\u0433\u043E \u043B\u0438\u0446\u0430 \u0434\u043B\u044F \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u0433\u043E\u0432\u043E\u0440\u0438\u0442\u044C \u2014 r\xE4\xE4giN. \u0423\u0431\u0440\u0430\u0432 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 \u2013N \u043F\u043E\u043B\u0443\u0447\u0430\u0435\u043C \u043E\u0441\u043D\u043E\u0432\u0443 r\xE4\xE4gi, \u0438 \u043F\u043E \u0430\u043D\u0430\u043B\u043E\u0433\u0438\u0438 \u0441 \u0443\u0436\u0435 \u0437\u043D\u0430\u043A\u043E\u043C\u044B\u043C \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u043C olema \u0444\u043E\u0440\u043C\u0438\u0440\u0443\u0435\u043C \u043E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u0435 \u0434\u043B\u044F \u0432\u0441\u0435\u0445 \u043B\u0438\u0446.\n\n<p>\n    <table>\n        <tr>\n            <td>Mina</td>\n            <td rowspan="6">+ ei ole</td>\n        </tr>\n        <tr>\n            <td>Sina</td>\n        </tr>\n        <tr>\n            <td>Tema</td>\n        </tr>\n        <tr>\n            <td>Meie</td>\n        </tr>\n        <tr>\n            <td>Teie</td>\n        </tr>\n        <tr>\n            <td>Nemad</td>\n        </tr>\n    </table>\n</p>\n\n<p>\n    <table>\n        <tr>\n            <td>Mina</td>\n            <td rowspan="6">+ ei r\xE4\xE4gi</td>\n        </tr>\n        <tr>\n            <td>Sina</td>\n        </tr>\n        <tr>\n            <td>Tema</td>\n        </tr>\n        <tr>\n            <td>Meie</td>\n        </tr>\n        <tr>\n            <td>Teie</td>\n        </tr>\n        <tr>\n            <td>Nemad</td>\n        </tr>\n    </table>\n</p>\n\n##### N\xE4ide. \u041F\u0440\u0438\u043C\u0435\u0440\n\n\u041C\u044B \u0441 \u0412\u0430\u043C\u0438 \u0431\u0440\u0430\u043B\u0438 \u043D\u0430 \u043F\u0440\u043E\u0448\u043B\u043E\u043C \u0437\u0430\u043D\u044F\u0442\u0438\u0438 \u0435\u0449\u0435 \u043E\u0434\u0438\u043D \u0433\u043B\u0430\u0433\u043E\u043B \u2014 \u043B\u044E\u0431\u0438\u0442\u044C (armastama, armastada, armastab). \u0414\u043E\u0431\u0430\u0432\u0438\u043C \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F \u0438 \u0435\u0433\u043E.\nMina r\xE4\xE4gin, et mina ei ole T\xF5nis. \u042F \u0433\u043E\u0432\u043E\u0440\u044E, \u0447\u0442\u043E \u044F \u043D\u0435 \u0422\u044B\u043D\u0438\u0441.\nMina ei r\xE4\xE4gi, et mina ei armasta. \u042F \u043D\u0435 \u0433\u043E\u0432\u043E\u0440\u044E, \u0447\u0442\u043E \u044F \u043D\u0435 \u043B\u044E\u0431\u043B\u044E.\nMina armastan sind v\xE4ga. \u042F \u043B\u044E\u0431\u043B\u044E \u0442\u0435\u0431\u044F \u0441\u0438\u043B\u044C\u043D\u043E.\n\n\u041D\u0443 \u0432\u043E\u0442 \u043C\u044B \u0441 \u0432\u0430\u043C\u0438 \u0438 \u043F\u043E\u0437\u043D\u0430\u043A\u043E\u043C\u0438\u043B\u0438\u0441\u044C \u0441 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0438\u043C \u0432\u0440\u0435\u043C\u0435\u043D\u0435\u043C \u0433\u043B\u0430\u0433\u043E\u043B\u0430. \u0418 \u043C\u044B \u043C\u043E\u0436\u0435\u043C \u043D\u0430\u0447\u0438\u043D\u0430\u0442\u044C \u0443\u0436\u0435 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u044B\u0432\u0430\u0442\u044C \u0441\u0432\u043E\u0438 \u043F\u0435\u0440\u0432\u044B\u0435 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F. \u0417\u0430 \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C\u0438 \u043E\u0431\u0440\u0430\u0449\u0430\u0435\u043C\u0441\u044F \u0432 \u0441\u043B\u043E\u0432\u0430\u0440\u044C, \u0431\u0435\u0440\u0435\u043C \u0442\u0440\u0435\u0442\u044C\u044E \u0444\u043E\u0440\u043C\u0443 \u0438 \u0432\u0441\u0435, \u043B\u0435\u0433\u043A\u043E \u043F\u043E \u0441\u0445\u0435\u043C\u0435 \u0441\u043F\u0440\u044F\u0433\u0430\u0435\u043C \u0433\u043B\u0430\u0433\u043E\u043B \u0432 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u043C \u0432\u0440\u0435\u043C\u0435\u043D\u0438. \u0418 \u043A\u043E\u043D\u0435\u0447\u043D\u043E, \u043F\u0435\u0440\u0432\u043E\u0435 \u0447\u0442\u043E \u043D\u0430\u043C \u0437\u0430\u0445\u043E\u0447\u0435\u0442\u0441\u044F  \u2014 \u044D\u0442\u043E \u043D\u0430\u0439\u0442\u0438 \u043F\u0440\u0438\u043C\u0435\u043D\u0435\u043D\u0438\u0435 \u0432\u044B\u0443\u0447\u0435\u043D\u043D\u044B\u043C \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C \u043D\u0430 \u043F\u0440\u0430\u043A\u0442\u0438\u043A\u0435, \u0437\u0430\u043A\u0430\u0437\u0430\u0442\u044C \u0447\u0442\u043E-\u0442\u043E \u0432 \u043A\u0430\u0444\u0435, \u0440\u0430\u0441\u043F\u043B\u0430\u0442\u0438\u0442\u044C\u0441\u044F \u0432 \u043C\u0430\u0433\u0430\u0437\u0438\u043D\u0435, \u043D\u0443 \u0438 \u043A\u043E\u043D\u0435\u0447\u043D\u043E \u0441\u043E\u0441\u0442\u0430\u0432\u0438\u0442\u044C \u043F\u0435\u0440\u0432\u044B\u0439 \u0442\u0435\u043A\u0441\u0442 \u043F\u0440\u043E \u0441\u0435\u0431\u044F. \u041A\u0442\u043E \u043C\u044B, \u0433\u0434\u0435 \u0436\u0438\u0432\u0435\u043C, \u043E\u0442\u043A\u0443\u0434\u0430 \u043F\u0440\u0438\u0435\u0445\u0430\u043B\u0438... \u0422\u0430\u043A \u0432\u043E\u0442 \u043A \u0442\u0435\u043C\u0430\u043C \u043D\u0430\u0447\u0430\u043B\u044C\u043D\u043E\u0433\u043E \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u0447\u0435\u0441\u043A\u043E\u0433\u043E \u0431\u043B\u043E\u043A\u0430 \u043E\u0442\u043D\u043E\u0441\u044F\u0442\u0441\u044F \u0435\u0449\u0435 \u043D\u0435\u0441\u043A\u043E\u043B\u044C\u043A\u043E, \u0438 \u043C\u044B \u0441\u0435\u0439\u0447\u0430\u0441 \u0441 \u043D\u0438\u043C\u0438 \u043A\u0430\u043A \u0440\u0430\u0437 \u0438 \u043F\u0440\u043E\u0434\u043E\u043B\u0436\u0438\u043C \u0437\u043D\u0430\u043A\u043E\u043C\u0438\u0442\u044C\u0441\u044F. \n\n### \u041F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u0430\n### K\xE4skiv k\xF5neviis\n\n\u041F\u0440\u043E\u0434\u043E\u043B\u0436\u0430\u0435\u043C \u0437\u043D\u0430\u043A\u043E\u043C\u0438\u0442\u044C\u0441\u044F \u0441 \u0440\u0430\u0437\u043B\u0438\u0447\u043D\u044B\u043C\u0438 \u0444\u043E\u0440\u043C\u0430\u043C\u0438 \u0433\u043B\u0430\u0433\u043E\u043B\u0430, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u043C\u044B \u0441\u043C\u043E\u0436\u0435\u043C \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C \u0432 \u043F\u043E\u0432\u0441\u0435\u0434\u043D\u0435\u0432\u043D\u043E\u0439 \u0436\u0438\u0437\u043D\u0438. \u041F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0435 \u043D\u0430\u043C \u043D\u0443\u0436\u043D\u043E, \u0447\u0442\u043E\u0431\u044B \u043A\u043E\u043C\u0443-\u0442\u043E \u0447\u0442\u043E-\u0442\u043E \u043F\u0440\u0438\u043A\u0430\u0437\u0430\u0442\u044C, \u043F\u0440\u0438\u0437\u0432\u0430\u0442\u044C \u043A \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044E. \u041E\u043D\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u043A\u0430\u043A \u0432 \u0444\u043E\u0440\u043C\u0435 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E, \u0442\u0430\u043A \u0438 \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430.\n        \u0421\u043A\u0430\u0436\u0438! \u0423\u0447\u0438! \u0418\u0434\u0438\u0442\u0435! \u0418\u0437\u0432\u0438\u043D\u0438\u0442\u0435!\n\u0415\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E\n\u0424\u043E\u0440\u043C\u0443 \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043C\u044B \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u043C \u043E\u0442 \u043F\u0435\u0440\u0432\u043E\u0433\u043E \u043B\u0438\u0446\u0430 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430, \u0443\u0431\u0440\u0430\u0432 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -N. \u0412\u043E\u0437\u044C\u043C\u0435\u043C \u0433\u043B\u0430\u0433\u043E\u043B \u043B\u044E\u0431\u0438\u0442\u044C:\n\u0415\u0441\u043B\u0438 3\u0438\u0435 \u043B\u0438\u0446\u043E armastab, \u0442\u043E\u0433\u0434\u0430 \u043F\u0435\u0440\u0432\u043E\u0435 \u0432\u044B\u0433\u043B\u044F\u0434\u0438\u0442 \u043A\u0430\u043A armastan: \nArmasta! \u041B\u044E\u0431\u0438!\n\u041E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u0435 \u0432 \u0434\u0430\u043D\u043D\u043E\u0439 \u0444\u043E\u0440\u043C\u0435 \u0431\u0443\u0434\u0435\u0442 \u0432\u044B\u0433\u043B\u044F\u0434\u0435\u0442\u044C \u0442\u043E\u0447\u043D\u043E \u0442\u0430\u043A\u0436\u0435 \u043A\u0430\u043A \u0443\u0442\u0432\u0435\u0440\u0434\u0438\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430, \u0442\u043E\u043B\u044C\u043A\u043E \u043F\u0435\u0440\u0435\u0434 \u044D\u0442\u0438\u043C \u0441\u043B\u043E\u0432\u043E\u043C \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0435\u043C \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0443\u044E \u0447\u0430\u0441\u0442\u0438\u0446\u0443 \xC4RA.\n\xC4ra armasta! \u041D\u0435 \u043B\u044E\u0431\u0438!\n\u041C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E\n\u0424\u043E\u0440\u043C\u0443 \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u043C \u043E\u0442 \u0432\u0442\u043E\u0440\u043E\u0433\u043E \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u0430, \u0442.\u0435 -DA \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u0430. \u0423\u0431\u0438\u0440\u0430\u0435\u043C \u043F\u043E\u043A\u0430\u0437\u0430\u0442\u0435\u043B\u044C \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u043D\u043E\u0441\u0442\u0438 ( -DA, -TA, -A). \n\u0418 \u0432\u043C\u0435\u0441\u0442\u043E \u043D\u0435\u0433\u043E \u043F\u043E\u0434\u0441\u0442\u0430\u0432\u043B\u044F\u0435\u043C \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -GE \u0438\u043B\u0438 -KE:\n\u0415\u0441\u043B\u0438 \u0431\u044B\u043B\u043E  -DA     \u0442\u043E \u0441\u0442\u0430\u0432\u0438\u043C   -GE\n-TA                         -KE\n-A                           -KE\nArmastada: DA \u043C\u0435\u043D\u044F\u0435\u043C \u043D\u0430 GE, Armastage! \u041B\u044E\u0431\u0438\u0442\u0435!\n\u0414\u043B\u044F \u043E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u044F \u0432\u043E\u0437\u044C\u043C\u0435\u043C \u0443\u0436\u0435 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u043D\u0443\u044E \u0432\u044B\u0448\u0435 \u0444\u043E\u0440\u043C\u0443 \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u0438 \u0432\u043F\u0435\u0440\u0435\u0434\u0438 \u043F\u043E\u0441\u0442\u0430\u0432\u0438\u043C  \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0443\u044E \u0447\u0430\u0441\u0442\u0438\u0446\u0443 \xC4RGE. \u041D\u0435 \u0437\u0430\u0432\u0438\u0441\u0438\u043C\u043E \u043E\u0442 \u0442\u043E\u0433\u043E, \u0431\u0443\u0434\u0435\u0442 \u043B\u0438 \u0443 \u044D\u0442\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u0432 \u0444\u043E\u0440\u043C\u0435 \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 \u0432\u044B\u0433\u043B\u044F\u0434\u0435\u0442\u044C \u043A\u0430\u043A -ge, \u0438\u043B\u0438 \u043A\u0430\u043A -ke, \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0447\u0430\u0441\u0442\u0438\u0446\u0430 \u0431\u0443\u0434\u0435\u0442 \u0432\u0441\u0435\u0433\u0434\u0430 \u0442\u043E\u043B\u044C\u043A\u043E \xE4rge.\n\xC4rge armastage! \u041D\u0435 \u043B\u044E\u0431\u0438\u0442\u0435!\n\u041F\u0440\u0438 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u0438 \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u0441\u0442\u043E\u0438\u0442 \u043E\u0431\u0440\u0430\u0442\u0438\u0442\u044C \u043E\u0441\u043E\u0431\u043E\u0435 \u0432\u043D\u0438\u043C\u0430\u043D\u0438\u0435 \u043D\u0430 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u0430. \u0418\u043D\u043E\u0433\u0434\u0430 \u0431\u0443\u043A\u0432\u0430 \u0422, \u0438\u043B\u0438 D \u043C\u043E\u0436\u0435\u0442 \u0431\u044B\u0442\u044C \u0447\u0430\u0441\u0442\u044C\u044E \u043E\u0441\u043D\u043E\u0432\u044B \u0441\u043B\u043E\u0432\u0430, \u0430 \u043F\u043E\u043A\u0430\u0437\u0430\u0442\u0435\u043B\u044C \u043E\u0441\u0442\u0430\u043D\u0435\u0442\u0441\u044F \u0442\u043E\u043B\u044C\u043A\u043E -A. \u0414\u043B\u044F \u043F\u0440\u0438\u043C\u0435\u0440\u0430 \u0432\u043E\u0437\u044C\u043C\u0435\u043C \u0433\u043B\u0430\u0433\u043E\u043B S\xD5ITMA-S\xD5ITA-S\xD5IDAN \u0435\u0445\u0430\u0442\u044C.\n\u041F\u0440\u0430\u0432\u0438\u043B\u043E \u0437\u0434\u0435\u0441\u044C \u0437\u0432\u0443\u0447\u0438\u0442 \u0442\u0430\u043A: \u0435\u0441\u043B\u0438 \u0431\u0443\u043A\u0432\u044B -T \u0438\u043B\u0438 -D \u043F\u0440\u043E\u0441\u043B\u0435\u0436\u0438\u0432\u0430\u044E\u0442\u0441\u044F \u0445\u043E\u0442\u044F \u0431\u044B \u0432 2\u0445 \u0438\u0437 \u0442\u0440\u0435\u0445 \u043E\u0441\u043D\u043E\u0432\u043D\u044B\u0445 \u0444\u043E\u0440\u043C \u0433\u043B\u0430\u0433\u043E\u043B\u0430, \u0442\u043E\u0433\u0434\u0430 \u043E\u043D\u0438 \u043F\u0440\u0438\u043D\u0430\u0434\u043B\u0435\u0436\u0430\u0442 \u043A \u043E\u0441\u043D\u043E\u0432\u0435. S\xF5itma, s\xF5ita. \u041F\u043E\u043B\u0443\u0447\u0430\u0435\u0442\u0441\u044F, \u0447\u0442\u043E  S\xD5IT \u044D\u0442\u043E \u043E\u0441\u043D\u043E\u0432\u0430, \u0430 \u043F\u043E\u043A\u0430\u0437\u0430\u0442\u0435\u043B\u044C \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u0430 \u0442\u043E\u043B\u044C\u043A\u043E -\u0410, \u043A\u043E\u0442\u043E\u0440\u044B\u0439 \u043F\u0440\u0438 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u0438 \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F \u043C\u0435\u043D\u044F\u0435\u0442\u0441\u044F \u043D\u0430 -KE. \nS\xF5itke! \u0415\u0437\u0436\u0430\u0439\u0442\u0435!\n\n\n### \u0421\u043E\u0441\u0442\u0430\u0432\u043D\u044B\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u044B\n### \xDChends\xF5nad\n\n\u0412\u0441\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u044B, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u043D\u0430\u043C \u0432\u0441\u0442\u0440\u0435\u0442\u0438\u043B\u0438\u0441\u044C \u0434\u043E \u044D\u0442\u043E\u0433\u043E \u043C\u043E\u043C\u0435\u043D\u0442\u0430, \u043C\u043E\u0436\u043D\u043E \u043D\u0430\u0437\u0432\u0430\u0442\u044C \u043F\u0440\u043E\u0441\u0442\u044B\u043C\u0438 \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C\u0438. \u0414\u0430, \u043A\u043E\u043D\u0435\u0447\u043D\u043E, \u043C\u044B \u0434\u043B\u044F \u043D\u0438\u0445 \u0443\u0447\u0438\u043B\u0438 \u0438 \u0443\u0447\u0438\u043C 3 \u043E\u0441\u043D\u043E\u0432\u043D\u044B\u0435 \u0444\u043E\u0440\u043C\u044B, \u043D\u043E, \u043F\u043E \u043A\u0440\u0430\u0439\u043D\u0435\u0439 \u043C\u0435\u0440\u0435, \u0432\u0441\u0435 \u044D\u0442\u0438 \u0444\u043E\u0440\u043C\u044B \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u0441\u043E\u0441\u0442\u043E\u044F\u043B\u0438 \u0438\u0437 \u043E\u0434\u043D\u043E\u0433\u043E \u0441\u043B\u043E\u0432\u0430. \u0415\u0441\u0442\u044C \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u044B, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0441\u043E\u0441\u0442\u043E\u044F\u0442 \u0438\u0437 \u0447\u0430\u0441\u0442\u0438\u0446\u044B \u0438 \u0443\u0436\u0435 \u0442\u043E\u043B\u044C\u043A\u043E \u043F\u043E\u0442\u043E\u043C \u0433\u043B\u0430\u0433\u043E\u043B\u0430.  \u0418\u0445 \u043C\u044B \u0431\u0443\u0434\u0435\u043C \u043D\u0430\u0437\u044B\u0432\u0430\u0442\u044C \u0441\u043E\u0441\u0442\u0430\u0432\u043D\u044B\u043C\u0438. \u0412 \u043F\u0440\u0438\u043D\u0446\u0438\u043F\u0435, \u043C\u044B \u043C\u043E\u0436\u0435\u043C \u0432\u0441\u0442\u0440\u0435\u0442\u0438\u0442\u044C \u044D\u0442\u0438 \u0433\u043B\u0430\u0433\u043E\u043B\u044B \u0438 \u0431\u0435\u0437 \u0447\u0430\u0441\u0442\u0438\u0446, \u043D\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043D\u0438\u0435 \u0438\u0445 \u0432\u043C\u0435\u0441\u0442\u0435 \u0441 \u0434\u043E\u043F\u043E\u043B\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u043C\u0438 \u0441\u043B\u043E\u0432\u0430\u043C\u0438 \u043F\u0440\u0438\u0434\u0430\u0441\u0442 \u0438\u043C \u043D\u043E\u0432\u043E\u0435 \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0435.\n\n\\* \u0424\u0438\u043B\u043E\u043B\u043E\u0433\u0438 \u043D\u0430\u0437\u044B\u0432\u0430\u044E\u0442 \u044D\u0442\u0438 \u0433\u043B\u0430\u0433\u043E\u043B\u044B \u0430\u0444\u0444\u0438\u043A\u0441\u0430\u043B\u044C\u043D\u044B\u043C\u0438, \u0442\u0430\u043A \u043A\u0430\u043A \u0442\u043E, \u0447\u0442\u043E \u044F \u043D\u0430\u0437\u0432\u0430\u043B\u0430 \u0447\u0430\u0441\u0442\u0438\u0446\u0435\u0439, \u043E\u043D\u0438 \u0438\u043C\u0435\u043D\u0443\u044E\u0442 \u0430\u0444\u0444\u0438\u043A\u0441\u043E\u043C. \u0414\u043B\u044F \u043D\u0430\u0441 \u0436\u0435, \u0437\u0435\u043C\u043D\u044B\u0445 \u043B\u044E\u0434\u0435\u0439, \u044D\u0442\u043E \u043D\u0435 \u0438\u043C\u0435\u0435\u0442 \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u044F, \u0432\u0435\u0434\u044C \u0433\u043B\u0430\u0432\u043D\u043E\u0435 \u043F\u043E\u043D\u044F\u0442\u044C \u0441\u043C\u044B\u0441\u043B \u0438 \u043F\u0440\u0435\u0434\u043D\u0430\u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0435 \u0442\u0430\u043A\u0438\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u0441 \u0445\u0432\u043E\u0441\u0442\u0438\u043A\u043E\u043C.\n\n##### N\xE4ide\n\nSaama \u2014 saada \u2014 saab \u2014 \u043F\u043E\u043B\u0443\u0447\u0430\u0442\u044C, \u043C\u043E\u0447\u044C, \u0441\u0442\u0430\u043D\u043E\u0432\u0438\u0442\u044C\u0441\u044F\n\u0418 \u0435\u0441\u043B\u0438 \u0434\u043E\u0431\u0430\u0432\u0438\u0442\u044C \u043A \u044D\u0442\u043E\u043C\u0443 \u0433\u043B\u0430\u0433\u043E\u043B\u0443 \u0440\u0430\u0437\u043D\u044B\u0435 \u0447\u0430\u0441\u0442\u0438\u0446\u044B, \u0442\u043E \u043F\u043E\u043B\u0443\u0447\u0438\u0442\u0441\u044F:\nAru saama \u2014 aru saada \u2014 saab aru \u2014 \u043F\u043E\u043D\u0438\u043C\u0430\u0442\u044C (\u043F\u043E\u043B\u0443\u0447\u0430\u0442\u044C \u044F\u0441\u043D\u043E\u0441\u0442\u044C)\nTuttavaks saama \u2014 tuttavaks saada \u2014 saab tuttavks \u2014 \u0437\u043D\u0430\u043A\u043E\u043C\u0438\u0442\u044C\u0441\u044F (\u0441\u0442\u0430\u043D\u043E\u0432\u0438\u0442\u044C\u0441\u044F \u0437\u043D\u0430\u043A\u043E\u043C\u044B\u043C\u0438)\n\u041E\u0441\u043E\u0431\u0435\u043D\u043D\u043E\u0441\u0442\u044C \u0442\u0430\u043A\u0438\u0445 \u0441\u043B\u043E\u0432 \u0432 \u0442\u043E\u043C, \u0447\u0442\u043E \u0438\u0437\u043C\u0435\u043D\u044F\u0435\u043C \u043C\u044B \u0441\u0430\u043C \u0433\u043B\u0430\u0433\u043E\u043B, \u0430 \u0432\u043E\u0442 \u0447\u0430\u0441\u0442\u0438\u0446\u0430 \u043E\u0441\u0442\u0430\u0435\u0442\u0441\u044F \u0431\u0435\u0437 \u0438\u0437\u043C\u0435\u043D\u0435\u043D\u0438\u0439, \u043E\u043D\u0430 \u043F\u043E\u043C\u0435\u043D\u044F\u0435\u0442 \u0442\u043E\u043B\u044C\u043A\u043E \u043B\u0438\u0448\u044C \u0441\u0432\u043E\u0435 \u043C\u0435\u0441\u0442\u043E\u043F\u043E\u043B\u043E\u0436\u0435\u043D\u0438\u0435.  \u0412 \u0444\u043E\u0440\u043C\u0430\u0445 -MA \u0438 -DA \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u0430 \u044D\u0442\u0430 \u0447\u0430\u0441\u0442\u0438\u0446\u0430 \u0431\u0443\u0434\u0435\u0442 \u0441\u0442\u043E\u044F\u0442\u044C \u043F\u0435\u0440\u0435\u0434 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u043C, \u0430 \u0432\u043E\u0442 \u0443\u0436\u0435 \u043A\u043E\u0433\u0434\u0430 \u043D\u0430\u0447\u0438\u043D\u0430\u0435\u043C \u0438\u0437\u043C\u0435\u043D\u044F\u0442\u044C \u0433\u043B\u0430\u0433\u043E\u043B, \u0442\u043E \u0447\u0430\u0441\u0442\u0438\u0446\u0430 \u0443\u0445\u043E\u0434\u0438\u0442 \u0437\u0430 \u043D\u0435\u0433\u043E.\n\u041A\u043E\u043D\u0435\u0447\u043D\u043E \u0433\u043B\u0430\u0433\u043E\u043B saama \u043D\u0435 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u044B\u0439, \u043A \u043A\u043E\u0442\u043E\u0440\u043E\u043C\u0443 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u044E\u0442\u0441\u044F \u0447\u0430\u0441\u0442\u0438\u0446\u044B, \u0434\u0430 \u0438 \u0441\u043F\u0438\u0441\u043E\u043A \u0441\u0430\u043C\u0438\u0445 \u0447\u0430\u0441\u0442\u0438\u0446 \u043D\u0435 \u043E\u0433\u0440\u0430\u043D\u0438\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u0434\u0432\u0443\u043C\u044F \u043F\u0440\u0435\u0434\u0441\u0442\u0430\u0432\u043B\u0435\u043D\u043D\u044B\u043C\u0438. \u0412\u043E\u0442 \u0412\u0430\u043C \u0434\u043B\u044F \u043F\u0440\u0438\u043C\u0435\u0440\u0430 \u0435\u0449\u0435 \u043E\u0434\u0438\u043D \u0433\u043B\u0430\u0433\u043E\u043B:\n Hiljaks j\xE4\xE4ma \u2014 hiljaks j\xE4\xE4da \u2014 j\xE4\xE4b hiljaks \u2014 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0442\u044C (\u043E\u0441\u0442\u0430\u0432\u0430\u0442\u044C\u0441\u044F \u043F\u043E\u0437\u0434\u043D\u0438\u043C)\n\n\u041F\u0440\u0435\u0434\u043B\u0430\u0433\u0430\u044E, \u0447\u0442\u043E\u0431\u044B \u043D\u0435\u043C\u043D\u043E\u0433\u043E \u0437\u0430\u043A\u0440\u0435\u043F\u0438\u0442\u044C \u0432\u0441\u0435 \u0432\u044B\u0443\u0447\u0435\u043D\u043D\u044B\u0435 \u0432\u044B\u0448\u0435 \u0444\u043E\u0440\u043C\u044B, \u043F\u0440\u043E\u0441\u043F\u0440\u044F\u0433\u0430\u0442\u044C \u043A\u0430\u043A \u0440\u0430\u0437 \u044D\u0442\u043E\u0442 \u0433\u043B\u0430\u0433\u043E\u043B.\n\u041D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0435 \u0432\u0440\u0435\u043C\u044F \u0443\u0442\u0432\u0435\u0440\u0436\u0434\u0435\u043D\u0438\u0435:\nMina j\xE4\xE4n hiljaks. \u042F \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u044E.\nSina j\xE4\xE4d hiljaks. \u0422\u044B \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0435\u0448\u044C.\nTema j\xE4\xE4b hiljaks. \u041E\u043D, \u043E\u043D\u0430 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0435\u0442.\nMeie j\xE4\xE4me hiljaks. \u041C\u044B \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0435\u043C.\nTeie j\xE4\xE4te hiljaks. \u0412\u044B \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0435\u0442\u0435.\nNemad j\xE4\xE4vad hiljaks. \u041E\u043D\u0438 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u044E\u0442.\n\u041D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0435 \u0432\u0440\u0435\u043C\u044F, \u043E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u0435:\nMina ei j\xE4\xE4 hiljaks. \u042F \u043D\u0435 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u044E. \nSina ei j\xE4\xE4 hiljaks. \u0422\u044B \u043D\u0435 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0435\u0448\u044C.\nTema ei j\xE4\xE4 hiljaks. \u041E\u043D, \u043E\u043D\u0430 \u043D\u0435 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0435\u0442.\nMeie ei j\xE4\xE4 hiljaks. \u041C\u044B \u043D\u0435 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0435\u043C.\nTeie ei j\xE4\xE4 hiljaks. \u0412\u044B \u043D\u0435 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0435\u0442\u0435.\nNemad ei j\xE4\xE4 hiljaks. \u041E\u043D\u0438 \u043D\u0435 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u044E\u0442.\n\u041F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0435:\nJ\xE4\xE4 hiljaks! \xC4ra j\xE4\xE4 hiljaks! \u041E\u043F\u043E\u0437\u0434\u0430\u0439! \u041D\u0435 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0439!\nJ\xE4\xE4ge hiljaks! \xC4rge j\xE4\xE4ge hiljaks! \u041E\u043F\u043E\u0437\u0434\u0430\u0439\u0442\u0435! \u041D\u0435 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0439\u0442\u0435!\n\u041A\u043E\u043D\u0435\u0447\u043D\u043E, \u0443\u0442\u0432\u0435\u0440\u0434\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435 \u0444\u043E\u0440\u043C\u044B \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F  \u0438\u043C\u0435\u043D\u043D\u043E \u044D\u0442\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u0440\u0435\u0434\u043A\u043E \u043A\u043E\u0433\u0434\u0430 \u0443\u0441\u043B\u044B\u0448\u0438\u0448\u044C,  \u043F\u0440\u043E\u0449\u0435 \u043F\u0440\u0435\u0434\u0441\u0442\u0430\u0432\u0438\u0442\u044C \u0441\u0438\u0442\u0443\u0430\u0446\u0438\u0438, \u0433\u0434\u0435 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \xAB\u041D\u0435 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0439!\xBB \u0438\u043B\u0438 \xAB\u041D\u0435 \u043E\u043F\u0430\u0437\u0434\u044B\u0432\u0430\u0439\u0442\u0435!\xBB.  \u041D\u043E \u0435\u0449\u0435 \u0447\u0430\u0449\u0435 \u043C\u043E\u0436\u043D\u043E \u0443\u0441\u043B\u044B\u0448\u0430\u0442\u044C \u0441\u043B\u043E\u0432\u0430 \xAB\u044F \u043E\u043F\u043E\u0437\u0434\u0430\u043B\xBB. \u0422\u0430\u043A \u0432\u043E\u0442 \u043F\u0440\u0438\u0448\u043B\u043E \u0432\u0440\u0435\u043C\u044F \u043F\u043E\u0433\u043E\u0432\u043E\u0440\u0438\u0442\u044C \u0438 \u043E \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u043C \u0432\u0440\u0435\u043C\u0435\u043D\u0438.\n\n### \u041F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0432\u0440\u0435\u043C\u044F \u0433\u043B\u0430\u0433\u043E\u043B\u0430\n### Mineviku ajad\n\n\u041A\u0430\u043A \u0412\u044B \u043F\u043E\u043C\u043D\u0438\u0442\u0435, \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u0432\u0441\u0435\u0433\u043E 4 \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u2014 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0435 \u043E\u0434\u043D\u043E \u0438 \u0442\u0440\u0438 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0438\u0445. \u0421\u0430\u043C\u043E \u0441\u043B\u043E\u0432\u043E \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0432\u0440\u0435\u043C\u044F \u0437\u0432\u0443\u0447\u0438\u0442 \u043F\u043E-\u044D\u0441\u0442\u043E\u043D\u0441\u043A\u0438 \u043A\u0430\u043A minevik. \u042D\u0442\u043E \u0441\u043B\u043E\u0432\u043E \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043B\u043E\u0441\u044C \u043E\u0442 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 minema (minema, minna, l\xE4heb) \u2014 \u0438\u0434\u0442\u0438, \u0442\u043E \u0435\u0441\u0442\u044C \u0442\u043E, \u0447\u0442\u043E \u0443\u0436\u0435 \u0443\u0448\u043B\u043E. \n\u041F\u043E\u0441\u043A\u043E\u043B\u044C\u043A\u0443 \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0438\u0445 \u0432\u0440\u0435\u043C\u0435\u043D \u0442\u0440\u0438, \u0442\u043E \u0432\u043E\u0437\u043D\u0438\u043A\u0430\u0435\u0442 \u0432\u043E\u043F\u0440\u043E\u0441, \u043A\u043E\u0433\u0434\u0430 \u043A\u0430\u043A\u043E\u0435 \u0432\u0440\u0435\u043C\u044F \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C.\n\u041F\u0435\u0440\u0432\u043E\u0435 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0432\u0440\u0435\u043C\u044F \u043D\u0430\u0437\u044B\u0432\u0430\u0435\u0442\u0441\u044F lihtminevik, \u0447\u0442\u043E \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u0441\u044F \u043A\u0430\u043A \u043F\u0440\u043E\u0441\u0442\u043E\u0435 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0432\u0440\u0435\u043C\u044F. \u0412\u043E\u043E\u0431\u0449\u0435, \u044D\u0442\u043E \u0441\u0430\u043C\u043E\u0435 \u0440\u0430\u0441\u043F\u0440\u043E\u0441\u0442\u0440\u0430\u043D\u0435\u043D\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430 \u0438\u0437 \u0442\u0440\u0435\u0445 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0438\u0445 \u0432\u0440\u0435\u043C\u0435\u043D, \u043D\u043E \u0447\u0435\u0441\u0442\u043D\u043E\u0435 \u0441\u043B\u043E\u0432\u043E, \u043D\u0438\u0447\u0435\u0433\u043E \u043F\u0440\u043E\u0441\u0442\u043E\u0433\u043E \u0432 \u0435\u0433\u043E \u0441\u043F\u043E\u0441\u043E\u0431\u0435 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u043D\u0435\u0442 \u0438 \u0432 \u043F\u043E\u043C\u0438\u043D\u0435. \u041F\u0440\u0430\u0432\u0438\u043B\u043E \u0442\u043E \u043C\u043E\u0436\u0435\u0442 \u0431\u044B\u0442\u044C \u0435\u0449\u0435 \u043D\u0438\u0447\u0435\u0433\u043E, \u043D\u043E \u0432\u043E\u0442 \u0437\u0430\u0431\u0438\u0432\u0430\u0442\u044C \u0412\u0430\u0448\u0438 \u0433\u043E\u043B\u043E\u0432\u044B \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u044F\u043C\u0438 \u043D\u0430 \u0432\u0442\u043E\u0440\u043E\u043C \u0437\u0430\u043D\u044F\u0442\u0438\u0438 \u0443\u0436 \u0442\u043E\u0447\u043D\u043E \u043D\u0435 \u0431\u0443\u0434\u0435\u043C. \u0413\u043B\u0430\u0432\u043D\u043E\u0435 \u0437\u0430\u043F\u043E\u043C\u043D\u0438\u043C, \u0447\u0442\u043E \u044D\u0442\u043E \u043F\u0440\u043E\u0441\u0442\u043E\u0435 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0432\u0440\u0435\u043C\u044F \u043D\u0443\u0436\u043D\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C \u0432 \u0442\u0435\u0445 \u0441\u043B\u0443\u0447\u0430\u044F\u0445, \u043A\u043E\u0433\u0434\u0430 \u043C\u044B \u0441\u043E\u043E\u0431\u0449\u0430\u0435\u043C \u0434\u0430\u0442\u0443, \u0432\u0440\u0435\u043C\u044F, \u043F\u0435\u0440\u0438\u043E\u0434. \u0422.\u0435. \u043A\u043E\u0433\u0434\u0430 \u043E\u043F\u0438\u0441\u044B\u0432\u0430\u0435\u0442\u0435 \u043A\u043E\u043D\u043A\u0440\u0435\u0442\u043D\u044B\u0435 \u0441\u043E\u0431\u044B\u0442\u0438\u044F \u0432 \u043F\u0440\u043E\u0448\u043B\u043E\u043C, \u043D\u0443\u0436\u043D\u043E \u0443\u043F\u043E\u0442\u0440\u0435\u0431\u0438\u0442\u044C lihtminevik.\n\u041D\u043E \u0432\u0435\u0434\u044C \u0432 \u043F\u043E\u0432\u0441\u0435\u0434\u043D\u0435\u0432\u043D\u043E\u0439 \u0440\u0435\u0447\u0438 \u043C\u043E\u0436\u043D\u043E \u043E\u043F\u0435\u0440\u0438\u0440\u043E\u0432\u0430\u0442\u044C \u0438 \u043F\u0440\u043E\u0441\u0442\u044B\u043C\u0438 \u043E\u0431\u043E\u0431\u0449\u0435\u043D\u043D\u044B\u043C\u0438 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F\u043C\u0438 \u0442\u0438\u043F\u0430: \u044F \u0447\u0438\u0442\u0430\u043B\u0430, \u0443\u0447\u0438\u043B\u0430\u0441\u044C, \u0440\u043E\u0434\u0438\u043B\u0441\u044F \u0438 \u0442\u0434. \u0411\u0435\u0437 \u0443\u043A\u0430\u0437\u0430\u043D\u0438\u044F \u0434\u0430\u0442\u044B, \u043C\u0435\u0441\u0442\u0430 \u0438 \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u043A\u0430\u043A \u0442\u0430\u043A\u043E\u0432\u043E\u0433\u043E. \u0417\u0430 \u044D\u0442\u043E \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u043E\u0442\u0432\u0435\u0447\u0430\u0435\u0442 \u043F\u043E\u043B\u043D\u043E\u0435 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0432\u0440\u0435\u043C\u044F \u2014 t\xE4isminevik. \u041E\u0431\u0440\u0430\u0437\u0443\u0435\u0442\u0441\u044F \u043E\u043D\u043E \u0434\u043E\u0441\u0442\u0430\u0442\u043E\u0447\u043D\u043E \u043B\u0435\u0433\u043A\u043E, \u043F\u043E\u044D\u0442\u043E\u043C\u0443 \u0438\u043C\u0435\u043D\u043D\u043E \u0435\u0433\u043E \u044F \u0412\u0430\u043C \u0438 \u0440\u0435\u043A\u043E\u043C\u0435\u043D\u0434\u0443\u044E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C \u043D\u0430 \u043D\u0430\u0447\u0430\u043B\u044C\u043D\u043E\u043C \u044D\u0442\u0430\u043F\u0435 \u0438\u0437\u0443\u0447\u0435\u043D\u0438\u044F \u044F\u0437\u044B\u043A\u0430, \u043A\u043E\u0433\u0434\u0430 \u0437\u0430\u0445\u043E\u0447\u0435\u0442\u0441\u044F \u0441\u043A\u0430\u0437\u0430\u0442\u044C \u0447\u0442\u043E-\u0442\u043E \u0432 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u043C \u0432\u0440\u0435\u043C\u0435\u043D\u0438.\n\u041F\u0440\u043E \u0442\u0440\u0435\u0442\u044C\u0435 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0432\u0440\u0435\u043C\u044F \u0441\u043A\u0430\u0436\u0443 \u0442\u043E\u043B\u044C\u043A\u043E \u043B\u0438\u0448\u044C \u0442\u043E, \u0447\u0442\u043E \u043D\u0430\u0437\u044B\u0432\u0430\u0435\u0442\u0441\u044F \u043E\u043D\u043E \u0434\u0430\u0432\u043D\u043E\u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0432\u0440\u0435\u043C\u044F \u2014 enneminevik. \u0421\u0430\u043C\u0438 \u044D\u0441\u0442\u043E\u043D\u0446\u044B \u0435\u0433\u043E \u0434\u043E\u0441\u0442\u0430\u0442\u043E\u0447\u043D\u043E \u0440\u0435\u0434\u043A\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u044E\u0442 \u0432 \u0440\u0435\u0447\u0438, \u0442\u0430\u043A \u0447\u0442\u043E \u0438 \u043C\u044B c \u0412\u0430\u043C\u0438 \u043F\u043E\u043A\u0430 \u0435\u0433\u043E \u0442\u0440\u043E\u0433\u0430\u0442\u044C \u043D\u0435 \u0431\u0443\u0434\u0435\u043C.\n\n### \u041F\u043E\u043B\u043D\u043E\u0435 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0432\u0440\u0435\u043C\u044F\n### T\xE4isminevik\n\n\u041A\u0430\u043A \u044F \u0443\u0436\u0435 \u0433\u043E\u0432\u043E\u0440\u0438\u043B\u0430 \u0432\u044B\u0448\u0435, \u043C\u044B \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u043C \u043F\u043E\u043B\u043D\u043E\u0435 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435, \u043A\u043E\u0433\u0434\u0430 \u0445\u043E\u0442\u0438\u043C \u0440\u0430\u0441\u0441\u043A\u0430\u0437\u0430\u0442\u044C \u043E \u0441\u043E\u0431\u044B\u0442\u0438\u0438 \u0432 \u043F\u0440\u043E\u0448\u043B\u043E\u043C \u0431\u0435\u0437 \u0443\u043A\u0430\u0437\u0430\u043D\u0438\u044F \u0432\u0440\u0435\u043C\u0435\u043D\u0438, \u0438 \u043A\u043E\u0442\u043E\u0440\u043E\u0435 \u043A \u043C\u043E\u043C\u0435\u043D\u0442\u0443 \u0440\u0435\u0447\u0438 \u0443\u0436\u0435 \u0437\u0430\u0432\u0435\u0440\u0448\u0438\u043B\u043E\u0441\u044C. \n\u041F\u043E\u043B\u043D\u043E\u0435 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435 \u0432\u0440\u0435\u043C\u044F \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442\u0441\u044F \u043F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0432\u0441\u043F\u043E\u043C\u043E\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430 olema \u0432 \u043D\u0443\u0436\u043D\u043E\u043C \u043D\u0430\u043C \u043B\u0438\u0446\u0435 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u0438 \u043E\u0441\u043D\u043E\u0432\u043D\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u0432 \u0444\u043E\u0440\u043C\u0435 \u043F\u0440\u0438\u0447\u0430\u0441\u0442\u0438\u044F -nud.  \u041F\u0440\u0438\u0447\u0430\u0441\u0442\u0438\u0435 \u0436\u0435 \u0432 \u0441\u0432\u043E\u044E \u043E\u0447\u0435\u0440\u0435\u0434\u044C \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442\u0441\u044F \u0434\u043E\u0441\u0442\u0430\u0442\u043E\u0447\u043D\u043E \u043B\u0435\u0433\u043A\u043E, \u0438 \u043F\u0440\u0430\u043A\u0442\u0438\u0447\u0435\u0441\u043A\u0438 \u0431\u0435\u0437 \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u0439. \u0414\u043B\u044F \u044D\u0442\u043E\u0433\u043E \u043D\u0430\u043C \u043F\u043E\u043D\u0430\u0434\u043E\u0431\u0438\u0442\u0441\u044F 2\u043E\u0439 \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432, -DA \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432. \u0423\u0431\u0438\u0440\u0430\u0435\u043C \u0435\u0433\u043E \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 (DA, TA, A) \u0438 \u043F\u043E\u0434\u0441\u0442\u0430\u0432\u043B\u044F\u0435\u043C \u2013NUD.  \u041F\u0440\u0438\u043C\u0435\u0440, r\xE4\xE4kida \u2014 r\xE4\xE4kinud, s\xF5ita \u2014 s\xF5itnud. \n\u041E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u0435 \u043F\u043E\u043B\u043D\u043E\u0433\u043E \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442\u0441\u044F \u043F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0439 \u0444\u043E\u0440\u043C\u044B ei ole \u0438 \u043F\u0440\u0438\u0447\u0430\u0441\u0442\u0438\u044F \u043D\u0430 -nud. \n\u041D\u0438\u0436\u0435 \u0432 \u0442\u0430\u0431\u043B\u0438\u0446\u0435 \u043C\u043E\u0436\u043D\u043E \u0443\u0432\u0438\u0434\u0435\u0442\u044C \u0438\u0437\u043C\u0435\u043D\u0435\u043D\u0438\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u043B\u044E\u0431\u0438\u0442\u044C \u043F\u043E \u043B\u0438\u0446\u0430\u043C \u0438 \u0447\u0438\u0441\u043B\u0430\u043C \u0432 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u043C \u0432\u0440\u0435\u043C\u0435\u043D\u0438.\n\n<p>\n    <table>\n        <tr>\n            <th>\u0423\u0442\u0432\u0435\u0440\u0434\u0438\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430<br>Jaatav vorm</th>\n            <th>\u041E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430<br>Eitav vorm</th>\n        </tr>\n        <tr>\n            <td>ma <b><u>olen</u></b> armastanud</td>\n            <td>ma <b><u>ei ole</u></b> armastanud</td>\n        </tr>\n        <tr>\n            <td>sa <b><u>oled</u></b> armastanud</td>\n            <td>sa <b><u>ei ole</u></b> armastanud</td>\n        </tr>\n        <tr>\n            <td>ta <b><u>on</u></b> armastanud</td>\n            <td>ta <b><u>ei ole</u></b> armastanud</td>\n        </tr>\n        <tr>\n            <td>me <b><u>oleme</u></b> armastanud</td>\n            <td>me <b><u>ei ole</u></b> armastanud</td>\n        </tr>\n        <tr>\n            <td>te <b><u>olete</u></b> armastanud</td>\n            <td>te <b><u>ei ole</u></b> armastanud</td>\n        </tr>\n        <tr>\n            <td>nad <b><u>on</u></b> armastanud</td>\n            <td>nad <b><u>ei ole</u></b> armastanud</td>\n        </tr>\n    </table>\n</p>\n\n\u041D\u0443 \u0447\u0442\u043E, \u0432\u044B \u0431\u043E\u043B\u044C\u0448\u0438\u0435 \u043C\u043E\u043B\u043E\u0434\u0446\u044B. \u0421\u0435\u0433\u043E\u0434\u043D\u044F \u043F\u043E\u043B\u0443\u0447\u0438\u043B\u0441\u044F \u043E\u0447\u0435\u043D\u044C \u043D\u0430\u0441\u044B\u0449\u0435\u043D\u043D\u044B\u0439 \u0443\u0440\u043E\u043A \u0438 \u0447\u0442\u043E\u0431\u044B \u043D\u0430\u0448\u0438 \u043F\u0440\u0430\u0432\u0438\u043B\u0430 \u043D\u0435 \u043F\u0440\u0435\u0432\u0440\u0430\u0442\u0438\u043B\u0438\u0441\u044C \u0432 \u0433\u043E\u043B\u043E\u0432\u0435 \u0432 \u0441\u043B\u043E\u0448\u043D\u043E\u0439 \u0432\u0438\u043D\u0435\u0433\u0440\u0435\u0442, \u043D\u0443\u0436\u043D\u043E \u0441\u0440\u043E\u0447\u043D\u043E \u0437\u0430\u043A\u0440\u0435\u043F\u043B\u044F\u0442\u044C \u044D\u0442\u043E \u0432\u0441\u0435 \u043D\u0430 \u043F\u0440\u0430\u043A\u0442\u0438\u043A\u0435. \u042D\u0442\u0438\u043C \u043C\u044B \u0438 \u0437\u0430\u0439\u043C\u0435\u043C\u0441\u044F \u0432 \u0437\u0430\u0434\u0430\u043D\u0438\u044F\u0445 \u043A \u044D\u0442\u043E\u043C\u0443 \u0443\u0440\u043E\u043A\u0443. \u0410 \u043F\u043E\u043A\u0430 \u043D\u0430 \u0437\u0430\u043A\u0443\u0441\u043A\u0443 \u0432\u0430\u043C \u043D\u0435\u0441\u043A\u043E\u043B\u044C\u043A\u043E \u043D\u043E\u0432\u044B\u0445 \u0444\u0440\u0430\u0437 \u0431\u043B\u0430\u0433\u043E\u0434\u0430\u0440\u043D\u043E\u0441\u0442\u0438 \u0438 \u0438\u0437\u0432\u0438\u043D\u0435\u043D\u0438\u0439.\n\n### \u0412\u044B\u0440\u0430\u0436\u0435\u043D\u0438\u0435 \u0431\u043B\u0430\u0433\u043E\u0434\u0430\u0440\u043D\u043E\u0441\u0442\u0438 \u0438 \u0438\u0437\u0432\u0438\u043D\u0435\u043D\u0438\u0435\n### T\xE4nus\xF5nad ja andestamine\n\n\u041F\u0440\u043E\u0434\u043E\u043B\u0436\u0430\u0435\u043C \u043D\u0430\u0440\u0430\u0449\u0438\u0432\u0430\u0442\u044C \u0441\u0432\u043E\u0439 \u0441\u043B\u043E\u0432\u0430\u0440\u043D\u044B\u0439 \u0437\u0430\u043F\u0430\u0441, \u0443\u0447\u0438\u043C\u0441\u044F \u0431\u043B\u0430\u0433\u043E\u0434\u0430\u0440\u0438\u0442\u044C, \u0430 \u0442\u0430\u043A\u0436\u0435 \u043F\u0440\u043E\u0441\u0438\u0442\u044C \u043F\u0440\u043E\u0449\u0435\u043D\u0438\u044F. \u041F\u043B\u044E\u0441 \u043D\u0438\u0436\u0435 \u043F\u0440\u0435\u0434\u0441\u0442\u0430\u0432\u043B\u0435\u043D\u044B \u043A\u043E\u0435-\u043A\u0430\u043A\u0438\u0435 \u043E\u0442\u0432\u0435\u0442\u044B, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u043C\u043E\u0436\u043D\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C \u0432 \u043F\u043E\u0432\u0441\u0435\u0434\u043D\u0435\u0432\u043D\u043E\u0439 \u0436\u0438\u0437\u043D\u0438. \n\n* Ait\xE4h! \u2014 \u0421\u043F\u0430\u0441\u0438\u0431\u043E!\n* Palun! \u2014 \u041F\u043E\u0436\u0430\u043B\u0443\u0439\u0441\u0442\u0430!\n* T\xE4nan! \u2014 \u0411\u043B\u0430\u0433\u043E\u0434\u0430\u0440\u044E!\n* Suur t\xE4nu! \u2014 \u0411\u043E\u043B\u044C\u0448\u043E\u0435 \u0441\u043F\u0430\u0441\u0438\u0431\u043E!\n* Suur ait\xE4h! \u2014 \u0411\u043E\u043B\u044C\u0448\u043E\u0435 \u0441\u043F\u0430\u0441\u0438\u0431\u043E!\n* Pole t\xE4nu v\xE4\xE4rt. \u2014 \u041D\u0435 \u0437\u0430 \u0447\u0442\u043E (\u0434\u043E\u0441\u043B\u043E\u0432\u043D\u043E: \u043D\u0435\u0442 \u0446\u0435\u043D\u044B \u0431\u043B\u0430\u0433\u043E\u0434\u0430\u0440\u043D\u043E\u0441\u0442\u0438).\n* Vabanda! / Vabandage! \u2014 \u0418\u0437\u0432\u0438\u043D\u0438! / \u0418\u0437\u0432\u0438\u043D\u0438\u0442\u0435!\n* Vabandust! \u2014 \u041F\u0440\u043E\u0441\u0442\u0438(\u0442\u0435)! \n* Anna andeks/ Andke andeks! \u2014 \u041F\u0440\u043E\u0441\u0442\u0438! / \u041F\u0440\u043E\u0441\u0442\u0438\u0442\u0435!\n* Pole midagi. \u2014 \u041D\u0438\u0447\u0435\u0433\u043E.\n* K\xF5ik on korras! \u2014 \u0412\u0441\u0435 \u0432 \u043F\u043E\u0440\u044F\u0434\u043A\u0435!\n* V\xF5ta heaks / v\xF5tke heaks! \u2014 \u041D\u0430 \u0437\u0434\u043E\u0440\u043E\u0432\u044C\u0435! (\u0435\u0434. \u0447.) / (\u043C\u043D. \u0447.)\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('lesson',{number:3}),source:source});}));

