var source='\nInimene \xF5pib, kuni elab. \u041A\u0430\u043A \u0433\u043E\u0432\u043E\u0440\u044F\u0442 \u043F\u043E-\u0440\u0443\u0441\u0441\u043A\u0438, \u0432\u0435\u043A \u0436\u0438\u0432\u0438 \u2014 \u0432\u0435\u043A \u0443\u0447\u0438\u0441\u044C. \u0427\u0435\u0441\u0442\u043D\u043E, \u043F\u043E\u0437\u043D\u0430\u0442\u044C \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u0432 \u0440\u0430\u043C\u043A\u0430\u0445 \u0431\u044B\u0441\u0442\u0440\u043E\u0433\u043E \u0438\u043D\u0442\u0435\u043D\u0441\u0438\u0432\u043D\u043E\u0433\u043E \u043A\u0443\u0440\u0441\u0430 \u043F\u0440\u0430\u043A\u0442\u0438\u0447\u0435\u0441\u043A\u0438 \u043D\u0435\u0432\u043E\u0437\u043C\u043E\u0436\u043D\u043E, \u0432\u0435\u0434\u044C \u0441\u043E\u0432\u0435\u0440\u0448\u0435\u043D\u0441\u0442\u0432\u0443 \u043D\u0435\u0442 \u043F\u0440\u0435\u0434\u0435\u043B\u0430. \u0425\u043E\u0442\u044F \u043C\u044B \u0441 \u0412\u0430\u043C\u0438 \u043F\u0440\u043E\u0448\u043B\u0438 \u0443\u0436\u0435 \u0431\u043E\u043B\u044C\u0448\u043E\u0439 \u043F\u0443\u0442\u044C \u2014 \u0441\u0435\u043C\u044C \u0443\u0440\u043E\u043A\u043E\u0432, \u043D\u043E \u0432\u043F\u0435\u0440\u0435\u0434\u0438 \u043D\u0430\u0441 \u0436\u0434\u0435\u0442 \u0435\u0449\u0435 \u043C\u043D\u043E\u0433\u043E \u0438\u043D\u0442\u0435\u0440\u0435\u0441\u043D\u043E\u0433\u043E. \u041F\u0440\u043E\u0434\u043E\u043B\u0436\u0430\u0435\u043C \u0433\u0440\u044B\u0437\u0442\u044C \u0433\u0440\u0430\u043D\u0438\u0442 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0439 \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u043A\u0438.\n\u0421\u0443\u043C\u043C\u0438\u0440\u0443\u0435\u043C \u0438\u043D\u0444\u043E\u0440\u043C\u0430\u0446\u0438\u044E, \u043A\u0430\u0441\u0430\u044E\u0449\u0443\u044E\u0441\u044F \u043F\u0430\u0434\u0435\u0436\u0435\u0439. \u041C\u044B \u0441 \u0412\u0430\u043C\u0438 \u043E\u0442\u0434\u0435\u043B\u044C\u043D\u043E \u0431\u0440\u0430\u043B\u0438 \u0441\u043D\u0430\u0447\u0430\u043B\u0430 3 \u043F\u0430\u0434\u0435\u0436\u0430 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430: nimetav, omastav, osastav. \u041D\u0430\u0437\u0432\u0430\u043B\u0438 \u0438\u0445 \u043E\u0441\u043D\u043E\u0432\u043D\u044B\u043C\u0438 \u043F\u0430\u0434\u0435\u0436\u0430\u043C\u0438, \u0438 \u0443\u0441\u043B\u043E\u0432\u0438\u043B\u0438\u0441\u044C, \u0447\u0442\u043E \u0432\u0441\u0435 \u0441\u043A\u043B\u043E\u043D\u044F\u0435\u043C\u044B\u0435 \u0441\u043B\u043E\u0432\u0430 \u043C\u044B \u0431\u0443\u0434\u0435\u043C \u0443\u0447\u0438\u0442\u044C \u0441\u0440\u0430\u0437\u0443 \u0432 \u0442\u0440\u0435\u0445 \u0444\u043E\u0440\u043C\u0430\u0445 \u0438\u043C\u0435\u043D\u043D\u043E \u044D\u0442\u0438\u0445 \u043F\u0430\u0434\u0435\u0436\u0435\u0439. \u0414\u0430\u043B\u0435\u0435 \u043C\u044B \u0432\u0437\u044F\u043B\u0438 \u0433\u0440\u0443\u043F\u043F\u0443 \xAB\u043F\u0430\u0434\u0435\u0436\u0435\u0439 \u043C\u0435\u0441\u0442\u0430\xBB \u2014 \u044D\u0442\u043E \u0446\u0435\u043B\u044B\u0445 6 \u0448\u0442\u0443\u043A \u0441\u0440\u0430\u0437\u0443. \u0420\u0430\u0441\u0441\u043C\u043E\u0442\u0440\u0435\u043B\u0438 \u0438\u0445 \u0441\u043B\u0443\u0447\u0430\u0438 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043D\u0438\u044F, \u0440\u0430\u0437\u0434\u0435\u043B\u0438\u043B\u0438 \u043E\u0441\u043D\u043E\u0432\u043D\u0443\u044E \u0444\u0443\u043D\u043A\u0446\u0438\u044E, \u0442.\u0435. \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0435 \u043C\u0435\u0441\u0442\u043E\u043F\u043E\u043B\u043E\u0436\u0435\u043D\u0438\u044F, \u043E\u0442 \u0432\u0442\u043E\u0440\u043E\u0441\u0442\u0435\u043F\u0435\u043D\u043D\u044B\u0445. \u0427\u0442\u043E \u0436\u0435 \u0434\u0430\u043B\u044C\u0448\u0435. \u041F\u0440\u043E\u0441\u0442\u0430\u044F \u043C\u0430\u0442\u0435\u043C\u0430\u0442\u0438\u043A\u0430: \u043E\u0442 14 \u043F\u0430\u0434\u0435\u0436\u0435\u0439 \u043E\u0442\u043D\u044F\u0442\u044C 3 \u0438 6, \u043F\u043E\u043B\u0443\u0447\u0438\u043B\u043E\u0441\u044C 5. \u0414\u0430\u0432\u0430\u0439\u0442\u0435 \u043D\u0435 \u0431\u0443\u0434\u0435\u043C \u043E\u0442\u043A\u043B\u0430\u0434\u044B\u0432\u0430\u0442\u044C \u0432 \u0434\u043E\u043B\u0433\u0438\u0439 \u044F\u0449\u0438\u043A, \u0438 \u0432\u043E\u0437\u044C\u043C\u0435\u043C \u043E\u0441\u0442\u0430\u0432\u0448\u0438\u0435\u0441\u044F 5 \u043F\u0430\u0434\u0435\u0436\u0435\u0439 \u043F\u0440\u044F\u043C\u043E \u0441\u0435\u0439\u0447\u0430\u0441.\n\n### \u041F\u0430\u0434\u0435\u0436 Saav\n\n\u041E\u0441\u043D\u043E\u0432\u043D\u044B\u0435 \u0432\u043E\u043F\u0440\u043E\u0441\u044B, \u043D\u0430 \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u043E\u0442\u0432\u0435\u0447\u0430\u0435\u0442 \u044D\u0442\u043E\u0442 \u043F\u0430\u0434\u0435\u0436: Kelleks? Milleks? Missuguseks? \u2014 \u041A\u0435\u043C? \u0427\u0435\u043C? \u0414\u043B\u044F \u0447\u0435\u0433\u043E? \u041A\u0430\u043A\u0438\u043C? \u0412 \u0440\u0443\u0441\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u0435\u043C\u0443 \u0447\u0430\u0449\u0435 \u0432\u0441\u0435\u0433\u043E \u0441\u043E\u043E\u0442\u0432\u0435\u0442\u0441\u0442\u0432\u0443\u0435\u0442 \u0442\u0432\u043E\u0440\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0439 \u043F\u0430\u0434\u0435\u0436. \u041F\u0440\u0438\u0437\u043D\u0430\u043A\u043E\u043C \u043F\u0430\u0434\u0435\u0436\u0430 \u044F\u0432\u043B\u044F\u0435\u0442\u0441\u044F \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -ks. \u0421 \u043F\u043E\u043C\u043E\u0449\u044C\u044E \u043F\u0430\u0434\u0435\u0436\u0430 saav \u043C\u043E\u0436\u043D\u043E \u0432\u044B\u0440\u0430\u0437\u0438\u0442\u044C:\n1. \u041F\u0435\u0440\u0435\u0445\u043E\u0434 \u0438\u0437 \u043E\u0434\u043D\u043E\u0433\u043E \u0441\u043E\u0441\u0442\u043E\u044F\u043D\u0438\u044F \u0438\u043B\u0438 \u043A\u0430\u0447\u0435\u0441\u0442\u0432\u0430 \u0432 \u0434\u0440\u0443\u0433\u043E\u0435. \u0418\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u0441 \u0442\u0430\u043A\u0438\u043C\u0438 \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C\u0438 \u043A\u0430\u043A: muutuma \u2014 \u043C\u0435\u043D\u044F\u0442\u044C\u0441\u044F, saama \u2014 \u0441\u0442\u0430\u043D\u043E\u0432\u0438\u0442\u044C\u0441\u044F, osutuma \u2014 \u043E\u043A\u0430\u0437\u0430\u0442\u044C\u0441\u044F, minema \u2014 \u0438\u0434\u0442\u0438, j\xE4\xE4ma \u2014 \u043E\u0441\u0442\u0430\u0432\u0430\u0442\u044C\u0441\u044F \u0438 \u0442.\u0434.\nVesi muutub j\xE4\xE4KS. \u0412\u043E\u0434\u0430 \u0441\u0442\u0430\u043D\u043E\u0432\u0438\u0442\u0441\u044F \u043B\u044C\u0434\u043E\u043C.\nMina tahan saada \xF5petajaKS. \u042F \u0445\u043E\u0447\u0443 \u0441\u0442\u0430\u0442\u044C \u0443\u0447\u0438\u0442\u0435\u043B\u0435\u043C.\nTalvel l\xE4heb vara pimedaKS. \xAB\u0417\u0438\u043C\u043E\u0439 \u0438\u0434\u0435\u0442 \u0440\u0430\u043D\u043E \u043D\u0430 \u0442\u0435\u043C\u043D\u0443\u044E\xBB, \u0417\u0438\u043C\u043E\u0439 \u0440\u0430\u043D\u043E \u0442\u0435\u043C\u043D\u0435\u0435\u0442.\n2. \u0412\u0440\u0117\u043C\u0435\u043D\u043D\u043E\u0435 \u0441\u043E\u0441\u0442\u043E\u044F\u043D\u0438\u0435, \u0434\u043E\u043B\u0436\u043D\u043E\u0441\u0442\u044C. \u0418\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u0441 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u043C olema \u2014 \u0431\u044B\u0442\u044C:\nMina olen t\xE4na \xF5htul emaKS. \u0421\u0435\u0433\u043E\u0434\u043D\u044F \u0432\u0435\u0447\u0435\u0440\u043E\u043C \u044F \u2014 \u043C\u0430\u043C\u0430.\n3. \u0421\u0440\u043E\u043A, \u043A \u043A\u043E\u0442\u043E\u0440\u043E\u043C\u0443 \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435 \u0437\u0430\u0432\u0435\u0440\u0448\u0438\u0442\u0441\u044F. Mis ajaks? \u041A \u043A\u0430\u043A\u043E\u043C\u0443 \u0441\u0440\u043E\u043A\u0443?\nTehke see harjutus homseKS! \u0421\u0434\u0435\u043B\u0430\u0439\u0442\u0435 \u044D\u0442\u043E \u0443\u043F\u0440\u0430\u0436\u043D\u0435\u043D\u0438\u0435 \u043A \u0437\u0430\u0432\u0442\u0440\u0430\u0448\u043D\u0435\u043C\u0443 \u0434\u043D\u044E!\n4. \u041F\u0440\u043E\u0434\u043E\u043B\u0436\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0441\u0442\u044C \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044F. Kui kauaks? \u0414\u043E\u0441\u043B\u043E\u0432\u043D\u043E  \u043F\u0435\u0440\u0435\u0432\u0435\u0434\u0435\u043C: \u041D\u0430 \u043A\u0430\u043A \u0434\u043E\u043B\u0433\u043E? \u0414\u0440\u0443\u0433\u0438\u043C\u0438 \u0441\u043B\u043E\u0432\u0430\u043C: \u041D\u0430 \u043A\u0430\u043A\u043E\u0439 \u0441\u0440\u043E\u043A?\nTema s\xF5idab kuuKS puhkusele. \u041E\u043D \u0435\u0434\u0435\u0442 \u043D\u0430 \u043C\u0435\u0441\u044F\u0446 \u0432 \u043E\u0442\u043F\u0443\u0441\u043A.\n5. \u0426\u0435\u043B\u044C. Milleks? \u0414\u043B\u044F \u0447\u0435\u0433\u043E?\nSalatiKS on vaja \xF5li. \u0414\u043B\u044F \u0441\u0430\u043B\u0430\u0442\u0430 \u043D\u0443\u0436\u043D\u043E \u043C\u0430\u0441\u043B\u043E.\n6. \u0427\u0430\u0441\u0442\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u043F\u0440\u0438 \u043F\u0435\u0440\u0435\u0447\u0438\u0441\u043B\u0435\u043D\u0438\u0438 \u0438 \u0432 \u043F\u043E\u0437\u0434\u0440\u0430\u0432\u043B\u0435\u043D\u0438\u044F\u0445.\nEsiteKS, see on minu t\xF6\xF6. Ja teiseKS, mulle see meeldib. \u0412\u043E-\u043F\u0435\u0440\u0432\u044B\u0445, \u044D\u0442\u043E \u043C\u043E\u044F \u0440\u0430\u0431\u043E\u0442\u0430. \u0418 \u0432\u043E-\u0432\u0442\u043E\u0440\u044B\u0445, \u043C\u043D\u0435 \u044D\u0442\u043E \u043D\u0440\u0430\u0432\u0438\u0442\u0441\u044F.\nPalju \xF5nne s\xFCnnip\xE4evaKS! \u041F\u043E\u0437\u0434\u0440\u0430\u0432\u043B\u044F\u044E \u0441 \u0434\u043D\u0435\u043C \u0440\u043E\u0436\u0434\u0435\u043D\u0438\u044F!\n\n### \u041F\u0430\u0434\u0435\u0436 Rajav\n\n\u0412\u043E\u043F\u0440\u043E\u0441\u044B \u2014 Kelleni? Milleni? \u2014 \u0414\u043E \u043A\u043E\u0433\u043E? \u0414\u043E \u0447\u0435\u0433\u043E? \u041F\u0440\u0438\u0437\u043D\u0430\u043A\u043E\u043C \u043F\u0430\u0434\u0435\u0436\u0430 \u044F\u0432\u043B\u044F\u0435\u0442\u0441\u044F \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -ni.  \u041F\u0430\u0434\u0435\u0436 rajav \u0432\u044B\u0440\u0430\u0436\u0430\u0435\u0442:\n1. \u041F\u0440\u043E\u0441\u0442\u0440\u0430\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u0443\u044E \u0433\u0440\u0430\u043D\u0438\u0446\u0443, \u043F\u0440\u0435\u0434\u0435\u043B:\nMinuNI j\xF5uab muusika. \u0414\u043E \u043C\u0435\u043D\u044F \u0434\u043E\u0445\u043E\u0434\u0438\u0442 \u043C\u0443\u0437\u044B\u043A\u0430. \nMe jalutame pargiNI. \u041C\u044B \u043F\u0440\u043E\u0433\u0443\u043B\u0438\u0432\u0430\u0435\u043C\u0441\u044F \u0434\u043E \u043F\u0430\u0440\u043A\u0430.\n2. \u0412\u0440\u0435\u043C\u0435\u043D\u043D\u043E\u0439 \u043F\u0440\u0435\u0434\u0435\u043B:\nKauplus on avatud esmasp\xE4evast reedeNI. \u041C\u0430\u0433\u0430\u0437\u0438\u043D \u043E\u0442\u043A\u0440\u044B\u0442 \u0441 \u043F\u043E\u043D\u0435\u0434\u0435\u043B\u044C\u043D\u0438\u043A\u0430 \u043F\u043E \u043F\u044F\u0442\u043D\u0438\u0446\u0443.\n\n### \u041F\u0430\u0434\u0435\u0436 Olev\n\n\u0414\u0430\u043D\u043D\u044B\u0439 \u043F\u0430\u0434\u0435\u0436 \u043E\u0442\u0432\u0435\u0447\u0430\u0435\u0442 \u0432 \u043E\u0441\u043D\u043E\u0432\u043D\u043E\u043C \u043D\u0430 \u0432\u043E\u043F\u0440\u043E\u0441\u044B Kellena? Millena? Missugusena? Kuidas? \u2014 \u041A\u0435\u043C? \u0427\u0435\u043C? \u041A\u0430\u043A\u0438\u043C? \u0438\u043B\u0438 \u041A\u0430\u043A? \u041E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 \u0434\u043B\u044F  \u043F\u0430\u0434\u0435\u0436\u0430 olev \u2014 \u044D\u0442\u043E \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -na. \u0421 \u043F\u043E\u043C\u043E\u0449\u044C\u044E \u043D\u0435\u0433\u043E \u043C\u043E\u0436\u043D\u043E \u0432\u044B\u0440\u0430\u0437\u0438\u0442\u044C:\n1. \u0412\u044B\u043F\u043E\u043B\u043D\u0435\u043D\u0438\u0435 \u0444\u0443\u043D\u043A\u0446\u0438\u0439, \u043F\u0440\u0438\u0437\u043D\u0430\u043A \u043F\u0440\u0435\u0434\u043C\u0435\u0442\u0430 \u0438\u043B\u0438 \u043B\u0438\u0446\u0430:\nIsa t\xF6\xF6tab arstinNA. \u041F\u0430\u043F\u0430 \u0440\u0430\u0431\u043E\u0442\u0430\u0435\u0442 \u0432\u0440\u0430\u0447\u043E\u043C.\nEma tuli koju v\xE4sinuNA. \u041C\u0430\u043C\u0430 \u043F\u0440\u0438\u0448\u043B\u0430 \u0434\u043E\u043C\u043E\u0439 \u0443\u0441\u0442\u0430\u0432\u0448\u0435\u0439.\n2. \u0421\u0440\u0430\u0432\u043D\u0435\u043D\u0438\u0435. \u0412 \u044D\u0442\u043E\u043C \u0441\u043B\u0443\u0447\u0430\u0435 \u0432\u043E\u043F\u0440\u043E\u0441 \xABkellena?\xBB \u043C\u044B \u043F\u0435\u0440\u0435\u0432\u0435\u0434\u0435\u043C \xAB\u043A\u0430\u043A \u043A\u0442\u043E?\xBB , \u0442\u043E \u0435\u0441\u0442\u044C \u0443\u043A\u0430\u0436\u0435\u043C \u0432 \u043A\u0430\u043A\u043E\u043C \u043A\u0430\u0447\u0435\u0441\u0442\u0432\u0435 \u0441\u0443\u0431\u044A\u0435\u043A\u0442 \u0432\u044B\u043F\u043E\u043B\u043D\u044F\u0435\u0442 \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435:\nTa on inimeseNA v\xE4ga hea. \u041A\u0430\u043A \u0447\u0435\u043B\u043E\u0432\u0435\u043A \u043E\u043D \u043E\u0447\u0435\u043D\u044C \u0445\u043E\u0440\u043E\u0448\u0438\u0439.\nKooli direktoriNA peab tema ka eksamil istuma. \u041A\u0430\u043A \u0434\u0438\u0440\u0435\u043A\u0442\u043E\u0440 \u0448\u043A\u043E\u043B\u044B \u043E\u043D \u0442\u043E\u0436\u0435 \u0434\u043E\u043B\u0436\u0435\u043D \u0441\u0438\u0434\u0435\u0442\u044C \u043D\u0430 \u044D\u043A\u0437\u0430\u043C\u0435\u043D\u0435.\n\n\u041A\u0430\u043A \u0432\u044B \u043C\u043E\u0433\u043B\u0438 \u0437\u0430\u043C\u0435\u0442\u0438\u0442\u044C, \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u043D\u0430 \u0432\u043E\u043F\u0440\u043E\u0441\u044B \xAB\u041A\u0435\u043C?\xBB, \xAB\u0427\u0435\u043C?\xBB \u043E\u0442\u0432\u0435\u0447\u0430\u044E\u0442 \u0441\u0440\u0430\u0437\u0443 2 \u043F\u0430\u0434\u0435\u0436\u0430. \u041D\u043E \u0440\u0430\u0437\u043D\u0438\u0446\u0430 \u043C\u0435\u0436\u0434\u0443 \u043D\u0438\u043C\u0438 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u0430\u044F. \u0421\u043C\u043E\u0442\u0440\u0438\u0442\u0435, \u0435\u0441\u043B\u0438 \u0447\u0435\u043B\u043E\u0432\u0435\u043A, \u043F\u0440\u0435\u0434\u043C\u0435\u0442 \u0443\u0436\u0435 \u043A\u0435\u043C-\u0442\u043E \u044F\u0432\u043B\u044F\u0435\u0442\u0441\u044F, \u0440\u0430\u0431\u043E\u0442\u0430\u0435\u0442, \u0438\u043B\u0438 \u0434\u0430\u0436\u0435 \u0432\u044B\u0433\u043B\u044F\u0434\u0438\u0442 \u043A\u0430\u043A-\u0442\u043E, \u043A\u0435\u043C-\u0442\u043E \u043A\u0430\u0436\u0435\u0442\u0441\u044F, \u0442\u043E \u0432 \u044D\u0442\u043E\u043C \u0441\u043B\u0443\u0447\u0430\u0435 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u043F\u0430\u0434\u0435\u0436 OLEV, \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -NA.  \u0410 \u0435\u0441\u043B\u0438 \u043E\u043D, \u043E\u043D\u043E \u0442\u043E\u043B\u044C\u043A\u043E \u043A\u0435\u043C-\u0442\u043E, \u0447\u0435\u043C-\u0442\u043E \u0441\u0442\u0430\u043D\u043E\u0432\u0438\u0442\u0441\u044F, \u0438\u043B\u0438 \u0442\u043E\u043B\u044C\u043A\u043E \u0435\u0449\u0435 \u0445\u043E\u0447\u0435\u0442 \u0441\u0442\u0430\u0442\u044C, \u0442\u043E\u0433\u0434\u0430 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F SAAV \u0441 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435\u043C -KS.\n\n### \u041F\u0430\u0434\u0435\u0436 Ilma\xFCtlev\n\n\u041E\u0442\u0432\u0435\u0447\u0430\u0435\u0442 \u043D\u0430 \u0432\u043E\u043F\u0440\u043E\u0441\u044B: Kelleta? Milleta? \u2014 \u0431\u0435\u0437 \u043A\u043E\u0433\u043E? \u0431\u0435\u0437 \u0447\u0435\u0433\u043E? \u041E\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u0432\u0441\u0435\u0433\u0434\u0430 \u043D\u0430 -ta.\n\u0421 \u044D\u0442\u0438\u043C \u043F\u0430\u0434\u0435\u0436\u043E\u043C \u0432\u0441\u0435 \u043F\u0440\u043E\u0441\u0442\u043E, \u043E\u043D \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0435\u0442 \u043E\u0442\u0441\u0443\u0442\u0441\u0442\u0432\u0438\u0435 \u043A\u043E\u0433\u043E-\u043D\u0438\u0431\u0443\u0434\u044C \u0438\u043B\u0438 \u0447\u0435\u0433\u043E-\u043D\u0438\u0431\u0443\u0434\u044C. \nN\xE4ide:\nIlma minuTA poleks sa hakkama saanud. \u0411\u0435\u0437 \u043C\u0435\u043D\u044F \u0442\u044B \u0431\u044B \u043D\u0435 \u0441\u043F\u0440\u0430\u0432\u0438\u043B\u0441\u044F.\nT\xFCdruk l\xE4heb raamatuTA kooli. \u0414\u0435\u0432\u043E\u0447\u043A\u0430 \u0438\u0434\u0435\u0442 \u0432 \u0448\u043A\u043E\u043B\u0443 \u0431\u0435\u0437 \u043A\u043D\u0438\u0433\u0438.\nLapsed s\xF6\xF6vad ilma isuTA. \u0414\u0435\u0442\u0438 \u0435\u0434\u044F\u0442 \u0431\u0435\u0437 \u0430\u043F\u043F\u0435\u0442\u0438\u0442\u0430.\nNB! \u041F\u0440\u0435\u0434\u043B\u043E\u0433 \xABilma\xBB \u043C\u043E\u0436\u043D\u043E \u0447\u0430\u0441\u0442\u043E \u0443\u0432\u0438\u0434\u0435\u0442\u044C \u0432\u043C\u0435\u0441\u0442\u0435 \u0441 \u044D\u0442\u0438\u043C \u043F\u0430\u0434\u0435\u0436\u043E\u043C. \u041F\u0435\u0440\u0435\u0432\u0435\u0434\u0435\u043C \u0435\u0433\u043E \u043A\u0430\u043A \xAB\u0431\u0435\u0437\xBB. \u041E\u043D \u043D\u0435 \u043E\u0442\u043C\u0435\u043D\u044F\u0435\u0442 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -TA,  \u043A\u043E\u0442\u043E\u0440\u043E\u0435 \u0441\u0442\u043E\u0438\u0442 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C \u0432 \u043B\u044E\u0431\u043E\u043C \u0441\u043B\u0443\u0447\u0430\u0435, \u0434\u0430\u043D\u043D\u044B\u0439 \u043F\u0440\u0435\u0434\u043B\u043E\u0433 \u043B\u0438\u0448\u044C \u0443\u0441\u0438\u043B\u0438\u0432\u0430\u0435\u0442 \u0435\u0433\u043E \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0435.\n\n### \u041F\u0430\u0434\u0435\u0436 Kaasa\xFCtlev\n\n\u041E\u0442\u0432\u0435\u0447\u0430\u0435\u0442 \u043D\u0430 \u0432\u043E\u043F\u0440\u043E\u0441\u044B: Kellega? Millega? \u2014 \u0441 \u043A\u0435\u043C? \u0441 \u0447\u0435\u043C? \u0438\u043B\u0438 \u043F\u0440\u043E\u0441\u0442\u043E \u043A\u0435\u043C? \u0447\u0435\u043C? \u0414\u0430\u043D\u043D\u044B\u0439 \u043F\u0430\u0434\u0435\u0436 \u0432\u0441\u0435\u0433\u0434\u0430 \u043E\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u043D\u0430 -ga, \u0438 \u0438\u043C\u0435\u0435\u0442 \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u0441\u043B\u0435\u0434\u0443\u044E\u0449\u0438\u0435 \u043E\u0441\u043D\u043E\u0432\u043D\u044B\u0435 \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u044F:\n1. \u041E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0435\u0442 \u0441\u043E\u0432\u043C\u0435\u0441\u0442\u043D\u043E\u0435 \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435 \u0438 \u0441\u043E\u043F\u0440\u043E\u0432\u043E\u0436\u0434\u0435\u043D\u0438\u0435. \u041D\u0430 \u0440\u0443\u0441\u0441\u043A\u0438\u043C \u044F\u0437\u044B\u043A \u043C\u044B \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u043C \u0442\u0432\u043E\u0440\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u043C \u043F\u0430\u0434\u0435\u0436\u043E\u043C \u0441 \u043F\u0440\u0435\u0434\u043B\u043E\u0433\u043E\u043C  \xAB\u0441\xBB:\nSel \xF5htul mina olen s\xF5pradeGA. \u042D\u0442\u0438\u043C \u0432\u0435\u0447\u0435\u0440\u043E\u043C \u044F \u0431\u0443\u0434\u0443 \u0441 \u0434\u0440\u0443\u0437\u044C\u044F\u043C\u0438.\nSuure r\xF5\xF5muGA ma tulen sinna.  \u0421 \u0431\u043E\u043B\u044C\u0448\u0438\u043C \u0443\u0434\u043E\u0432\u043E\u043B\u044C\u0441\u0442\u0432\u0438\u0435\u043C \u044F \u043F\u0440\u0438\u0434\u0443 \u0442\u0443\u0434\u0430.\nMina elan koos emaGA. \u042F \u0436\u0438\u0432\u0443 \u0432\u043C\u0435\u0441\u0442\u0435 \u0441 \u043C\u0430\u043C\u043E\u0439.\nNB! \u041F\u0440\u0435\u0434\u043B\u043E\u0433 \xABkoos\xBB \u0443\u0441\u0438\u043B\u0438\u0432\u0430\u0435\u0442 \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0435 \u043F\u0430\u0434\u0435\u0436\u0430 kaasa\xFCtlev, \u0438 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u0441\u044F \u043A\u0430\u043A \xAB\u0432\u043C\u0435\u0441\u0442\u0435\xBB.\n2. \u041E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0435\u0442 \u043E\u0440\u0443\u0434\u0438\u0435 \u0438\u043B\u0438 \u0441\u0440\u0435\u0434\u0441\u0442\u0432\u043E \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044F (\u0447\u0435\u043C? \u043F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0447\u0435\u0433\u043E?): \n\u0422\u0430 t\xF6\xF6tab labidaGA. \u041E\u043D \u0440\u0430\u0431\u043E\u0442\u0430\u0435\u0442 \u043B\u043E\u043F\u0430\u0442\u043E\u0439. \nMina kirjutan pliiatsiGA. \u042F \u043F\u0438\u0448\u0443 \u0440\u0443\u0447\u043A\u043E\u0439.\n3. \u041E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0435\u0442 \u0441\u0440\u0435\u0434\u0441\u0442\u0432\u043E \u043F\u0435\u0440\u0435\u0434\u0432\u0438\u0436\u0435\u043D\u0438\u044F. \u041D\u0430 \u0440\u0443\u0441\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u043F\u0435\u0440\u0435\u0432\u0435\u0434\u0435\u043C \u0442\u0432\u043E\u0440\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u043C \u043F\u0430\u0434\u0435\u0436\u043E\u043C \u0441 \u043F\u0440\u0435\u0434\u043B\u043E\u0433\u043E\u043C \xAB\u043D\u0430\xBB:\nMina s\xF5idan bussiGA. \u042F \u0435\u0434\u0443 \u043D\u0430 \u0430\u0432\u0442\u043E\u0431\u0443\u0441\u0435.\nMeie l\xE4heme puhkusele rongiGA. \u041C\u044B \u043F\u043E\u0435\u0434\u0435\u043C \u0432 \u043E\u0442\u043F\u0443\u0441\u043A \u043D\u0430 \u043F\u043E\u0435\u0437\u0434\u0435.\n4. \u041E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0435\u0442 \u043F\u0440\u0438\u0437\u043D\u0430\u043A \u043A\u0430\u043A\u043E\u0433\u043E-\u043B\u0438\u0431\u043E \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0430, \u043F\u0440\u0435\u0434\u043C\u0435\u0442\u0430, \u044F\u0432\u043B\u0435\u043D\u0438\u044F. \u041F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u0441\u044F \u0442\u0432\u043E\u0440\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u043C \u043F\u0430\u0434\u0435\u0436\u043E\u043C \u0441 \u043F\u0440\u0435\u0434\u043B\u043E\u0433\u043E\u043C \xAB\u0441\xBB.\nSee oli pika kaelaGA lind. \u042D\u0442\u043E \u0431\u044B\u043B\u0430 \u043F\u0442\u0438\u0446\u0430 \u0441 \u0434\u043B\u0438\u043D\u043D\u043E\u0439 \u0448\u0435\u0435\u0439. \nNaisel on lukuGA saapad. \u0423 \u0434\u0430\u043C\u044B \u0441\u0430\u043F\u043E\u0433\u0438 \u043D\u0430 \u043C\u043E\u043B\u043D\u0438\u0438 (\u0437\u0430\u043C\u043A\u0435).\n5. \u041E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0435\u0442 \u043F\u0440\u043E\u043C\u0435\u0436\u0443\u0442\u043E\u043A \u0432\u0440\u0435\u043C\u0435\u043D\u0438, \u043D\u0435\u043E\u0431\u0445\u043E\u0434\u0438\u043C\u044B\u0439 \u0434\u043B\u044F \u0441\u043E\u0432\u0435\u0440\u0448\u0435\u043D\u0438\u044F \u043A\u0430\u043A\u043E\u0433\u043E-\u043B\u0438\u0431\u043E \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044F. \u041E\u0431\u044B\u0447\u043D\u043E \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F\u0445 \u0442\u0430\u043A\u043E\u0433\u043E \u0440\u043E\u0434\u0430 \u0432\u043E\u043F\u0440\u043E\u0441\u043E\u043C \u0441\u043B\u0443\u0436\u0438\u0442 \xABmis ajaga?\xBB \u2014 \xAB\u0437\u0430 \u043A\u0430\u043A\u043E\u0435 \u0432\u0440\u0435\u043C\u044F?\xBB. \u041D\u0430 \u0440\u0443\u0441\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u043C\u043E\u0436\u043D\u043E \u043F\u0435\u0440\u0435\u0432\u0435\u0441\u0442\u0438 \u0442\u0430\u043A\u0436\u0435 \u0440\u043E\u0434\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u043C \u043F\u0430\u0434\u0435\u0436\u043E\u043C \u0441 \u043F\u0440\u0435\u0434\u043B\u043E\u0433\u043E\u043C \xAB\u0432 \u0442\u0435\u0447\u0435\u043D\u0438\u0435\xBB:\nMina l\xF5petan t\xF6\xF6 poole tunniGA. \u042F \u0437\u0430\u043A\u043E\u043D\u0447\u0443  \u0440\u0430\u0431\u043E\u0442\u0443 \u0437\u0430 \u043F\u043E\u043B\u0447\u0430\u0441\u0430.\nN\xE4dalaGA saab see portree valmis. \u0412 \u0442\u0435\u0447\u0435\u043D\u0438\u0435 \u043D\u0435\u0434\u0435\u043B\u0438 \u044D\u0442\u043E\u0442 \u043F\u043E\u0440\u0442\u0440\u0435\u0442 \u0431\u0443\u0434\u0435\u0442 \u0437\u0430\u043A\u043E\u043D\u0447\u0435\u043D.\n\n### \u041F\u0430\u0434\u0435\u0436\u0438 NINATAGA\n\n\u041D\u0430\u043F\u043E\u043C\u043D\u044E, \u043F\u0430\u0434\u0435\u0436\u0435\u0439 \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u0432\u0441\u0435\u0433\u043E 14 \u0448\u0442\u0443\u043A, \u0438 \u043D\u0430 \u043F\u0430\u0434\u0435\u0436\u0435 kaasa\xFCtlev \u043E\u043D\u0438 \u0443 \u043D\u0430\u0441 \u0437\u0430\u043A\u043E\u043D\u0447\u0438\u043B\u0438\u0441\u044C. \u041F\u0440\u0430\u0432\u0438\u043B\u043E, \u043E\u043F\u0438\u0441\u0430\u043D\u043D\u043E\u0435 \u043D\u0438\u0436\u0435, \u043A\u0430\u0441\u0430\u0435\u0442\u0441\u044F \u043D\u0435 \u043A\u0430\u043A\u0438\u0445-\u0442\u043E \u043D\u043E\u0432\u044B\u0445 \u043F\u0430\u0434\u0435\u0436\u0435\u0439, \u0430 \u043B\u0438\u0448\u044C \u0440\u0430\u0437\u0431\u0438\u0440\u0430\u0435\u0442 \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u0447\u0435\u0441\u043A\u0438\u0435 \u043E\u0441\u043E\u0431\u0435\u043D\u043D\u043E\u0441\u0442\u0438 \u043D\u0435\u043A\u043E\u0442\u043E\u0440\u044B\u0445 \u0438\u0437 14\u0442\u0438 \u0443\u0436\u0435 \u0438\u0437\u0432\u0435\u0441\u0442\u043D\u044B\u0445 \u043D\u0430\u043C. \n\u0412 \u043F\u0440\u0438\u043C\u0435\u0440\u0430\u0445 \u043A \u043F\u044F\u0442\u0438 \u043F\u043E\u0441\u043B\u0435\u0434\u043D\u0438\u043C \u043F\u0430\u0434\u0435\u0436\u0430\u043C \u0432 \u043E\u0441\u043D\u043E\u0432\u043D\u043E\u043C \u0431\u044B\u043B\u0438 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F, \u0433\u0434\u0435 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043B\u043E\u0441\u044C \u0442\u043E\u043B\u044C\u043A\u043E \u043E\u0434\u043D\u043E \u0441\u043B\u043E\u0432\u043E, \u044F \u0438\u043C\u0435\u044E \u0432 \u0432\u0438\u0434\u0443 \u043D\u0435 \u0441\u043B\u043E\u0432\u043E\u0441\u043E\u0447\u0435\u0442\u0430\u043D\u0438\u0435, \u0438 \u043D\u0435 \u043F\u0435\u0440\u0435\u0447\u0438\u0441\u043B\u0435\u043D\u0438\u0435. \u0421 \u043E\u0434\u043D\u0438\u043C \u0441\u043B\u043E\u0432\u043E\u043C \u043A\u0430\u043A \u0433\u043E\u0432\u043E\u0440\u0438\u0442\u0441\u044F \u043C\u0435\u043D\u044C\u0448\u0435 \u043F\u0440\u043E\u0431\u043B\u0435\u043C. \u0410 \u0432\u043E\u0442 \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435 \u044D\u0442\u0438\u0445 \u0436\u0435 \u043F\u0430\u0434\u0435\u0436\u0435\u0439 \u0443\u0436\u0435 \u0432 \u0441\u043B\u043E\u0432\u043E\u0441\u043E\u0447\u0435\u0442\u0430\u043D\u0438\u044F\u0445 \u0438\u043B\u0438 \u043F\u0440\u0438 \u043F\u0435\u0440\u0435\u0447\u0438\u0441\u043B\u0435\u043D\u0438\u0438 \u0441\u0442\u043E\u0438\u0442 \u0440\u0430\u0441\u0441\u043C\u043E\u0442\u0440\u0435\u0442\u044C \u043E\u0442\u0434\u0435\u043B\u044C\u043D\u043E. \n\u041F\u0430\u0434\u0435\u0436\u0438, \u0440\u0430\u0441\u0441\u043C\u0430\u0442\u0440\u0438\u0432\u0430\u0435\u043C\u044B\u0435 \u0432 \u044D\u0442\u043E\u043C \u0443\u0440\u043E\u043A\u0435, \u0443\u0436\u0435 \u043D\u0435 \u043F\u0435\u0440\u0432\u044B\u0435 \u0434\u043B\u044F \u043D\u0430\u0441. \u041C\u044B \u0443\u0436\u0435 \u0438\u0437\u0443\u0447\u0438\u043B\u0438 \u043F\u0435\u0440\u0432\u044B\u0435 \u0442\u0440\u0438 \u0433\u043B\u0430\u0432\u043D\u044B\u0445 \u043F\u0430\u0434\u0435\u0436\u0430, \u0438 \xAB\u043F\u0430\u0434\u0435\u0436\u0430\u043C\u0438 \u043C\u0435\u0441\u0442\u0430\xBB. \u041A\u0430\u043A \u0412\u044B \u043F\u043E\u043C\u043D\u0438\u0442\u0435, \u0440\u0430\u0441\u0441\u043C\u0430\u0442\u0440\u0438\u0432\u0430\u044F \u0432\u0441\u0435 \u044D\u0442\u0438 \u043F\u0430\u0434\u0435\u0436\u0438, \u0434\u043B\u044F \u043D\u0430\u0441 \u0431\u044B\u043B \u0432\u0430\u0436\u0435\u043D \u0432\u0441\u0435\u0433\u0434\u0430 \u0432\u043E\u043F\u0440\u043E\u0441 (\u043A\u0442\u043E, \u0447\u0442\u043E, \u0433\u0434\u0435, \u043A\u0443\u0434\u0430, \u043E\u0442\u043A\u0443\u0434\u0430 \u0438\u0442\u0434.), \u043D\u0430 \u043A\u043E\u0442\u043E\u0440\u044B\u0439 \u0442\u043E\u0442 \u0438\u043B\u0438 \u0438\u043D\u043E\u0439 \u043F\u0430\u0434\u0435\u0436 \u043E\u0442\u0432\u0435\u0447\u0430\u0435\u0442, \u0438 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435, \u043A\u0430\u043A \u0435\u0433\u043E \u043F\u0440\u0438\u0437\u043D\u0430\u043A. \u0415\u0441\u043B\u0438 \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0438 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043B\u043E\u0441\u044C \u0441\u043B\u043E\u0432\u043E\u0441\u043E\u0447\u0435\u0442\u0430\u043D\u0438\u0435, \u0442\u043E \u0432 \u043D\u0435\u043C \u0432\u0441\u0435 \u0441\u043B\u043E\u0432\u0430, \u043A \u043A\u043E\u0442\u043E\u0440\u044B\u043C \u0437\u0430\u0434\u0430\u0432\u0430\u043B\u0441\u044F \u043E\u0434\u0438\u043D \u0438 \u0442\u043E\u0442 \u0436\u0435 \u043F\u0430\u0434\u0435\u0436\u043D\u044B\u0439 \u0432\u043E\u043F\u0440\u043E\u0441, \u0441\u043E\u0433\u043B\u0430\u0441\u043E\u0432\u044B\u0432\u0430\u043B\u0438\u0441\u044C \u043F\u043E \u0447\u0438\u0441\u043B\u0443 \u0438 \u043F\u0430\u0434\u0435\u0436\u0443. \nN\xE4ide:\nSelleL meheL on hea naine. (\u0423 \u043A\u043E\u0433\u043E?) -> \u0423 \u044D\u0442\u043E\u0433\u043E \u043C\u0443\u0436\u0447\u0438\u043D\u044B (\u0435\u0441\u0442\u044C \u043A\u0442\u043E?) ->\u0445\u043E\u0440\u043E\u0448\u0430\u044F \u0436\u0435\u043D\u0430.\nMina r\xE4\xE4gin oma huvitavaST t\xF6\xF6ST. \u042F \u0440\u0430\u0441\u0441\u043A\u0430\u0436\u0443 (\u043E \u0447\u0435\u043C?) -> \u043E \u0441\u0432\u043E\u0435\u0439 \u0438\u043D\u0442\u0435\u0440\u0435\u0441\u043D\u043E\u0439 \u0440\u0430\u0431\u043E\u0442\u0435.\nMeie s\xF5idame Kohtla-J\xE4rveLE, TapaLE ja J\xE4gevaLE. \u041C\u044B \u0435\u0434\u0435\u043C (\u043A\u0443\u0434\u0430?-> \u0432 \u041A\u043E\u0445\u0442\u043B\u0430-\u042F\u0440\u0432\u0435, \u0432 \u0422\u0430\u043F\u0430 \u0438 \u0432 \u0419\u044B\u0433\u0435\u0432\u0430.\n\u0422\u043E\u0447\u043D\u043E \u0442\u0430\u043A\u0436\u0435 \u0431\u0443\u0434\u0435\u0442 \u0441\u0435\u0431\u044F \u0432\u0435\u0441\u0442\u0438 \u0438 \u043F\u0430\u0434\u0435\u0436 Saav. \u041E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -ks \u0431\u0443\u0434\u0435\u0442 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0442\u044C\u0441\u044F \u043A \u043A\u0430\u0436\u0434\u043E\u043C\u0443 \u0441\u043B\u043E\u0432\u0443:\nN\xE4ide:\nTema saab varsti heaKS arstiKS. \u041E\u043D \u0441\u043A\u043E\u0440\u043E \u0441\u0442\u0430\u043D\u0435\u0442 \u0445\u043E\u0440\u043E\u0448\u0438\u043C \u0432\u0440\u0430\u0447\u043E\u043C. \nLugege raamat l\xE4bi j\xE4rgmiseKS esmasp\xE4evaKS. \u041F\u0440\u043E\u0447\u0438\u0442\u0430\u0439\u0442\u0435 \u043A\u043D\u0438\u0433\u0443 \u043A \u0441\u043B\u0435\u0434\u0443\u044E\u0449\u0435\u043C\u0443 \u043F\u043E\u043D\u0435\u0434\u0435\u043B\u044C\u043D\u0438\u043A\u0443.\n\u0418\u043D\u0430\u0447\u0435 \u043E\u0431\u0441\u0442\u043E\u0438\u0442 \u0434\u0435\u043B\u043E \u0441 \u043F\u043E\u0441\u043B\u0435\u0434\u043D\u0438\u043C\u0438 \u0447\u0435\u0442\u044B\u0440\u044C\u043C\u044F \u043F\u0430\u0434\u0435\u0436\u0430\u043C\u0438 \u0438\u0437 \u043D\u0430\u0448\u0435\u0433\u043E \u043E\u0431\u0449\u0435\u0433\u043E \u0441\u043F\u0438\u0441\u043A\u0430. \u0412 \u0441\u043B\u0443\u0447\u0430\u0435 \u0441 \u043D\u0438\u043C\u0438, \u043F\u0430\u0434\u0435\u0436\u043D\u043E\u0435 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 \u0431\u0443\u0434\u0435\u0442 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0442\u044C\u0441\u044F \u0442\u043E\u043B\u044C\u043A\u043E \u043A \u043F\u043E\u0441\u043B\u0435\u0434\u043D\u0435\u043C\u0443 \u0441\u043B\u043E\u0432\u0443, \u043D\u0435\u0437\u0430\u0432\u0438\u0441\u0438\u043C\u043E \u043E\u0442 \u0442\u043E\u0433\u043E, \u044D\u0442\u043E \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u0441 \u043E\u043F\u0438\u0441\u044B\u0432\u0430\u044E\u0449\u0438\u043C\u0438 \u0435\u0433\u043E \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u043C\u0438, \u0438\u043B\u0438 \u0436\u0435 \u044D\u0442\u043E \u043F\u0435\u0440\u0435\u0447\u0438\u0441\u043B\u0435\u043D\u0438\u0435. \u0412\u0441\u0435 \u0441\u043B\u043E\u0432\u0430 \u0441\u0442\u043E\u044F\u0449\u0438\u0435 \u043F\u0435\u0440\u0435\u0434 \u043F\u043E\u0441\u043B\u0435\u0434\u043D\u0438\u043C \u0441\u043B\u043E\u0432\u043E\u043C \u043E\u0441\u0442\u0430\u044E\u0442\u0441\u044F \u0432\u043E \u0432\u0442\u043E\u0440\u043E\u043C \u043F\u0430\u0434\u0435\u0436\u0435.\nN\xE4ide:\nOotame koos kuni j\xE4rgmise filmiNI. \u041F\u043E\u0434\u043E\u0436\u0434\u0435\u043C \u0432\u043C\u0435\u0441\u0442\u0435 \u0434\u043E \u0441\u043B\u0435\u0434\u0443\u044E\u0449\u0435\u0433\u043E \u0444\u0438\u043B\u044C\u043C\u0430.\nTa on tuntud hea n\xE4itlejaNA. \u041E\u043D \u0438\u0437\u0432\u0435\u0441\u0442\u0435\u043D \u043A\u0430\u043A \u0445\u043E\u0440\u043E\u0448\u0438\u0439 \u0430\u043A\u0442\u0435\u0440.\nMina joon tavaliselt kohvi ilma koore ja suhkruTA. \u042F \u043F\u044C\u044E \u043E\u0431\u044B\u0447\u043D\u043E \u043A\u043E\u0444\u0435 \u0431\u0435\u0437 \u0441\u043B\u0438\u0432\u043E\u043A \u0438 \u0441\u0430\u0445\u0430\u0440\u0430.\nMeie s\xF5idame takso, bussi ja laevaGA. \u041C\u044B \u043F\u043E\u0435\u0434\u0435\u043C \u043D\u0430 \u0442\u0430\u043A\u0441\u0438, \u0430\u0432\u0442\u043E\u0431\u0443\u0441\u0435 \u0438 \u043D\u0430 \u043A\u043E\u0440\u0430\u0431\u043B\u0435.\n\u041F\u0440\u0430\u0432\u0438\u043B\u043E, \u0440\u0435\u0433\u043B\u0430\u043C\u0435\u043D\u0442\u0438\u0440\u0443\u044E\u0449\u0435\u0435 \u0443\u043A\u0430\u0437\u0430\u043D\u043D\u043E\u0435 \u0432\u044B\u0448\u0435, \u043D\u0430\u0437\u044B\u0432\u0430\u0435\u0442\u0441\u044F \u043F\u0440\u0430\u0432\u0438\u043B\u043E\u043C \u043F\u0430\u0434\u0435\u0436\u0435\u0439 NINATAGA (\u043F\u043E \u0441\u043F\u043E\u0441\u043E\u0431\u0443 \u0441\u043E\u0435\u0434\u0438\u043D\u0435\u043D\u0438\u044F \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0439 \u2014 ni+na+ta+ga). \u0418\u0445 \u0435\u0449\u0435 \u043D\u0430\u0437\u044B\u0432\u0430\u044E\u0442 \u043F\u0430\u0434\u0435\u0436\u0438 \xAB\u0437\u0430 \u043D\u043E\u0441\u043E\u043C\xBB, \u0442\u0430\u043A \u043A\u0430\u043A \xABnina taga\xBB \u0438\u043C\u0435\u043D\u043D\u043E \u0442\u0430\u043A \u0438 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u0441\u044F.\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('lesson',{number:8}),source:source});}));

