var source='\n\u041C\u0438\u0440 \u043D\u0435 \u0441\u0442\u043E\u0438\u0442 \u043D\u0430 \u043C\u0435\u0441\u0442\u0435, \u0438 \u043C\u044B \u0442\u043E\u0436\u0435! \u041D\u0430 \u043F\u0440\u0435\u0434\u044B\u0434\u0443\u0449\u0435\u043C \u0437\u0430\u043D\u044F\u0442\u0438\u0438 \u043C\u044B \u043D\u0430\u0447\u0430\u043B\u0438 \u0441 \u0412\u0430\u043C\u0438 \u0440\u0430\u0441\u0441\u043C\u0430\u0442\u0440\u0438\u0432\u0430\u0442\u044C \u043C\u0430\u0442\u0435\u0440\u0438\u0430\u043B \u043F\u043E \u043F\u0440\u0435\u0434\u043B\u043E\u0433\u0430\u043C \u0438 \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0430\u043C \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430. \u0412\u0437\u044F\u043B\u0438 \u043F\u043E\u0434\u0433\u0440\u0443\u043F\u043F\u0443, \u043A\u043E\u0442\u043E\u0440\u0430\u044F \u043A\u0430\u0441\u0430\u0435\u0442\u0441\u044F \u043F\u0440\u043E\u0441\u0442\u0440\u0430\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0435\u043D\u0438\u044F. \u0421\u0435\u0433\u043E\u0434\u043D\u044F \u0436\u0435 \u044F \u043F\u0440\u0435\u0434\u043B\u0430\u0433\u0430\u044E \u0412\u0430\u043C \u043F\u043E\u0437\u043D\u0430\u043A\u043E\u043C\u0438\u0442\u044C\u0441\u044F \u0441 \u043E\u0441\u0442\u0430\u0432\u0448\u0438\u043C\u0438\u0441\u044F \u043F\u043E\u0434\u0433\u0440\u0443\u043F\u043F\u0430\u043C\u0438 \u043F\u0440\u0435\u0434\u043B\u043E\u0433\u043E\u0432 \u0438 \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u043E\u0432. \u0425\u043E\u0442\u044F \u0443\u0442\u043E\u0447\u043D\u044E, \u0441\u043B\u043E\u0432\u043E \u043E\u0441\u0442\u0430\u0432\u0448\u0438\u0435\u0441\u044F \u0437\u0434\u0435\u0441\u044C \u043F\u043E\u0434\u0440\u0430\u0437\u0443\u043C\u0435\u0432\u0430\u0435\u0442 \u043D\u0435 \u0442\u043E, \u0447\u0442\u043E \u044F \u0441\u043E\u0431\u0440\u0430\u043B\u0430 \u0432\u0441\u0435 \u043F\u0440\u0435\u0434\u043B\u043E\u0433\u0438 \u0438 \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0438 \u0432 \u043E\u0434\u043D\u043E\u043C \u043C\u0435\u0441\u0442\u0435! \u042D\u0442\u043E, \u043A \u0441\u043E\u0436\u0430\u043B\u0435\u043D\u0438\u044E, \u043D\u0435 \u0442\u0430\u043A. \u041D\u0435 \u0441\u043C\u043E\u0442\u0440\u044F \u043D\u0430 \u0442\u043E, \u0447\u0442\u043E \u0441\u043F\u0438\u0441\u043E\u043A \u043D\u0438\u0436\u0435 \u0434\u043E\u0441\u0442\u0430\u0442\u043E\u0447\u043D\u043E \u0431\u043E\u043B\u044C\u0448\u043E\u0439, \u043E\u043D \u0434\u0430\u043B\u0435\u043A\u043E \u043D\u0435 \u043F\u043E\u043B\u043D\u044B\u0439. \u041D\u043E \u043F\u043E\u0432\u0435\u0440\u044C\u0442\u0435, \u043D\u0430\u043C \u043F\u043E\u043A\u0430 \u0431\u0443\u0434\u0435\u0442 \u0434\u043E\u0441\u0442\u0430\u0442\u043E\u0447\u043D\u043E \u0438 \u044D\u0442\u043E\u0433\u043E. \u0422\u0435\u043C \u0431\u043E\u043B\u0435\u0435 \u044F \u043F\u043E\u0441\u0442\u0430\u0440\u0430\u043B\u0430\u0441\u044C \u0441\u043E\u0435\u0434\u0438\u043D\u0438\u0442\u044C \u0432 \u043E\u0434\u043D\u043E\u043C \u043C\u0430\u0442\u0435\u0440\u0438\u0430\u043B\u0435 \u043A\u0430\u043A \u043C\u043E\u0436\u043D\u043E \u0431\u043E\u043B\u044C\u0448\u0435 \u043F\u043E\u0434\u0442\u0435\u043C, \u0438 \u0442\u0435\u043C \u0441\u0430\u043C\u044B\u043C \u043E\u0445\u0432\u0430\u0442\u0438\u0442\u044C \u043A\u0430\u043A \u043C\u043E\u0436\u043D\u043E \u0448\u0438\u0440\u0435 \u0432\u0441\u0435 \u0441\u0444\u0435\u0440\u044B \u043D\u0430\u0448\u0435\u0439 \u043F\u043E\u0432\u0441\u0435\u0434\u043D\u0435\u0432\u043D\u043E\u0439 \u0436\u0438\u0437\u043D\u0438. \n\n\u041F\u0440\u0435\u0434\u043B\u043E\u0433\u0438 \u0438 \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0438, \u0432\u044B\u0440\u0430\u0436\u0430\u044E\u0449\u0438\u0435 \u043F\u0440\u0438\u0447\u0438\u043D\u0443 \u0438 \u0446\u0435\u043B\u044C \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044F.\n1.\u041F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0438, \u0442\u0440\u0435\u0431\u0443\u044E\u0442 \u043F\u0430\u0434\u0435\u0436\u0430 omastav: \nP\xE4rast=T\xF5ttu \u2014 \u0438\u0437-\u0437\u0430, \u043F\u043E, \u0432\u0441\u043B\u0435\u0434\u0441\u0442\u0432\u0438\u0435\nJaoks=Tarvis \u2014 \u0434\u043B\u044F\nHeaks \u2014 \u0434\u043B\u044F, \u0432\u043E \u0431\u043B\u0430\u0433\u043E\n\xDCle \u2014 \u0437\u0430 \u043A\u043E\u0433\u043E-\u0442\u043E, \u0437\u0430 \u0447\u0442\u043E-\u0442\u043E, \u043A\u043E\u043C\u0443, \u0447\u0435\u043C\u0443\nPuhul \u2014 \u0432 \u0447\u0435\u0441\u0442\u044C, \u043F\u043E \u0441\u043B\u0443\u0447\u0430\u044E\nP\xF5hjal \u2014 \u043D\u0430 \u043E\u0441\u043D\u043E\u0432\u0435\nN\xE4ide.\nTa ei l\xE4he t\xF6\xF6le haiguse p\xE4rast. \u041E\u043D \u043D\u0435 \u0438\u0434\u0435\u0442 \u043D\u0430 \u0440\u0430\u0431\u043E\u0442\u0443 \u0432\u0441\u043B\u0435\u0434\u0441\u0442\u0432\u0438\u0435 \u0431\u043E\u043B\u0435\u0437\u043D\u0438.\nSelle t\xF5ttu olen ma kodus. \u0418\u0437-\u0437\u0430 \u044D\u0442\u043E\u0433\u043E \u044F \u0434\u043E\u043C\u0430.\nMinu jaoks leiab ta alati aega. \u0414\u043B\u044F \u043C\u0435\u043D\u044F \u043E\u043D\u0430 \u0432\u0441\u0435\u0433\u0434\u0430 \u043D\u0430\u0439\u0434\u0435\u0442 \u0432\u0440\u0435\u043C\u044F.\nKasutan referaadi tarvis materjalid raamatukogust. \u0418\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u044E \u0434\u043B\u044F \u0440\u0435\u0444\u0435\u0440\u0430\u0442\u0430 \u043C\u0430\u0442\u0435\u0440\u0438\u0430\u043B\u044B \u0438\u0437 \u0431\u0438\u0431\u043B\u0438\u043E\u0442\u0435\u043A\u0438.\nTa teeb k\xF5ik oma pere heaks. \u041E\u043D \u0434\u0435\u043B\u0430\u0435\u0442 \u0432\u0441\u0435 \u0432\u043E \u0431\u043B\u0430\u0433\u043E \u0441\u0432\u043E\u0435\u0439 \u0441\u0435\u043C\u044C\u0438.\nMina olen \xF5nnelik s\xF5bra \xFCle. \u042F \u0441\u0447\u0430\u0441\u0442\u043B\u0438\u0432\u0430 \u0437\u0430 \u0434\u0440\u0443\u0433\u0430.\nTeen sulle s\xFCnnip\xE4eva puhul kingituse.\u0421\u0434\u0435\u043B\u0430\u044E \u0442\u0435\u0431\u0435 \u043F\u043E\u0434\u0430\u0440\u043E\u043A \u043F\u043E \u0441\u043B\u0443\u0447\u0430\u044E \u0434\u043D\u044F \u0440\u043E\u0436\u0434\u0435\u043D\u0438\u044F.\nMille p\xF5hjal kohtunik nii otsustab? \u041D\u0430 \u043E\u0441\u043D\u043E\u0432\u0435 \u0447\u0435\u0433\u043E \u0441\u0443\u0434\u044C\u044F \u0442\u0430\u043A \u0440\u0435\u0448\u0430\u0435\u0442?\n\n2. \u041F\u0440\u0435\u0434\u043B\u043E\u0433  t\xE4nu \u2014 \u0431\u043B\u0430\u0433\u043E\u0434\u0430\u0440\u044F, \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C alale\xFCtlev:\nT\xE4nu t\xF6\xF6tajatele saab see maja \xF5igeks ajaks valmis. \u0411\u043B\u0430\u0433\u043E\u0434\u0430\u0440\u044F \u0440\u0430\u0431\u043E\u0442\u043D\u0438\u043A\u0430\u043C \u0431\u0443\u0434\u0435\u0442 \u044D\u0442\u043E\u0442 \u0434\u043E\u043C \u0432\u043E\u0432\u0440\u0435\u043C\u044F \u0433\u043E\u0442\u043E\u0432.\n\n\u041F\u0440\u0435\u0434\u043B\u043E\u0433\u0438 \u0438 \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0438 \u0434\u043B\u044F \u0432\u044B\u0440\u0430\u0436\u0435\u043D\u0438\u044F \u0432\u0440\u0435\u043C\u0435\u043D\u043D\u044B\u0445 \u043E\u0442\u043D\u043E\u0448\u0435\u043D\u0438\u0439.\n1. \u041F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0438, \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u043C\u044B\u0435 \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C omastav:\nJooksul=kestel \u2014 \u0432 \u0442\u0435\u0447\u0435\u043D\u0438\u0435, \u0432\u043E \u0432\u0440\u0435\u043C\u044F, \nEest \u2014 \u0437\u0430, \xAB\u0442\u043E\u043C\u0443 \u043D\u0430\u0437\u0430\u0434\xBB\nP\xE4rast \u2014 \u0447\u0435\u0440\u0435\u0437\nEel \u2014 \u043F\u0435\u0440\u0435\u0434\nPeale \u2014 \u043F\u043E\u0441\u043B\u0435\nN\xE4ide:\nL\xF5peta t\xF6\xF6 nadala jooksul! \u0417\u0430\u043A\u043E\u043D\u0447\u0438 \u0440\u0430\u0431\u043E\u0442\u0443 \u0432 \u0442\u0435\u0447\u0435\u043D\u0438\u0435 \u043D\u0435\u0434\u0435\u043B\u0438! \nTa pole aasta kestel kordagi koolis k\xE4inud. \u0412 \u0442\u0435\u0447\u0435\u043D\u0438\u0435 \u0433\u043E\u0434\u0430 \u043E\u043D\u0430 \u043D\u0438 \u0440\u0430\u0437\u0443 \u043D\u0435 \u0431\u044B\u043B\u0430 \u0432 \u0448\u043A\u043E\u043B\u0435.\nMina jooksen kilomeetrit viie minuti eest. \u042F \u043F\u0440\u043E\u0431\u0435\u0433\u0430\u044E \u043A\u0438\u043B\u043E\u043C\u0435\u0442\u0440 \u0437\u0430 \u043F\u044F\u0442\u044C \u043C\u0438\u043D\u0443\u0442.\nSeminar algab kolme tunni p\xE4rast. \u0421\u0435\u043C\u0438\u043D\u0430\u0440 \u043D\u0430\u0447\u043D\u0435\u0442\u0441\u044F \u0447\u0435\u0440\u0435\u0437 \u0442\u0440\u0438 \u0447\u0430\u0441\u0430\nP\xE4ikese t\xF5usu eel on ilm k\xFClm. \u041F\u0435\u0440\u0435\u0434 \u0432\u043E\u0441\u0445\u043E\u0434\u043E\u043C \u0441\u043E\u043B\u043D\u0446\u0430 \u043F\u043E\u0433\u043E\u0434\u0430 \u0445\u043E\u043B\u043E\u0434\u043D\u0430\u044F.\nRaske t\xF6\xF6 peale tuleb puhata. \u041F\u043E\u0441\u043B\u0435 \u0442\u044F\u0436\u0435\u043B\u043E\u0439 \u0440\u0430\u0431\u043E\u0442\u044B \u0441\u043B\u0435\u0434\u0443\u0435\u0442 \u043E\u0442\u0434\u044B\u0445\u0430\u0442\u044C.\n\n2.\u041F\u0440\u0435\u0434\u043B\u043E\u0433\u0438, \u0442\u0440\u0435\u0431\u0443\u044E\u0449\u0438\u0435 \u043F\u0430\u0434\u0435\u0436\u0430 osastav:\nEnne \u2014 \u043F\u0435\u0440\u0435\u0434, \u0434\u043E\nP\xE4rast \u2014 \u043F\u043E\u0441\u043B\u0435 \nEnne eksamit pean veel \xF5piku l\xE4bi lugema. \u041F\u0435\u0440\u0435\u0434 (\u0434\u043E) \u044D\u043A\u0437\u0430\u043C\u0435\u043D\u043E\u043C \u0434\u043E\u043B\u0436\u0435\u043D \u0435\u0449\u0435 \u0443\u0447\u0435\u0431\u043D\u0438\u043A \u043F\u0440\u043E\u0447\u0438\u0442\u0430\u0442\u044C.\nP\xE4rast t\xF6\xF6d l\xE4hen ma kohvikusse. \u041F\u043E\u0441\u043B\u0435 \u0440\u0430\u0431\u043E\u0442\u044B \u043F\u043E\u0439\u0434\u0443 \u0432 \u043A\u0430\u0444\u0435.\n\n3. \u041F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0438, \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u043C\u044B\u0435 \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C nimetav:\nL\xE4bi \u2014 \xAB\u0432\u0435\u0441\u044C , \u0432\u0441\u0451, \u043D\u0430\u043F\u0440\u043E\u043B\u0435\u0442\xBB\nOtsa \u2014 \xAB\u0432\u0435\u0441\u044C, \u0446\u0435\u043B\u044B\u0439\xBB\nIstume \xF6\xF6d l\xE4bi \xFCleval. \u0421\u0438\u0434\u0438\u043C \u043D\u043E\u0447\u0438 \u043D\u0430\u043F\u0440\u043E\u043B\u0435\u0442 \u043D\u0430\u0432\u0435\u0440\u0445\u0443.\nRestoran on p\xE4ev otsa reserveeritud. \u0420\u0435\u0441\u0442\u043E\u0440\u0430\u043D \u0437\u0430\u0431\u0440\u043E\u043D\u0438\u0440\u043E\u0432\u0430\u043D \u043D\u0430 \u0446\u0435\u043B\u044B\u0439 \u0434\u0435\u043D\u044C.\n\n3. \u041F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0438, \u0442\u0440\u0435\u0431\u0443\u044E\u0449\u0438\u0435 \u043F\u0430\u0434\u0435\u0436\u0430 seest\xFCtlev:\nSaadik \u2014 \u0441, \u0441\u043E\nPeale \u2014 \u0441, \u0441\u043E\nAlates \u2014 \xAB\u043D\u0430\u0447\u0438\u043D\u0430\u044F \u0441\xBB. Alates \u043C\u043E\u0436\u0435\u0442 \u0431\u044B\u0442\u044C \u043A\u0430\u043A \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433, \u0442\u0430\u043A \u0438 \u043F\u0440\u0435\u0434\u043B\u043E\u0433, \u043D\u043E \u0432 \u043B\u044E\u0431\u043E\u043C \u0441\u043B\u0443\u0447\u0430\u0435 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u0432\u043C\u0435\u0441\u0442\u0435 \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C seest\xFCtlev.\nTa on lapsep\xF5lvest saadik linnas elanud. \u041E\u043D \u0441 \u0434\u0435\u0442\u0441\u0442\u0432\u0430 \u0436\u0438\u0432\u0451\u0442 \u0432 \u0433\u043E\u0440\u043E\u0434\u0435.\nMe oleme s\xF5brad esimesest kohtumisest peale. \u041C\u044B \u0434\u0440\u0443\u0437\u044C\u044F \u0441 \u043F\u0435\u0440\u0432\u043E\u0439 \u0432\u0441\u0442\u0440\u0435\u0447\u0438.\nTa t\xF6\xF6tab meil s\xFCgisest alates. \u041E\u043D \u0440\u0430\u0431\u043E\u0442\u0430\u0435\u0442 \u0443 \u043D\u0430\u0441 \u043D\u0430\u0447\u0438\u043D\u0430\u044F \u0441 \u043E\u0441\u0435\u043D\u0438.\n\n\u041F\u0440\u0435\u0434\u043B\u043E\u0433\u0438 \u0438 \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0438, \u0432\u044B\u0440\u0430\u0436\u0430\u044E\u0449\u0438\u0435 \u043A\u043E\u043B\u0438\u0447\u0435\u0441\u0442\u0432\u043E, \u043C\u0435\u0440\u0443, \u0440\u0430\u0437\u043C\u0435\u0440.\n1. \u041F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0438, \u0442\u0440\u0435\u0431\u0443\u044E\u0449\u0438\u0435 \u043F\u0430\u0434\u0435\u0436\u0430 omastav:\nKaupa=viisi \u2014 \u043F\u043E (\u043C\u043E\u0436\u043D\u043E \u043F\u0435\u0440\u0435\u0432\u0435\u0441\u0442\u0438 \u0442\u0430\u043A\u0436\u0435 \u0442\u0432\u043E\u0440\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u043C \u043F\u0430\u0434\u0435\u0436\u043E\u043C)\n\xDCmber \u2014 \u043E\u043A\u043E\u043B\u043E\nKohta \u2014 \u043D\u0430 \nV\xF5rra \u2014 \u043D\u0430 \nN\xE4ide:\nJ\xFCri on aastate kaupa kodust eemal viibinud. \u042E\u0440\u0438\u0439 \u043D\u0435 \u0436\u0438\u0432\u0435\u0442 \u0434\u043E\u043C\u0430 \u0433\u043E\u0434\u0430\u043C\u0438.\nT\xFCdruk v\xF5ib tundude viisi raamatuid lugeda. \u0414\u0435\u0432\u043E\u0447\u043A\u0430 \u043C\u043E\u0436\u0435\u0442 \u0447\u0430\u0441\u0430\u043C\u0438 \u0447\u0438\u0442\u0430\u0442\u044C \u043A\u043D\u0438\u0433\u0438.\nPeole tuleb kahek\xFCmne inimese \xFCmber. \u041D\u0430 \u043F\u0440\u0430\u0437\u0434\u043D\u0438\u043A \u043F\u0440\u0438\u0434\u0435\u0442 \u043E\u043A\u043E\u043B\u043E \u0434\u0432\u0430\u0434\u0446\u0430\u0442\u0438 \u0447\u0435\u043B\u043E\u0432\u0435\u043A.\nSee on ainus korralik maja terve k\xFCla kohta. \u042D\u0442\u043E \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u044B\u0439 \u043F\u0440\u0438\u043B\u0438\u0447\u043D\u044B\u0439 \u0434\u043E\u043C \u043D\u0430 \u0432\u0441\u044E \u0434\u0435\u0440\u0435\u0432\u043D\u044E.\nPalk t\xF5useb viiesaja rubla v\xF5rra. \u0417\u0430\u0440\u043F\u043B\u0430\u0442\u0430 \u043F\u043E\u0432\u044B\u0441\u0438\u0442\u0441\u044F \u043D\u0430 \u043F\u044F\u0442\u044C\u0441\u043E\u0442 \u0440\u0443\u0431\u043B\u0435\u0439.\n\n2. \u041F\u0440\u0435\u0434\u043B\u043E\u0433\u0438, \u0442\u0440\u0435\u0431\u0443\u044E\u0449\u0438\u0435 \u043F\u0430\u0434\u0435\u0436\u0430 omastav:\nAlla \u2014 \u043C\u0435\u043D\u044C\u0448\u0435, \u043D\u0438\u0436\u0435\n\xDCle \u2014 \u0431\u043E\u043B\u044C\u0448\u0435, \u0441\u0432\u044B\u0448\u0435\nTa on meil veidi alla aasta t\xF6\xF6tanud. \u041E\u043D \u0443 \u043D\u0430\u0441 \u0440\u0430\u0431\u043E\u0442\u0430\u0435\u0442 \u043D\u0435\u043C\u043D\u043E\u0433\u043E \u043C\u0435\u043D\u044C\u0448\u0435 \u0433\u043E\u0434\u0430.\nToas on \xFCle viieteistk\xFCmne inimese. \u0412 \u043A\u043E\u043C\u043D\u0430\u0442\u0435 \u0431\u043E\u043B\u0435\u0435 \u043F\u044F\u0442\u043D\u0430\u0434\u0446\u0430\u0442\u0438 \u0447\u0435\u043B\u043E\u0432\u0435\u043A.\n\n\u041F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0438, \u0432\u044B\u0440\u0430\u0436\u0430\u044E\u0449\u0438\u0435 \u043E\u0442\u043D\u043E\u0448\u0435\u043D\u0438\u044F \u0438 \u0441\u0432\u044F\u0437\u044C.\n1. \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0438, \u0442\u0440\u0435\u0431\u0443\u044E\u0449\u0438\u0435 \u043F\u0430\u0434\u0435\u0436\u0430 omastav:\nKohta \u2014 \u043E(\u043E\u0431), \u043F\u0440\u043E, \u043A\u0430\u0441\u0430\u0442\u0435\u043B\u044C\u043D\u043E\nPoolest \u2014 \u0432 \u043E\u0442\u043D\u043E\u0448\u0435\u043D\u0438\u0438, \u043F\u043E (\u043A\u043E\u043C\u0443-\u0447\u0435\u043C\u0443)\nSuhtes \u2014 \u043D\u0430\u0441\u0447\u0435\u0442, \u043E\u0442\u043D\u043E\u0441\u0438\u0442\u0435\u043B\u044C\u043D\u043E, \u043F\u043E \u043E\u0442\u043D\u043E\u0448\u0435\u043D\u0438\u044E\nMeelest \u2014 \u043F\u043E \u043C\u043D\u0435\u043D\u0438\u044E (\u043A\u043E\u0433\u043E-\u0447\u0435\u0433\u043E)\nJ\xE4rgi \u2014 \u0441\u043E\u0433\u043B\u0430\u0441\u043D\u043E\nN\xE4ide:\nAndmed tema edasise saatuse kohta puuduvad. \u0421\u0432\u0435\u0434\u0435\u043D\u0438\u044F \u043E \u0435\u0433\u043E \u0434\u0430\u043B\u044C\u043D\u0435\u0439\u0448\u0435\u0439 \u0441\u0443\u0434\u044C\u0431\u0435 \u043E\u0442\u0441\u0443\u0442\u0441\u0442\u0432\u0443\u044E\u0442.\nVanuse poolest on ta teistest \xFCle.  \u041F\u043E \u0432\u043E\u0437\u0440\u0430\u0441\u0442\u0443 \u043E\u043D \u043E\u043F\u0435\u0440\u0435\u0434\u0438\u043B \u0434\u0440\u0443\u0433\u0438\u0445.\nSee kehtib ka sinu suhtes. \u042D\u0442\u043E \u0434\u0435\u0439\u0441\u0442\u0432\u0443\u0435\u0442 \u0438 \u043F\u043E \u043E\u0442\u043D\u043E\u0448\u0435\u043D\u0438\u044E \u043A \u0442\u0435\u0431\u0435. (\u042D\u0442\u043E \u0438 \u0442\u0435\u0431\u044F \u043A\u0430\u0441\u0430\u0435\u0442\u0441\u044F.)\nMinu meelest on T\xF5nis k\xF5iges s\xFC\xFCdi. \u041F\u043E \u043C\u043E\u0435\u043C\u0443 \u043C\u043D\u0435\u043D\u0438\u044E, \u0422\u044B\u043D\u0438\u0441 \u0432\u0438\u043D\u043E\u0432\u0430\u0442 \u0432\u043E \u0432\u0441\u0435\u043C.\nLisan soola suppi maitse j\xE4rgi. \u0421\u043E\u043B\u044C \u0432 \u0441\u0443\u043F \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u044E \u043F\u043E \u0432\u043A\u0443\u0441\u0443.\n\n\u041F\u0440\u0435\u0434\u043B\u043E\u0433\u043E\u0432 \u0432 \u0440\u0443\u0441\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0443\u0435\u0442 \u043D\u0435\u0441\u043C\u0435\u0442\u043D\u043E\u0435 \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u043E, \u0442\u043E\u0447\u043D\u043E \u0442\u0430\u043A\u0436\u0435 \u043A\u0430\u043A \u0438 \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435. \u0412 \u0434\u043E\u043F\u043E\u043B\u043D\u0435\u043D\u0438\u0435 \u043A \u0432\u044B\u0448\u0435\u0441\u043A\u0430\u0437\u0430\u043D\u043D\u044B\u043C \u043F\u0440\u0435\u0434\u043B\u043E\u0433\u0430\u043C \u0438 \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u0430\u043C \u043C\u043E\u0436\u043D\u043E \u0432\u044B\u0434\u0435\u043B\u0438\u0442\u044C \u0435\u0449\u0435 \u043D\u0435\u0441\u043A\u043E\u043B\u044C\u043A\u043E \u043D\u0435\u0431\u043E\u043B\u044C\u0448\u0438\u0445 \u043F\u043E\u0434\u0433\u0440\u0443\u043F\u043F.\n\u0414\u043E\u043F\u043E\u043B\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435 \u043F\u043E\u0434\u0433\u0440\u0443\u043F\u043F\u044B \u043F\u0440\u0435\u0434\u043B\u043E\u0433\u043E\u0432 \u0438 \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u043E\u0432, \u0432\u044B\u0440\u0430\u0436\u0430\u044E\u0449\u0438\u0435 \u0441\u043B\u0435\u0434\u0443\u044E\u0449\u0438\u0435 \u043E\u0442\u043D\u043E\u0448\u0435\u043D\u0438\u044F:\n1. \u041E\u0440\u0443\u0434\u0438\u0435 \u0438\u043B\u0438 \u0441\u0440\u0435\u0434\u0441\u0442\u0432\u043E.\nAbil \u2014 \xAB\u043F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438\xBB (\u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433 \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C omastav)\nKelle abil sa oled selle t\xF6\xF6 l\xF5petanud? \u0421 \u043F\u043E\u043C\u043E\u0449\u044C\u044E \u043A\u043E\u0433\u043E (\u043A\u0442\u043E \u043F\u043E\u043C\u043E\u0433) \u0442\u044B \u0437\u0430\u043A\u043E\u043D\u0447\u0438\u043B \u044D\u0442\u0443 \u0440\u0430\u0431\u043E\u0442\u0443?\nTeel \u2014 \u043F\u043E (\u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433 \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C omastav)\nMa helistan sulle telefoni teel. \u042F \u043F\u043E\u0437\u0432\u043E\u043D\u044E \u0442\u0435\u0431\u0435 \u043F\u043E \u0442\u0435\u043B\u0435\u0444\u043E\u043D\u0443.\n2. \u0417\u0430\u043C\u0435\u043D\u0443.\nAsemel \u2014 \u0432\u043C\u0435\u0441\u0442\u043E, \u0437\u0430 (\u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433 \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C omastav)\nSa pead kellegi enda asemel saatma.  \u0422\u044B \u0434\u043E\u043B\u0436\u0435\u043D \u043A\u043E\u0433\u043E-\u0442\u043E \u0432\u043C\u0435\u0441\u0442\u043E \u0441\u0435\u0431\u044F \u043E\u0442\u043F\u0440\u0430\u0432\u0438\u0442\u044C.\nEest \u2014 \u0432\u043C\u0435\u0441\u0442\u043E, \u0437\u0430 (\u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433 \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C omastav)\nTa t\xF6\xF6tab kahe eest. \u041E\u043D \u0440\u0430\u0431\u043E\u0442\u0430\u0435\u0442 \u0437\u0430 \u0434\u0432\u043E\u0438\u0445.\n3. \u0421\u043E\u043F\u0440\u043E\u0432\u043E\u0436\u0434\u0435\u043D\u0438\u0435.  \nSaatel \u2014 \xAB\u0432 \u0441\u043E\u043F\u0440\u043E\u0432\u043E\u0436\u0434\u0435\u043D\u0438\u0438\xBB (\u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433 \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C omastav)\nTuristid s\xF5idavad giidi saatel hotelli.  \u0422\u0443\u0440\u0438\u0441\u0442\u044B \u0435\u0434\u0443\u0442 \u0432 \u043E\u0442\u0435\u043B\u044C \u0432 \u0441\u043E\u043F\u0440\u043E\u0432\u043E\u0436\u0434\u0435\u043D\u0438\u0438 \u0433\u0438\u0434\u0430. \nKoos \u2014 \u0432\u043C\u0435\u0441\u0442\u0435 (\u043F\u0440\u0435\u0434\u043B\u043E\u0433 \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C kaasa\xFCtlev)\nTa tuleb koos vennaga. \u041E\u043D \u043F\u0440\u0438\u0435\u0434\u0435\u0442 \u0432\u043C\u0435\u0441\u0442\u0435 \u0441 \u0431\u0440\u0430\u0442\u043E\u043C.\n4. \u041E\u0442\u0441\u0443\u0442\u0441\u0432\u0438\u0435.\nIlma \u2014 \u0431\u0435\u0437 (\u043F\u0440\u0435\u0434\u043B\u043E\u0433 \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C ilma\xFCtlev)\nIlma t\xF6\xF6ta pole v\xF5imalik rahulikult elada. \u0411\u0435\u0437 \u0440\u0430\u0431\u043E\u0442\u044B \u043D\u0435 \u0432\u043E\u0437\u043C\u043E\u0436\u043D\u043E \u0441\u043F\u043E\u043A\u043E\u0439\u043D\u043E \u0436\u0438\u0442\u044C.\n5. \u0418\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u0435.\nPeale \u2014 \u043A\u0440\u043E\u043C\u0435 (\u043F\u0440\u0435\u0434\u043B\u043E\u0433 \u0441 \u043F\u0430\u0434\u0435\u0436\u043E\u043C omastav)\nPeale direktori ei tea sellest otsusest veel keegi. \u041A\u0440\u043E\u043C\u0435 \u0434\u0438\u0440\u0435\u043A\u0442\u043E\u0440\u0430 \u043D\u0438\u043A\u0442\u043E \u043D\u0435 \u0437\u043D\u0430\u0435\u0442 \u043E\u0431 \u044D\u0442\u043E\u043C \u0440\u0435\u0448\u0435\u043D\u0438\u0438.\n\n\u041F\u0440\u0435\u0434\u043B\u043E\u0433\u043E\u0432 \u0438 \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u043E\u0432 \u043C\u043D\u043E\u0433\u043E. \u0412\u0435\u0440\u044E, \u0447\u0442\u043E \u0437\u0430\u043F\u043E\u043C\u043D\u0438\u0442\u044C \u0438\u0445 \u0441 \u043F\u0435\u0440\u0432\u043E\u0433\u043E \u0440\u0430\u0437\u0430, \u043C\u0430\u043B\u043E \u043A\u043E\u043C\u0443 \u0443\u0434\u0430\u0441\u0442\u0441\u044F. \u041D\u0435 \u043E\u0442\u0447\u0430\u0438\u0432\u0430\u0439\u0442\u0435\u0441\u044C, \u043F\u043E\u0441\u0442\u0435\u043F\u0435\u043D\u043D\u043E \u043E\u043D\u0438 \u0432\u0441\u0435 \u043E\u0442\u043B\u043E\u0436\u0430\u0442\u0441\u044F. \u041D\u043E, \u043E\u0431\u0440\u0430\u0442\u0438\u0442\u0435 \u043E\u0431\u044F\u0437\u0430\u0442\u0435\u043B\u044C\u043D\u043E \u0432\u043D\u0438\u043C\u0430\u043D\u0438\u0435 \u0432\u043E\u0442 \u043D\u0430 \u043A\u0430\u043A\u043E\u0439 \u0444\u0430\u043A\u0442, \u043D\u0435\u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0441\u043B\u043E\u0432\u0430 \u0438\u0437 \u0432\u044B\u0448\u0435 \u043F\u0435\u0440\u0435\u0447\u0438\u0441\u043B\u0435\u043D\u043D\u044B\u0445 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u044E\u0442\u0441\u044F \u0438 \u043A\u0430\u043A \u0432 \u043A\u0430\u0447\u0435\u0441\u0442\u0432\u0435 \u043F\u0440\u0435\u0434\u043B\u043E\u0433\u043E\u0432, \u0442\u0430\u043A \u0438 \u043F\u043E\u0441\u043B\u0435\u043B\u043E\u0433\u043E\u0432, \u0434\u0430 \u0435\u0449\u0435 \u0438 \u0441 \u0440\u0430\u0437\u043D\u044B\u043C\u0438 \u043F\u0430\u0434\u0435\u0436\u0430\u043C\u0438 \u0432\u0437\u0430\u0438\u043C\u043E\u0434\u0435\u0439\u0441\u0442\u0432\u0443\u044E\u0442. \u0411\u0443\u0434\u044C\u0442\u0435 \u043E\u0441\u0442\u043E\u0440\u043E\u0436\u043D\u044B, \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0435 \u0432\u0441\u0435\u0433\u043E \u0441\u043A\u0430\u0437\u0430\u043D\u043D\u043E\u0433\u043E \u043C\u043E\u0436\u0435\u0442 \u043A\u0430\u0440\u0434\u0438\u043D\u0430\u043B\u044C\u043D\u043E \u043F\u043E\u043C\u0435\u043D\u044F\u0442\u044C\u0441\u044F, \u0435\u0441\u043B\u0438 \u0432\u044B \u0434\u043E\u043F\u0443\u0441\u0442\u0438\u0442\u0435 \u0432 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043D\u0438\u0438 \u044D\u0442\u0438\u0445 \u0447\u0430\u0441\u0442\u0438\u0447\u0435\u043A \u043E\u0448\u0438\u0431\u043A\u0443.\nN\xE4ide:\nMe kohtume aasta p\xE4rast. \u041C\u044B \u0432\u0441\u0442\u0440\u0435\u0442\u0438\u043C\u0441\u044F \u0447\u0435\u0440\u0435\u0437 \u0433\u043E\u0434. (omastav+p\xE4rast) \nP\xE4rast t\xF6\xF6d l\xE4heme koju. \u041F\u043E\u0441\u043B\u0435 \u0440\u0430\u0431\u043E\u0442\u044B \u0438\u0434\u0435\u043C \u0434\u043E\u043C\u043E\u0439. (p\xE4rast+osastav)\nLaps puudub koolist haiguse p\xE4rast. \u0420\u0435\u0431\u0451\u043D\u043E\u043A \u043E\u0442\u0441\u0443\u0442\u0441\u0442\u0432\u0443\u0435\u0442 \u0432 \u0448\u043A\u043E\u043B\u0435 \u0438\u0437-\u0437\u0430 \u0431\u043E\u043B\u0435\u0437\u043D\u0438. (omastav+p\xE4rast)\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('lesson',{number:11}),source:source});}));

