var source='\n### \u041F\u0440\u043E\u0434\u043E\u043B\u0436\u0430\u0435\u043C \u0433\u043E\u0432\u043E\u0440\u0438\u0442\u044C \u043E \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445\n\n\u0421\u0442\u0435\u043F\u0435\u043D\u0438 \u0441\u0440\u0430\u0432\u043D\u0435\u043D\u0438\u044F \u0438\u043C\u0435\u043D \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445. Omaduss\xF5na v\xF5rdlusastmed.\n\u0415\u0449\u0435 \u043E\u0434\u043D\u0430 \u0442\u0435\u043C\u0430, \u043A\u043E\u0442\u043E\u0440\u0430\u044F \u043A\u0430\u0441\u0430\u0435\u0442\u0441\u044F \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445, \u044D\u0442\u043E \u0441\u0442\u0435\u043F\u0435\u043D\u0438 \u0441\u0440\u0430\u0432\u043D\u0435\u043D\u0438\u044F. \u0412 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u0438\u043C\u044F \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u0438\u043C\u0435\u0435\u0442 \u0442\u0440\u0438 \u0441\u0442\u0435\u043F\u0435\u043D\u0438 \u0441\u0440\u0430\u0432\u043D\u0435\u043D\u0438\u044F:\n1. \u041D\u0430\u0447\u0430\u043B\u044C\u043D\u0430\u044F \u0441\u0442\u0435\u043F\u0435\u043D\u044C \u0438\u043B\u0438 \u043F\u043E\u043B\u043E\u0436\u0438\u0442\u0435\u043B\u044C\u043D\u0430\u044F. Algv\xF5rre. \u0414\u0430\u043D\u043D\u0430\u044F \u0441\u0442\u0435\u043F\u0435\u043D\u044C \u043F\u043E\u043A\u0430\u0437\u044B\u0432\u0430\u0435\u0442\u0441\u044F \u043D\u0430\u043B\u0438\u0447\u0438\u0435 \u043A\u0430\u0447\u0435\u0441\u0442\u0432\u0430 \u0443 \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E, \u043D\u0435 \u0432\u044B\u0440\u0430\u0436\u0430\u044F \u043F\u0440\u0438 \u044D\u0442\u043E\u043C \u0435\u0433\u043E \u043C\u0435\u0440\u0443. \u0414\u0430\u043D\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430 \u043D\u0435 \u0438\u043C\u0435\u0435\u0442 \u0441\u043F\u0435\u0446\u0438\u0430\u043B\u044C\u043D\u043E\u0433\u043E \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u0447\u0435\u0441\u043A\u043E\u0433\u043E \u043F\u0440\u0438\u0437\u043D\u0430\u043A\u0430, \u0442\u043E \u0435\u0441\u0442\u044C \u044D\u0442\u043E \u0442\u0435 \u0441\u0430\u043C\u044B\u0435 \u0441\u043B\u043E\u0432\u0430 \u0438 \u0438\u0445 \u0444\u043E\u0440\u043C\u044B, \u043E \u043A\u043E\u0442\u043E\u0440\u044B\u0445 \u043C\u044B \u0433\u043E\u0432\u043E\u0440\u0438\u043B\u0438 \u0434\u043E \u044D\u0442\u043E\u0433\u043E.\nHea \u2014 \u0445\u043E\u0440\u043E\u0448\u0438\u0439 \nSuur \u2014 \u0431\u043E\u043B\u044C\u0448\u043E\u0439 \nIlus \u2014 \u043A\u0440\u0430\u0441\u0438\u0432\u044B\u0439 \n\n2. \u0421\u0440\u0430\u0432\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0441\u0442\u0435\u043F\u0435\u043D\u044C. Keskv\xF5rre. \u041F\u043E\u043A\u0430\u0437\u044B\u0432\u0430\u0435\u0442 \u043D\u0430\u043B\u0438\u0447\u0438\u0435 \u043A\u0430\u043A\u043E\u0433\u043E-\u043B\u0438\u0431\u043E \u043A\u0430\u0447\u0435\u0441\u0442\u0432\u0430 \u0432 \u0431\u043E\u043B\u044C\u0448\u0435\u0439 \u043C\u0435\u0440\u0435, \u0447\u0435\u043C \u0443 \u0434\u0440\u0443\u0433\u043E\u0433\u043E \u0447\u0435\u043B\u043E\u0432\u0435\u043A\u0430, \u043F\u0440\u0435\u0434\u043C\u0435\u0442\u0430, \u044F\u0432\u043B\u0435\u043D\u0438\u044F. \u0413\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u0447\u0435\u0441\u043A\u0438\u0439 \u043F\u0440\u0438\u0437\u043D\u0430\u043A \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u0441\u0440\u0430\u0432\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438 \u044F\u0432\u043B\u044F\u0435\u0442\u0441\u044F -m. \u041E\u0441\u043D\u043E\u0432\u043D\u043E\u0435 \u043F\u0440\u0430\u0432\u0438\u043B\u043E \u0437\u0432\u0443\u0447\u0438\u0442 \u0442\u0430\u043A: keskv\xF5rre = algv\xF5rre omastavas (\u0432 \u0444\u043E\u0440\u043C\u0435 2\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430) + m\n\n<table>\n    <tr>\n        <td>Nimetav</td>\n        <td>\u043F\u0435\u0440\u0435\u0432\u043E\u0434</td>\n        <td>Omastav</td>\n        <td>keskv\xF5rre</td>\n    </tr>\n    <tr>\n        <td>Vaba</td>\n        <td>\u0441\u0432\u043E\u0431\u043E\u0434\u043D\u044B\u0439</td>\n        <td>Vaba</td>\n        <td>Vabam</td>\n    </tr>\n    <tr>\n        <td>Palav</td>\n        <td>\u0436\u0430\u0440\u043A\u0438\u0439</td>\n        <td>Palava</td>\n        <td>Palavam</td>\n    </tr>\n    <tr>\n        <td>Kuulus</td>\n        <td>\u0438\u0437\u0432\u0435\u0441\u0442\u043D\u044B\u0439</td>\n        <td>Kuulsa</td>\n        <td>Kuulsam</td>\n    </tr>\n    <tr>\n        <td>Tugev</td>\n        <td>\u0441\u0438\u043B\u044C\u043D\u044B\u0439</td>\n        <td>Tugeva</td>\n        <td>Tugevam</td>\n    </tr>\n</table>\n\nN\xE4ide:\nPoiss on tugevam kui t\xFCdruk. \u041C\u0430\u043B\u044C\u0447\u0438\u043A \u0441\u0438\u043B\u044C\u043D\u0435\u0435, \u0447\u0435\u043C \u0434\u0435\u0432\u043E\u0447\u043A\u0430.\n\u041D\u043E, \u0443\u0432\u044B, \u043D\u0435 \u0432\u0441\u0435 \u0442\u0430\u043A \u043F\u0440\u043E\u0441\u0442\u043E. \u042D\u0441\u0442\u043E\u043D\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u2014 \u044D\u0442\u043E \u044F\u0437\u044B\u043A \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u0439. \u0427\u0430\u0441\u0442\u043E \u0432 \u0434\u0432\u0443\u0445\u0441\u043B\u043E\u0436\u043D\u044B\u0445 \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445, \u0432\u0442\u043E\u0440\u043E\u0439 \u043F\u0430\u0434\u0435\u0436 \u043A\u043E\u0442\u043E\u0440\u044B\u0445 \u0437\u0430\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u043D\u0430 \u0433\u043B\u0430\u0441\u043D\u044B\u0435 -a,  \u0438 \u0435\u0449\u0435 \u0440\u0435\u0436\u0435 -u, \u043F\u0435\u0440\u0435\u0434 \u0434\u043E\u0431\u0430\u0432\u043B\u0435\u043D\u0438\u0435\u043C \u043F\u0440\u0438\u0437\u043D\u0430\u043A\u0430 -m, \u0431\u0443\u0434\u0435\u0442 \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0434\u0438\u0442\u044C \u0438\u0437\u043C\u0435\u043D\u0435\u043D\u0438\u0435 \u044D\u0442\u0438\u0445 \u043F\u043E\u0441\u043B\u0435\u0434\u043D\u0438\u0445 \u0433\u043B\u0430\u0441\u043D\u044B\u0445 \u043D\u0430 -e.\n-A \u043F\u0435\u0440\u0435\u0445\u043E\u0434\u0438\u0442 \u0432 -E:\n\n<table>\n    <tr>\n        <td>Nimetav</td>\n        <td>\u043F\u0435\u0440\u0435\u0432\u043E\u0434</td>\n        <td>Omastav</td>\n        <td>keskv\xF5rre</td>\n    </tr>\n    <tr>\n        <td>Arg</td>\n        <td>\u0442\u0440\u0443\u0441\u043B\u0438\u0432\u044B\u0439</td>\n        <td>Ara</td>\n        <td>Arem</td>\n    </tr>\n    <tr>\n        <td>Halb</td>\n        <td>\u043F\u043B\u043E\u0445\u043E\u0439</td>\n        <td>Halva</td>\n        <td>Halvem</td>\n    </tr>\n    <tr>\n        <td>Kurb</td>\n        <td>\u043F\u0435\u0447\u0430\u043B\u044C\u043D\u044B\u0439</td>\n        <td>Kurva</td>\n        <td>Kurvem</td>\n    </tr>\n    <tr>\n        <td>Kuum</td>\n        <td>\u0433\u043E\u0440\u044F\u0447\u0438\u0439</td>\n        <td>Kuuma</td>\n        <td>kuumem</td>\n    </tr>\n    <tr>\n        <td>K\xF5va</td>\n        <td>\u0442\u0432\u0435\u0440\u0434\u044B\u0439</td>\n        <td>K\xF5va</td>\n        <td>k\xF5vem</td>\n    </tr>\n    <tr>\n        <td>K\xFClm</td>\n        <td>\u0445\u043E\u043B\u043E\u0434\u043D\u044B\u0439</td>\n        <td>K\xFClma</td>\n        <td>k\xFClmem</td>\n    </tr>\n    <tr>\n        <td>Lai</td>\n        <td>\u0448\u0438\u0440\u043E\u043A\u0438\u0439</td>\n        <td>Laia</td>\n        <td>laiem</td>\n    </tr>\n    <tr>\n        <td>Laisk</td>\n        <td>\u043B\u0435\u043D\u0438\u0432\u044B\u0439</td>\n        <td>Laisa</td>\n        <td>laisem</td>\n    </tr>\n    <tr>\n        <td>Must</td>\n        <td>\u0447\u0435\u0440\u043D\u044B\u0439, \u0433\u0440\u044F\u0437\u043D\u044B\u0439</td>\n        <td>Musta</td>\n        <td>mustem</td>\n    </tr>\n    <tr>\n        <td>N\xF5rk</td>\n        <td>\u0441\u043B\u0430\u0431\u044B\u0439</td>\n        <td>N\xF5rga</td>\n        <td>n\xF5rgem</td>\n    </tr>\n    <tr>\n        <td>Pikk</td>\n        <td>\u0434\u043B\u0438\u043D\u043D\u044B\u0439</td>\n        <td>Pika</td>\n        <td>pikem</td>\n    </tr>\n    <tr>\n        <td>Soe</td>\n        <td>\u0442\u0435\u043F\u043B\u044B\u0439</td>\n        <td>Sooja</td>\n        <td>soojem</td>\n    </tr>\n    <tr>\n        <td>Tark</td>\n        <td>\u0443\u043C\u043D\u044B\u0439</td>\n        <td>Targa</td>\n        <td>targem</td>\n    </tr>\n    <tr>\n        <td>T\xFChi</td>\n        <td>\u043F\u0443\u0441\u0442\u043E\u0439</td>\n        <td>T\xFChja</td>\n        <td>t\xFChjem</td>\n    </tr>\n    <tr>\n        <td>Vana</td>\n        <td>\u0441\u0442\u0430\u0440\u044B\u0439</td>\n        <td>Vana</td>\n        <td>vanem</td>\n    </tr>\n    <tr>\n        <td>\xD5rn</td>\n        <td>\u043D\u0435\u0436\u043D\u044B\u0439</td>\n        <td>\xD5rna</td>\n        <td>\xF5rnem</td>\n    </tr>\n</table>\n\n\n-U \u043F\u0435\u0440\u0435\u0445\u043E\u0434\u0438\u0442 \u0432 -E:\n\n<table>\n    <tr>\n        <td>Nimetav</td>\n        <td>\u043F\u0435\u0440\u0435\u0432\u043E\u0434</td>\n        <td>Omastav</td>\n        <td>keskv\xF5rre</td>\n    </tr>\n    <tr>\n        <td>J\xE4rsk</td>\n        <td>\u043A\u0440\u0443\u0442\u043E\u0439</td>\n        <td>J\xE4rsu</td>\n        <td>j\xE4rsem</td>\n    </tr>\n    <tr>\n        <td>Paks</td>\n        <td>\u0442\u043E\u043B\u0441\u0442\u044B\u0439</td>\n        <td>Paksu</td>\n        <td>paksem</td>\n    </tr>\n    <tr>\n        <td>Vali</td>\n        <td>\u0433\u0440\u043E\u043C\u043A\u0438\u0439</td>\n        <td>Valju</td>\n        <td>valjem</td>\n    </tr>\n</table>\n\n\u0421\u043B\u0435\u0434\u0443\u044E\u0449\u0430\u044F \u0433\u0440\u0443\u043F\u043F\u0430 \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u0439 \u0432\u043A\u043B\u044E\u0447\u0430\u0435\u0442\u0441\u044F \u0432 \u0441\u0435\u0431\u044F \u0441\u043B\u043E\u0432\u0430, \u043E\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u044E\u0449\u0438\u0435\u0441\u044F \u043D\u0430 \u0443\u043C\u0435\u043D\u044C\u0448\u0438\u0442\u0435\u043B\u044C\u043D\u043E-\u043B\u0430\u0441\u043A\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0439 \u0441\u0443\u0444\u0444\u0438\u043A\u0441 -ke. \u0412 \u044D\u0442\u043E\u043C \u0441\u043B\u0443\u0447\u0430\u0435 \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0434\u0438\u0442 \u0441\u043E\u043A\u0440\u0430\u0449\u0435\u043D\u0438\u0435 \u043E\u0441\u043D\u043E\u0432\u044B:\nl\xFChike-l\xFChikese-l\xFChem \u043A\u043E\u0440\u043E\u0447\u0435 \n\xF5huke-\xF5hukese-\xF5hem \u0442\u043E\u043D\u044C\u0448\u0435\npisike-pisikese-pisem \u043C\u0435\u043D\u044C\u0448\u0435\nNB! \u0421\u043B\u043E\u0432\u043E \u0445\u043E\u0440\u043E\u0448\u0438\u0439 \u0432\u043E \u043C\u043D\u043E\u0433\u0438\u0445 \u044F\u0437\u044B\u043A\u0430\u0445 \u043C\u0438\u0440\u0430 \u0438\u043C\u0435\u0435\u0442 \u0438\u0441\u043A\u043B\u044E\u0447\u0438\u0442\u0435\u043B\u044C\u043D\u0443\u044E \u0444\u043E\u0440\u043C\u0443 \u0441\u0440\u0430\u0432\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438.\nHea \u2014 \u0445\u043E\u0440\u043E\u0448\u0438\u0439 Parem \u2014 \u043B\u0443\u0447\u0448\u0435 \n\n\u0415\u0441\u0442\u044C \u0434\u0432\u0430 \u0441\u043F\u043E\u0441\u043E\u0431\u0430, \u043A\u0430\u043A \u043C\u043E\u0436\u043D\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C \u0441\u0440\u0430\u0432\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u0443\u044E \u0441\u0442\u0435\u043F\u0435\u043D\u044C \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445 \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0438:\n1. \u0421 \u043F\u0440\u0435\u0434\u043B\u043E\u0433\u043E\u043C kui, \u043A\u043E\u0442\u043E\u0440\u044B\u0439  \u0432 \u044D\u0442\u043E\u043C \u0441\u043B\u0443\u0447\u0430\u0435 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u0441\u044F \u043A\u0430\u043A \xAB\u0447\u0435\u043C\xBB. \u0421\u043B\u043E\u0432\u043E, \u0441 \u043A\u043E\u0442\u043E\u0440\u044B\u043C \u0432 \u044D\u0442\u043E\u043C \u0441\u043B\u0443\u0447\u0430\u0435 \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0434\u0438\u0442 \u0441\u0440\u0430\u0432\u043D\u0435\u043D\u0438\u0435, \u0431\u0443\u0434\u0435\u0442 \u0441\u0442\u043E\u044F\u0442\u044C \u0432 \u043F\u0430\u0434\u0435\u0436\u0435 nimetav.\nT\xFCdruk on n\xF5rgem kui poiss. \u0414\u0435\u0432\u043E\u0447\u043A\u0430 \u0441\u043B\u0430\u0431\u0435\u0435, \u0447\u0435\u043C \u043C\u0430\u043B\u044C\u0447\u0438\u043A.\nPirn on magusam kui \xF5un. \u0413\u0440\u0443\u0448\u0430 \u0441\u043B\u0430\u0449\u0435, \u0447\u0435\u043C \u044F\u0431\u043B\u043E\u043A\u043E.\n2. \u0421 \u043F\u0430\u0434\u0435\u0436\u043E\u043C seest\xFCtlev.\nT\xFCdruk on poisist n\xF5rgem. \u0414\u0435\u0432\u043E\u0447\u043A\u0430 \u0441\u043B\u0430\u0431\u0435\u0435 \u043C\u0430\u043B\u044C\u0447\u0438\u043A\u0430.\nPirn on \xF5unast magusam. \u0413\u0440\u0443\u0448\u0430 \u0441\u043B\u0430\u0449\u0435 \u044F\u0431\u043B\u043E\u043A\u0430.\n\u0415\u0441\u0442\u044C \u0435\u0449\u0435 \u043E\u0434\u0438\u043D \u043D\u044E\u0430\u043D\u0441, \u043A\u043E\u0442\u043E\u0440\u044B\u0439 \u0441\u0442\u043E\u0438\u0442 \u0440\u0430\u0437\u043E\u0431\u0440\u0430\u0442\u044C, \u043A\u043E\u0433\u0434\u0430 \u043C\u044B \u0433\u043E\u0432\u043E\u0440\u0438\u043C \u043E \u0441\u0442\u0435\u043F\u0435\u043D\u044F\u0445 \u0441\u0440\u0430\u0432\u043D\u0435\u043D\u0438\u044F \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435. \u0424\u043E\u0440\u043C\u044B \u0441\u043B\u043E\u0432 \u0441\u0440\u0430\u0432\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438 \u0441\u043A\u043B\u043E\u043D\u044F\u044E\u0442\u0441\u044F, \u0442\u043E \u0435\u0441\u0442\u044C \u0432\u0435\u0434\u0443\u0442 \u0441\u0435\u0431\u044F \u0442\u043E\u0447\u043D\u043E \u0442\u0430\u043A \u0436\u0435 \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0438, \u043A\u0430\u043A \u0438 \u0444\u043E\u0440\u043C\u044B \u043F\u043E\u043B\u043E\u0436\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438. \u041A \u0441\u0447\u0430\u0441\u0442\u044C\u044E, \u0441\u043A\u043B\u043E\u043D\u044F\u044E\u0442\u0441\u044F \u0432\u0441\u0435 \u0441\u043B\u043E\u0432\u0430 \u043E\u0434\u043D\u0438\u043C \u0438 \u0442\u0435\u043C \u0436\u0435 \u043E\u0431\u0440\u0430\u0437\u043E\u043C.\n\n<table>\n    <tr>\n        <td>\n            \u041F\u0430\u0434\u0435\u0436\u0438\n        </td>\n        <td>\u0415\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E \u0447\u0438\u0441\u043B\u043E.\n            Ainsus\n        </td>\n        <td></td>\n        <td>\u041C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E.\n            Mitmus.\n        </td>\n    </tr>\n    <tr>\n        <td>Nimetav</td>\n        <td>ilusam</td>\n        <td>Noorem</td>\n        <td>ilusamad</td>\n        <td>nooremad</td>\n    </tr>\n    <tr>\n        <td>Omastav</td>\n        <td>ilusama</td>\n        <td>noorema</td>\n        <td>ilusamate</td>\n        <td>nooremate</td>\n    </tr>\n    <tr>\n        <td>Osastav</td>\n        <td>ilusamat</td>\n        <td>nooremat</td>\n        <td>ilusamaid</td>\n        <td>nooremaid</td>\n    </tr>\n    <tr>\n        <td>Sisse\xFCtlev</td>\n        <td>ilusamasse</td>\n        <td>nooremasse</td>\n        <td>ilusamatesse/\n            ilusamaisse\n        </td>\n        <td>noorematesse/nooremaisse</td>\n    </tr>\n    <tr>\n        <td>Sees\xFCtlev</td>\n        <td>iluamas</td>\n        <td>nooremas</td>\n        <td>ilusamates/\n            ilusamais\n        </td>\n        <td>nooremates/ nooremais</td>\n    </tr>\n    <tr>\n        <td>... \u0438 \u0442.\u0434.</td>\n    </tr>\n</table>\n\nN\xE4ide:\n\nTa tahab elada suuremas toas. \u041E\u043D \u0445\u043E\u0447\u0435\u0442 \u0436\u0438\u0442\u044C \u0432 \u043A\u043E\u043C\u043D\u0430\u0442\u0435 \u043F\u043E\u0431\u043E\u043B\u044C\u0448\u0435.\nMulle meeldivad k\xF5rgemad majad. \u041C\u043D\u0435 \u043D\u0440\u0430\u0432\u044F\u0442\u0441\u044F \u0431\u043E\u043B\u0435\u0435 \u0432\u044B\u0441\u043E\u043A\u0438\u0435 \u0434\u043E\u043C\u0430.\n3. \u041F\u0440\u0435\u0432\u043E\u0441\u0445\u043E\u0434\u043D\u0430\u044F \u0441\u0442\u0435\u043F\u0435\u043D\u044C. \xDCliv\xF5rre. \u0414\u043B\u044F \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u044D\u0442\u043E\u0439 \u0444\u043E\u0440\u043C\u044B \u0435\u0441\u0442\u044C \u0434\u0432\u0430 \u0441\u043F\u043E\u0441\u043E\u0431\u0430. \n\u041F\u0435\u0440\u0432\u044B\u0439, \u043E\u0441\u043D\u043E\u0432\u043D\u043E\u0439 \u0441\u043F\u043E\u0441\u043E\u0431 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u043F\u0440\u0435\u0432\u043E\u0441\u0445\u043E\u0434\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438 \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445, \u044D\u0442\u043E \u0434\u043E\u0431\u0430\u0432\u0438\u0442\u044C \u043F\u0435\u0440\u0435\u0434 \u0443\u0436\u0435 \u0437\u043D\u0430\u043A\u043E\u043C\u043E\u0439 \u0444\u043E\u0440\u043C\u043E\u0439 \u0441\u0440\u0430\u0432\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438 \u0441\u043B\u043E\u0432\u043E k\xF5ige. \u042D\u0442\u043E \u0438 \u0431\u0443\u0434\u0435\u0442 \u0441\u043E\u043E\u0442\u0432\u0435\u0442\u0441\u0442\u0432\u043E\u0432\u0430\u0442\u044C \u0441\u043B\u043E\u0432\u0443 \xAB\u0441\u0430\u043C\u044B\u0439\xBB \u0440\u0443\u0441\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430:\n\n<table>\n    <tr>\n        <td>Nimetav</td>\n        <td>\u043F\u0435\u0440\u0435\u0432\u043E\u0434</td>\n        <td>omastav</td>\n        <td>keskv\xF5rre</td>\n        <td>\xFCliv\xF5rre</td>\n    </tr>\n    <tr>\n        <td>K\xF5rge</td>\n        <td>\u0432\u044B\u0441\u043E\u043A\u0438\u0439</td>\n        <td>k\xF5rge</td>\n        <td>k\xF5rgem</td>\n        <td>k\xF5ige k\xF5rgem</td>\n    </tr>\n    <tr>\n        <td>Vajalik</td>\n        <td>\u043D\u0443\u0436\u043D\u044B\u0439</td>\n        <td>vajaliku</td>\n        <td>vajalikum</td>\n        <td>k\xF5ige vajalikum</td>\n    </tr>\n    <tr>\n        <td>Noor</td>\n        <td>\u043C\u043E\u043B\u043E\u0434\u043E\u0439</td>\n        <td>noore</td>\n        <td>noorem</td>\n        <td>k\xF5ige noorem</td>\n    </tr>\n</table>\n\nMa olen k\xF5ige pikem t\xFCdruk klassis. \u042F \u0441\u0430\u043C\u0430\u044F \u0432\u044B\u0441\u043E\u043A\u0430\u044F \u0434\u0435\u0432\u043E\u0447\u043A\u0430 \u0432 \u043A\u043B\u0430\u0441\u0441\u0435.\nMart on k\xF5ige halvem sportlane, keda ma tunnen. \u041C\u0430\u0440\u0442 \u0441\u0430\u043C\u044B\u0439 \u043F\u043B\u043E\u0445\u043E\u0439 \u0441\u043F\u043E\u0440\u0442\u0441\u043C\u0435\u043D, \u043A\u043E\u0442\u043E\u0440\u043E\u0433\u043E \u044F \u0437\u043D\u0430\u044E.\n\u041D\u043E \u0443 \u043F\u0440\u0435\u0432\u043E\u0441\u0445\u043E\u0434\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438 \u0435\u0441\u0442\u044C \u0435\u0449\u0435 \u043E\u0434\u043D\u0430 \u0444\u043E\u0440\u043C\u0430. \u0420\u0430\u0437\u043E\u0431\u0440\u0430\u043D\u043D\u0430\u044F \u0432\u044B\u0448\u0435 \u0441\u0447\u0438\u0442\u0430\u0435\u0442\u0441\u044F \u043F\u043E\u043B\u043D\u043E\u0439 \u0444\u043E\u0440\u043C\u043E\u0439 \u043F\u0440\u0435\u0432\u043E\u0441\u0445\u043E\u0434\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438. \u041E\u043D\u0430 \u0435\u0441\u0442\u044C \u0443 \u0432\u0441\u0435\u0445 \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445. \u0410 \u0432\u043E\u0442 \u0443\u0436\u0435 \u0432\u043E\u0437\u043C\u043E\u0436\u043D\u043E\u0441\u0442\u044C \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u0442\u044C \u0432\u0442\u043E\u0440\u0443\u044E, \u043A\u043E\u0440\u043E\u0442\u043A\u0443\u044E \u0444\u043E\u0440\u043C\u0443 \u043F\u0440\u0435\u0432\u043E\u0441\u0445\u043E\u0434\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438 \u2014 i-\xFCliv\xF5rre, \u0435\u0441\u0442\u044C \u043D\u0435 \u0443 \u0432\u0441\u0435\u0445 \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445. \u0412\u043B\u0438\u044F\u0442\u044C \u043D\u0430 \u044D\u0442\u0443 \u0432\u043E\u0437\u043C\u043E\u0436\u043D\u043E\u0441\u0442\u044C \u0431\u0443\u0434\u0443\u0442 \u0432 \u043E\u0441\u043D\u043E\u0432\u043D\u043E\u043C \u0444\u043E\u0440\u043C\u044B \u0442\u0440\u0435\u0442\u044C\u0435\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430 \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430,  \u0433\u0434\u0435 \u0443 \u043D\u0435\u043A\u043E\u0442\u043E\u0440\u044B\u0445 \u0441\u043B\u043E\u0432 \u0435\u0441\u0442\u044C \u0432\u0430\u0440\u0438\u0430\u043D\u0442 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u043F\u0430\u0434\u0435\u0436\u0430 \u043F\u043E\u0441\u0440\u0435\u0434\u0441\u0442\u0432\u043E\u043C \u0434\u043E\u0431\u0430\u0432\u043E\u0447\u043D\u043E\u0433\u043E \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u0430 -i. \u042D\u0442\u043E\u0442 \u043F\u0430\u0434\u0435\u0436 \u0432\u043E \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u043C \u0447\u0438\u0441\u043B\u0435 \u043C\u044B \u043F\u043E\u043A\u0430 \u043D\u0435 \u043F\u0440\u043E\u0445\u043E\u0434\u0438\u043B\u0438, \u0438 \u043D\u0430\u043C \u0431\u0443\u0434\u0435\u0442 \u0434\u043E\u0441\u0442\u0430\u0442\u043E\u0447\u043D\u043E \u0441\u043B\u043E\u0436\u043D\u043E \u043F\u043E\u043D\u044F\u0442\u044C, \u0443 \u043A\u0430\u043A\u0438\u0445 \u0441\u043B\u043E\u0432 \u044D\u0442\u0430 \u0432\u043E\u0437\u043C\u043E\u0436\u043D\u043E\u0441\u0442\u044C \u0435\u0441\u0442\u044C, \u0430 \u0443 \u043A\u0430\u043A\u0438\u0445 \u043D\u0435\u0442. \u041F\u043E\u044D\u0442\u043E\u043C\u0443 \u043D\u0430 \u043D\u0430\u0447\u0430\u043B\u044C\u043D\u043E\u043C \u044D\u0442\u0430\u043F\u0435 \u0438\u0437\u0443\u0447\u0435\u043D\u0438\u044F, \u0446\u0435\u043B\u0435\u0441\u043E\u043E\u0431\u0440\u0430\u0437\u043D\u043E \u0431\u0443\u0434\u0435\u0442 \u0437\u0430\u043F\u043E\u043C\u043D\u0438\u0442\u044C, \u043A\u0430\u043A \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442\u0441\u044F \u043F\u043E\u043B\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430 \u043F\u0440\u0435\u0432\u043E\u0441\u0445\u043E\u0434\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438, \u0438 \u043F\u0440\u0438 \u043D\u0435\u043E\u0431\u0445\u043E\u0434\u0438\u043C\u043E\u0441\u0442\u0438 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C \u0438\u043C\u0435\u043D\u043D\u043E \u0435\u0435. \u041D\u043E \u0435\u0441\u043B\u0438 \u0432\u044B \u0432 \u0442\u0435\u043A\u0441\u0442\u0435 \u0432\u0441\u0442\u0440\u0435\u0442\u0438\u0442\u0435 \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0435, \u0443 \u043A\u043E\u0442\u043E\u0440\u044B\u0445, \u0432\u043C\u0435\u0441\u0442\u043E \u0437\u043D\u0430\u043A\u043E\u043C\u043E\u0433\u043E \u043F\u0440\u0438\u0437\u043D\u0430\u043A\u0430 -m \u0441\u0440\u0430\u0432\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438, \u0431\u0443\u0434\u0435\u0442 \u043A \u0441\u043B\u043E\u0432\u0443 \u0434\u043E\u0431\u0430\u0432\u043B\u0435\u043D\u043E -im, \u0442\u043E \u043F\u0443\u0433\u0430\u0442\u044C\u0441\u044F \u043D\u0435 \u0441\u0442\u043E\u0438\u0442. \u042D\u0442\u043E \u0438 \u0431\u0443\u0434\u0435\u0442 \u043A\u043E\u0440\u043E\u0442\u043A\u0438\u0439 \u0432\u0430\u0440\u0438\u0430\u043D\u0442 \u043F\u0440\u0435\u0432\u043E\u0441\u0445\u043E\u0434\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438. \u0422\u0430\u043A \u0436\u0435 \u043C\u043E\u0436\u043D\u043E \u0432\u044B\u044F\u0432\u0438\u0442\u044C \u0443\u0436\u0435 \u0441\u0435\u0439\u0447\u0430\u0441 \u0442\u0430\u043A\u0443\u044E \u0437\u0430\u043A\u043E\u043D\u043E\u043C\u0435\u0440\u043D\u043E\u0441\u0442\u044C, \u0447\u0442\u043E \u0435\u0441\u043B\u0438 \u0432 \u0441\u0440\u0430\u0432\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438 \u0443 \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E \u043F\u0435\u0440\u0435\u0434 \u0431\u0443\u043A\u0432\u043E\u0439 \u2013m  \u0441\u0442\u043E\u0438\u0442 \u2013e-,\u0442\u043E \u0441\u043A\u043E\u0440\u0435\u0435 \u0432\u0441\u0435\u0433\u043E \u0443 \u0442\u0430\u043A\u043E\u0433\u043E \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u0442\u043E\u0436\u0435 \u0431\u0443\u0434\u0435\u0442 \u043A\u043E\u0440\u043E\u0442\u043A\u0430\u044F \u0444\u043E\u0440\u043C\u0430 \u043F\u0440\u0435\u0432\u043E\u0441\u0445\u043E\u0434\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438, \u0438 \u2013em  \u043F\u043E\u043C\u0435\u043D\u044F\u0435\u0442\u0441\u044F \u043D\u0430 -im:\n\n<table>\n    <tr>\n        <td>Nimetav</td>\n        <td>\u043F\u0435\u0440\u0435\u0432\u043E\u0434</td>\n        <td>omastav</td>\n        <td>keskv\xF5rre</td>\n        <td>\xFCliv\xF5rre</td>\n    </tr>\n    <tr>\n        <td>Suur</td>\n        <td>\u0431\u043E\u043B\u044C\u0448\u043E\u0439</td>\n        <td>suure</td>\n        <td>suurem</td>\n        <td>suurim\n            k\xF5ige suurem\n            Hea\n        </td>\n        <td>\u0445\u043E\u0440\u043E\u0448\u0438\u0439</td>\n        <td>Hea</td>\n        <td>parem</td>\n        <td>parim\n            k\xF5ige parem\n        </td>\n    </tr>\n    <tr>\n        <td>T\xE4htis</td>\n        <td>\u0432\u0430\u0436\u043D\u044B\u0439</td>\n        <td>t\xE4htsa</td>\n        <td>t\xE4htsam</td>\n        <td>t\xE4htsaim\n            k\xF5ige t\xE4htsam\n        </td>\n    </tr>\n</table>\n\nMe elame linna k\xF5ige suuremas majas = Me elame linna suurimas majas. \u041C\u044B \u0436\u0438\u0432\u0435\u043C \u0432 \u0441\u0430\u043C\u043E\u043C \u0431\u043E\u043B\u044C\u0448\u043E\u043C \u0434\u043E\u043C\u0435 \u0433\u043E\u0440\u043E\u0434\u0430.\nKorvis on k\xF5ige hapumad \xF5unad. \u0412 \u043A\u043E\u0440\u0437\u0438\u043D\u0435 \u0441\u0430\u043C\u044B\u0435 \u043A\u0438\u0441\u043B\u044B\u0435 \u044F\u0431\u043B\u043E\u043A\u0438. (\u041A\u043E\u0440\u043E\u0442\u043A\u043E\u0439 \u0444\u043E\u0440\u043C\u044B \u043F\u0440\u0435\u0432\u043E\u0441\u0445\u043E\u0434\u043D\u043E\u0439 \u0441\u0442\u0435\u043F\u0435\u043D\u0438 \u043D\u0435\u0442.)\n\u041D\u0430 \u044D\u0442\u043E\u043C \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u0447\u0435\u0441\u043A\u0430\u044F \u0447\u0430\u0441\u0442\u044C \u043F\u043E \u0442\u0435\u043C\u0435 \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0435 \u0437\u0430\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F. \u041C\u044B \u043F\u043E\u0433\u043E\u0432\u043E\u0440\u0438\u043B\u0438 \u043E \u0438\u0445 \u0440\u043E\u043B\u0438 \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0438, \u043E\u0431 \u043E\u0441\u043E\u0431\u0435\u043D\u043D\u043E\u0441\u0442\u044F\u0445 \u0441\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F, \u0438 \u043E\u0431 \u0438\u0445 \u0438\u0437\u043C\u0435\u043D\u0435\u043D\u0438\u0438 \u043F\u043E \u0441\u0442\u0435\u043F\u0435\u043D\u044F\u043C \u0441\u0440\u0430\u0432\u043D\u0435\u043D\u0438\u044F. \u0412\u0430\u043C \u043B\u0438\u0448\u044C \u043E\u0441\u0442\u0430\u0435\u0442\u0441\u044F \u043E\u0442\u0440\u0430\u0431\u0430\u0442\u044B\u0432\u0430\u0442\u044C \u043F\u043E\u043B\u0443\u0447\u0435\u043D\u043D\u044B\u0435 \u0437\u043D\u0430\u043D\u0438\u044F \u043D\u0430 \u043F\u0440\u0430\u043A\u0442\u0438\u043A\u0435, \u0438 \u043D\u0430\u0447\u0430\u0442\u044C \u0441 \u0443\u043F\u0440\u0430\u0436\u043D\u0435\u043D\u0438\u0439 \u043F\u043E\u0441\u043B\u0435 \u044D\u0442\u043E\u0433\u043E \u0443\u0440\u043E\u043A\u0430.\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('lesson',{number:13}),source:source});}));

