var source='\n\u041D\u0430 \u043F\u0440\u043E\u0448\u043B\u043E\u043C \u0443\u0440\u043E\u043A\u0435 \u043C\u044B \u0438 \u043F\u0440\u0430\u0432\u0434\u0430 \u0432\u043F\u043B\u043E\u0442\u043D\u0443\u044E \u0437\u0430\u043D\u044F\u043B\u0438\u0441\u044C \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C\u0438. \u0412\u0435\u0440\u044E, \u0447\u0442\u043E \u0432\u0430\u043C \u0432\u0441\u0442\u0440\u0435\u0442\u0438\u043B\u043E\u0441\u044C \u043C\u043D\u043E\u0433\u043E \u043D\u043E\u0432\u044B\u0445 \u0441\u043B\u043E\u0432. \u0418 \u043C\u044B \u043F\u043E\u043C\u043D\u0438\u043C, \u0447\u0442\u043E \u0443\u0447\u0438\u0442\u044C \u0433\u043B\u0430\u0433\u043E\u043B\u044B \u043D\u0443\u0436\u043D\u043E \u0432 \u0442\u0440\u0435\u0445 \u043E\u0441\u043D\u043E\u0432\u043D\u044B\u0445 \u0444\u043E\u0440\u043C\u0430\u0445: 2 \u0444\u043E\u0440\u043C\u044B \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u0430 \u0438 \u0444\u043E\u0440\u043C\u0443 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438. \u0412\u0430\u0448\u0438 \u0441\u043F\u0438\u0441\u043A\u0438 \u0432\u044B\u043F\u0438\u0441\u0430\u043D\u043D\u044B\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u0434\u043E\u043B\u0436\u043D\u044B \u043F\u043E\u043F\u043E\u043B\u043D\u044F\u0442\u044C\u0441\u044F \u043A\u0430\u0436\u0434\u044B\u0439 \u0434\u0435\u043D\u044C. \u041D\u043E \u043E \u0432\u043E\u0442 \u043E \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u044F\u0445 \u0438 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043D\u0438\u0438 \u043D\u0435\u043A\u043E\u0442\u043E\u0440\u044B\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u044F \u0432\u0441\u0435-\u0442\u0430\u043A\u0438 \u0445\u043E\u0442\u0435\u043B\u0430 \u0431\u044B \u043F\u043E\u0433\u043E\u0432\u043E\u0440\u0438\u0442\u044C \u043F\u043E\u0434\u0440\u043E\u0431\u043D\u0435\u0435. \u041D\u0443 \u0438 \u043A\u043E\u043D\u0435\u0447\u043D\u043E \u0436\u0435 \u043D\u0435 \u0432\u0441\u0435 \u0435\u0449\u0435 \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u0447\u0435\u0441\u043A\u0438\u0445 \u0444\u043E\u0440\u043C\u044B \u0432\u044B \u0440\u0430\u0437\u043E\u0431\u0440\u0430\u043B\u0438, \u0442\u0430\u043A \u0447\u0442\u043E \u043F\u0440\u0438\u0441\u0442\u0443\u043F\u0438\u043C \u0438 \u043E\u0431\u043E \u0432\u0441\u0435\u043C \u043F\u043E\u043F\u043E\u0440\u044F\u0434\u043A\u0443.\n\u0413\u043B\u0430\u0433\u043E\u043B\u044B \u0434\u0432\u0438\u0436\u0435\u043D\u0438\u044F \u2014 minema, k\xE4ima, s\xF5itma, tulema.\n\u0413\u043B\u0430\u0433\u043E\u043B minema \u043D\u0430 \u0440\u0443\u0441\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u0441\u044F \u043A\u0430\u043A \u0438\u0434\u0442\u0438, \u0432\u044B\u0440\u0430\u0436\u0430\u0435\u0442 \u043E\u0431\u044B\u0447\u043D\u043E \u043A\u043E\u043D\u043A\u0440\u0435\u0442\u043D\u043E\u0435 \u0434\u0432\u0438\u0436\u0435\u043D\u0438\u0435 \xAB\u043A\u0443\u0434\u0430-\u0442\u043E\xBB \u0438/\u0438\u043B\u0438 \xAB\u043E\u0442\u043A\u0443\u0434\u0430-\u0442\u043E\xBB, \u043E\u0442\u0434\u0430\u043B\u0435\u043D\u0438\u0435, \u0441 \u0443\u043A\u0430\u0437\u0430\u043D\u0438\u0435\u043C \u043D\u0430\u043F\u0440\u0430\u0432\u043B\u0435\u043D\u0438\u044F. Ma l\xE4hen kooli. \u042F \u0438\u0434\u0443 \u0432 \u0448\u043A\u043E\u043B\u0443. NB! \u041C\u043E\u0436\u043D\u043E \u0442\u0430\u043A\u0436\u0435  \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C \u0432 \u0441\u043B\u0443\u0447\u0430\u0435 \u0434\u0432\u0438\u0436\u0435\u043D\u0438\u044F \u043F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0442\u0440\u0430\u043D\u0441\u043F\u043E\u0440\u0442\u043D\u043E\u0433\u043E \u0441\u0440\u0435\u0434\u0441\u0442\u0432\u0430. Ma l\xE4hen t\xE4na kooli bussiga. \u042F  \u0435\u0434\u0443 (\u043D\u0430\u043F\u0440\u0430\u0432\u043B\u044F\u044E\u0441\u044C) \u0432 \u0448\u043A\u043E\u043B\u0443 \u043D\u0430 \u0430\u0432\u0442\u043E\u0431\u0443\u0441\u0435.\n\u0413\u043B\u0430\u0433\u043E\u043B s\xF5itma. \u041F\u0435\u0440\u0435\u0432\u043E\u0434 \u2014 \u0435\u0445\u0430\u0442\u044C. \u0412\u044B\u0440\u0430\u0436\u0430\u0435\u0442 \u0434\u0432\u0438\u0436\u0435\u043D\u0438\u0435 \u043D\u0430 \u0442\u0440\u0430\u043D\u0441\u043F\u043E\u0440\u0442\u0435 \u043A\u0430\u043A \u0432 \u043D\u0430\u043F\u0440\u0430\u0432\u043B\u0435\u043D\u0438\u0438 \xAB\u043A\u0443\u0434\u0430?\xBB, \u0442\u0430\u043A \u0438 \xAB\u043E\u0442\u043A\u0443\u0434\u0430?\xBB. Ma s\xF5idan kodust t\xF6\xF6le autoga. \u0418\u0437 \u0434\u043E\u043C\u0430 \u043D\u0430 \u0440\u0430\u0431\u043E\u0442\u0443 \u044F \u0435\u0434\u0443 \u043D\u0430 \u043C\u0430\u0448\u0438\u043D\u0435.\nK\xE4ima. \u041C\u043E\u0436\u0435\u043C \u043F\u0435\u0440\u0435\u0432\u0435\u0441\u0442\u0438 \u043A\u0430\u043A \u2014 \u0445\u043E\u0434\u0438\u0442\u044C, \u0431\u044B\u0432\u0430\u0442\u044C. \u041E\u0431\u044F\u0437\u0430\u0442\u0435\u043B\u044C\u043D\u043E \u0442\u0440\u0435\u0431\u0443\u0435\u0442 \u0432\u043E\u043F\u0440\u043E\u0441\u0430 \u2014 \xAB\u0433\u0434\u0435?\xBB \u2014 \xABkus?\xBB, \u0438 \u043D\u0438 \u0432 \u043A\u043E\u0435\u043C \u0441\u043B\u0443\u0447\u0430\u0435 \xAB\u043A\u0443\u0434\u0430?\xBB, \u0442\u0430\u043A \u043A\u0430\u043A \u043E\u0431\u044B\u0447\u043D\u043E \u0432\u044B\u0440\u0430\u0436\u0430\u0435\u0442 \u043C\u043D\u043E\u0433\u043E\u043A\u0440\u0430\u0442\u043D\u043E\u0435, \u043F\u043E\u0432\u0442\u043E\u0440\u044F\u044E\u0449\u0435\u0435\u0441\u044F \u0434\u0432\u0438\u0436\u0435\u043D\u0438\u0435 \u043A\u0430\u043A \u043F\u0435\u0448\u043A\u043E\u043C, \u0442\u0430\u043A \u0438 \u0442\u0440\u0430\u043D\u0441\u043F\u043E\u0440\u0442\u043E\u043C. Ma k\xE4in lasteaias. \u042F \u0445\u043E\u0436\u0443 \u0432 \u0434\u0435\u0442\u0441\u043A\u0438\u0439 \u0441\u0430\u0434. Ma k\xE4in poes ja ostan piima koju. \u042F \u0445\u043E\u0436\u0443 \u0432 \u043C\u0430\u0433\u0430\u0437\u0438\u043D \u0438 \u043F\u043E\u043A\u0443\u043F\u0430\u044E \u043C\u043E\u043B\u043E\u043A\u043E \u0434\u043E\u043C\u043E\u0439.\n\u0418 \u043D\u0430\u043F\u043E\u0441\u043B\u0435\u0434\u043E\u043A \u0433\u043B\u0430\u0433\u043E\u043B tulema \u2014 \u043F\u0440\u0438\u0445\u043E\u0434\u0438\u0442\u044C, \u0432\u043E\u0437\u0432\u0440\u0430\u0449\u0430\u0442\u044C\u0441\u044F. \u041E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0435\u0442 \u043F\u0440\u0438\u0431\u043B\u0438\u0436\u0435\u043D\u0438\u0435, \u0432\u043E\u0437\u043C\u043E\u0436\u043D\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C  \u0432 \u043D\u0430\u043F\u0440\u0430\u0432\u043B\u0435\u043D\u0438\u0438  \u043A\u0430\u043A \xAB\u043A\u0443\u0434\u0430?\xBB, \u0442\u0430\u043A \u0438 \xAB\u043E\u0442\u043A\u0443\u0434\u0430?\xBB. Millal sa koju tuled? \u041A\u043E\u0433\u0434\u0430 \u0442\u044B \u0432\u0435\u0440\u043D\u0435\u0448\u044C\u0441\u044F \u0434\u043E\u043C\u043E\u0439? Ma tulen t\xF6\xF6lt koju kell viis. \u042F \u0432\u0435\u0440\u043D\u0443\u0441\u044C \u0441 \u0440\u0430\u0431\u043E\u0442\u044B \u0434\u043E\u043C\u043E\u0439 \u0432 5 \u0447\u0430\u0441\u043E\u0432.\n\n### \u041C\u043E\u0434\u0430\u043B\u044C\u043D\u044B\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u044B\n\n\u041E\u0442\u0434\u0435\u043B\u044C\u043D\u043E\u0439 \u0433\u0440\u0443\u043F\u043F\u043E\u0439 \u043C\u043E\u0436\u043D\u043E \u0432\u044B\u0434\u0435\u043B\u0438\u0442\u044C \u0442\u0430\u043A \u043D\u0430\u0437\u044B\u0432\u0430\u0435\u043C\u044B\u0435 \u043C\u043E\u0434\u0430\u043B\u044C\u043D\u044B\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u044B. \u042D\u0442\u043E \u0442\u0430\u043A\u0438\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u044B, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0432\u044B\u0440\u0430\u0436\u0430\u044E\u0442 \u043D\u0435 \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435, \u0430 \u043E\u0442\u043D\u043E\u0448\u0435\u043D\u0438\u0435 \u043A \u043D\u0435\u043C\u0443: \u0432\u043E\u0437\u043C\u043E\u0436\u043D\u043E\u0441\u0442\u044C, \u043D\u0435\u043E\u0431\u0445\u043E\u0434\u0438\u043C\u043E\u0441\u0442\u044C, \u0441\u043F\u043E\u0441\u043E\u0431\u043D\u043E\u0441\u0442\u044C, \u0432\u0435\u0440\u043E\u044F\u0442\u043D\u043E\u0441\u0442\u044C \u0438 \u0442. \u0434. \u0441\u043E\u0432\u0435\u0440\u0448\u0435\u043D\u0438\u044F \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044F.  \u041A\u0430\u043A \u0412\u044B \u043C\u043E\u0433\u043B\u0438 \u0437\u0430\u043C\u0435\u0442\u0438\u0442\u044C \u0438\u0437 \u0442\u0430\u0431\u043B\u0438\u0446 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u043E\u0432, \u0442\u0430\u043A\u0438\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u0434\u043E\u0441\u0442\u0430\u0442\u043E\u0447\u043D\u043E \u043C\u043D\u043E\u0433\u043E. \u0412 \u0441\u043F\u0438\u0441\u043E\u043A \u043D\u0430\u0438\u0431\u043E\u043B\u0435\u0435 \u0447\u0430\u0441\u0442\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u043C\u044B\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u043C\u043E\u0436\u043D\u043E \u0432\u043D\u0435\u0441\u0442\u0438: saama, tohtima, v\xF5ima, suutma, j\xF5udma, pidama (\u0431\u044B\u0442\u044C \u0434\u043E\u043B\u0436\u043D\u044B\u043C), tulema (\u043F\u0440\u0438\u0445\u043E\u0434\u0438\u0442\u044C\u0441\u044F), oskama (\u0437\u043D\u0430\u0442\u044C, \u0443\u043C\u0435\u0442\u044C), pruukima (\u0441\u0442\u043E\u0438\u0442\u044C, \u0434\u043E\u043B\u0436\u0435\u043D), tarvitsema (\u0441\u0442\u043E\u0438\u0442\u044C, \u0434\u043E\u043B\u0436\u0435\u043D) \u0438 \u0442.\u0434.. \u041D\u043E \u043E\u0441\u043E\u0431\u043E \u043F\u0440\u043E\u0431\u043B\u0435\u043C\u0430\u0442\u0438\u0447\u043D\u044B\u043C\u0438 \u0431\u0443\u0434\u0443\u0442 \u0437\u0434\u0435\u0441\u044C \u043F\u0435\u0440\u0432\u044B\u0435 \u043F\u044F\u0442\u044C \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432, \u0442\u0430\u043A \u043A\u0430\u043A \u0432\u0441\u0435 \u043E\u0434\u0438\u043D\u0430\u043A\u043E\u0432\u043E \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u044F\u0442\u0441\u044F \u2014 \xAB\u043C\u043E\u0447\u044C\xBB. \u041F\u043E\u0433\u043E\u0432\u043E\u0440\u0438\u043C \u043E \u043A\u0430\u0436\u0434\u043E\u043C \u0438\u0437 \u043D\u0438\u0445 \u043F\u043E-\u043E\u0442\u0434\u0435\u043B\u044C\u043D\u043E\u0441\u0442\u0438 \u0438 \u043E\u043F\u0440\u0435\u0434\u0435\u043B\u0438\u043C \u0440\u0430\u0437\u043B\u0438\u0447\u0438\u044F \u043C\u0435\u0436\u0434\u0443 \u043D\u0438\u043C\u0438.\n\u0413\u043B\u0430\u0433\u043E\u043B saama (saada, saab). \u041E\u043D \u043F\u043E\u043A\u0430\u0437\u044B\u0432\u0430\u0435\u0442, \u0447\u0442\u043E \u0447\u0442\u043E-\u0442\u043E \u0432\u043E\u0437\u043C\u043E\u0436\u043D\u043E \u0441\u0434\u0435\u043B\u0430\u0442\u044C, \u0442\u0430\u043A \u043A\u0430\u043A \u0434\u043B\u044F \u044D\u0442\u043E\u0433\u043E \u0441\u043E\u0437\u0434\u0430\u043D\u044B \u0432\u0441\u0435 \u043D\u0435\u043E\u0431\u0445\u043E\u0434\u0438\u043C\u044B\u0435 \u0443\u0441\u043B\u043E\u0432\u0438\u044F. Ma saan restoranis s\xFC\xFCa, kuna mul on raha. \u042F \u043C\u043E\u0433\u0443 \u043F\u043E\u043A\u0443\u0448\u0430\u0442\u044C \u0432 \u0440\u0435\u0441\u0442\u043E\u0440\u0430\u043D\u0435, \u0442\u0430\u043A \u043A\u0430\u043A \u0443 \u043C\u0435\u043D\u044F \u0435\u0441\u0442\u044C \u0434\u0435\u043D\u044C\u0433\u0438. Ma ei saa k\xFClla tulla, sest mul ei ole kingi. \u042F \u043D\u0435 \u043C\u043E\u0433\u0443 \u043F\u0440\u0438\u0439\u0442\u0438 \u0432 \u0433\u043E\u0441\u0442\u0438, \u0442\u0430\u043A \u043A\u0430\u043A \u0443 \u043C\u0435\u043D\u044F \u043D\u0435\u0442 \u043F\u043E\u0434\u0430\u0440\u043A\u0430.\nTohtima (tohtida, tohib). \u041E\u0441\u043E\u0431\u0435\u043D\u043D\u043E\u0441\u0442\u044C \u044D\u0442\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430: \u0435\u0433\u043E \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0435 \u0443\u043A\u0430\u0437\u044B\u0432\u0430\u0435\u0442 \u043D\u0430 \u0442\u043E, \u0447\u0442\u043E \u0447\u0442\u043E-\u0442\u043E \u0432\u043E\u0437\u043C\u043E\u0436\u043D\u043E \u0441\u0434\u0435\u043B\u0430\u0442\u044C, \u0442\u0430\u043A \u043A\u0430\u043A \u0435\u0441\u0442\u044C \u043D\u0430 \u044D\u0442\u043E \u0440\u0430\u0437\u0440\u0435\u0448\u0435\u043D\u0438\u0435. \u041F\u0440\u0438 \u043E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u0438 \u044F\u0432\u043D\u044B\u0439 \u0437\u0430\u043F\u0440\u0435\u0442 \u2014 \xAB\u043D\u0435\u043B\u044C\u0437\u044F\xBB. Siin tohib jopes istuda. \u0417\u0434\u0435\u0441\u044C \u043C\u043E\u0436\u043D\u043E \u0441\u0438\u0434\u0435\u0442\u044C \u0432 \u043A\u0443\u0440\u0442\u043A\u0435. Lennukis ei tohi suitsetada. \u0412 \u0441\u0430\u043C\u043E\u043B\u0435\u0442\u0435 \u043D\u0435\u043B\u044C\u0437\u044F \u043A\u0443\u0440\u0438\u0442\u044C.\nV\xF5ima (v\xF5ida, v\xF5ib). \u0423 \u0434\u0430\u043D\u043D\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430 3 \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u044F. \u0412\u043E-\u043F\u0435\u0440\u0432\u044B\u0445, \u043E\u043D \u043C\u043E\u0436\u0435\u0442 \u0431\u044B\u0442\u044C \u0441\u0438\u043D\u043E\u043D\u0438\u043C\u043E\u043C tohtima, \u0442\u043E \u0435\u0441\u0442\u044C \u0432\u044B\u0440\u0430\u0436\u0430\u0442\u044C \u0440\u0430\u0437\u0440\u0435\u0448\u0435\u043D\u0438\u0435 \u043D\u0430 \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435. Lennukis ei v\xF5i suitsetada. \u0412 \u0441\u0430\u043C\u043E\u043B\u0435\u0442\u0435 \u043D\u0435\u043B\u044C\u0437\u044F \u043A\u0443\u0440\u0438\u0442\u044C. \u0412\u043E-\u0432\u0442\u043E\u0440\u044B\u0445, \u0433\u043B\u0430\u0433\u043E\u043B \u043F\u043E\u043A\u0430\u0437\u044B\u0432\u0430\u0435\u0442, \u0447\u0442\u043E \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435 \u0432\u043E\u0437\u043C\u043E\u0436\u043D\u043E, \u0438 \u043F\u0440\u0438 \u044D\u0442\u043E\u043C \u0443 \u043B\u0438\u0446\u0430, \u0432\u044B\u043F\u043E\u043B\u043D\u044F\u044E\u0449\u0435\u0433\u043E \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435, \u0435\u0441\u0442\u044C \u0436\u0435\u043B\u0430\u043D\u0438\u0435 \u044D\u0442\u043E \u0434\u0435\u043B\u0430\u0442\u044C. Ma v\xF5in sind aidata. \u042F \u043C\u043E\u0433\u0443 \u0442\u0435\u0431\u0435 \u043F\u043E\u043C\u043E\u0447\u044C. \u0418 \u0432-\u0442\u0440\u0435\u0442\u044C\u0438\u0445, \u0433\u043B\u0430\u0433\u043E\u043B \u043C\u043E\u0436\u0435\u0442 \u043F\u043E\u043A\u0430\u0437\u044B\u0432\u0430\u0442\u044C \u0432\u0435\u0440\u043E\u044F\u0442\u043D\u043E\u0441\u0442\u044C \u0431\u0443\u0434\u0443\u0449\u0438\u0445 \u0441\u043E\u0431\u044B\u0442\u0438\u0439, \u043F\u0440\u043E\u0433\u043D\u043E\u0437, \u0447\u0442\u043E \u0438\u043C\u0435\u043D\u043D\u043E \u0442\u0430\u043A \u0432\u0441\u0435 \u0438 \u043C\u043E\u0436\u0435\u0442 \u043F\u0440\u043E\u0438\u0437\u043E\u0439\u0442\u0438. Homme v\xF5ib hakata lund sadama. \u0417\u0430\u0432\u0442\u0440\u0430 \u043C\u043E\u0436\u0435\u0442 \u043F\u043E\u0439\u0442\u0438 \u0441\u043D\u0435\u0433.\nSuutma (suuta, suudab). \u042D\u0442\u043E\u0442 \u0433\u043B\u0430\u0433\u043E\u043B \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u0432 \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0438 \u043C\u043E\u0433\u0443, \u0442\u043E \u0435\u0441\u0442\u044C \u0443  \u043D\u0430\u0441 \u0435\u0441\u0442\u044C \u0441\u0438\u043B\u044B \u0438 \u0436\u0435\u043B\u0430\u043D\u0438\u0435 \u044D\u0442\u043E \u0441\u0434\u0435\u043B\u0430\u0442\u044C. Ma suudan olla terve p\xE4eva s\xF6\xF6mata. \u042F \u043C\u043E\u0433\u0443 \u043D\u0435 \u0435\u0441\u0442\u044C \u0446\u0435\u043B\u044B\u0439 \u0434\u0435\u043D\u044C.\nJ\xF5udma (j\xF5uda, j\xF5uab). \u041E\u0441\u043D\u043E\u0432\u0430 \u044D\u0442\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430 j\xF5ud, \u0441\u0438\u043B\u0430. \u0422\u043E \u0435\u0441\u0442\u044C \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0435 \xAB\u043C\u043E\u0433\u0443\xBB \u0440\u0430\u0441\u0446\u0435\u043D\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u043A\u0430\u043A \u0431\u044B\u0442\u044C \u0432 \u0441\u043E\u0441\u0442\u043E\u044F\u043D\u0438\u0438, \u0432 \u0441\u0438\u043B\u0435 \u0447\u0442\u043E-\u043B\u0438\u0431\u043E \u0441\u0434\u0435\u043B\u0430\u0442\u044C. Ma ei j\xF5ua enam kannatada.  \u042F \u043D\u0435 \u043C\u043E\u0433\u0443 \u0431\u043E\u043B\u044C\u0448\u0435 \u0442\u0435\u0440\u043F\u0435\u0442\u044C (\u0443 \u043C\u0435\u043D\u044F \u043D\u0435\u0442 \u0441\u0438\u043B \u0442\u0435\u0440\u043F\u0435\u0442\u044C). \u0422\u0430\u043A\u0436\u0435 \u0435\u0441\u0442\u044C \u0432\u0430\u0440\u0438\u0430\u043D\u0442 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0430 \u043A\u0430\u043A \u043C\u043E\u0447\u044C, \u0432 \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0438 \u0443\u0441\u043F\u0435\u0432\u0430\u0442\u044C. Ma j\xF5uan koju kella kaheksani. \u042F \u0443\u0441\u043F\u0435\u044E \u0434\u043E\u043C\u043E\u0439, \u043A \u0432\u043E\u0441\u044C\u043C\u0438 (\u0441\u043C\u043E\u0433\u0443 \u0434\u043E\u0431\u0440\u0430\u0442\u044C\u0441\u044F).\n\n\u0421 \u043C\u043E\u0434\u0430\u043B\u044C\u043D\u044B\u043C\u0438 \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C\u0438 \u043C\u044B \u043F\u043E\u0437\u043D\u0430\u043A\u043E\u043C\u0438\u043B\u0438\u0441\u044C, \u043D\u043E \xAB\u043C\u043E\u0434\u0430\u043B\u044C\u043D\u043E\u0441\u0442\u044C\xBB, \u0442\u043E \u0435\u0441\u0442\u044C  \u043E\u0446\u0435\u043D\u043A\u0443 \u043E\u0442\u043D\u043E\u0448\u0435\u043D\u0438\u044F \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0439 \u0433\u043E\u0432\u043E\u0440\u044F\u0449\u0435\u0433\u043E \u043A \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0441\u0442\u0438, \u043C\u043E\u0436\u043D\u043E \u0432\u044B\u0440\u0430\u0437\u0438\u0442\u044C \u0438 \u0434\u0440\u0443\u0433\u0438\u043C\u0438 \u0441\u043F\u043E\u0441\u043E\u0431\u0430\u043C\u0438. \u041E\u0434\u0438\u043D \u0438\u0437 \u043D\u0438\u0445 \u2014 \u044D\u0442\u043E \u0432\u044B\u0440\u0430\u0436\u0435\u043D\u0438\u0435 \u043F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0444\u043E\u0440\u043C \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0439 \u0433\u043B\u0430\u0433\u043E\u043B\u0430. \u041A\u0430\u0442\u0435\u0433\u043E\u0440\u0438\u044F \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F \u2014 \u044D\u0442\u043E \u0442\u0430\u043A\u0430\u044F \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u0447\u0435\u0441\u043A\u0430\u044F \u0444\u043E\u0440\u043C\u0430 \u0433\u043B\u0430\u0433\u043E\u043B\u0430, \u043E\u043F\u0440\u0435\u0434\u0435\u043B\u044F\u044E\u0449\u0430\u044F \u043C\u043E\u0434\u0430\u043B\u044C\u043D\u043E\u0441\u0442\u044C \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044F. \u0412 \u0440\u0443\u0441\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u0432\u044B\u0434\u0435\u043B\u044F\u044E\u0442 \u0442\u0440\u0438 \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F: \u0438\u0437\u044A\u044F\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435, \u0441\u043E\u0441\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0435, \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435. \u0412 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u043A \u043D\u0438\u043C \u0441\u043B\u0435\u0434\u0443\u0435\u0442 \u0434\u043E\u0431\u0430\u0432\u0438\u0442\u044C \u0435\u0449\u0435 \u043A\u043E\u0441\u0432\u0435\u043D\u043D\u043E\u0435 \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0435.\n\u0418\u0437\u044A\u044F\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0435 \u0445\u043E\u0442\u044C \u043C\u044B \u0438 \u043D\u0435 \u0432\u044B\u0434\u0435\u043B\u044F\u043B\u0438 \u043E\u0442\u0434\u0435\u043B\u044C\u043D\u043E, \u043D\u043E \u043E\u043D\u043E \u043D\u0430\u043C \u0437\u043D\u0430\u043A\u043E\u043C\u043E. \u0413\u043B\u0430\u0433\u043E\u043B\u044B \u0432 \u0438\u0437\u044A\u044F\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u043C \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0438 \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u044E\u0442 \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044F, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0434\u044F\u0442, \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0434\u0438\u043B\u0438 \u0438\u043B\u0438 \u0431\u0443\u0434\u0443\u0442 \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0434\u0438\u0442\u044C \u043D\u0430 \u0441\u0430\u043C\u043E\u043C \u0434\u0435\u043B\u0435: mina olen armastanud, armastan praegu, v\xF5i alles hakkan sind armastama \u2014 \u044F \u043B\u044E\u0431\u0438\u043B, \u043B\u044E\u0431\u043B\u044E \u0441\u0435\u0439\u0447\u0430\u0441 \u0438\u043B\u0438 \u0442\u043E\u043B\u044C\u043A\u043E \u043D\u0430\u0447\u0438\u043D\u0430\u044E \u043B\u044E\u0431\u0438\u0442\u044C. \u042D\u0442\u043E \u0432\u0441\u0435 \u0438\u0437\u0432\u0435\u0441\u0442\u043D\u044B\u0435 \u043D\u0430\u043C \u0444\u043E\u0440\u043C\u044B \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0433\u043E, \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0433\u043E \u0438 \u0431\u0443\u0434\u0443\u0449\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438.\n\u0421 \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u043C \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0435\u043C \u043C\u044B \u0442\u043E\u0436\u0435 \u0441 \u0412\u0430\u043C\u0438 \u0443\u0436\u0435 \u0437\u043D\u0430\u043A\u043E\u043C\u0438\u043B\u0438\u0441\u044C. \u041C\u044B \u0432\u044B\u0434\u0435\u043B\u044F\u043B\u0438 \u0444\u043E\u0440\u043C\u044B \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u0438 \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E. \u041F\u0440\u0438 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0435 \u043C\u044B \u043A\u0430\u043A \u0431\u044B \u043E\u0431\u0440\u0430\u0449\u0430\u0435\u0442\u0441\u044F \xAB\u043A \u0442\u0435\u0431\u0435\xBB, \u0438\u043B\u0438 \xAB\u043A \u0432\u0430\u043C\xBB \u043F\u0440\u0438\u043A\u0430\u0437\u044B\u0432\u0430\u044F, \u0442\u0440\u0435\u0431\u0443\u044F, \u043F\u0440\u043E\u0441\u044F \u0447\u0442\u043E-\u043B\u0438\u0431\u043E \u0441\u0434\u0435\u043B\u0430\u0442\u044C. Tee palun, tehke \u2014 \u0441\u0434\u0435\u043B\u0430\u0439 \u043F\u043E\u0436\u0430\u043B\u0443\u0439\u0441\u0442\u0430, \u0441\u0434\u0435\u043B\u0430\u0439\u0442\u0435. \n\u0421\u043E\u0441\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0435 \u0438\u043B\u0438 \u0443\u0441\u043B\u043E\u0432\u043D\u043E\u0435.\n\u0413\u043B\u0430\u0433\u043E\u043B\u044B \u0432 \u0441\u043E\u0441\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u043C \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0438 \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u044E\u0442 \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044F, \u0436\u0435\u043B\u0430\u0435\u043C\u044B\u0435 \u0438\u043B\u0438 \u0432\u043E\u0437\u043C\u043E\u0436\u043D\u044B\u0435 \u043F\u0440\u0438 \u043E\u043F\u0440\u0435\u0434\u0435\u043B\u0451\u043D\u043D\u044B\u0445 \u0443\u0441\u043B\u043E\u0432\u0438\u044F\u0445: teeksin \u2014 \u0441\u0434\u0435\u043B\u0430\u043B \u0431\u044B, tooksin \u2014 \u043F\u0440\u0438\u043D\u0451\u0441 \u0431\u044B. \u041A\u0430\u043A \u0432\u0438\u0434\u043D\u043E \u0438\u0437 \u043F\u0440\u0438\u043C\u0435\u0440\u043E\u0432, \u043D\u0430 \u0440\u0443\u0441\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u043C\u044B \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u043C, \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u044E \u0447\u0430\u0441\u0442\u0438\u0446\u0443 \u0431(\u044B). \u0414\u0430\u0432\u0430\u0439\u0442\u0435 \u0440\u0430\u0437\u0431\u0435\u0440\u0435\u043C \u0442\u0435\u043F\u0435\u0440\u044C, \u043A\u0430\u043A \u043D\u0430\u043C \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u0442\u044C \u044D\u0442\u0438 \u0441\u0430\u043C\u044B\u0435 \u0444\u043E\u0440\u043C\u044B \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435.\n\u041F\u0440\u0438\u0437\u043D\u0430\u043A \u0443\u0441\u043B\u043E\u0432\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F \u2014 \u044D\u0442\u043E \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u044B -ksi-, -ks, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u044E\u0442\u0441\u044F \u043A \u043E\u0441\u043D\u043E\u0432\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u0432 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u043C \u0432\u0440\u0435\u043C\u0435\u043D\u0438. \u041E\u0431\u0440\u0430\u0442\u0438\u0442\u0435 \u0432\u043D\u0438\u043C\u0430\u043D\u0438\u044F \u043D\u0430 \u043B\u0438\u0447\u043D\u044B\u0435 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u044F \u043F\u043E\u0441\u043B\u0435 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u043E\u0432, \u0443 \u0442\u0440\u0435\u0442\u044C\u0435\u0433\u043E \u043B\u0438\u0446\u0430 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043E\u043D\u043E \u0441\u043E\u0432\u0441\u0435\u043C \u043E\u0442\u0441\u0443\u0442\u0441\u0442\u0432\u0443\u0435\u0442, \u0430 \u0443 \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u044D\u0442\u043E\u0433\u043E \u0436\u0435 \u043B\u0438\u0446\u0430 \u0432\u043C\u0435\u0441\u0442\u043E \u043F\u0440\u0438\u0432\u044B\u0447\u043D\u043E\u0433\u043E -vad, \u043C\u044B \u0432\u0438\u0434\u0438\u043C \u043F\u0440\u043E\u0441\u0442\u043E -d. \nTahtma, tahta, tahab \u2014 \u0445\u043E\u0442\u0435\u0442\u044C:\n\n<table>\n    <tr>\n        <td>\u0423\u0442\u0432\u0435\u0440\u0434\u0438\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430\n            Jaatav vorm\n        </td>\n        <td>\u041E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430\n            Eitav vorm\n        </td>\n    </tr>\n    <tr>\n        <td>ma tahaksin armastada</td>\n        <td>ma ei tahaks armastada\n    <tr>\n        <td>sa tahaksid armastada</td>\n        <td>sa ei tahaks armastada\n    <tr>\n        <td>ta tahaks armastada</td>\n        <td>ta ei tahaks armastada\n    <tr>\n        <td>me tahaksime armastada</td>\n        <td>me ei tahaks armastada\n    <tr>\n        <td>te tahaksite armastada</td>\n        <td>te ei tahaks armastada\n    <tr>\n        <td>nad tahaksid armastada</td>\n        <td>nad ei tahaks armastada\n</table>\n\n\u0412 \u043F\u0435\u0440\u0432\u043E\u0439 \u043A\u043E\u043B\u043E\u043D\u043A\u0435 \u0442\u0430\u0431\u043B\u0438\u0446\u044B \u0432\u044B\u0448\u0435 \u043C\u043E\u0436\u043D\u043E \u0443\u0432\u0438\u0434\u0435\u0442\u044C \u0442\u0430\u043A \u043D\u0430\u0437\u044B\u0432\u0430\u0435\u043C\u044B\u0435 \u043F\u043E\u043B\u043D\u044B\u0435 \u0443\u0442\u0432\u0435\u0440\u0434\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435 \u0444\u043E\u0440\u043C\u044B \u0443\u0441\u043B\u043E\u0432\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F. \u0412\u0441\u0435 \u0447\u0430\u0449\u0435 \u0432 \u0441\u043E\u0432\u0440\u0435\u043C\u0435\u043D\u043D\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u043C\u043E\u0436\u043D\u043E \u0443\u0441\u043B\u044B\u0448\u0430\u0442\u044C \u0438 \u043A\u043E\u0440\u043E\u0442\u043A\u0438\u0435 \u0444\u043E\u0440\u043C\u044B, \u0433\u0434\u0435 \u0434\u043B\u044F \u0432\u0441\u0435\u0445 \u043B\u0438\u0446 \u0438 \u0447\u0438\u0441\u0435\u043B \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u043E\u0434\u043D\u0430 \u0438 \u0442\u0430 \u0436\u0435 \u0444\u043E\u0440\u043C\u0430 \u0441 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u043E\u043C -ks: ma tahaks armastada, sa tahaks armastada\u2026\n\u042D\u0442\u0438 \u0444\u043E\u0440\u043C\u044B \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u0443\u0441\u043B\u043E\u0432\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u044E\u0442\u0441\u044F, \u043A\u043E\u0433\u0434\u0430 \u0438\u043C\u0435\u0435\u0442\u0441\u044F \u0432 \u0432\u0438\u0434\u0443 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0435, \u0431\u0443\u0434\u0443\u0449\u0435\u0435 \u0438\u043B\u0438 \u043D\u0435 \u043A\u043E\u043D\u043A\u0440\u0435\u0442\u0438\u0437\u0438\u0440\u043E\u0432\u0430\u043D\u043D\u043E\u0435 \u0432\u0440\u0435\u043C\u044F, \u0445\u043E\u0442\u044F \u043D\u0430 \u0440\u0443\u0441\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u0438 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u043C \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0438\u043C \u0432\u0440\u0435\u043C\u0435\u043D\u0435\u043C. \nMa l\xE4heksin n\xFC\xFCd magama, aga ei taha. \u042F \u0431\u044B \u043F\u043E\u0448\u0435\u043B \u0441\u0435\u0439\u0447\u0430\u0441 \u0441\u043F\u0430\u0442\u044C, \u043D\u043E \u043D\u0435 \u0445\u043E\u0447\u0443.\n\u0414\u043B\u044F \u0432\u044B\u0440\u0430\u0436\u0435\u043D\u0438\u044F \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044F, \u043A\u043E\u0442\u043E\u0440\u043E\u0435 \u043C\u043E\u0433\u043B\u043E \u0431\u044B \u0441\u043B\u0443\u0447\u0438\u0442\u044C\u0441\u044F \u0432 \u043F\u0440\u043E\u0448\u043B\u043E\u043C, \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u0444\u043E\u0440\u043C\u0430 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u0443\u0441\u043B\u043E\u0432\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F. \u042D\u0442\u0430 \u0444\u043E\u0440\u043C\u0430 \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442\u0441\u044F \u043F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0432\u0441\u043F\u043E\u043C\u043E\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430 olema \u0432 \u0444\u043E\u0440\u043C\u0435 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u0441\u043E\u0441\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F \u0438 \u043F\u0440\u0438\u0447\u0430\u0441\u0442\u0438 -nud. \n\u0422\u043E\u0447\u043D\u043E \u0442\u0430\u043A\u0436\u0435 \u043A\u0430\u043A \u0432 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u043C \u0432\u0440\u0435\u043C\u0435\u043D\u0438, \u0437\u0434\u0435\u0441\u044C \u0432\u043E \u0432\u0441\u0435\u0445 \u043B\u0438\u0446\u0430\u0445 \u043C\u043E\u0436\u043D\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C \u0434\u043B\u044F \u0433\u043B\u0430\u0433\u043E\u043B\u0430 olema \u043A\u043E\u0440\u043E\u0442\u043A\u0443\u044E \u0444\u043E\u0440\u043C\u0443 \u0442\u043E\u043B\u044C\u043A\u043E \u0441 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u043E\u043C -ks \u0431\u0435\u0437 \u043B\u0438\u0447\u043D\u044B\u0445 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0439.\n\n<table>\n    <tr>\n        <td>\u0423\u0442\u0432\u0435\u0440\u0434\u0438\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430\n            Jaatav vorm\n        </td>\n        <td>\u041E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430\n            Eitav vorm\n        </td>\n    </tr>\n    <tr>\n        <td>ma oleks(in) armastanud</td>\n        <td>ma ei oleks (poleks) armastanud\n    <tr>\n        <td>sa oleks(id) armastanud</td>\n        <td>sa ei oleks armastanud\n    <tr>\n        <td>ta oleks armastanud</td>\n        <td>ta ei oleks armastanud\n    <tr>\n        <td>me oleks(ime) armastanud</td>\n        <td>me ei oleks armastanud\n    <tr>\n        <td>te oleks(ite) armastanud</td>\n        <td>te ei oleks armastanud\n    <tr>\n        <td>nad oleks(id) armastanud</td>\n        <td>nad ei oleks armastanud\n</table>\n\n\u041A\u043E\u0441\u0432\u0435\u043D\u043D\u043E\u0435 \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0435.\n\u041F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u043A\u043E\u0441\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F \u043C\u044B \u0432\u044B\u0440\u0430\u0436\u0430\u0435\u043C \u0438\u043D\u0444\u043E\u0440\u043C\u0430\u0446\u0438\u044E, \u0432 \u043A\u043E\u0442\u043E\u0440\u043E\u0439 \u043D\u0435 \u043C\u043E\u0436\u0435\u043C \u0431\u044B\u0442\u044C \u043F\u043E\u043B\u043D\u043E\u0441\u0442\u044C\u044E \u0443\u0432\u0435\u0440\u0435\u043D\u043D\u044B. \u041C\u043E\u0436\u0435\u0442 \u0431\u044B\u0442\u044C \u044D\u0442\u043E \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0434\u0438\u043B\u043E, \u0430 \u043C\u043E\u0436\u0435\u0442 \u0431\u044B\u0442\u044C \u0438 \u043D\u0435\u0442. \n\u041F\u0440\u0438\u0437\u043D\u0430\u043A\u043E\u043C \u044D\u0442\u043E\u0439 \u0444\u043E\u0440\u043C\u044B \u0434\u043B\u044F \u0432\u0441\u0435\u0445 \u043B\u0438\u0446 \u044F\u0432\u043B\u044F\u0435\u0442\u0441\u044F \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -vat, \u043A\u043E\u0442\u043E\u0440\u043E\u0435 \u043C\u044B \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0435\u0442 \u043A \u043E\u0441\u043D\u043E\u0432\u0435 ma-\u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u0430. \u041D\u0430 \u0440\u0443\u0441\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u044F\u0442\u0441\u044F \u0441 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043D\u0438\u0435\u043C \u0441\u043B\u043E\u0432: \u0431\u0443\u0434\u0442\u043E \u0431\u044B, \u0433\u043E\u0432\u043E\u0440\u044F\u0442, \u044F\u043A\u043E\u0431\u044B, \u0432\u0440\u043E\u0434\u0435, \u043C\u043E\u043B \u0438 \u0442.\u043F.. \u041E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u0435 \u043F\u0435\u0440\u0435\u0434\u0430\u0435\u0442\u0441\u044F \u043F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0447\u0430\u0441\u0442\u0438\u0446\u044B ei. \nN\xE4ide:\nTa abielluvat (abiellu/ma) talvel. \u041E\u043D \u0432\u0440\u043E\u0434\u0435 \u043A\u0430\u043A \u0436\u0435\u043D\u0438\u0442\u0441\u044F \u0437\u0438\u043C\u043E\u0439.\nSa tantsivat (tantsi/ma) h\xE4sti. \u0413\u043E\u0432\u043E\u0440\u044F\u0442, \u0442\u044B \u0445\u043E\u0440\u043E\u0448\u043E \u0442\u0430\u043D\u0446\u0443\u0435\u0448\u044C. \nTe s\xF5itvat (s\xF5it/ma) puhkusele Kreekasse. \u0413\u043E\u0432\u043E\u0440\u044F\u0442, \u0432\u044B \u0431\u0443\u0434\u0442\u043E \u0431\u044B \u0435\u0434\u0435\u0442\u0435 \u0432 \u0413\u0440\u0435\u0446\u0438\u044E \u0432 \u043E\u0442\u043F\u0443\u0441\u043A. \nNad ei t\xF6\xF6tavat (t\xF6\xF6ta/ma) tehases. \u041E\u043D\u0438 \u044F\u043A\u043E\u0431\u044B \u043D\u0435 \u0440\u0430\u0431\u043E\u0442\u0430\u044E\u0442 \u043D\u0430 \u0437\u0430\u0432\u043E\u0434\u0435.\n\n\u0424\u043E\u0440\u043C\u044B \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u043E\u0431\u0440\u0430\u0437\u0443\u044E\u0442\u0441\u044F \u0441\u043E\u0447\u0435\u0442\u0430\u043D\u0438\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 olema \u0432 \u043A\u043E\u0441\u0432\u0435\u043D\u043D\u043E\u043C \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0438 \u0438 \u043F\u0440\u0438\u0447\u0430\u0441\u0442\u0438\u044F \u2013nud, \u043A\u043E\u0442\u043E\u0440\u043E\u0435, \u043A\u0430\u043A \u043C\u044B \u043F\u043E\u043C\u043D\u0438\u043C, \u0432 \u0441\u0432\u043E\u044E \u043E\u0447\u0435\u0440\u0435\u0434\u044C \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442\u0441\u044F \u043E\u0442 da-\u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u0430.\nN\xE4ide: \nTa olevat abiellunud (abiellu/da) talvel. \u041E\u043D \u0432\u0440\u043E\u0434\u0435 \u043A\u0430\u043A \u0436\u0435\u043D\u0438\u043B\u0441\u044F \u0437\u0438\u043C\u043E\u0439.\nSa olevat varem tantsinud (tantsi/da) h\xE4sti. \u0413\u043E\u0432\u043E\u0440\u044F\u0442, \u0442\u044B \u0445\u043E\u0440\u043E\u0448\u043E \u0440\u0430\u043D\u044C\u0448\u0435 \u0442\u0430\u043D\u0446\u0435\u0432\u0430\u043B. \nTe olevat s\xF5itnud (s\xF5it/a) puhkusele Kreekasse. \u0413\u043E\u0432\u043E\u0440\u044F\u0442, \u0432\u044B \u0431\u0443\u0434\u0442\u043E \u0431\u044B \u0435\u0437\u0434\u0438\u043B\u0438 \u0432 \u0413\u0440\u0435\u0446\u0438\u044E \u0432 \u043E\u0442\u043F\u0443\u0441\u043A. \nNad ei olevat kunagi t\xF6\xF6tanud (t\xF6\xF6ta/da) tehases. \u041E\u043D\u0438 \u044F\u043A\u043E\u0431\u044B \u043D\u0438\u043A\u043E\u0433\u0434\u0430 \u043D\u0435 \u0440\u0430\u0431\u043E\u0442\u0430\u043B\u0438 \u043D\u0430 \u0437\u0430\u0432\u043E\u0434\u0435.\n\u0427\u0442\u043E \u043A\u0430\u0441\u0430\u0435\u0442\u0441\u044F \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u0447\u0435\u0441\u043A\u0438\u0445 \u0444\u043E\u0440\u043C \u0433\u043B\u0430\u0433\u043E\u043B\u0430, \u0442\u043E \u043D\u0430 \u044D\u0442\u043E\u043C \u043C\u044B \u0441 \u0412\u0430\u043C\u0438 \u043E\u0441\u0442\u0430\u043D\u043E\u0432\u0438\u043C\u0441\u044F. \u0415\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u044B\u0439 \u043D\u044E\u0430\u043D\u0441, \u043A\u043E\u0442\u043E\u0440\u044B\u0439 \u044F \u0431\u044B \u0445\u043E\u0442\u0435\u043B\u0430 \u0412\u0430\u043C \u043E\u0431\u044A\u044F\u0441\u043D\u0438\u0442\u044C, \u044D\u0442\u043E \u043D\u0435\u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u044B, \u0441 \u043F\u043E\u043C\u043E\u0449\u044C\u044E \u043A\u043E\u0442\u043E\u0440\u044B\u0445 \u043C\u043E\u0436\u043D\u043E \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u044B\u0432\u0430\u0442\u044C \u0433\u043B\u0430\u0433\u043E\u043B\u044B. \u041A\u043E\u043D\u0435\u0447\u043D\u043E, \u0433\u043B\u0430\u0433\u043E\u043B\u044B \u043C\u043E\u0436\u043D\u043E \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u044B\u0432\u0430\u0442\u044C \u043E\u0442 \u0440\u0430\u0437\u043D\u044B\u0445 \u0447\u0430\u0441\u0442\u0435\u0439 \u0440\u0435\u0447\u0438: \u043E\u0442 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0445, \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445, \u043D\u0430\u0440\u0435\u0447\u0438\u0439, \u0438 \u0434\u0430\u0436\u0435 \u043E\u0442 \u0434\u0440\u0443\u0433\u0438\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432. \u0417\u0430\u0431\u0438\u0432\u0430\u0442\u044C \u0433\u043E\u043B\u043E\u0432\u0443 \u0432\u0441\u0435\u043C\u0438 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u044F \u043D\u0435\u0442 \u0441\u043C\u044B\u0441\u043B\u0430. \u041A\u043E\u0433\u0434\u0430 \u0412\u044B \u0431\u0443\u0434\u0435\u0442\u0435 \u0432 \u0442\u0435\u043A\u0441\u0442\u0435 \u0432\u0441\u0442\u0440\u0435\u0447\u0430\u0442\u044C \u043D\u043E\u0432\u044B\u0439 \u0433\u043B\u0430\u0433\u043E\u043B, \u0441\u043C\u043E\u0442\u0440\u0435\u0442\u044C \u0441\u0442\u043E\u0438\u0442 \u043D\u0435 \u0442\u043E\u043B\u044C\u043A\u043E \u043D\u0430 \u0441\u0443\u0444\u0444\u0438\u043A\u0441, \u043D\u043E \u0438 \u043D\u0430 \u0435\u0433\u043E \u043E\u0441\u043D\u043E\u0432\u0443, \u0432\u0435\u0434\u044C \u0435\u0441\u043B\u0438 \u0442\u0430\u043C \u043F\u0440\u0438\u0441\u0443\u0442\u0441\u0442\u0432\u0443\u0435\u0442 \u0437\u043D\u0430\u043A\u043E\u043C\u043E\u0435 \u0441\u043B\u043E\u0432\u043E, \u044D\u0442\u043E \u043B\u0438\u0448\u044C \u043F\u043E\u043C\u043E\u0436\u0435\u0442 \u0432 \u0435\u0433\u043E \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0435. Suurenema \u0432 \u0441\u0432\u043E\u0435\u0439 \u043E\u0441\u043D\u043E\u0432\u0435 \u0438\u043C\u0435\u0435\u0442 \u043A\u043E\u0440\u0435\u043D\u044C suur, \u043A\u043E\u0442\u043E\u0440\u044B\u0439 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u0441\u044F \u043A\u0430\u043A \u0431\u043E\u043B\u044C\u0448\u043E\u0439, \u0438 \u0443\u0436\u0435 \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u0438\u0449\u0438\u0442\u0435 \u0432 \u0431\u043B\u0438\u0437\u0438 \u044D\u0442\u043E\u0433\u043E \u0441\u043B\u043E\u0432\u0430. Suurenema \u2014 \u0443\u0432\u0435\u043B\u0438\u0447\u0438\u0442\u044C\u0441\u044F, \u0440\u0430\u0441\u0442\u0438.\n\u041D\u0430 \u0447\u0435\u043C \u0441\u0442\u043E\u0438\u0442 \u043E\u0441\u0442\u0430\u043D\u043E\u0432\u0438\u0442\u044C\u0441\u044F \u043F\u043E\u0434\u0440\u043E\u0431\u043D\u0435\u0435, \u0442\u0430\u043A \u044D\u0442\u043E \u043D\u0430 \u043F\u0430\u0440\u043D\u044B\u0445 \u043F\u0435\u0440\u0435\u0445\u043E\u0434\u043D\u044B\u0445 \u0438 \u043D\u0435\u043F\u0435\u0440\u0435\u0445\u043E\u0434\u043D\u044B\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u0445. \u0412 \u0440\u0443\u0441\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u043D\u0435\u043F\u0435\u0440\u0435\u0445\u043E\u0434\u043D\u044B\u043C \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C \u043E\u0431\u044B\u0447\u043D\u043E \u0441\u043E\u043E\u0442\u0432\u0435\u0442\u0441\u0442\u0432\u0443\u044E\u0442 \u0444\u043E\u0440\u043C\u044B, \u0437\u0430\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u044E\u0449\u0438\u0435 \u043D\u0430 -\u0441\u044F: \u043D\u0430\u0447\u0438\u043D\u0430\u0442\u044C \u2014 \u043D\u0430\u0447\u0438\u043D\u0430\u0442\u044C\u0441\u044F, \u043E\u0434\u0435\u0432\u0430\u0442\u044C-\u043E\u0434\u0435\u0432\u0430\u0442\u044C\u0441\u044F \u0438 \u0442.\u043F.. \u0412\u0441\u0442\u0440\u0435\u0447\u0430\u0435\u0442\u0441\u044F, \u0447\u0442\u043E \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u044D\u0442\u0438 \u043F\u0430\u0440\u044B \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u044F\u0442\u0441\u044F \u0441\u043E\u0432\u0435\u0440\u0448\u0435\u043D\u043D\u043E \u0440\u0430\u0437\u043D\u044B\u043C\u0438 \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C\u0438, \u0438 \u0434\u0430\u0436\u0435 \u043D\u0435 \u043E\u0434\u043D\u043E\u043A\u043E\u0440\u0435\u043D\u043D\u044B\u043C\u0438. \u041D\u043E \u0432 \u0431\u043E\u043B\u044C\u0448\u0438\u043D\u0441\u0442\u0432\u0435 \u0441\u043B\u0443\u0447\u0430\u0435\u0432 \u043A\u043E\u0440\u0435\u043D\u044C \u0443 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u0441\u043E\u0445\u0440\u0430\u043D\u044F\u0435\u0442\u0441\u044F, \u043C\u0435\u043D\u044F\u0435\u0442\u0441\u044F \u043B\u0438\u0448\u044C \u0441\u043B\u043E\u0432\u043E\u043E\u0431\u0440\u0430\u0437\u0443\u044E\u0449\u0438\u0435 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u044B.\n\u0421\u0443\u0444\u0444\u0438\u043A\u0441\u044B, \u043E\u0431\u0440\u0430\u0437\u0443\u044E\u0449\u0438\u0435 \u043F\u0435\u0440\u0435\u0445\u043E\u0434\u043D\u044B\u0435 \u0438 \u043D\u0435\u043F\u0435\u0440\u0435\u0445\u043E\u0434\u043D\u044B\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u044B.\n-u-\n\u0421\u0443\u0444\u0444\u0438\u043A\u0441 -u- \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u2014 \u044D\u0442\u043E \u0432\u0441\u0435\u0433\u0434\u0430 \u043F\u043E\u043A\u0430\u0437\u0430\u0442\u0435\u043B\u044C \u043D\u0435\u043F\u0435\u0440\u0435\u0445\u043E\u0434\u043D\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430. \u041F\u0440\u0438 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u0438 \u043E\u043D \u0438\u043B\u0438 \u043F\u0440\u0438\u0441\u043E\u0435\u0434\u0438\u043D\u044F\u0435\u0442\u0441\u044F \u043A \u043E\u0441\u043D\u043E\u0432\u0435 \u043F\u0435\u0440\u0435\u0445\u043E\u0434\u043D\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430, \u0435\u0441\u043B\u0438 \u043E\u043D\u0430 \u0437\u0430\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u043D\u0430 \u0441\u043E\u0433\u043B\u0430\u0441\u043D\u0443\u044E \u0431\u0443\u043A\u0432\u0443, \u0438\u043B\u0438 \u0437\u0430\u043C\u0435\u043D\u044F\u0435\u0442 \u0433\u043B\u0430\u0441\u043D\u0443\u044E \u0432 \u043A\u043E\u043D\u0446\u0435 \u043E\u0441\u043D\u043E\u0432\u044B. \nMuutma \u2014 muutUma, \u043C\u0435\u043D\u044F\u0442\u044C \u2014 \u043C\u0435\u043D\u044F\u0442\u044C\u0441\u044F.\nPeatama \u2014 peatuma, \u043E\u0441\u0442\u0430\u043D\u0430\u0432\u043B\u0438\u0432\u0430\u0442\u044C \u2014 \u043E\u0441\u0442\u0430\u043D\u0430\u0432\u043B\u0438\u0432\u0430\u0442\u044C\u0441\u044F.\nKordama \u2014 korduma, \u043F\u043E\u0432\u0442\u043E\u0440\u044F\u0442\u044C \u2014 \u043F\u043E\u0432\u0442\u043E\u0440\u044F\u0442\u044C\u0441\u044F.\n-ta- (-da-), -sta-, -nda-\n\u041F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u044D\u0442\u0438\u0445 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u043E\u0432 \u043D\u0430\u043E\u0431\u043E\u0440\u043E\u0442 \u043E\u0431\u0440\u0430\u0437\u0443\u044E\u0442\u0441\u044F \u0432\u0441\u0435\u0433\u0434\u0430 \u043F\u0435\u0440\u0435\u0445\u043E\u0434\u043D\u044B\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u044B. \u041F\u0440\u0430\u0432\u0434\u0430 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u044B\u0432\u0430\u0442\u044C\u0441\u044F \u043C\u043E\u0433\u0443\u0442 \u043E\u0442 \u043E\u0441\u043D\u043E\u0432 \u0440\u0430\u0437\u043D\u044B\u0445 \u0444\u043E\u0440\u043C \u0433\u043B\u0430\u0433\u043E\u043B\u0430, \u0438 \u0434\u0430\u0436\u0435 \u0441\u043E\u043A\u0440\u0430\u0449\u0435\u043D\u043D\u044B\u0445.\nL\xF5ppema \u2014 L\xF5peTAma, \u0437\u0430\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0442\u044C\u0441\u044F \u2014 \u0437\u0430\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0442\u044C.\nKaduma (kaob) \u2014 kaoTAma, \u0442\u0435\u0440\u044F\u0442\u044C\u0441\u044F \u2014 \u0442\u0435\u0440\u044F\u0442\u044C.\nAlgama \u2014 AlusTAma, \u043D\u0430\u0447\u0438\u043D\u0430\u0442\u044C\u0441\u044F \u2014 \u043D\u0430\u0447\u0438\u043D\u0430\u0442\u044C.\nAuruma \u2014 auruSTAma, \u0438\u0441\u043F\u0430\u0440\u044F\u0442\u044C\u0441\u044F \u2014 \u0438\u0441\u043F\u0430\u0440\u044F\u0442\u044C.\nM\xF5juma \u2014 m\xF5juTAma=m\xF5juSTAma, \u0432\u043B\u0438\u044F\u0442\u044C, \u0441\u043A\u0430\u0437\u044B\u0432\u0430\u0442\u044C\u0441\u044F \u2014  \u0432\u043B\u0438\u044F\u0442\u044C, \u0432\u043E\u0437\u0434\u0435\u0439\u0441\u0442\u0432\u043E\u0432\u0430\u0442\u044C.\nSuurenema \u2014 suureNDAma, \u0443\u0432\u0435\u043B\u0438\u0447\u0438\u0432\u0430\u0442\u044C\u0441\u044F \u2014 \u0443\u0432\u0435\u043B\u0438\u0447\u0438\u0432\u0430\u0442\u044C.\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('lesson',{number:15}),source:source});}));

