var source='\n\u041A\u0430\u043A \u0412\u044B \u043C\u043E\u0433\u043B\u0438 \u0437\u0430\u043C\u0435\u0442\u0438\u0442\u044C, \u043A\u043E\u0433\u0434\u0430 \u043C\u044B \u0431\u0440\u0430\u043B\u0438 \u043E\u0447\u0435\u0440\u0435\u0434\u043D\u0443\u044E \u0447\u0430\u0441\u0442\u044C \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0439 \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u043A\u0438, \u0441\u0432\u044F\u0437\u0430\u043D\u043D\u0443\u044E \u0441 \u0442\u043E\u0439 \u0438\u043B\u0438 \u0438\u043D\u043E\u0439 \u0447\u0430\u0441\u0442\u044C\u044E \u0440\u0435\u0447\u0438, \u044F \u043A\u0430\u0436\u0434\u044B\u0439 \u0440\u0430\u0437 \u043F\u0440\u0435\u0434\u0432\u0430\u0440\u0438\u0442\u0435\u043B\u044C\u043D\u043E \u043D\u0430\u043F\u043E\u043C\u0438\u043D\u0430\u043B\u0430 \u0412\u0430\u043C, \u0447\u0442\u043E \u043C\u044B \u0443\u0436\u0435 \u043F\u043E \u044D\u0442\u043E\u0439 \u0442\u0435\u043C\u0435 \u0437\u043D\u0430\u0435\u043C. \u041F\u0440\u0435\u0434\u043B\u0430\u0433\u0430\u044E \u0441\u0435\u0439\u0447\u0430\u0441 \u0441\u0445\u0435\u043C\u0430\u0442\u0438\u0447\u043D\u043E \u043F\u043E\u0432\u0442\u043E\u0440\u0438\u0442\u044C \u043F\u0430\u0434\u0435\u0436\u0438, \u0442\u043E \u0435\u0441\u0442\u044C \u043A\u043E\u0441\u043D\u0443\u0442\u044C\u0441\u044F \u0441\u043A\u043B\u043E\u043D\u044F\u0435\u043C\u044B\u0445 \u0447\u0430\u0441\u0442\u0435\u0439 \u0440\u0435\u0447\u0438 (\u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0445, \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445, \u0447\u0438\u0441\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0445 \u0438 \u043C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u0439). \n\u0412 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435, \u0432 \u043E\u0442\u043B\u0438\u0447\u0438\u0435 \u043E\u0442 \u0440\u0443\u0441\u0441\u043A\u043E\u0433\u043E, 14 \u043F\u0430\u0434\u0435\u0436\u0435\u0439, \u043D\u043E \u043A\u0430\u043A \u043C\u044B \u043F\u043E\u043C\u043D\u0438\u043C \u0432 \u0438\u0442\u043E\u0433\u0435 \u0443\u0447\u0438\u0442\u044C \u043D\u0430\u0438\u0437\u0443\u0441\u0442\u044C \u043D\u0430\u043C \u043F\u0440\u0438\u0445\u043E\u0434\u0438\u0442\u044C\u0441\u044F \u0442\u043E\u043B\u044C\u043A\u043E \u0444\u043E\u0440\u043C\u044B \u043F\u0435\u0440\u0432\u044B\u0445 \u0442\u0440\u0435\u0445 \u0438\u0437 \u043D\u0438\u0445. \n\u041F\u0435\u0440\u0432\u044B\u0439 \u043F\u0430\u0434\u0435\u0436 \u2014 nimetav, \u0438\u043C\u0435\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0439. \u041E\u043D \u0442\u0430\u043A \u0438 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043B\u0441\u044F \u043E\u0442 \u0441\u043B\u043E\u0432\u0430 nimi \u2014 \u0438\u043C\u044F. \u041E\u0442\u0432\u0435\u0447\u0430\u0435\u0442 \u043D\u0430 \u0432\u043E\u043F\u0440\u043E\u0441\u044B Kes? Mis? \u041A\u0442\u043E? \u0427\u0442\u043E? \u042D\u0442\u043E \u043E\u0441\u043D\u043E\u0432\u0430 \u043E\u0441\u043D\u043E\u0432 \u0434\u043B\u044F \u043B\u044E\u0431\u043E\u0433\u043E \u0441\u043B\u043E\u0432\u0430. \u0418\u043C\u0435\u043D\u043D\u043E \u044D\u0442\u0443 \u0444\u043E\u0440\u043C\u0443 \u043C\u044B \u0432\u0441\u0435\u0433\u0434\u0430 \u043D\u0430\u0439\u0434\u0435\u043C \u0432 \u0441\u043B\u043E\u0432\u0430\u0440\u0435. See on \xF5petaja. Need on \xF5pikud. \n\u0412\u0442\u043E\u0440\u043E\u0439 \u043F\u0430\u0434\u0435\u0436 \u2014 omastav. \u041D\u0430\u0437\u0432\u0430\u043D\u0438\u0435 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043B\u043E\u0441\u044C \u043E\u0442 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 omama, \u0447\u0442\u043E \u0437\u043D\u0430\u0447\u0438\u0442 \xAB\u0438\u043C\u0435\u0442\u044C\xBB. \u042D\u0442\u043E\u0442 \u043F\u0430\u0434\u0435\u0436 \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0435\u0442 \u043F\u0440\u0438\u043D\u0430\u0434\u043B\u0435\u0436\u043D\u043E\u0441\u0442\u044C. \u041E\u0442\u0432\u0435\u0447\u0430\u0435\u0442 \u043D\u0430 \u0432\u043E\u043F\u0440\u043E\u0441\u044B Kelle? Mille? \u041A\u043E\u0433\u043E? \u0427\u0435\u0439? \u0427\u044C\u044F? \u0427\u044C\u0451? See on eesti keele \xF5petaja. Need on targa t\xFCdruku \xF5pikud. \u041E\u0442 \u044D\u0442\u043E\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430 \u043E\u0431\u0440\u0430\u0437\u0443\u044E\u0442\u0441\u044F \u0432\u0441\u0435 \u043E\u0441\u0442\u0430\u043B\u044C\u043D\u044B\u0435 \u043F\u0430\u0434\u0435\u0436\u0438, \u043D\u0430\u0447\u0438\u043D\u0430\u044F \u0441 \u0447\u0435\u0442\u0432\u0435\u0440\u0442\u043E\u0433\u043E. \u0418\u043C\u0435\u043D\u043D\u043E \u043F\u043E\u044D\u0442\u043E\u043C\u0443 \u0432\u0441\u0435 \u0441\u043B\u043E\u0432\u0430 \u0432 \u0444\u043E\u0440\u043C\u0435 omastav \u0431\u0443\u0434\u0443\u0442 \u043E\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0442\u044C\u0441\u044F \u043D\u0430 \u0433\u043B\u0430\u0441\u043D\u0443\u044E \u0431\u0443\u043A\u0432\u0443, \u0432\u0435\u0434\u044C \u0434\u043E\u0431\u0430\u0432\u0438\u0442\u044C \u0441\u043E\u0433\u043B\u0430\u0441\u043D\u0443\u044E \u0431\u0443\u043A\u0432\u0443 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0439 \u043A \u043E\u0441\u043D\u043E\u0432\u0435 \u0441\u043B\u043E\u0432\u0430 \u043D\u0430 \u0441\u043E\u0433\u043B\u0430\u0441\u043D\u0443\u044E \u043E\u0447\u0435\u043D\u044C \u043D\u0435\u0443\u0434\u043E\u0431\u043D\u043E \u0434\u043B\u044F \u043F\u0440\u043E\u0438\u0437\u043D\u043E\u0448\u0435\u043D\u0438\u044F.\n\u0422\u0440\u0435\u0442\u0438\u0439 \u043F\u0430\u0434\u0435\u0436 \u2014 osastav. \u041E\u0441\u043D\u043E\u0432\u043D\u043E\u0439 \u0444\u0443\u043D\u043A\u0446\u0438\u0435\u0439 \u0434\u0430\u043D\u043D\u043E\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430 \u043E\u0441\u0442\u0430\u0435\u0442\u0441\u044F \u0432\u044B\u0440\u0430\u0436\u0435\u043D\u0438\u0435 \u0434\u043E\u043F\u043E\u043B\u043D\u0435\u043D\u0438\u044F \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0438, \u0442\u043E \u0435\u0441\u0442\u044C \u043A\u043E\u0433\u0434\u0430 \u043C\u044B \u043E\u0442 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u0437\u0430\u0434\u0430\u0435\u043C \u0434\u043E\u043F\u043E\u043B\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0439 \u0432\u043E\u043F\u0440\u043E\u0441, \u043F\u0435\u0440\u0435\u043D\u043E\u0441\u044F \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435 \u043D\u0430 \u043F\u0440\u0435\u0434\u043C\u0435\u0442: \u044F \u043B\u044E\u0431\u043B\u044E -> \u043A\u043E\u0433\u043E? \u0442\u0435\u0431\u044F (armastan sind), \u044F \u0447\u0438\u0442\u0430\u044E ->\u0447\u0442\u043E? \u043A\u043D\u0438\u0433\u0443 (loen raamatut), \u044F \u043F\u0438\u0448\u0443 -> \u0447\u0442\u043E? \u043F\u0438\u0441\u044C\u043C\u043E (kirjutan kirja). \u041E\u0442\u0432\u0435\u0447\u0430\u0435\u0442 \u043D\u0430 \u0432\u043E\u043F\u0440\u043E\u0441\u044B Keda? Mida? \u041A\u043E\u0433\u043E? \u0427\u0442\u043E? \u0422\u0430\u043A\u0436\u0435 \u043F\u0430\u0434\u0435\u0436 osastav \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u0441 \u0447\u0438\u0441\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u043C\u0438 \u0431\u043E\u043B\u044C\u0448\u0435 \u0435\u0434\u0438\u043D\u0438\u0446\u044B (\xFCks kass \u2014 \u0441\u043B\u043E\u0432\u043E \u043A\u043E\u0448\u043A\u0430 \u0441\u0442\u043E\u0438\u0442 \u0432 \u043F\u0435\u0440\u0432\u043E\u043C \u043F\u0430\u0434\u0435\u0436\u0435, \u043D\u043E \u0434\u0432\u0435, \u0442\u0440\u0438 \u0438 \u0442\u0430\u043A \u0434\u0430\u043B\u0435\u0435 \u043A\u043E\u0448\u0435\u043A \u0431\u0443\u0434\u0435\u0442 \u0443\u0436\u0435 \u0432 \u0442\u0440\u0435\u0442\u044C\u0435\u043C \u043F\u0430\u0434\u0435\u0436\u0435 \u2014 kaks, kolm kassi). \u0422\u0440\u0435\u0442\u0438\u0439 \u043E\u0441\u043D\u043E\u0432\u043D\u043E\u0439 \u0441\u043B\u0443\u0447\u0430\u0439 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u044D\u0442\u043E\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430, \u044D\u0442\u043E \u0432 \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F\u0445 \u2014 mul ei ole \xF5petajat, mina ei loe \xF5pikut.\n4-9. \u0421\u0440\u0430\u0437\u0443 6 \u0441\u043B\u0435\u0434\u0443\u044E\u0449\u0438\u0445 \u043F\u0430\u0434\u0435\u0436\u0435\u0439 \u043C\u044B \u0432\u044B\u0434\u0435\u043B\u0438\u043B\u0438 \u0432 \u043E\u0434\u043D\u0443 \u0433\u0440\u0443\u043F\u043F\u0443 \u2014 \u043F\u0430\u0434\u0435\u0436\u0438 \u043C\u0435\u0441\u0442\u0430 (sisse\xFCtlev, sees\xFCtlev, seest\xFCtlev, alale\xFCtlev, alal\xFCtlev, alalt\xFCtlev). \u041A\u043E\u043D\u0435\u0447\u043D\u043E \u0443 \u044D\u0442\u0438\u0445 \u043F\u0430\u0434\u0435\u0436\u0435\u0439 \u043C\u043D\u043E\u0433\u043E \u0444\u0443\u043D\u043A\u0446\u0438\u0439, \u043D\u043E \u0433\u043B\u0430\u0432\u043D\u043E\u0439 \u043E\u0441\u0442\u0430\u0435\u0442\u0441\u044F \u0432\u0441\u0435-\u0442\u0430\u043A\u0438 \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0435 \u043C\u0435\u0441\u0442\u043E\u043F\u043E\u043B\u043E\u0436\u0435\u043D\u0438\u044F. \u0418\u043C\u0435\u043D\u043D\u043E \u043F\u043E\u044D\u0442\u043E\u043C\u0443 \u043E\u0441\u043D\u043E\u0432\u043D\u044B\u043C\u0438 \u0432\u043E\u043F\u0440\u043E\u0441\u0430\u043C\u0438 \u0434\u043B\u044F \u044D\u0442\u0438\u0445 \u0448\u0435\u0441\u0442\u0438 \u043F\u0430\u0434\u0435\u0436\u0435\u0439 \u0431\u0443\u0434\u0443\u0442 Kuhu? Kus? Kust? \u041A\u0443\u0434\u0430? \u0413\u0434\u0435? \u041E\u0442\u043A\u0443\u0434\u0430? \u041D\u0430 \u043A\u0430\u0436\u0434\u044B\u0439 \u0438\u0437 \u044D\u0442\u0438\u0445 \u0432\u043E\u043F\u0440\u043E\u0441\u043E\u0432 \u0431\u0443\u0434\u0435\u0442 \u043F\u043E \u043F\u0430\u0440\u0435 \u043F\u0430\u0434\u0435\u0436\u0435\u0439: \u043E\u0434\u0438\u043D \u0432\u043D\u0443\u0442\u0440\u0435\u043D\u043D\u0438\u0439 \u043F\u0430\u0434\u0435\u0436, \u043E\u0434\u0438\u043D \u0432\u043D\u0435\u0448\u043D\u0438\u0439. \u041E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u044F \u0432\u043D\u0443\u0442\u0440\u0435\u043D\u043D\u0438\u0445 \u043F\u0430\u0434\u0435\u0436\u0435\u0439: -sse(\xD8), -s, -st. \u041E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u044F \u0432\u043D\u0435\u0448\u043D\u0438\u0445 \u043F\u0430\u0434\u0435\u0436\u0435\u0439: -le, -l, -lt. \n\u0414\u0435\u0441\u044F\u0442\u044B\u0439 \u043F\u0430\u0434\u0435\u0436 \u2014 saav. \u0427\u0430\u0441\u0442\u043E \u044D\u0442\u043E\u043C\u0443 \u043F\u0430\u0434\u0435\u0436\u0443 \u0432 \u0440\u0443\u0441\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u0441\u043E\u043E\u0442\u0432\u0435\u0442\u0441\u0442\u0432\u0443\u0435\u0442 \u0442\u0432\u043E\u0440\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0439 \u043F\u0430\u0434\u0435\u0436. \u041E\u0441\u043D\u043E\u0432\u043D\u044B\u0435 \u0432\u043E\u043F\u0440\u043E\u0441\u044B: Kelleks? Milleks? \u041A\u0435\u043C? \u0427\u0435\u043C? \u041A\u0430\u043A\u0438\u043C? \u041F\u0440\u0438\u0437\u043D\u0430\u043A\u043E\u043C \u0434\u0430\u043D\u043D\u043E\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430 \u0431\u0443\u0434\u0435\u0442 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u044F -ks. \u041E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0435\u0442 \u0441\u043E\u0441\u0442\u043E\u044F\u043D\u0438\u0435 \u0438\u043B\u0438 \u043A\u0430\u0447\u0435\u0441\u0442\u0432\u043E, \u0432 \u043A\u043E\u0442\u043E\u0440\u043E\u0435 \u0447\u0442\u043E-\u0442\u043E \u0438\u043B\u0438 \u043A\u0442\u043E-\u0442\u043E \u0435\u0449\u0435 \u0442\u043E\u043B\u044C\u043A\u043E \u043F\u0435\u0440\u0435\u0445\u043E\u0434\u0438\u0442, \u043F\u0440\u0435\u0432\u0440\u0430\u0449\u0430\u0435\u0442\u0441\u044F \u0438\u043B\u0438 \u0445\u043E\u0447\u0435\u0442 \u0441\u0442\u0430\u0442\u044C. Mina tahan \xF5petajaks saada. \u0418\u0437 \u0432\u0442\u043E\u0440\u043E\u0441\u0442\u0435\u043F\u0435\u043D\u043D\u044B\u0445 \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u0439 \u0441\u0442\u043E\u0438\u0442 \u0442\u0430\u043A\u0436\u0435 \u0432\u044B\u0434\u0435\u043B\u0438\u0442\u044C \u0443\u043F\u043E\u0442\u0440\u0435\u0431\u043B\u0435\u043D\u0438\u0435 \u043F\u0430\u0434\u0435\u0436\u0430 saav, \u043A\u043E\u0433\u0434\u0430 \u043C\u044B \u0445\u043E\u0442\u0438\u043C \u0432\u044B\u0440\u0430\u0437\u0438\u0442\u044C \u043D\u0430 \u043A\u0430\u043A\u043E\u0439 \u0441\u0440\u043E\u043A? \u0438\u043B\u0438 \u043A \u043A\u0430\u043A\u043E\u043C\u0443 \u0432\u0440\u0435\u043C\u0435\u043D\u0438? \u0447\u0442\u043E-\u0442\u043E \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0434\u0438\u0442. Mina s\xF5idan puhkusele n\xE4dalaks. Homseks on k\xF5ik valmis. \n\u041E\u0434\u0438\u043D\u043D\u0430\u0434\u0446\u0430\u0442\u044B\u0439 \u043F\u0430\u0434\u0435\u0436 \u2014 rajav. Kelleni? Milleni? \u0414\u043E \u043A\u043E\u0433\u043E? \u0414\u043E \u0447\u0435\u0433\u043E? \u0418\u0437 \u044D\u0442\u043E\u0433\u043E \u0441\u043B\u0435\u0434\u0443\u0435\u0442, \u0447\u0442\u043E \u043F\u0430\u0434\u0435\u0436 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F \u0434\u043B\u044F \u0432\u044B\u0440\u0430\u0436\u0435\u043D\u0438\u044F \u043F\u0440\u0435\u0434\u0435\u043B\u0430. Mina t\xF6\xF6tan kaheksast viieni. J\xE4rgmise majani on veel 10 meetrit. \n\u0421\u043B\u0435\u0434\u0443\u044E\u0449\u0438\u043C \u0431\u0443\u0434\u0435\u0442 \u043F\u0430\u0434\u0435\u0436 olev. \u041E\u0441\u043D\u043E\u0432\u043D\u044B\u0435 \u0432\u043E\u043F\u0440\u043E\u0441\u044B: Kellena? Millena? \u041A\u0435\u043C? \u0427\u0435\u043C? \u0422\u043E\u0436\u0435 \u0432 \u043A\u0430\u043A\u043E\u0439-\u0442\u043E \u043C\u0435\u0440\u0435 \u0442\u0432\u043E\u0440\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0439 \u043F\u0430\u0434\u0435\u0436 \u0440\u0443\u0441\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430, \u043D\u043E \u043E\u0442\u043B\u0438\u0447\u0438\u0435 \u043E\u0442 \u043F\u0430\u0434\u0435\u0436\u0430 saav \u0432 \u0442\u043E\u043C, \u0447\u0442\u043E \u043F\u0440\u0435\u0434\u043C\u0435\u0442 \u0438\u043B\u0438 \u043B\u0438\u0446\u043E \u0443\u0436\u0435 \u043F\u0440\u0435\u0431\u044B\u0432\u0430\u0435\u0442 \u0432 \u0442\u043E\u043C \u0438\u043B\u0438 \u0438\u043D\u043E\u043C \u043A\u0430\u0447\u0435\u0441\u0442\u0432\u0435, \u0441\u043E\u0441\u0442\u043E\u044F\u043D\u0438\u0438, \u0430 \u043D\u0435 \u0442\u043E\u043B\u044C\u043A\u043E \u0445\u043E\u0447\u0435\u0442 \u0438\u043C \u0441\u0442\u0430\u0442\u044C. \u041A \u0444\u043E\u0440\u043C\u0435 \u0441\u043B\u043E\u0432\u0430 \u0432\u043E \u0432\u0442\u043E\u0440\u043E\u043C \u043F\u0430\u0434\u0435\u0436\u0435 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0435\u0442 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -na. Mina t\xF6\xF6tan \xF5petajana. Naine tundub v\xE4sinuna.  \n\u0422\u0440\u0438\u043D\u0430\u0434\u0446\u0430\u0442\u044B\u0439 \u043F\u0430\u0434\u0435\u0436 \u2014 ilma\xFCtlev. \u042D\u0442\u043E\u0442 \u043F\u0430\u0434\u0435\u0436 \u0432\u044B\u0440\u0430\u0436\u0430\u0435\u0442 \u043E\u0442\u0441\u0443\u0442\u0441\u0442\u0432\u0438\u0435 \u043A\u043E\u0433\u043E-\u043D\u0438\u0431\u0443\u0434\u044C \u0438\u043B\u0438 \u0447\u0435\u0433\u043E-\u043D\u0438\u0431\u0443\u0434\u044C, \u0438 \u043E\u0442\u0432\u0435\u0447\u0430\u0435\u0442 \u043D\u0430 \u0432\u043E\u043F\u0440\u043E\u0441\u044B Kelleta? Milleta? \u0411\u0435\u0437 \u043A\u043E\u0433\u043E? \u0411\u0435\u0437 \u0447\u0435\u0433\u043E? \u041F\u0440\u0438\u0437\u043D\u0430\u043A \u043F\u0430\u0434\u0435\u0436\u0430 \u2014 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -ta. Ma ei saa ilma \xF5petajata kirjutada. \n\u041F\u043E\u0441\u043B\u0435\u0434\u043D\u0438\u0439 \u043F\u0430\u0434\u0435\u0436 \u043D\u0430\u0437\u044B\u0432\u0430\u0435\u0442\u0441\u044F kaasa\xFCtlev. \u041E\u0441\u043D\u043E\u0432\u043D\u044B\u0435 \u0432\u043E\u043F\u0440\u043E\u0441\u044B: Kellega? Millega? \u0421 \u043A\u0435\u043C? \u0421 \u0447\u0435\u043C? \u041F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0447\u0435\u0433\u043E? \u041D\u0430 \u0447\u0451\u043C? \u041E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435\u043C \u0434\u043B\u044F \u0434\u0430\u043D\u043D\u043E\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430 \u0431\u0443\u0434\u0435\u0442 -ga. Mina loen koos \xF5petajaga. Mina s\xF5idan t\xF6\xF6le tavaliselt autoga. \n\n\u0414\u0430\u043B\u0435\u0435 \u043D\u0430\u043C \u0441\u0442\u043E\u0438\u0442 \u043F\u043E\u0433\u043E\u0432\u043E\u0440\u0438\u0442\u044C \u043E \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u043C \u0447\u0438\u0441\u043B\u0435 \u0434\u043B\u044F \u044D\u0442\u0438\u0445 \u043F\u0430\u0434\u0435\u0436\u0435\u0439. \u041F\u043E\u043A\u0430 \u043C\u044B \u043A\u0430\u0441\u0430\u043B\u0438\u0441\u044C \u0442\u043E\u043B\u044C\u043A\u043E \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u0434\u043B\u044F \u043F\u0430\u0434\u0435\u0436\u0430 nimetav. \u042D\u0442\u043E \u0441\u0447\u0438\u0442\u0430\u0435\u0442\u0441\u044F \u043E\u0434\u043D\u0438\u043C \u0438\u0437 \u0441\u0430\u043C\u044B\u0445 \u043F\u0440\u043E\u0441\u0442\u044B\u0445 \u043F\u0440\u0430\u0432\u0438\u043B \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u043A\u0438 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430, \u0442\u0430\u043A \u043A\u0430\u043A \u0432 \u043D\u0435\u043C \u043D\u0435\u0442 \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u0439. \u0427\u0442\u043E\u0431\u044B \u0441\u0444\u043E\u0440\u043C\u0438\u0440\u043E\u0432\u0430\u0442\u044C \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E \u043F\u0435\u0440\u0432\u043E\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430, \u043D\u0430\u043C \u043D\u0443\u0436\u043D\u043E \u0432\u0437\u044F\u0442\u044C \u0432\u0442\u043E\u0440\u043E\u0439 \u043F\u0430\u0434\u0435\u0436 \u0441\u043B\u043E\u0432\u0430 \u0438 \u0434\u043E\u0431\u0430\u0432\u0438\u0442\u044C \u043A \u043D\u0435\u043C\u0443 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -d. \u0420\u0430\u0441\u0441\u043C\u043E\u0442\u0440\u0438\u043C \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435 \u044D\u0442\u043E\u0433\u043E \u043F\u0440\u0430\u0432\u0438\u043B\u0430 \u043D\u0430 \u043F\u0440\u0438\u043C\u0435\u0440\u0435 \u0441\u043B\u043E\u0432\u0430 \u043A\u043D\u0438\u0433\u0430:\n\n<table>\n    <tr>\n        <td>raamat</td>\n        <td>raamatud</td>\n    </tr>\n    <tr>\n        <td>raamatu</td>\n    </tr>\n    <tr>\n        <td>raamatut</td>\n    </tr>\n</table>\n\n\u0422\u0435\u043F\u0435\u0440\u044C \u0441\u043F\u0443\u0441\u0442\u0438\u043C\u0441\u044F \u043D\u0438\u0436\u0435 \u0432 \u0442\u0430\u0431\u043B\u0438\u0446\u0435 \u043F\u0430\u0434\u0435\u0436\u0435\u0439. \u041C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E \u0432\u0442\u043E\u0440\u043E\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430. \u041E\u0431 \u044D\u0442\u043E\u043C \u043F\u0440\u0430\u0432\u0438\u043B\u0435 \u0441\u0442\u043E\u0438\u0442 \u043F\u043E\u0433\u043E\u0432\u043E\u0440\u0438\u0442\u044C \u043E\u0442\u0434\u0435\u043B\u044C\u043D\u043E, \u0432\u0435\u0434\u044C \u0437\u043D\u0430\u0447\u0438\u043C\u043E\u0441\u0442\u044C \u043F\u0430\u0434\u0435\u0436\u0430 omastav \u043E\u0447\u0435\u043D\u044C \u0432\u044B\u0441\u043E\u043A\u0430 \u2014 \u043E\u0442 \u043D\u0435\u0433\u043E \u043E\u0431\u0440\u0430\u0437\u0443\u044E\u0442\u0441\u044F \u0444\u043E\u0440\u043C\u044B \u0432\u0441\u0435\u0445 \u043E\u0441\u0442\u0430\u043B\u044C\u043D\u044B\u0445 \u043F\u0430\u0434\u0435\u0436\u0435\u0439, \u043D\u0430\u0447\u0438\u043D\u0430\u044F \u0441 \u0447\u0435\u0442\u0432\u0435\u0440\u0442\u043E\u0433\u043E. \u0427\u0442\u043E\u0431\u044B \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u0442\u044C \u043F\u0430\u0434\u0435\u0436, \u043D\u0430\u043F\u0440\u0438\u043C\u0435\u0440, kaasa\xFCtlev \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430, \u043C\u044B \u0431\u0435\u0440\u0435\u043C \u0444\u043E\u0440\u043C\u0443 \u0432\u0442\u043E\u0440\u043E\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u0438 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0435\u043C \u043A \u043D\u0435\u0439 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -ga. \u041F\u043E \u044D\u0442\u043E\u0439 \u0436\u0435 \u0441\u0445\u0435\u043C\u0435 \u043C\u044B \u0431\u0443\u0434\u0435\u043C \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u044B\u0432\u0430\u0442\u044C \u0438 \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E \u2014 \u0431\u0435\u0440\u0435\u043C \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E \u0432\u0442\u043E\u0440\u043E\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430, \u043A \u043D\u0435\u043C\u0443 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0435\u043C \u0432\u0441\u0435 \u0442\u043E\u0436\u0435 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -ga \u0438 \u043F\u043E\u043B\u0443\u0447\u0430\u0435\u043C \u0432 \u0438\u0442\u043E\u0433\u0435 \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E \u043F\u0430\u0434\u0435\u0436\u0430 kaasa\xFCtlev.\n\n\u041C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E \u043F\u0430\u0434\u0435\u0436\u0430 omastav.\n\u041F\u0440\u0438\u0437\u043D\u0430\u043A\u043E\u043C \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u0432\u0442\u043E\u0440\u043E\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430 \u0431\u0443\u0434\u0443\u0442 \u0441\u043B\u0443\u0436\u0438\u0442\u044C \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u044F -de, -te, \u0438 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0442\u044C\u0441\u044F \u043E\u043D\u0438 \u0431\u0443\u0434\u0443\u0442 \u0432 \u043E\u0441\u043D\u043E\u0432\u043D\u043E\u043C (!) \u043A \u0444\u043E\u0440\u043C\u0435 \u0442\u0440\u0435\u0442\u044C\u0435\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430 osastav \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430. \u0422\u0435\u043F\u0435\u0440\u044C \u0441\u0442\u043E\u0438\u0442 \u0440\u0430\u0437\u043E\u0431\u0440\u0430\u0442\u044C\u0441\u044F, \u043A\u043E\u0433\u0434\u0430 \u043A\u0430\u043A\u043E\u0435 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 \u0438\u0437 \u044D\u0442\u0438\u0445 \u0434\u0432\u0443\u0445 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u0442\u0441\u044F. \n\u0421\u043B\u0443\u0447\u0430\u0438 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u044F -DE:\n\u0415\u0441\u043B\u0438 \u0441\u043B\u043E\u0432\u043E \u0432 \u043F\u0430\u0434\u0435\u0436\u0435 osastav \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043E\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u043D\u0430 \u0433\u043B\u0430\u0441\u043D\u0443\u044E \u0431\u0443\u043A\u0432\u0443, \u0442\u043E \u0434\u043B\u044F \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F omastav \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043A \u044D\u0442\u043E\u0439 \u0444\u043E\u0440\u043C\u0435 \u043F\u0440\u043E\u0441\u0442\u043E \u0434\u043E\u0431\u0430\u0432\u0438\u043C -de:\n\n<table>\n    <tr>\n        <td>ema</td>\n        <td></td>\n        <td>kool</td>\n    </tr>\n    <tr>\n        <td>ema</td>\n        <td>emade</td>\n        <td>kooli</td>\n        <td>koolide</td>\n    </tr>\n    <tr>\n        <td>ema</td>\n        <td></td>\n        <td>kooli</td>\n    </tr>\n</table>\n\n\u0415\u0441\u043B\u0438 \u0441\u043B\u043E\u0432\u043E \u0432 \u043F\u0430\u0434\u0435\u0436\u0435 osastav \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043E\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u043D\u0430 -D, \u0442\u043E \u0434\u043B\u044F \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F omastav \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043A \u044D\u0442\u043E\u0439 \u0444\u043E\u0440\u043C\u0435 \u043C\u044B \u0434\u043E\u0431\u0430\u0432\u0438\u043C -e, \u043D\u043E \u043F\u0440\u0438\u0437\u043D\u0430\u043A\u043E\u043C \u0431\u0443\u0434\u0435\u0442 \u0441\u0447\u0438\u0442\u0430\u0442\u044C\u0441\u044F \u0432\u0441\u0435 \u0440\u0430\u0432\u043D\u043E \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -de:\n\n<table>\n    <tr>\n        <td>maa</td>\n        <td></td>\n        <td>pea</td>\n    </tr>\n    <tr>\n        <td>maa</td>\n        <td>maade</td>\n        <td>pea</td>\n        <td>peade</td>\n    </tr>\n    <tr>\n        <td>maad</td>\n        <td></td>\n        <td>pead</td>\n    </tr>\n</table>\n\n\u0421\u043B\u0443\u0447\u0430\u0438 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u044F -TE:\n\u0415\u0441\u043B\u0438 \u0441\u043B\u043E\u0432\u043E \u0432 \u043F\u0430\u0434\u0435\u0436\u0435 osastav \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043E\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u043D\u0430 -T, \u0442\u043E \u0434\u043B\u044F \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F omastav \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043A \u044D\u0442\u043E\u0439 \u0444\u043E\u0440\u043C\u0435 \u043F\u0440\u043E\u0441\u0442\u043E \u0434\u043E\u0431\u0430\u0432\u0438\u043C -e:\n\n<table>\n    <tr>\n        <td>raamat</td>\n        <td></td>\n        <td>\xF5htu</td>\n    </tr>\n    <tr>\n        <td>raamatu</td>\n        <td>raamatute</td>\n        <td>\xF5htu</td>\n        <td>\xF5htute</td>\n    </tr>\n    <tr>\n        <td>raamatut</td>\n        <td></td>\n        <td>\xF5htut</td>\n    </tr>\n</table>\n\n\u0422\u0430\u043A\u0436\u0435 \u0441\u043B\u043E\u043D\u044F\u044E\u0442\u0441\u044F: s\xF6\xF6kla \u2014 s\xF6\xF6klate, m\xF5te \u2014 m\xF5tete, inimene \u2014 inimeste, vaene \u2014 vaeste, tore \u2014 toredate, hammas \u2014 hammaste, number \u2014 numbrite \u0438 \u0442.\u0434.\n\n\u0418\u0421\u041A\u041B\u042E\u0427\u0415\u041D\u0418\u042F:\n\u041D\u0430\u0447\u043D\u0435\u043C \u0441 \u0441\u0430\u043C\u043E\u0433\u043E \u0441\u043B\u043E\u0436\u043D\u043E\u0433\u043E. \u041A \u0441\u043E\u0436\u0430\u043B\u0435\u043D\u0438\u044E, \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u0432\u0441\u0442\u0440\u0435\u0447\u0430\u044E\u0442 \u0441\u043E\u0432\u0441\u0435\u043C \u043D\u0435\u043E\u0431\u044A\u044F\u0441\u043D\u0438\u043C\u044B\u0435 \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u044F, \u0438 \u0447\u0442\u043E \u0435\u0449\u0435 \u0445\u0443\u0436\u0435, \u043F\u043E\u043B\u043D\u043E\u0433\u043E \u0441\u043F\u0438\u0441\u043A\u0430 \u0438\u0445 \u043D\u0438\u0433\u0434\u0435 \u043D\u0435\u0442, \u044F\u0437\u044B\u043A \u043F\u043E\u0441\u0442\u043E\u044F\u043D\u043D\u043E \u0438\u0437\u043C\u0435\u043D\u044F\u0435\u0442\u0441\u044F. \u0418\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u044F \u044D\u0442\u0438 \u043C\u043E\u0433\u0443\u0442 \u0431\u044B\u0442\u044C \u043A\u0430\u043A \u0432 \u043F\u0440\u0430\u0432\u0438\u043B\u0435 \u0434\u043E\u0431\u0430\u0432\u043B\u0435\u043D\u0438\u044F \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u044F -de, \u0442\u0430\u043A \u0438 -te. \u041F\u0435\u0440\u0432\u044B\u043C \u043F\u0440\u0435\u0434\u0441\u0442\u0430\u0432\u043B\u0435\u043D\u043E \u0441\u043B\u043E\u0432\u0430 \u0442\u0438\u043F\u0430 \xABpere\xBB \u2014 \u0441\u044E\u0434\u0430 \u043E\u0442\u043D\u043E\u0441\u044F\u0442\u0441\u044F \u043D\u0435\u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0434\u0432\u0443\u0445\u0441\u043B\u043E\u0436\u043D\u044B\u0435 \u0441\u043B\u043E\u0432\u0430, \u0433\u0434\u0435 \u0432\u043C\u0435\u0441\u0442\u043E \u043F\u0440\u0438\u0437\u043D\u0430\u043A\u0430 -te \u0431\u0443\u0434\u0435\u0442 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0442\u044C\u0441\u044F \u0434\u043B\u044F \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -de. \u0412\u043E \u0432\u0442\u043E\u0440\u043E\u0439 \u043A\u043E\u043B\u043E\u043D\u043A\u0435 \u043F\u0440\u0438\u043C\u0435\u0440 \u0441\u043B\u043E\u0432\u0430, \u0433\u0434\u0435 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0443\u044E\u0442 \u043F\u043E\u043A\u0430 \u0435\u0449\u0435 \u0434\u0432\u0435 \u0440\u0430\u0432\u043D\u043E\u0441\u0438\u043B\u044C\u043D\u044B\u0435 \u0444\u043E\u0440\u043C\u044B \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043F\u0430\u0434\u0435\u0436\u0430 omastav: \u043A\u043E\u0440\u043E\u0442\u043A\u0430\u044F \u2014 \u0438\u0441\u043A\u043B\u044E\u0447\u0438\u0442\u0435\u043B\u044C\u043D\u0430\u044F \u0444\u043E\u0440\u043C\u0430 \u0438 \u043F\u043E\u043B\u043D\u0430\u044F, \u0441 \u0434\u043E\u0431\u0430\u0432\u043B\u0435\u043D\u043D\u044B\u043C \u043F\u043E \u0432\u0441\u0435\u043C \u043F\u0440\u0430\u0432\u0438\u043B\u0430\u043C \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435\u043C. \u0412 \u0441\u043B\u043E\u0432\u0430\u0445 \u043F\u043E\u0434\u043E\u0431\u043D\u044B\u0445 \u0441\u043B\u043E\u0432\u0443 \xABmeri\xBB \u0434\u043B\u044F \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u043F\u0430\u0434\u0435\u0436\u0430 omastav \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043C\u044B \u0432\u043E\u0437\u044C\u043C\u0435\u043C \u043D\u0435 \u0442\u0440\u0435\u0442\u0438\u0439 \u043F\u0430\u0434\u0435\u0436, \u0430 \u0432\u0442\u043E\u0440\u043E\u0439 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430.\n\n<table>\n    <tr>\n        <td>pere</td>\n        <td></td>\n        <td>poiss</td>\n    </tr>\n    <tr>\n        <td>pere</td>\n        <td>perede</td>\n        <td>poisi</td>\n        <td>poiste (poisside)</td>\n    </tr>\n    <tr>\n        <td>peret</td>\n        <td></td>\n        <td>poissi</td>\n    </tr>\n    <tr>\n        <td>meri</td>\n    </tr>\n    <tr>\n        <td>mere</td>\n        <td>merede</td>\n    </tr>\n    <tr>\n        <td>merd</td>\n    </tr>\n</table>\n\n\u0422\u0430\u043A\u0436\u0435 \u0441\u043A\u043B\u043E\u043D\u044F\u044E\u0442\u0441\u044F: k\xF5ne \u2014 k\xF5nede, auto \u2014 autode, male \u2014 malede, vale \u2014 valede, ime \u2014 imede, tubli \u2014 tublide, summa \u2014 summade, kirju \u2014 kirjude, neiu \u2014 neiude, teema \u2014 teemade, koer \u2014 koerte, nael \u2014 naelte, pael \u2014 paelte, \xF5un \u2014 \xF5unte, hein \u2014 heinte, m\xF5ni \u2014 m\xF5nede, tuli \u2014 tulede, mure \u2014 murede, k\xFCmme \u2014 k\xFCmnete  \u0438 \u0442.\u0434.\n\u0421\u043B\u0435\u0434\u0443\u044E\u0449\u0430\u044F \u0433\u0440\u0443\u043F\u043F\u0430 \u0441\u043B\u043E\u0432 \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u0439 \u0431\u043E\u043B\u0435\u0435 \u043A\u043E\u043D\u043A\u0440\u0435\u0442\u043D\u0430. \u0415\u0441\u043B\u0438 \u0434\u0432\u0443\u0445\u0441\u043B\u043E\u0436\u043D\u043E\u0435 \u0441\u043B\u043E\u0432\u043E \u0432 \u043F\u0430\u0434\u0435\u0436\u0435 osastav \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043E\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u043D\u0430 \u0431\u0443\u043A\u0432\u0443 -t, \u0438 \u043F\u0435\u0440\u0435\u0434 \u043D\u0435\u0439 \u0432 \u043E\u0441\u043D\u043E\u0432\u0435 \u0441\u043B\u043E\u0432\u0430 \u0441\u0442\u043E\u044F\u0442 \u0431\u0443\u043A\u0432\u044B -l-, -n-, -m-, -r-, -v-, \u0442\u043E \u0434\u043B\u044F \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F omastav \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 -t \u043C\u0435\u043D\u044F\u0435\u0442 \u043D\u0430 -de:\n\n<table>\n    <tr>\n        <td>aken</td>\n        <td></td>\n        <td>t\xFCtar</td>\n    </tr>\n    <tr>\n        <td>akna</td>\n        <td>akende</td>\n        <td>t\xFCtre</td>\n        <td>t\xFCtarde</td>\n    </tr>\n    <tr>\n        <td>akent</td>\n        <td></td>\n        <td>t\xFCtart</td>\n    </tr>\n    <tr>\n        <td>pipar</td>\n        <td></td>\n        <td>k\xFC\xFCnal</td>\n    </tr>\n    <tr>\n        <td>pipra</td>\n        <td>piparde</td>\n        <td>k\xFC\xFCnla</td>\n        <td>k\xFC\xFCnalde</td>\n    </tr>\n    <tr>\n        <td>pipart</td>\n        <td></td>\n        <td>k\xFC\xFCnalt</td>\n    </tr>\n</table>\n\n\u042D\u0441\u0442\u043E\u043D\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u2014 \u044D\u0442\u043E \u043D\u0430 \u0441\u0430\u043C\u043E\u043C \u0434\u0435\u043B\u0435 \u044F\u0437\u044B\u043A \u043A\u043E\u0440\u043E\u0442\u043A\u0438\u0445 \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u0447\u0435\u0441\u043A\u0438\u0445 \u043F\u0440\u0430\u0432\u0438\u043B \u0438 \u0434\u043B\u0438\u043D\u043D\u044B\u0445 \u0441\u043F\u0438\u0441\u043A\u043E\u0432 \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u0439, \u0438 \u0442\u043E\u043B\u044C\u043A\u043E \u0437\u0434\u0435\u0441\u044C \u0432\u043E\u0437\u043C\u043E\u0436\u043D\u044B \u0434\u0430\u0436\u0435 \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u044F \u0438\u0437 \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u0439! \u0412 \u043F\u0440\u0435\u0434\u044B\u0434\u0443\u0449\u0435\u043C \u043F\u0443\u043D\u043A\u0442\u0435 \u043C\u044B \u0432\u044B\u0434\u0435\u043B\u0438\u043B\u0438 \u0432\u043F\u043E\u043B\u043D\u0435 \u043B\u043E\u0433\u0438\u0447\u043D\u0443\u044E \u0433\u0440\u0443\u043F\u043F\u0443 \u0441\u043B\u043E\u0432, \u0433\u0434\u0435 \u0431\u0443\u0434\u0435\u0442 \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0442\u044C\u0441\u044F -de \u0432\u043C\u0435\u0441\u0442\u043E \u043F\u0440\u0438\u0437\u043D\u0430\u043A\u0430 \u2013te. \u041D\u043E \u043C\u043E\u0433\u0443\u0442 \u0432\u0441\u0442\u0440\u0435\u0447\u0430\u0442\u044C\u0441\u044F \u0432\u0441\u0435-\u0442\u0430\u043A\u0438 \u0441\u043B\u043E\u0432\u0430, \u043A\u043E\u0442\u043E\u0440\u044B\u0435, \u043D\u0435 \u0441\u043C\u043E\u0442\u0440\u044F \u043D\u0430 \u0432\u044B\u043F\u043E\u043B\u043D\u0435\u043D\u0438\u0435 \u0432\u0441\u0435\u0445 \u0432\u044B\u0448\u0435\u043F\u0435\u0440\u0435\u0447\u0438\u0441\u043B\u0435\u043D\u043D\u044B\u0445 \u0443\u0441\u043B\u043E\u0432\u0438\u0439, \u043F\u043E\u0434\u0447\u0438\u043D\u044F\u0442\u0441\u044F \u043E\u0441\u043D\u043E\u0432\u043D\u043E\u043C\u0443 \u043F\u0440\u0430\u0432\u0438\u043B\u0443.   \n\n<table>\n    <tr>\n        <td>seen</td>\n        <td></td>\n        <td>suur</td>\n    </tr>\n    <tr>\n        <td>seene</td>\n        <td>seente</td>\n        <td>suure</td>\n        <td>suurte</td>\n    </tr>\n    <tr>\n        <td>seent</td>\n        <td></td>\n        <td>suurt</td>\n    </tr>\n</table>\n\n\u0415\u0441\u043B\u0438 \u0434\u043B\u044F \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u043F\u0430\u0434\u0435\u0436\u0430 omastav \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043A \u0441\u043A\u043B\u043E\u043D\u044F\u0435\u043C\u043E\u043C\u0443 \u0441\u043B\u043E\u0432\u0443 \u0434\u043E\u0431\u0430\u0432\u0438\u043B\u043E\u0441\u044C -me, \u0442\u043E \u043E\u0431\u044B\u0447\u043D\u0430\u044F \u0446\u0435\u043F\u043E\u0447\u043A\u0430, \u0432 \u043A\u043E\u0442\u043E\u0440\u043E\u0439 \u043C\u044B \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0435 \u0447\u0438\u0441\u043B\u043E \u0432\u0442\u043E\u0440\u043E\u0433\u043E \u043F\u0430\u0434\u0435\u0436\u0430 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u044B\u0432\u0430\u043B\u0438 \u043E\u0442 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u0442\u0440\u0435\u0442\u044C\u0435\u0433\u043E, \u043D\u0430\u0440\u0443\u0448\u0430\u0435\u0442\u0441\u044F. \u0412 \u044D\u0442\u043E\u043C \u0441\u043B\u0443\u0447\u0430\u0435 \u043C\u044B \u0432\u043E\u0437\u044C\u043C\u0435\u043C \u0444\u043E\u0440\u043C\u0443 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 omastav \u0438 \u043A \u043D\u0435\u0439 \u0434\u043E\u0431\u0430\u0432\u0438\u043C \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -te.  \n\n<table>\n    <tr>\n        <td>v\xF5ti</td>\n        <td></td>\n        <td>liige</td>\n    </tr>\n    <tr>\n        <td>v\xF5tme</td>\n        <td>v\xF5tmete</td>\n        <td>liikme</td>\n        <td>liikmete</td>\n    </tr>\n    <tr>\n        <td>v\xF5tit</td>\n        <td></td>\n        <td>liiget</td>\n    </tr>\n</table>\n\n\u0415\u0441\u043B\u0438 \u0441\u043B\u043E\u0432\u043E \u0432 \u043F\u0430\u0434\u0435\u0436\u0435 osastav \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043E\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u0435\u0442\u0441\u044F \u043D\u0430 -kku, \u0442\u043E \u0432 omastav \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 \u043C\u043E\u0436\u043D\u043E \u0443\u043F\u043E\u0442\u0440\u0435\u0431\u0438\u0442\u044C \u043A\u0430\u043A \u043F\u043E\u043B\u043D\u0443\u044E \u0444\u043E\u0440\u043C\u0443 \u0441 \u043E\u043A\u043E\u043D\u0447\u0430\u043D\u0438\u0435 -de, \u0442\u0430\u043A \u0438 \u043A\u043E\u0440\u043E\u0442\u043A\u0443\u044E, \u0433\u0434\u0435 \u0431\u0443\u043A\u0432\u0443 -u \u043C\u0435\u043D\u044F\u0435\u0442\u0441\u044F \u043D\u0430 -e. \u0412 \u044D\u0442\u0443 \u0433\u0440\u0443\u043F\u043F\u0443 \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u0439 \u043C\u044B \u043C\u043E\u0436\u0435\u043C \u043E\u0442\u043D\u0435\u0441\u0442\u0438 \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0435 \u043D\u0430 -lik, \u0438 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435, \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u043D\u044B\u0435 \u043F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u0430 -nik.\n\n<table>\n    <tr>\n        <td>kuntsnik</td>\n        <td></td>\n        <td>\xF5nnelik</td>\n    </tr>\n    <tr>\n        <td>kuntsniku</td>\n        <td>kuntsnike (kuntsnikkude)</td>\n        <td>\xF5nneliku</td>\n        <td>\xF5nnelike (\xF5nnelikkude)</td>\n    </tr>\n    <tr>\n        <td>kuntsnikku</td>\n        <td></td>\n        <td>\xF5nnelikku</td>\n    </tr>\n</table>\n\n\u041D\u0430 \u044D\u0442\u043E\u043C \u043F\u0440\u0430\u0432\u0438\u043B\u043E \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430 omastav, \u0438, \u0447\u0442\u043E \u043D\u0435 \u043C\u0435\u043D\u0435\u0435 \u0432\u0430\u0436\u043D\u043E, \u0435\u0433\u043E \u0438\u0441\u043A\u043B\u044E\u0447\u0435\u043D\u0438\u044F \u0437\u0430\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u044E\u0442\u0441\u044F. \u041D\u043E \u0440\u0430\u0437 \u0443\u0436 \u043C\u044B \u0432 \u043F\u043E\u0441\u043B\u0435\u0434\u043D\u0435\u043C \u043F\u0443\u043D\u043A\u0442\u0435 \u0437\u0430\u0442\u0440\u043E\u043D\u0443\u043B\u0438 \u0442\u0435\u043C\u0443 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u043E\u0432 \u0434\u043B\u044F \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u0442\u0435\u0445 \u0438\u043B\u0438 \u0438\u043D\u044B\u0445 \u0447\u0430\u0441\u0442\u0435\u0439 \u0440\u0435\u0447\u0438, \u0442\u043E \u0434\u0430\u0432\u0430\u0439\u0442\u0435 \u0435\u0435 \u043F\u0440\u043E\u0434\u043E\u043B\u0436\u0438\u043C. \u041C\u044B \u0443\u0436\u0435 \u0440\u0430\u0441\u0441\u043C\u0430\u0442\u0440\u0438\u0432\u0430\u043B\u0438 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u044B \u0434\u043B\u044F \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0445 \u0438 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432, \u0441\u0435\u0439\u0447\u0430\u0441 \u043D\u0430 \u043F\u043E\u0432\u0435\u0441\u0442\u043A\u0435 \u0434\u043D\u044F \u0443 \u043D\u0430\u0441 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u0435 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0445.\n\n\u041D\u0435\u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u044B \u0434\u043B\u044F \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0445.\n \u041F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u043E\u0432 \u043C\u043E\u0436\u043D\u043E \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u0442\u044C \u0438\u043C\u0435\u043D\u0430 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u043C\u043E\u0433\u0443\u0442 \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0442\u044C:\n\u0414\u0435\u0439\u0441\u0442\u0432\u0438\u0435.\n-mine\n\u0421 \u043F\u043E\u043C\u043E\u0449\u044C\u044E \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u0430 -mine \u043E\u0431\u0440\u0430\u0437\u0443\u044E\u0442\u0441\u044F \u0438\u043C\u0435\u043D\u0430 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435, \u043D\u0430\u0437\u044B\u0432\u0430\u044E\u0449\u0438\u0435 \u0441\u0430\u043C\u043E \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435, \u043F\u0440\u043E\u0446\u0435\u0441\u0441. \u0414\u0430\u043D\u043D\u044B\u0439 \u0441\u0443\u0444\u0444\u0438\u043A\u0441 \u043F\u0440\u0438\u0441\u043E\u0435\u0434\u0438\u043D\u044F\u0435\u0442\u0441\u044F \u043A \u043E\u0441\u043D\u043E\u0432\u0435 ma-\u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u0430.\n\n<table>\n    <tr>\n        <td>ehitama</td>\n        <td>\u0441\u0442\u0440\u043E\u0438\u0442\u044C</td>\n        <td>ehitamine</td>\n        <td>\u0441\u0442\u0440\u043E\u0438\u0442\u0435\u043B\u044C\u0441\u0442\u0432\u043E</td>\n    </tr>\n    <tr>\n        <td>lugema</td>\n        <td>\u0447\u0438\u0442\u0430\u0442\u044C</td>\n        <td>lugemine</td>\n        <td>\u0447\u0442\u0435\u043D\u0438\u0435</td>\n    </tr>\n    <tr>\n        <td>ostma</td>\n        <td>\u043F\u043E\u043A\u0443\u043F\u0430\u0442\u044C</td>\n        <td>ostmine</td>\n        <td>\u043F\u043E\u043A\u0443\u043F\u043A\u0430</td>\n    </tr>\n</table>\n\n-us\n\u0423 \u044D\u0442\u043E\u0433\u043E \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u0430 \u0435\u0441\u0442\u044C \u0434\u0432\u0430 \u0432\u0430\u0440\u0438\u0430\u043D\u0442\u0430 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u043D\u0438\u044F. \u041F\u0440\u0438 \u0434\u043E\u0431\u0430\u0432\u043B\u0435\u043D\u0438\u0438 \u043A \u043E\u0441\u043D\u043E\u0432\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u043D\u043E\u0435 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u0431\u0443\u0434\u0435\u0442 \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0442\u044C \u043D\u0430\u0437\u0432\u0430\u043D\u0438\u0435 \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044F. \u041F\u0440\u0438 \u0434\u043E\u0431\u0430\u0432\u043B\u0435\u043D\u0438\u0438 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u0430 \u043A \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u043C \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442 \u0441\u0443\u0444\u0444\u0438\u043A\u0441 -us \u043D\u0430\u0437\u0432\u0430\u043D\u0438\u044F \u0430\u0431\u0441\u0442\u0440\u0430\u043A\u0442\u043D\u044B\u0445 \u044F\u0432\u043B\u0435\u043D\u0438\u0439 \u0438\u043B\u0438 \u0441\u0432\u043E\u0439\u0441\u0442\u0432. \n\n<table>\n    <tr>\n        <td>kartma</td>\n        <td>\u0431\u043E\u044F\u0442\u044C\u0441\u044F</td>\n        <td>kartus</td>\n        <td>\u0431\u043E\u044F\u0437\u043D\u044C, \u0441\u0442\u0440\u0430\u0445</td>\n    </tr>\n    <tr>\n        <td>harjutama</td>\n        <td>\u0443\u043F\u0440\u0430\u0436\u043D\u044F\u0442\u044C\u0441\u044F</td>\n        <td>harjutus</td>\n        <td>\u0443\u043F\u0440\u0430\u0436\u043D\u0435\u043D\u0438\u0435</td>\n    </tr>\n    <tr>\n        <td>armastama</td>\n        <td>\u043B\u044E\u0431\u0438\u0442\u044C</td>\n        <td>armastus</td>\n        <td>\u043B\u044E\u0431\u043E\u0432\u044C</td>\n    </tr>\n    <tr>\n        <td>rumal</td>\n        <td>\u0433\u043B\u0443\u043F\u044B\u0439</td>\n        <td>rumalus</td>\n        <td>\u0433\u043B\u0443\u043F\u043E\u0441\u0442\u044C</td>\n    </tr>\n    <tr>\n        <td>n\xF5rk</td>\n        <td>\u0441\u043B\u0430\u0431\u044B\u0439</td>\n        <td>n\xF5rkus</td>\n        <td>\u0441\u043B\u0430\u0431\u043E\u0441\u0442\u044C</td>\n    </tr>\n    <tr>\n        <td>korralik</td>\n        <td>\u043F\u043E\u0440\u044F\u0434\u043E\u0447\u043D\u044B\u0439</td>\n        <td>NB! korralikkus</td>\n        <td>\u043F\u043E\u0440\u044F\u0434\u043E\u0447\u043D\u043E\u0441\u0442\u044C</td>\n    </tr>\n</table>\n\n-k\n\u041F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u0430 -k \u043E\u0431\u0440\u0430\u0437\u0443\u044E\u0442\u0441\u044F \u043D\u0430\u0437\u0432\u0430\u043D\u0438\u044F \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u044F \u0438\u043B\u0438 \u043E\u0431\u044A\u0435\u043A\u0442\u0430, \u043F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u043A\u043E\u0442\u043E\u0440\u043E\u0433\u043E \u044D\u0442\u043E \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435 \u0441\u043E\u0432\u0435\u0440\u0448\u0430\u0435\u0442\u0441\u044F. \u0421\u0443\u0444\u0444\u0438\u043A\u0441 \u043F\u0440\u0438\u0441\u043E\u0435\u0434\u0438\u043D\u044F\u0435\u0442\u0441\u044F \u043A \u043E\u0441\u043D\u043E\u0432\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u0430, \u043E\u043A\u0430\u043D\u0447\u0438\u0432\u0430\u044E\u0449\u0435\u0439\u0441\u044F \u043D\u0430 \u0433\u043B\u0430\u0441\u043D\u044B\u0439 \u0437\u0432\u0443\u043A. \n\n<table>\n    <tr>\n        <td>jooma</td>\n        <td>\u043F\u0438\u0442\u044C</td>\n        <td>jook</td>\n        <td>\u043D\u0430\u043F\u0438\u0442\u043E\u043A</td>\n    </tr>\n    <tr>\n        <td>s\xF6\xF6ma</td>\n        <td>\u043A\u0443\u0448\u0430\u0442\u044C</td>\n        <td>s\xF6\xF6k</td>\n        <td>\u0435\u0434\u0430</td>\n    </tr>\n    <tr>\n        <td>m\xFC\xFCma</td>\n        <td>\u043F\u0440\u043E\u0434\u0430\u0432\u0430\u0442\u044C</td>\n        <td>m\xFC\xFCk</td>\n        <td>\u043F\u0440\u043E\u0434\u0430\u0436\u0430</td>\n    </tr>\n    <tr>\n        <td>\xF5ppima</td>\n        <td>\u0443\u0447\u0438\u0442\u044C\u0441\u044F</td>\n        <td>\xF5pik</td>\n        <td>\u0443\u0447\u0435\u0431\u043D\u0438\u043A</td>\n    </tr>\n</table>\n\n-ndus\n\u0421\u0443\u0444\u0444\u0438\u043A\u0441 -ndus \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442 \u0438\u043C\u0435\u043D\u0430 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u044E\u0442 \u0441\u0444\u0435\u0440\u0443 \u0434\u0435\u044F\u0442\u0435\u043B\u044C\u043D\u043E\u0441\u0442\u0438. \u0414\u043E\u0431\u0430\u0432\u043B\u044F\u0435\u0442\u0441\u044F \u043E\u043D \u043A \u043E\u0441\u043D\u043E\u0432\u0435 \u043F\u0430\u0434\u0435\u0436\u0430 omastav \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0445.\n\n<table>\n    <tr>\n        <td>laev</td>\n        <td>\u043F\u0430\u0440\u043E\u0445\u043E\u0434</td>\n        <td>laevandus</td>\n        <td>\u043F\u0430\u0440\u043E\u0445\u043E\u0434\u0441\u0442\u0432\u043E</td>\n    </tr>\n    <tr>\n        <td>aed</td>\n        <td>\u0441\u0430\u0434</td>\n        <td>aiandus</td>\n        <td>\u0441\u0430\u0434\u043E\u0432\u043E\u0434\u0441\u0442\u0432\u043E</td>\n    </tr>\n    <tr>\n        <td>kaup</td>\n        <td>\u0442\u043E\u0432\u0430\u0440</td>\n        <td>kaubandus</td>\n        <td>\u0442\u043E\u0440\u0433\u043E\u0432\u043B\u044F</td>\n    </tr>\n</table>\n\n\u041B\u0438\u0446\u043E.\n-ja\n\u0414\u0430\u043D\u043D\u044B\u0439 \u0441\u0443\u0444\u0444\u0438\u043A\u0441 \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435, \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u044E\u0449\u0438\u0435 \u0434\u0435\u0439\u0441\u0442\u0432\u0443\u044E\u0449\u0438\u0435 \u043B\u0438\u0446\u0430, \u0442\u043E \u0435\u0441\u0442\u044C \u0442\u0435\u0445, \u043A\u0442\u043E \u0432\u044B\u043F\u043E\u043B\u043D\u044F\u0435\u0442 \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435. \u0421\u0443\u0444\u0444\u0438\u043A\u0441 -ja \u0434\u043E\u0431\u0430\u0432\u043B\u044F\u0435\u0442\u0441\u044F \u043A \u043E\u0441\u043D\u043E\u0432\u0435 ma-\u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u0430.\n\n<table>\n    <tr>\n        <td>\xF5petama</td>\n        <td>\u0443\u0447\u0438\u0442\u044C</td>\n        <td>\xF5petaja</td>\n        <td>\u0443\u0447\u0438\u0442\u0435\u043B\u044C</td>\n    </tr>\n    <tr>\n        <td>t\xF6\xF6tama</td>\n        <td>\u0440\u0430\u0431\u043E\u0442\u0430\u0442\u044C</td>\n        <td>t\xF6\xF6taja</td>\n        <td>\u0440\u0430\u0431\u043E\u0442\u043D\u0438\u043A</td>\n    </tr>\n    <tr>\n        <td>lugema</td>\n        <td>\u0447\u0438\u0442\u0430\u0442\u044C</td>\n        <td>lugeja</td>\n        <td>\u0447\u0438\u0442\u0430\u0442\u0435\u043B\u044C</td>\n    </tr>\n</table>\n\n-lane\n\u0421\u0443\u0444\u0444\u0438\u043A\u0441 -lane \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u043E\u0442 \u043E\u0441\u043D\u043E\u0432\u044B (\u0438\u043D\u043E\u0433\u0434\u0430 \u0441\u043E\u043A\u0440\u0430\u0449\u0435\u043D\u043D\u043E\u0439) \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u0438\u043B\u0438 \u0434\u0440\u0443\u0433\u043E\u0433\u043E \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u0438 \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u0435\u0442 \u043B\u0438\u0446\u043E \u043F\u043E \u043A\u0430\u043A\u043E\u043C\u0443-\u043B\u0438\u0431\u043E \u043F\u0440\u0438\u0437\u043D\u0430\u043A\u0443: \u043F\u043E \u043D\u0430\u0446\u0438\u043E\u043D\u0430\u043B\u044C\u043D\u043E\u0441\u0442\u0438, \u043F\u043E \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0436\u0434\u0435\u043D\u0438\u044E, \u0441\u0444\u0435\u0440\u044B \u0434\u0435\u044F\u0442\u0435\u043B\u044C\u043D\u043E\u0441\u0442\u0438, \u043F\u043E \u043F\u0440\u0438\u043D\u0430\u0434\u043B\u0435\u0436\u043D\u043E\u0441\u0442\u0438 \u043A \u043A\u0430\u043A\u043E\u0439-\u043D\u0438\u0431\u0443\u0434\u044C \u0433\u0440\u0443\u043F\u043F\u0435 \u0438 \u0442.\u0434..\n\n<table>\n    <tr>\n        <td>Eesti</td>\n        <td>\u042D\u0441\u0442\u043E\u043D\u0438\u044F</td>\n        <td>eestlane</td>\n        <td>\u044D\u0441\u0442\u043E\u043D\u0435\u0446</td>\n    </tr>\n    <tr>\n        <td>Siber</td>\n        <td>\u0421\u0438\u0431\u0438\u0440\u044C</td>\n        <td>sibirlane</td>\n        <td>\u0441\u0438\u0431\u0438\u0440\u044F\u043A</td>\n    </tr>\n    <tr>\n        <td>teadus</td>\n        <td>\u043D\u0430\u0443\u043A\u0430</td>\n        <td>teadlane</td>\n        <td>\u0443\u0447\u0435\u043D\u044B\u0439</td>\n    </tr>\n    <tr>\n        <td>partei</td>\n        <td>\u043F\u0430\u0440\u0442\u0438\u044F</td>\n        <td>parteilane</td>\n        <td>\u043F\u0430\u0440\u0442\u0438\u0435\u0439</td>\n    </tr>\n</table>\n\n-line\n\u041F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0441\u0443\u0444\u0444\u0438\u043A\u0441 -line \u043E\u0431\u0440\u0430\u0437\u0443\u044E\u0442 \u043D\u0430\u0437\u0432\u0430\u043D\u0438\u044F \u043B\u0438\u0446\u0430 \u043F\u043E \u043A\u0430\u043A\u043E\u043C\u0443-\u043D\u0438\u0431\u0443\u0434\u044C \u0432\u0440\u0435\u043C\u0435\u043D\u043D\u043E\u043C\u0443 \u043F\u0440\u0438\u0437\u043D\u0430\u043A\u0443. \u041F\u0440\u0438\u0441\u043E\u0435\u0434\u0438\u043D\u044F\u0435\u0442\u0441\u044F \u043E\u043D \u043A \u0444\u043E\u0440\u043C\u0435 \u043F\u0430\u0434\u0435\u0436\u0430 omastav \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E. \n\n<table>\n    <tr>\n        <td>pulm</td>\n        <td>\u0441\u0432\u0430\u0434\u044C\u0431\u0430</td>\n        <td>pulmaline</td>\n        <td>\u0431\u0440\u0430\u0447\u0443\u044E\u0449\u0438\u0439\u0441\u044F</td>\n    </tr>\n    <tr>\n        <td>t\xF6\xF6</td>\n        <td>\u0440\u0430\u0431\u043E\u0442\u0430</td>\n        <td>t\xF6\xF6line</td>\n        <td>\u0440\u0430\u0431\u043E\u0442\u043D\u0438\u043A</td>\n    </tr>\n    <tr>\n        <td>seen</td>\n        <td>\u0433\u0440\u0438\u0431</td>\n        <td>seeneline</td>\n        <td>\u0433\u0440\u0438\u0431\u043D\u0438\u043A</td>\n    </tr>\n    <tr>\n        <td>jaht</td>\n        <td>\u043E\u0445\u043E\u0442\u0430</td>\n        <td>jahiline</td>\n        <td>\u043E\u0445\u043E\u0442\u043D\u0438\u043A</td>\n    </tr>\n</table>\n\n-nik \n\u0421 \u043F\u043E\u043C\u043E\u0449\u044C\u044E \u044D\u0442\u043E\u0433\u043E \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u0430 \u043E\u0431\u0440\u0430\u0437\u0443\u044E\u0442\u0441\u044F \u043D\u0430\u0437\u0432\u0430\u043D\u0438\u044F \u043B\u0438\u0446\u0430, \u0445\u0430\u0440\u0430\u043A\u0442\u0435\u0440\u0438\u0437\u0443\u044E\u0449\u0438\u0435\u0441\u044F \u0432 \u043E\u0441\u043D\u043E\u0432\u043D\u043E\u043C \u043F\u043E \u0435\u0433\u043E \u043F\u043E\u0441\u0442\u043E\u044F\u043D\u043D\u043E\u0439 \u0441\u0444\u0435\u0440\u0435 \u0434\u0435\u044F\u0442\u0435\u043B\u044C\u043D\u043E\u0441\u0442\u0438, \u043D\u043E \u0432\u0441\u0442\u0440\u0435\u0447\u0430\u044E\u0442\u0441\u044F \u0438 \u0434\u0440\u0443\u0433\u0438\u0435 \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u044F. \n\n<table>\n    <tr>\n        <td>kunst</td>\n        <td>\u0438\u0441\u043A\u0443\u0441\u0441\u0442\u0432\u043E</td>\n        <td>kuntsnik</td>\n        <td>\u0445\u0443\u0434\u043E\u0436\u043D\u0438\u043A</td>\n    </tr>\n    <tr>\n        <td>kohus</td>\n        <td>\u0441\u0443\u0434</td>\n        <td>kohtunik</td>\n        <td>\u0441\u0443\u0434\u044C\u044F</td>\n    </tr>\n    <tr>\n        <td>elama</td>\n        <td>\u0436\u0438\u0442\u044C</td>\n        <td>elanik</td>\n        <td>\u0436\u0438\u0442\u0435\u043B\u044C</td>\n    </tr>\n    <tr>\n        <td>hapu</td>\n        <td>\u043A\u0438\u0441\u043B\u044B\u0439</td>\n        <td>hapnik</td>\n        <td>\u043A\u0438\u0441\u043B\u043E\u0440\u043E\u0434</td>\n    </tr>\n</table>\n\n-nna, -tar\n\u041A\u0430\u043A \u043C\u044B \u043F\u043E\u043C\u043D\u0438\u043C, \u0432 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u043C \u044F\u0437\u044B\u043A\u0435 \u043E\u0442\u0441\u0443\u0442\u0441\u0442\u0432\u0443\u0435\u0442 \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u0447\u0435\u0441\u043A\u0430\u044F \u043A\u0430\u0442\u0435\u0433\u043E\u0440\u0438\u044F \u0440\u043E\u0434\u0430, \u0438 \u043F\u043E\u043D\u044F\u0442\u044C, \u043A\u0442\u043E \u0432\u044B\u043F\u043E\u043B\u043D\u044F\u0435\u0442\u0441\u044F \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435, \u043E\u043D \u0438\u043B\u0438 \u043E\u043D\u0430, \u043C\u043E\u0436\u043D\u043E \u0442\u043E\u043B\u044C\u043A\u043E \u0438\u0437 \u043A\u043E\u043D\u0442\u0435\u043A\u0441\u0442\u0430. \u041D\u043E \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0443\u0435\u0442 \u043F\u0430\u0440\u0430 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u043E\u0432 \u0434\u043B\u044F \u0443\u043A\u0430\u0437\u0430\u043D\u0438\u044F \u0438\u043C\u0435\u043D\u043D\u043E \u0436\u0435\u043D\u0441\u043A\u043E\u0433\u043E \u0440\u043E\u0434\u0430. \u0411\u043E\u043B\u0435\u0435 \u043E\u0431\u0449\u0438\u043C \u0438\u0437 \u043D\u0438\u0445 \u0441\u0447\u0438\u0442\u0430\u0435\u0442\u0441\u044F \u0441\u0443\u0444\u0444\u0438\u043A\u0441 -nna, \u0441\u0443\u0444\u0444\u0438\u043A\u0441 -tar \u0432\u0441\u0442\u0440\u0435\u0447\u0430\u0435\u0442\u0441\u044F \u0431\u043E\u043B\u044C\u0448\u0435 \u0432 \u043B\u0438\u0442\u0435\u0440\u0430\u0442\u0443\u0440\u043D\u044B\u0445 \u043F\u0440\u043E\u0438\u0437\u0432\u0435\u0434\u0435\u043D\u0438\u044F\u0445.\n\n<table>\n    <tr>\n        <td>\u041F\u043E\u0434\u0445\u043E\u0434\u0438\u0442 \u043A\u0430\u043A \u043C\u0443\u0436\u0447\u0438\u043D\u0430\u043C, \u0442\u0430\u043A \u0438 \u0436\u0435\u043D\u0449\u0438\u043D\u0430\u043C</td>\n        <td></td>\n        <td>\u0422\u043E\u043B\u044C\u043A\u043E \u0434\u043B\u044F \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0435\u043D\u0438\u044F \u0436\u0435\u043D\u0441\u043A\u043E\u0433\u043E \u043F\u043E\u043B\u0430</td>\n    </tr>\n    <tr>\n        <td>s\xF5ber</td>\n        <td>\u0434\u0440\u0443\u0433, \u043F\u043E\u0434\u0440\u0443\u0433\u0430</td>\n        <td>s\xF5branna/s\xF5bratar</td>\n        <td>\u043F\u043E\u0434\u0440\u0443\u0433\u0430</td>\n    </tr>\n    <tr>\n        <td>laulja</td>\n        <td>\u043F\u0435\u0432\u0435\u0446, \u043F\u0435\u0432\u0438\u0446\u0430</td>\n        <td>lauljanna/lauljatar</td>\n        <td>\u043F\u0435\u0432\u0438\u0446\u0430</td>\n    </tr>\n    <tr>\n        <td>venelane</td>\n        <td>\u0440\u0443\u0441\u0441\u043A\u0438\u0439, \u0440\u0443\u0441\u0441\u043A\u0430\u044F</td>\n        <td>venelanna</td>\n        <td>\u0440\u0443\u0441\u0441\u043A\u0430\u044F</td>\n    </tr>\n    <tr>\n        <td>juut</td>\n        <td>\u0435\u0432\u0440\u0435\u0439, \u0435\u0432\u0440\u0435\u0439\u043A\u0430</td>\n        <td>juuditar</td>\n        <td>\u0435\u0432\u0440\u0435\u0439\u043A\u0430</td>\n    </tr>\n</table>\n\n\u0421\u043E\u0432\u043E\u043A\u0443\u043F\u043D\u043E\u0441\u0442\u044C \u0447\u0435\u0433\u043E-\u043B\u0438\u0431\u043E (\u043A\u043E\u0433\u043E-\u043B\u0438\u0431\u043E) \u0438\u043B\u0438 \u043D\u0430\u0437\u0432\u0430\u043D\u0438\u0435 \u043C\u0435\u0441\u0442\u043D\u043E\u0441\u0442\u0438.\n-kond\n\u0421\u0443\u0444\u0444\u0438\u043A\u0441 -kond \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442 \u043D\u0430\u0437\u0432\u0430\u043D\u0438\u044F \u0433\u0440\u0443\u043F\u043F \u043B\u044E\u0434\u0435\u0439. \u0420\u0435\u0436\u0435 \u0432\u0441\u0442\u0440\u0435\u0447\u0430\u044E\u0442\u0441\u044F \u0441\u043B\u043E\u0432\u0430, \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u044E\u0449\u0438\u0435 \u043D\u0430\u0437\u0432\u0430\u043D\u0438\u044F \u043C\u0435\u0441\u0442\u043D\u043E\u0441\u0442\u0438. \n\n<table>\n    <tr>\n        <td>\xFChine</td>\n        <td>\u043E\u0431\u0449\u0438\u0439</td>\n        <td>\xFChiskond</td>\n        <td>\u043E\u0431\u0449\u0435\u0441\u0442\u0432\u043E</td>\n    </tr>\n    <tr>\n        <td>mees</td>\n        <td>\u043C\u0443\u0436\u0447\u0438\u043D\u0430</td>\n        <td>meeskond</td>\n        <td>\u043A\u043E\u043C\u0430\u043D\u0434\u0430</td>\n    </tr>\n    <tr>\n        <td>saatma</td>\n        <td>\u043E\u0442\u043F\u0440\u0430\u0432\u043B\u044F\u0442\u044C</td>\n        <td>saatkond</td>\n        <td>\u043F\u043E\u0441\u043E\u043B\u044C\u0441\u0442\u0432\u043E</td>\n    </tr>\n    <tr>\n        <td>piir</td>\n        <td>\u0433\u0440\u0430\u043D\u0438\u0446\u0430</td>\n        <td>piirkond</td>\n        <td>\u0440\u0430\u0439\u043E\u043D, \u043E\u0431\u043B\u0430\u0441\u0442\u044C</td>\n    </tr>\n    <tr>\n        <td>maa</td>\n        <td>\u0437\u0435\u043C\u043B\u044F</td>\n        <td>maakond</td>\n        <td>\u0443\u0435\u0437\u0434</td>\n    </tr>\n</table>\n\n-stik\n\u041F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u0430 -stik \u043E\u0431\u0440\u0430\u0437\u0443\u044E\u0442\u0441\u044F \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435, \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u044E\u0449\u0438\u0435 \u0441\u043E\u0431\u0440\u0430\u043D\u0438\u0435 \u0447\u0435\u0433\u043E-\u043B\u0438\u0431\u043E (\u043A\u043E\u0433\u043E-\u043B\u0438\u0431\u043E), \u0441\u043E\u0432\u043E\u043A\u0443\u043F\u043D\u043E\u0441\u0442\u044C. \u0421\u0443\u0444\u0444\u0438\u043A\u0441 \u043F\u0440\u0438\u0441\u043E\u0435\u0434\u0438\u043D\u044F\u0435\u0442\u0441\u044F \u043A \u043E\u0441\u043D\u043E\u0432\u0435 \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0445, \u0447\u0430\u0449\u0435 \u0432\u0441\u0435\u0433\u043E \u043A \u0444\u043E\u0440\u043C\u0435 \u043F\u0430\u0434\u0435\u0436\u0430 omastav.\n\n<table>\n    <tr>\n        <td>t\xE4ht</td>\n        <td>\u0431\u0443\u043A\u0432\u0430</td>\n        <td>t\xE4hestik</td>\n        <td>\u0430\u043B\u0444\u0430\u0432\u0438\u0442</td>\n    </tr>\n    <tr>\n        <td>s\xF5na</td>\n        <td>\u0441\u043B\u043E\u0432\u043E</td>\n        <td>s\xF5nastik</td>\n        <td>\u0441\u043B\u043E\u0432\u0430\u0440\u044C</td>\n    </tr>\n    <tr>\n        <td>m\xE4gi</td>\n        <td>\u0433\u043E\u0440\u0430</td>\n        <td>m\xE4estik</td>\n        <td>\u0433\u043E\u0440\u044B (\u0433\u043E\u0440\u043D\u0430\u044F \u0446\u0435\u043F\u044C)</td>\n    </tr>\n</table>\n\n\n-ndik\n\u0421\u0443\u0444\u0444\u0438\u043A\u0441 -ndik \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442 \u0447\u0430\u0449\u0435 \u0432\u0441\u0435\u0433\u043E \u043D\u0430\u0437\u0432\u0430\u043D\u0438\u044F \u043C\u0435\u0441\u0442\u043D\u043E\u0441\u0442\u0435\u0439, \u043D\u043E \u0432\u0441\u0442\u0440\u0435\u0447\u0430\u044E\u0442\u0441\u044F \u0438 \u0434\u0440\u0443\u0433\u0438\u0435 \u0437\u043D\u0430\u0447\u0435\u043D\u0438\u044F. \u041F\u0440\u0438\u0441\u043E\u0435\u0434\u0438\u043D\u044F\u0435\u0442\u0441\u044F \u043E\u0431\u044B\u0447\u043D\u043E \u043A \u043E\u0441\u043D\u043E\u0432\u0435 \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u043D\u0430 \u0433\u043B\u0430\u0441\u043D\u044B\u0439.\n\n<table>\n    <tr>\n        <td>tasa</td>\n        <td>\u043F\u043B\u043E\u0441\u043A\u0438\u0439</td>\n        <td>tasandik</td>\n        <td>\u0440\u0430\u0432\u043D\u0438\u043D\u0430</td>\n    </tr>\n    <tr>\n        <td>noor</td>\n        <td>\u043C\u043E\u043B\u043E\u0434\u043E\u0439</td>\n        <td>noorendik</td>\n        <td>\u043C\u043E\u043B\u043E\u0434\u043D\u044F\u043A (\u043B\u0435\u0441)</td>\n    </tr>\n    <tr>\n        <td>selg</td>\n        <td>\u0441\u043F\u0438\u043D\u0430</td>\n        <td>seljandik</td>\n        <td>\u0445\u0440\u0435\u0431\u0435\u0442</td>\n    </tr>\n    <tr>\n        <td>kasvama</td>\n        <td>\u0440\u0430\u0441\u0442\u0438\u0442\u044C</td>\n        <td>kasvandik</td>\n        <td>\u0432\u043E\u0441\u043F\u0438\u0442\u0430\u043D\u043D\u0438\u043A</td>\n    </tr>\n</table>\n\n\u0421\u0443\u0444\u0444\u0438\u043A\u0441\u043E\u0432 \u0434\u043B\u044F \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u044F \u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0445 \u043A\u043E\u043D\u0435\u0447\u043D\u043E \u043C\u043D\u043E\u0433\u043E. \u042F \u0432\u044B\u0431\u0440\u0430\u043B\u0430 \u0434\u043B\u044F \u0412\u0430\u0441 \u043D\u0430\u0438\u0431\u043E\u043B\u0435\u0435 \u0447\u0430\u0441\u0442\u043E \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u043C\u044B\u0435 \u0441\u0443\u0444\u0444\u0438\u043A\u0441\u044B, \u0445\u043E\u0442\u044F \u0438 \u044D\u0442\u043E\u0442 \u0441\u043F\u0438\u0441\u043E\u043A \u043E\u043A\u0430\u0437\u0430\u043B\u0441\u044F \u0434\u043E\u0441\u0442\u0430\u0442\u043E\u0447\u043D\u043E \u0434\u043B\u0438\u043D\u043D\u044B\u043C. \u0422\u0430\u043A \u0447\u0442\u043E \u043F\u0440\u0435\u0434\u043B\u0430\u0433\u0430\u044E \u043F\u043E\u043A\u0430 \u043D\u0430 \u044D\u0442\u043E\u043C \u043E\u0441\u0442\u0430\u043D\u043E\u0432\u0438\u0442\u044C\u0441\u044F, \u0438 \u0432\u0435\u0440\u043D\u0443\u0442\u044C\u0441\u044F \u043A \u0440\u0430\u0437\u0433\u043E\u0432\u043E\u0440\u0443 \u043F\u0440\u043E \u043F\u0430\u0434\u0435\u0436\u0438 \u0438 \u0441\u043A\u043B\u043E\u043D\u044F\u0435\u043C\u044B\u0435 \u0447\u0430\u0441\u0442\u0438 \u0440\u0435\u0447\u0438.\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('lesson',{number:16}),source:source});}));

