var source='\n\u041A\u0430\u043A \u0412\u044B \u0437\u0430\u043C\u0435\u0442\u0438\u043B\u0438, \u043E\u0431\u044A\u0435\u043C \u043C\u0430\u0442\u0435\u0440\u0438\u0430\u043B\u0430 \u043E\u0434\u043D\u043E\u0433\u043E \u0437\u0430\u043D\u044F\u0442\u0438\u044F \u043E\u0447\u0435\u043D\u044C \u0431\u043E\u043B\u044C\u0448\u043E\u0439. \u041C\u044B \u043D\u0435 \u043C\u043E\u0436\u0435\u043C \u0441\u0442\u043E\u044F\u0442\u044C \u043D\u0430 \u043C\u0435\u0441\u0442\u0435, \u043D\u0430\u0448\u0430 \u043E\u0441\u043D\u043E\u0432\u043D\u0430\u044F \u0446\u0435\u043B\u044C \u2013 \u043D\u0430\u0447\u0430\u0442\u044C \u0433\u043E\u0432\u043E\u0440\u0438\u0442\u044C, \u043F\u043E\u044D\u0442\u043E\u043C\u0443 \u0438 \u0432\u0441\u0435 \u043D\u043E\u0432\u044B\u0435 \u043F\u0440\u0430\u0432\u0438\u043B\u0430, \u0441\u043B\u043E\u0432\u0430 \u043D\u0430\u043C \u043D\u0443\u0436\u043D\u043E \u043D\u0430\u043A\u043B\u0430\u0434\u044B\u0432\u0430\u0442\u044C \u0434\u0440\u0443\u0433 \u043D\u0430 \u0434\u0440\u0443\u0433\u0430 \u043F\u043E \u043F\u0440\u0438\u043D\u0446\u0438\u043F\u0443 \u0441\u043D\u0435\u0436\u043D\u043E\u0433\u043E \u043A\u043E\u043C\u0430. \u0413\u043B\u0430\u0432\u043D\u043E\u0435, \u0447\u0442\u043E\u0431\u044B \u0432 \u0433\u043E\u043B\u043E\u0432\u0435 \u0432\u0441\u0435 \u043D\u0435 \u0441\u043C\u0435\u0448\u0430\u043B\u043E\u0441\u044C, \u0438 \u0434\u043B\u044F \u044D\u0442\u043E\u0433\u043E \u044F \u043E\u0442 \u0443\u0447\u0435\u043D\u0438\u043A\u043E\u0432 \u0442\u0440\u0435\u0431\u0443\u044E, \u0447\u0442\u043E\u0431\u044B \u043E\u043D\u0438 \u043D\u0430\u0445\u043E\u0434\u0438\u043B\u0438 \u0441\u0432\u043E\u0431\u043E\u0434\u043D\u0443\u044E \u043C\u0438\u043D\u0443\u0442\u043A\u0443 \u0434\u043B\u044F \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430 \u043F\u043E\u043C\u0438\u043C\u043E \u043D\u0430\u0448\u0438\u0445 \u0443\u0440\u043E\u043A\u043E\u0432. \u0427\u0430\u0449\u0435 \u0432\u0441\u0435\u0433\u043E \u044D\u0442\u043E \u043D\u0435 \u0431\u0443\u0434\u0435\u0442 \u0437\u0430\u043D\u0438\u043C\u0430\u0442\u044C \u0443 \u0412\u0430\u0441 \u043C\u043D\u043E\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438, \u043E\u0442\u0432\u043B\u0435\u043A\u0438\u0442\u0435\u0441\u044C \u043E\u0442 \u0441\u0432\u043E\u0438\u0445 \u043F\u043E\u0432\u0441\u0435\u0434\u043D\u0435\u0432\u043D\u044B\u0445 \u0434\u0435\u043B \u0445\u043E\u0442\u044F \u0431\u044B \u043D\u0430 \u043C\u0438\u043D\u0443\u0442 5-10, \u0438 \u043F\u0440\u043E\u0441\u0442\u043E \u043F\u0435\u0440\u0435\u0447\u0438\u0442\u0430\u0439\u0442\u0435 \u0441\u0432\u043E\u0438 \u0437\u0430\u043C\u0435\u0442\u043A\u0438 \u043F\u043E \u0443\u0440\u043E\u043A\u0443, \u043F\u043E\u0432\u0442\u043E\u0440\u044F\u0439\u0442\u0435 \u0441\u043B\u043E\u0432\u0430.\n\n### Harjutus 1.\n\u0412\u0441\u0442\u0430\u0432\u044C\u0442\u0435 \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F \u0433\u043B\u0430\u0433\u043E\u043B \u0431\u044B\u0442\u044C \u0432 \u043D\u0443\u0436\u043D\u043E\u043C \u043B\u0438\u0446\u0435. \u041D\u0435 \u0437\u0430\u0431\u044B\u0432\u0430\u0439\u0442\u0435 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u044C \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F.\nMina ... Maria. 2. Sa ... \xF5pilane. 3. Ta ei ... hea. 4. Nad ... eestlased. 5. Me ei ... vanad 6. Ma \u2026t\xFCdruk, aga sina ... poiss. 7. Teie ei ... veel \xFCli\xF5pilased. 8. Tema ... muusik. 9. Te ... head direktorid. 10.Meie ... inimesed.\n### Harjutus 2. \n\u0417\u0430\u043F\u043E\u043B\u043D\u0438\u0442\u0435 \u043F\u0440\u043E\u0431\u0435\u043B\u044B \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F\u0445, \u0432\u0441\u0442\u0430\u0432\u0438\u0432 \u0433\u043B\u0430\u0433\u043E\u043B \u0431\u044B\u0442\u044C \u0438\u043B\u0438 \u043B\u0438\u0447\u043D\u043E\u0435 \u043C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u0435 \u0432 \u043D\u0443\u0436\u043D\u043E\u0439 \u0444\u043E\u0440\u043C\u0435. \u0418\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0439\u0442\u0435 \u043E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u0435, \u0433\u0434\u0435 \u044D\u0442\u043E \u0431\u0443\u0434\u0435\u0442 \u043D\u0435\u043E\u0431\u0445\u043E\u0434\u0438\u043C\u043E \u043F\u043E \u0441\u043C\u044B\u0441\u043B\u0443.\n1.Teie ... vanad, teie ... noored. 2. See p\xE4ev ... kaunis. 3. Nemad ... eestlased, nemad ... venelased. 4. Mina ... sekret\xE4r. 5. \u2026 oleme muusikud. 6. See hea naine ...Tiina, tema ... insener. 7.  ... oled kaunis t\xFCdruk. 8. Teie ... ilus inimene. 9.  Nad ... noored \xF5petajad. 10. See inimene ... naine, tema ... mees.\n_______________________________________________________________\n\u0428\u043F\u0430\u0440\u0433\u0430\u043B\u043A\u0430 \u0434\u043B\u044F \u0443\u043F\u0440\u0430\u0436\u043D\u0435\u043D\u0438\u0439 2-3. \u041D\u043E\u0432\u044B\u0435 \u0441\u043B\u043E\u0432\u0430 \u043F\u0435\u0440\u0432\u043E\u0433\u043E \u0443\u0440\u043E\u043A\u0430.\nInimene - \u0447\u0435\u043B\u043E\u0432\u0435\u043A\nNaine - \u0436\u0435\u043D\u0449\u0438\u043D\u0430\nMees - \u043C\u0443\u0436\u0447\u0438\u043D\u0430\nPoiss \u2013 \u043C\u0430\u043B\u044C\u0447\u0438\u043A\nT\xFCdruk - \u0434\u0435\u0432\u043E\u0447\u043A\u0430\n\xD5pilane - \u0443\u0447\u0435\u043D\u0438\u043A\n\xDCli\xF5pilane - \u0441\u0442\u0443\u0434\u0435\u043D\u0442\n\xD5petaja- \u0443\u0447\u0438\u0442\u0435\u043B\u044C\nEestlane- \u044D\u0441\u0442\u043E\u043D\u0435\u0446\nVenelane - \u0440\u0443\u0441\u0441\u043A\u0438\u0439\nHea - \u0445\u043E\u0440\u043E\u0448\u0438\u0439\nIlus - \u043A\u0440\u0430\u0441\u0438\u0432\u044B\u0439\nKaunis -\u043F\u0440\u0435\u043A\u0440\u0430\u0441\u043D\u044B\u0439\nVana - \u0441\u0442\u0430\u0440\u044B\u0439\nNoor - \u043C\u043E\u043B\u043E\u0434\u043E\u0439\nHommik - \u0443\u0442\u0440\u043E\nP\xE4ev - \u0434\u0435\u043D\u044C\n\xD5htu - \u0432\u0435\u0447\u0435\u0440\nOlema, olla, on - \u0431\u044B\u0442\u044C\nArmastama, armastada, armastab - \u043B\u044E\u0431\u0438\u0442\u044C\nVeel -\u0435\u0449\u0435 \nAga - \u043D\u043E\n\n\u0412\u0441\u0435\u0445 \u0441\u0432\u043E\u0438\u0445 \u0443\u0447\u0435\u043D\u0438\u043A\u043E\u0432 \u044F \u043F\u0440\u0438\u0437\u044B\u0432\u0430\u044E \u043A \u0441\u0430\u043C\u043E\u0441\u0442\u043E\u044F\u0442\u0435\u043B\u044C\u043D\u043E\u0441\u0442\u0438. \u041D\u0430\u0440\u0430\u0431\u0430\u0442\u044B\u0432\u0430\u0442\u044C \u0441\u0432\u043E\u0439 \u0441\u043E\u0431\u0441\u0442\u0432\u0435\u043D\u043D\u044B\u0439 \u0441\u043B\u043E\u0432\u0430\u0440\u043D\u044B\u0439 \u0437\u0430\u043F\u0430\u0441 \u043D\u0435\u0432\u043E\u0437\u043C\u043E\u0436\u043D\u043E \u0442\u043E\u043B\u044C\u043A\u043E \u043F\u0440\u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u0441\u043B\u043E\u0432, \u0432\u044B\u0443\u0447\u0435\u043D\u043D\u044B\u0445 \u043D\u0430 \u0437\u0430\u043D\u044F\u0442\u0438\u0438. \u041D\u0430 \u043F\u0435\u0440\u0432\u043E\u043C \u0437\u0430\u043D\u044F\u0442\u0438\u0438 \u044F \u043E\u0431\u043B\u0435\u0433\u0447\u0438\u043B\u0430 \u0412\u0430\u043C \u0436\u0438\u0437\u043D\u044C, \u0432\u044B\u043F\u0438\u0441\u0430\u0432 \u043E\u0442\u0434\u0435\u043B\u044C\u043D\u043E \u0432\u0441\u0435 \u043D\u043E\u0432\u044B\u0435 \u0441\u043B\u043E\u0432\u0430. \u0412 \u0431\u0443\u0434\u0443\u0449\u0435\u043C \u0412\u0430\u043C \u043F\u0440\u0438\u0434\u0435\u0442\u0441\u044F \u0438 \u0441\u0430\u043C\u043E\u0441\u0442\u043E\u044F\u0442\u0435\u043B\u044C\u043D\u043E \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u044C, \u0447\u0442\u043E\u0431\u044B \u043F\u0440\u0430\u0432\u0438\u043B\u044C\u043D\u043E \u0432\u044B\u043F\u043E\u043B\u043D\u0438\u0442\u044C \u0443\u043F\u0440\u0430\u0436\u043D\u0435\u043D\u0438\u044F. \u041F\u0440\u0435\u0434\u043B\u0430\u0433\u0430\u044E \u0432\u0430\u0448\u0435\u043C\u0443 \u0432\u043D\u0438\u043C\u0430\u043D\u0438\u044E \u043E\u0434\u0438\u043D \u043E\u0447\u0435\u043D\u044C \u0445\u043E\u0440\u043E\u0448\u0438\u0439 \u044D\u043B\u0435\u043A\u0442\u0440\u043E\u043D\u043D\u044B\u0439 \u0441\u043B\u043E\u0432\u0430\u0440\u044C, \u0441\u043E\u0437\u0434\u0430\u043D\u043D\u044B\u0439 \u0418\u043D\u0441\u0442\u0438\u0442\u0443\u0442\u043E\u043C \u042D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u042F\u0437\u044B\u043A\u0430 (Eesti Keele Instituut) - http://www.eki.ee/dict/evs/.\n\u0421\u043B\u043E\u0432\u0430\u0440\u044C \u0445\u043E\u0440\u043E\u0448 \u0442\u0435\u043C, \u0447\u0442\u043E \u0432 \u043D\u0435\u043C \u043A\u0430\u043A \u0440\u0430\u0437 \u043F\u0440\u0438\u0441\u0443\u0442\u0441\u0442\u0432\u0443\u044E\u0442 \u0432\u0441\u0435 \u043D\u0443\u0436\u043D\u044B\u0435 \u043D\u0430\u043C \u0444\u043E\u0440\u043C\u044B. \u041F\u0440\u043E\u0448\u0443 \u0437\u0430\u043C\u0435\u0442\u0438\u0442\u044C, \u044D\u0442\u043E \u043D\u0435 \u0440\u0435\u043A\u043B\u0430\u043C\u0430, \u044D\u0442\u043E \u043F\u0440\u043E\u0441\u0442\u043E \u043C\u043E\u0439 \u043B\u0438\u0447\u043D\u044B\u0439 \u0441\u043E\u0432\u0435\u0442 \u263A\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('task',{number:2}),source:source});}));

