var source='\n### Harjutus 1.\n\u041F\u0440\u0435\u0434\u043B\u0430\u0433\u0430\u044E \u0412\u0430\u043C, \u0437\u0430\u043D\u044F\u0442\u044C\u0441\u044F \u0432\u043F\u043B\u043E\u0442\u043D\u0443\u044E \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C\u0438 \u0438 \u043E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u0435\u043C \u0440\u0430\u0437\u043B\u0438\u0447\u043D\u044B\u0445 \u0444\u043E\u0440\u043C. \u041D\u0438\u0436\u0435 \u0432 \u0442\u0430\u0431\u043B\u0438\u0446\u0435 \u043F\u0440\u0435\u0434\u0441\u0442\u0430\u0432\u043B\u0435\u043D\u044B 10 \u0441\u043E\u0432\u0435\u0440\u0448\u0435\u043D\u043D\u043E \u043D\u043E\u0432\u044B\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u0438 7 \u0442\u0435\u0445, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0443\u0436\u0435 \u0432\u0441\u0442\u0440\u0435\u0447\u0430\u043B\u0438\u0441\u044C \u0432 \u0443\u0440\u043E\u043A\u0435. \u0412\u0430\u0448\u0430 \u0437\u0430\u0434\u0430\u0447\u0430 \u043F\u0440\u043E\u0441\u043F\u0440\u044F\u0433\u0430\u0442\u044C \u043A\u0430\u0436\u0434\u044B\u0439 \u0438\u0437 \u043D\u0438\u0445 \u0432 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u043C \u0432\u0440\u0435\u043C\u0435\u043D\u0438 \u0438 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u043C, \u043F\u043E\u0441\u0442\u0430\u0432\u0438\u0442\u044C \u0438\u0445 \u0432 \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0435, \u043D\u0443 \u0438 \u043A\u043E\u043D\u0435\u0447\u043D\u043E \u043D\u0435 \u0437\u0430\u0431\u044B\u0432\u0430\u044F \u0442\u0430\u043A\u0436\u0435 \u043F\u0440\u043E \u043E\u0442\u0440\u0438\u0446\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0435 \u0444\u043E\u0440\u043C\u044B. \u042F \u0412\u0430\u043C \u043E\u0431\u043B\u0435\u0433\u0447\u0438\u043B\u0430 \u0437\u0430\u0434\u0430\u0447\u0443, \u0443\u043A\u0430\u0437\u0430\u043B\u0430 \u0434\u043B\u044F \u043A\u0430\u0436\u0434\u043E\u0433\u043E \u0433\u043B\u0430\u0433\u043E\u043B\u0430 \u0432\u0441\u0435 3 \u043E\u0441\u043D\u043E\u0432\u043D\u044B\u0435 \u0444\u043E\u0440\u043C\u044B.\n\n\n\n\n### Harjutus 2.\n\u0420\u0430\u0441\u043A\u0440\u043E\u0439\u0442\u0435 \u0441\u043A\u043E\u0431\u043A\u0438, \u0432\u0441\u0442\u0430\u0432\u0438\u0432 \u0433\u043B\u0430\u0433\u043E\u043B \u0432 \u043F\u0440\u0430\u0432\u0438\u043B\u044C\u043D\u0443\u044E \u0444\u043E\u0440\u043C\u0443. NB! \u0412\u043D\u0438\u043C\u0430\u0442\u0435\u043B\u044C\u043D\u043E \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u0435 \u0432\u0441\u0435 \u0441\u043B\u043E\u0432\u0430 \u0432 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0438, \u043A\u0430\u0436\u0434\u043E\u0435 \u0438\u0437 \u043D\u0438\u0445 \u043C\u043E\u0436\u0435\u0442 \u043F\u043E\u0432\u043B\u0438\u044F\u0442\u044C \u043D\u0430 \u0441\u043C\u044B\u0441\u043B.\n1. Praegu me .......... (elama) Tallinnas (\u0432 \u0422\u0430\u043B\u043B\u0438\u043D\u043D\u0435 \u2013 \u0433\u0434\u0435?), aga kunagi ammu me .......... (elama) ka P\xE4rnus (\u0432 \u041F\u044F\u0440\u043D\u0443). 2. Tema .......... (aru saama) juba eesti keelest, aga ise ei .......... (r\xE4\xE4kima). 3. Ma .......... (arvama), et ma .......... (s\xF5idan) Londonisse. London .......... (olema) ilus linn. 4. Eesti \xF5pilased .......... (\xF5ppima) koolis inglise, saksa ja vene keelt. 5. ........... (vabandama) palun! Me ei ........... (aru saama). 6. Koolis ma olen .......... (\xF5ppima) inglise keelt. 7. K\xF5ik on lihtne: mina .......... (andma), sina .......... (v\xF5tma). 8. Nad ju .......... (r\xE4\xE4kima), et nad ei .......... (s\xF5itma) Tallinnasse (\u0432 \u0422\u0430\u043B\u043B\u0438\u043D\u043D \u2013 \u043A\u0443\u0434\u0430?), nad .......... (t\xF6\xF6tama). 9. Teie .......... (r\xE4\xE4kima), et te ei .......... (armastama) mind. 10. ........ (tuttavaks saama), see .......... (olema) minu hea s\xF5ber Toomas. 11. Tema .......... (\xF5ppima) ja .......... (t\xF6\xF6tama) palju, temal ei .......... (olema) aega armastada. 12. Me .......... (v\xF5tma) takso ja ........ (s\xF5itma) klubisse.13. Maris, \xE4ra .......... (r\xE4\xE4kima) nii palju telefoniga. Isa .......... (t\xF6\xF6tama). 14. Mina .......... (t\xE4nama) Liinat, et ta .......... (\xF5petama) mulle eesti keelt. 15. Jaan .......... (armastama) \xF6elda:  minu abikaasa .......... (olema) v\xE4ga kaunis!\n\n\u041E\u0431\u0440\u0430\u0437\u043E\u0432\u0430\u043D\u0438\u0435 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0433\u043E \u0438 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D, \u0430 \u0442\u0430\u043A\u0436\u0435 \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0433\u043E \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u044F, \u0434\u043B\u044F \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u0438\u0437 \u0437\u0430\u0434\u0430\u043D\u0438\u044F 1. \u0412\u043D\u0438\u043C\u0430\u0442\u0435\u043B\u044C\u043D\u043E \u043F\u0440\u043E\u0432\u0435\u0440\u044C\u0442\u0435 \u0441\u0435\u0431\u044F \u043F\u043E \u044D\u0442\u043E\u0439 \u0442\u0430\u0431\u043B\u0438\u0446\u0435!!!\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('task',{number:3}),source:source});}));

