var source='\n### Harjutus 1.\n\u0422\u0430\u043A \u043A\u0430\u043A \u0443\u0440\u043E\u043A 4 \u0431\u044B\u043B \u043F\u043E\u0441\u0432\u044F\u0449\u0435\u043D \u043D\u0435\u0441\u0440\u0430\u0431\u0430\u0442\u044B\u0432\u0430\u043D\u0438\u044E \u0441\u043B\u043E\u0432\u0430\u0440\u043D\u043E\u0433\u043E \u0437\u0430\u043F\u0430\u0441\u0430, \u0442\u043E \u043F\u0440\u043E\u0434\u043E\u043B\u0436\u0430\u0435\u043C \u0442\u0440\u0435\u043D\u0438\u0440\u043E\u0432\u0430\u0442\u044C \u0433\u0440\u0430\u043C\u043C\u0430\u0442\u0438\u043A\u0443 \u0433\u043B\u0430\u0433\u043E\u043B\u0430. \u041E\u0447\u0435\u0440\u0435\u0434\u043D\u0430\u044F \u043F\u0430\u0440\u0442\u0438\u044F \u043D\u043E\u0432\u044B\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u0434\u043B\u044F \u0440\u0430\u0437\u043C\u0438\u043D\u043A\u0438. \u041F\u043E\u043C\u043D\u0438\u0442\u0435, \u0441\u043F\u0440\u044F\u0433\u0430\u0435\u043C \u0438\u0445 \u0432 \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u043C \u0438 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438, \u043F\u043B\u044E\u0441 \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435 \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0435 \u0435\u0434\u0438\u043D\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0438 \u043C\u043D\u043E\u0436\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u0433\u043E \u0447\u0438\u0441\u043B\u0430. \u041D\u0443 \u0438 \u043A\u043E\u043D\u0435\u0447\u043D\u043E \u043D\u0435 \u0437\u0430\u0431\u044B\u0432\u0430\u0435\u043C \u043F\u0440\u043E \u043E\u0442\u0440\u0438\u0446\u0430\u043D\u0438\u0435 \u044D\u0442\u0438\u0445 \u0432\u0441\u0435\u0445 \u0444\u043E\u0440\u043C. \u0412 \u044D\u0442\u043E\u0442 \u0440\u0430\u0437 \u044F \u043D\u0435 \u0431\u0443\u0434\u0443 \u0432\u044B\u0434\u0430\u0432\u0430\u0442\u044C \u0432\u0430\u043C \u0432\u0441\u0435 \u044D\u0442\u043E \u0432 \u043E\u0442\u0432\u0435\u0442\u0430\u0445, \u0432\u044B\u043F\u0438\u0448\u0443 \u043B\u0438\u0448\u044C \u0442\u043E\u043B\u044C\u043A\u043E \u043D\u0435\u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0444\u043E\u0440\u043C\u044B \u043F\u043E\u0434\u0441\u043A\u0430\u0437\u043A\u0438. \u041D\u0443 \u0438 \u043D\u0435\u0431\u043E\u043B\u044C\u0448\u043E\u0435 \u0437\u0430\u043C\u0435\u0447\u0430\u043D\u0438\u0435, \u043D\u0435\u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0444\u043E\u0440\u043C\u044B \u044D\u0442\u0438\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u0431\u0443\u0434\u0443\u0442 \u043A\u043E\u0440\u044F\u0432\u043E \u0437\u0432\u0443\u0447\u0430\u0442\u044C \u043F\u0440\u0438 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0435. \u041D\u0435 \u043E\u0431\u0440\u0430\u0449\u0430\u0439\u0442\u0435 \u043D\u0430 \u044D\u0442\u043E \u0432\u043D\u0438\u043C\u0430\u043D\u0438\u0435, \u043D\u0430\u043C \u0433\u043B\u0430\u0432\u043D\u043E\u0435 \u043E\u0442\u0440\u0430\u0431\u043E\u0442\u0430\u0442\u044C \u0441\u0445\u0435\u043C\u0443, \u0447\u0442\u043E \u043E\u0442 \u0447\u0435\u0433\u043E \u0438 \u043A\u0430\u043A \u043E\u0431\u0440\u0430\u0437\u0443\u0435\u0442\u0441\u044F!\n\u042F \u0433\u0434\u0435-\u0442\u043E \u0447\u0438\u0442\u0430\u043B\u0430 \u043E\u0431 \u043E\u0434\u043D\u043E\u043C \u0438\u0441\u0441\u043B\u0435\u0434\u043E\u0432\u0430\u043D\u0438\u0438,  \u0432 \u043A\u043E\u0442\u043E\u0440\u043E\u043C \u0433\u043E\u0432\u043E\u0440\u0438\u043B\u043E\u0441\u044C, \u0447\u0442\u043E \u0432 \u043E\u0431\u044B\u0447\u043D\u043E\u0439 \u043F\u043E\u0432\u0441\u0435\u0434\u043D\u0435\u0432\u043D\u043E\u0439 \u0440\u0435\u0447\u0438 \u043C\u044B \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u043C \u0432\u0441\u0435\u0433\u043E \u043F\u043E\u0440\u044F\u0434\u043A\u0430 50-60 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432. \u041C\u044B \u043C\u043E\u0436\u043D\u043E \u0441\u043A\u0430\u0437\u0430\u0442\u044C \u0443\u0436\u0435 \u043D\u0430 \u043F\u043E\u043B\u043F\u0443\u0442\u0438 \u043A \u044D\u0442\u043E\u043C\u0443 \u043A\u043E\u043B\u0438\u0447\u0435\u0441\u0442\u0432\u0443, \u0442\u0430\u043A \u0447\u0442\u043E \u043B\u043E\u0432\u0438\u0442\u0435 \u043E\u0447\u0435\u0440\u0435\u0434\u043D\u0443\u044E \u0434\u0435\u0441\u044F\u0442\u043A\u0443 \u0440\u0430\u0441\u043F\u0440\u043E\u0441\u0442\u0440\u0430\u043D\u0435\u043D\u043D\u044B\u0445 \u0433\u043B\u0430\u0433\u043E\u043B\u043E\u0432 \u043E\u0442 \u043C\u0435\u043D\u044F: \n\n\nNB! \u0412\u043E\u0437\u044C\u043C\u0438\u0442\u0435 \u043D\u0430 \u0437\u0430\u043C\u0435\u0442\u043A\u0443\n\u041F\u0440\u043E\u0432\u0435\u0440\u0438\u0442\u044C \u0441\u0435\u0431\u044F \u0438 \u0443\u0437\u043D\u0430\u0442\u044C \u0444\u043E\u0440\u043C\u044B, \u0443\u043A\u0430\u0437\u0430\u043D\u043D\u044B\u0435 \u0432 \u043F\u043E\u0434\u0441\u043A\u0430\u0437\u043A\u0430\u0445, \u0412\u044B \u0441\u043C\u043E\u0436\u0435\u0442\u0435 \u0438 \u0441\u0430\u043C\u043E\u0441\u0442\u043E\u044F\u0442\u0435\u043B\u044C\u043D\u043E. \u0412 \u043F\u0435\u0440\u0432\u0443\u044E \u043E\u0447\u0435\u0440\u0435\u0434\u044C \u0412\u0430\u043C \u043C\u043E\u0436\u0435\u0442 \u043F\u043E\u043C\u043E\u0447\u044C \u0443\u0436\u0435 \u0437\u043D\u0430\u043A\u043E\u043C\u044B\u0439 \u044D\u043B\u0435\u043A\u0442\u0440\u043E\u043D\u043D\u044B\u0439 \u0441\u043B\u043E\u0432\u0430\u0440\u044C eki.ee. \u0412\u043E\u0437\u044C\u043C\u0435\u043C \u0434\u043B\u044F \u043F\u0440\u0438\u043C\u0435\u0440\u0430 \u0433\u043B\u0430\u0433\u043E\u043B Tulema, \u0438 \u043F\u0440\u0438 \u0432\u0432\u043E\u0434\u0435 \u0444\u043E\u0440\u043C\u044B -MA \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432\u0430 \u043C\u044B \u043F\u043E\u043B\u0443\u0447\u0438\u043C \u0441\u043B\u0435\u0434\u0443\u044E\u0449\u0435\u0435:\n\n\u0420\u0438\u0441. 1\n\u0422\u0430\u043A\u0436\u0435 \u043D\u0430 \u043F\u0440\u043E\u0441\u0442\u043E\u0440\u0430\u0445 \u0438\u043D\u0442\u0435\u0440\u043D\u0435\u0442\u0430 \u0435\u0441\u0442\u044C \u043E\u0447\u0435\u043D\u044C \u043F\u043E\u043B\u0435\u0437\u043D\u044B\u0439 \u0441\u0430\u0439\u0442:\nhttp://www.filosoft.ee/index.html \n\u0417\u0434\u0435\u0441\u044C \u043F\u0440\u0435\u0434\u0441\u0442\u0430\u0432\u043B\u0435\u043D\u043E \u043C\u043D\u043E\u0433\u043E \u0438\u043D\u0442\u0435\u0440\u0435\u0441\u043D\u044B\u0445 \u043F\u0440\u043E\u0433\u0440\u0430\u043C\u043C, \u043D\u043E \u043D\u0430\u043C \u043F\u043E\u043A\u0430 \u043D\u0430 \u0434\u0430\u043D\u043D\u043E\u043C \u044D\u0442\u0430\u043F\u0435 \u0431\u0443\u0434\u0443\u0442 \u0438\u043D\u0442\u0435\u0440\u0435\u0441\u043D\u044B 2 \u0438\u0437 \u043D\u0438\u0445. \u041F\u0435\u0440\u0432\u0430\u044F \u043D\u0430\u0437\u044B\u0432\u0430\u0435\u0442\u0441\u044F \u0441\u0438\u043D\u0442\u0435\u0437\u0430\u0442\u043E\u0440 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u043E\u0433\u043E \u044F\u0437\u044B\u043A\u0430. \u0412\u044B\u0433\u043B\u044F\u0434\u0438\u0442 \u043F\u0440\u043E\u0433\u0440\u0430\u043C\u043C\u0430 \u0441\u043B\u0435\u0434\u0443\u044E\u0449\u0438\u043C \u043E\u0431\u0440\u0430\u0437\u043E\u043C:\n\n\n\u0420\u0438\u0441. 2\n\n\u0412 \u0433\u0440\u0430\u0444\u0435 \xABS\xF5na\xBB (\u0441\u043B\u043E\u0432\u043E) \u0432\u0432\u043E\u0434\u0438\u0442\u0435 \u043D\u0430\u0447\u0430\u043B\u044C\u043D\u0443\u044E \u0444\u043E\u0440\u043C\u0443 \u0441\u043B\u043E\u0432\u0430, \u0438 \u0432\u044B\u0431\u0438\u0440\u0430\u0435\u0442\u0435, \u0447\u0442\u043E \u0445\u043E\u0442\u0438\u0442\u0435 \u043F\u0440\u043E \u043D\u0435\u0433\u043E \u0443\u0437\u043D\u0430\u0442\u044C. \u041F\u0435\u0440\u0432\u0430\u044F \u043A\u043E\u043B\u043E\u043D\u043A\u0430 \u043F\u043E\u0441\u0432\u044F\u0449\u0435\u043D\u0430 \u0441\u043A\u043B\u043E\u043D\u044F\u0435\u043C\u044B\u043C \u0441\u043B\u043E\u0432\u0430\u043C (\u0441\u0443\u0449\u0435\u0441\u0442\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435, \u043F\u0440\u0438\u043B\u0430\u0433\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u0435, \u0447\u0438\u0441\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435, \u043C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u044F), \u0430 \u0432\u043E\u0442 \u0432\u0442\u043E\u0440\u0430\u044F \u043A\u0430\u043A \u0440\u0430\u0437 \u0433\u043B\u0430\u0433\u043E\u043B\u0430\u043C (\u043C\u044B \u0443\u0436\u0435 \u0437\u043D\u0430\u0435\u043C, \u0447\u0442\u043E \u0433\u043B\u0430\u0433\u043E\u043B \u043F\u043E-\u044D\u0441\u0442\u043E\u043D\u0441\u043A\u0438 \xABverb\xBB, \u043D\u043E \u0443 \u043D\u0435\u0433\u043E \u0435\u0441\u0442\u044C \u0438 \u0432\u0442\u043E\u0440\u043E\u0435 \u043D\u0430\u0437\u0432\u0430\u043D\u0438\u0435 \xABtegus\xF5na\xBB). \u041C\u043E\u0436\u043D\u043E \u0432\u044B\u0431\u0440\u0430\u0442\u044C \u043B\u0438\u0446\u043E (isik) \u043A\u0442\u043E \u0432\u044B\u043F\u043E\u043B\u043D\u044F\u0435\u0442 \u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435, \u043E\u0431\u044F\u0437\u0430\u0442\u0435\u043B\u044C\u043D\u043E \u043D\u0443\u0436\u043D\u043E \u0443\u043A\u0430\u0437\u0430\u0442\u044C \u0432\u0440\u0435\u043C\u044F (aeg) \u043D\u0430\u0441\u0442\u043E\u044F\u0449\u0435\u0435 \u0438\u043B\u0438 \u043F\u0440\u043E\u0448\u0435\u0434\u0448\u0435\u0435. \u0422\u0430\u043A\u0436\u0435 \u043E\u0431\u044F\u0437\u0430\u0442\u0435\u043B\u044C\u043D\u044B\u043C \u043F\u043E\u043B\u0435\u043C \u0434\u043B\u044F \u0437\u0430\u043F\u043E\u043B\u043D\u0435\u043D\u0438\u044F \u044F\u0432\u043B\u044F\u0435\u0442\u0441\u044F \u043D\u0430\u043A\u043B\u043E\u043D\u0435\u043D\u0438\u0435 \u0433\u043B\u0430\u0433\u043E\u043B\u0430 (k\xF5neviis): \u0438\u0437\u044A\u044F\u0432\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435, \u043F\u043E\u0432\u0435\u043B\u0438\u0442\u0435\u043B\u044C\u043D\u043E\u0435, \u0443\u0441\u043B\u043E\u0432\u043D\u043E\u0435 \u0438\u043B\u0438 \u043A\u043E\u0441\u0432\u0435\u043D\u043D\u043E\u0435. \u041F\u043E\u0441\u043B\u0435\u0434\u043D\u044F\u044F \u0433\u0440\u0430\u0444\u0430 \u043F\u043E\u0437\u0432\u043E\u043B\u044F\u0435\u0442 \u0432\u044B\u0431\u0440\u0430\u0442\u044C \u0434\u043E\u043F\u043E\u043B\u043D\u0438\u0442\u0435\u043B\u044C\u043D\u044B\u0435 \u0444\u043E\u0440\u043C\u044B. \u0421\u0440\u0435\u0434\u0438 \u043D\u0438\u0445 \u043D\u0430\u043C \u0443\u0436\u0435 \u0437\u043D\u0430\u043A\u043E\u043C\u044B -DA \u0438\u043D\u0444\u0438\u043D\u0438\u0442\u0438\u0432 \u0438 \u0444\u043E\u0440\u043C\u0430 \u043F\u0440\u0438\u0447\u0430\u0441\u0442\u0438\u044F -NUD.  \u041F\u043E\u0441\u0442\u0430\u0432\u0438\u0432 \u0433\u0430\u043B\u043E\u0447\u043A\u0438 \u0432\u043E \u0432\u0441\u0435\u0445 \u0438\u043D\u0442\u0435\u0440\u0435\u0441\u0443\u044E\u0449\u0438\u0445 \u0412\u0430\u0441 \u043C\u0435\u0441\u0442\u0430\u0445 \u0438 \u043D\u0430\u0436\u0430\u0432 \u043D\u0430 \u043A\u043D\u043E\u043F\u043A\u0443 \xABs\xFCntees\xBB, \u043F\u043E\u043B\u0443\u0447\u0438\u0442\u0441\u044F \u0440\u0435\u0437\u0443\u043B\u044C\u0442\u0430\u0442 \u043A\u0430\u043A \u043D\u0430 \u043A\u0430\u0440\u0442\u0438\u043D\u043A\u0435 \u0441\u043F\u0440\u0430\u0432\u0430.\n\u0412\u0442\u043E\u0440\u0430\u044F \u043F\u0440\u043E\u0433\u0440\u0430\u043C\u043C\u0430, \u043A\u043E\u0442\u043E\u0440\u0430\u044F \u0437\u0430\u0441\u043B\u0443\u0436\u0438\u0432\u0430\u0435\u0442 \u043D\u0430\u0448\u0435\u0433\u043E \u0432\u043D\u0438\u043C\u0430\u043D\u0438\u044F \u2013 \u044D\u0442\u043E \u043B\u0435\u043C\u043C\u0430\u0442\u0438\u0437\u0430\u0442\u043E\u0440. \u042D\u0442\u0430 \u043F\u0440\u043E\u0433\u0440\u0430\u043C\u043C\u0430 \u0440\u0430\u0431\u043E\u0442\u0430\u0435\u0442 \u043F\u043E \u043E\u0431\u0440\u0430\u0442\u043D\u043E\u043C\u0443 \u043F\u0440\u0438\u043D\u0446\u0438\u043F\u0443. \u0414\u043E\u043F\u0443\u0441\u0442\u0438\u043C, \u0432\u044B \u0443\u0432\u0438\u0434\u0435\u043B\u0438 \u0432 \u0442\u0435\u043A\u0441\u0442\u0435 \u043D\u0435\u0437\u043D\u0430\u043A\u043E\u043C\u043E\u0435 \u0441\u043B\u043E\u0432\u043E, \u043D\u0435 \u0437\u043D\u0430\u0435\u0442\u0435 \u043D\u0438 \u043F\u0435\u0440\u0435\u0432\u043E\u0434\u0430, \u043D\u0438 \u0447\u0430\u0441\u0442\u0438 \u0440\u0435\u0447\u0438. \u0412\u0432\u043E\u0434\u0438\u0442\u0435 \u0435\u0433\u043E \u0432 \u0433\u0440\u0430\u0444\u0443 \xABs\xF5na\xBB, \u043D\u0430\u0436\u0438\u043C\u0430\u0435\u0442\u0435 \u043A\u043D\u043E\u043F\u043A\u0443 \xABlemmad\xBB, \u0438 \u043F\u043E\u043B\u0443\u0447\u0430\u0435\u0442\u0435 \u043D\u0430\u0447\u0430\u043B\u044C\u043D\u0443\u044E \u0444\u043E\u0440\u043C\u0443 \u0434\u043B\u044F \u044D\u0442\u043E\u0433\u043E \u0441\u043B\u043E\u0432\u0430, \u043A\u043E\u0442\u043E\u0440\u0443\u044E \u0443\u0436\u0435 \u0441\u043C\u043E\u0436\u0435\u0442\u0435 \u043B\u0435\u0433\u043A\u043E \u043F\u043E\u0441\u043C\u043E\u0442\u0440\u0435\u0442\u044C \u0432 \u043B\u044E\u0431\u043E\u043C \u0441\u043B\u043E\u0432\u0430\u0440\u0435.\n\n\n\u0420\u0438\u0441. 3\n\n\n\u0420\u0438\u0441. 4\n\n### Harjutus 2. \n\u0418\u0441\u043F\u0440\u0430\u0432\u044C\u0442\u0435 \u043E\u0448\u0438\u0431\u043A\u0438. \u0412 \u043A\u0430\u0436\u0434\u043E\u043C \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0438 \u043F\u043E \u043E\u0434\u043D\u043E\u0439 \u043E\u0448\u0438\u0431\u043A\u0438.\nSaake tuttavaks. See oleb minu hea kolleeg Tiina. Tema on ka \xF5ppetaja. Ma \xF5pid eesti keelt kaks kuud. Mina p\xE4rit olen Eestist. Ma olen Eestis s\xFCnninud. T\xFCdruk loete praegu ilusat raamatut. Lugege Sina ka! Ma arvab, et see raamat on hea. Poiss ei armastab matemaatikat. \n\n### Harjutus 3.\n\u0420\u0430\u0441\u043A\u0440\u043E\u0439\u0442\u0435 \u0441\u043A\u043E\u0431\u043A\u0438, \u043F\u043E\u0441\u0442\u0430\u0432\u0438\u0432 \u0433\u043B\u0430\u0433\u043E\u043B \u0432 \u043F\u0440\u0430\u0432\u0438\u043B\u044C\u043D\u0443\u044E \u0444\u043E\u0440\u043C\u0443.\nKus sa .......... (elama)? 2. Ma .......... (elama) Tartus. 3. Kas sinu s\xF5ber .......... (elama) ka Tartus, v\xF5i ta .......... (elama) ja .......... (t\xF6\xF6tama) Tallinnas. 4. Millal sa .......... (tulema)? 5. \xD5pilased ei .......... (\xF5ppima) praegu tunnis. 6. \xD5pilased ........... (m\xE4ngima), sest \xF5petaja .......... (magama). 7. \xD5htul .......... (lugema) tavaliselt naine raamatut. 8. Kas te .......... (s\xF5itma) muuseumisse homme? 9. Nad .......... (arvama), et me .......... (v\xF5tma) takso. 10. Sinu s\xF5ber ei .......... (teadma), et Sa nii h\xE4sti .......... (oskama) inglise keelt.\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('task',{number:4}),source:source});}));

