var source='\n### Harjutus 1\n\n\u0414\u043B\u044F \u0437\u0430\u043F\u043E\u043C\u0438\u043D\u0430\u043D\u0438\u044F \u043D\u043E\u0432\u044B\u0445 \u0441\u043B\u043E\u0432 \u043F\u043E \u043D\u0430\u0437\u0432\u0430\u043D\u0438\u044F\u043C \u0433\u043E\u0440\u043E\u0434\u043E\u0432 \u0438 \u0441\u0442\u0440\u0430\u043D, \u043F\u0440\u0435\u0434\u043B\u0430\u0433\u0430\u044E \u0441\u043B\u0435\u0434\u0443\u044E\u0449\u0435\u0435 \u0437\u0430\u0434\u0430\u043D\u0438\u0435: \u0441\u043E\u0441\u0442\u0430\u0432\u044C\u0442\u0435 \u043A\u0430\u043A \u043C\u043E\u0436\u043D\u043E \u0431\u043E\u043B\u044C\u0448\u0435 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u0439, \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u044F \u0438\u0437\u0432\u0435\u0441\u0442\u043D\u044B\u0435 \u0412\u0430\u043C \u0433\u043B\u0430\u0433\u043E\u043B\u044B \u0438 \u043D\u043E\u0432\u044B\u0435 \u0441\u043B\u043E\u0432\u0430 \u0438\u0437 \u0442\u0430\u0431\u043B\u0438\u0446\u044B \u0443\u0440\u043E\u043A\u0430. \nN\xE4ide.\nEestlane elab Tallinnas. L\xE4tlased r\xE4\xE4givad l\xE4ti keelt. Roma on Itaalia pealinn. \u0418 \u0442\u043E\u043C\u0443 \u043F\u043E\u0434\u043E\u0431\u043D\u043E\u0435. \u0427\u0435\u043C \u0440\u0430\u0437\u043D\u043E\u043E\u0431\u0440\u0430\u0437\u043D\u0435\u0435 \u0431\u0443\u0434\u0443\u0442 \u0412\u0430\u0448\u0438 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F, \u0442\u0435\u043C \u043B\u0443\u0447\u0448\u0435. Leedulane ei oska inglise keelt, kuid ta elab Londonis.\n\n### Harjutus 2\n\n\u041F\u043E\u0441\u0442\u0430\u0432\u044C\u0442\u0435 \u0441\u043B\u043E\u0432\u0430 \u0432 \u0441\u043A\u043E\u0431\u043A\u0430\u0445 \u0432 \u043D\u0443\u0436\u043D\u044B\u0439 \u043F\u0430\u0434\u0435\u0436 \u043C\u0435\u0441\u0442\u0430.\n\n1. Kas te kolite varsti oma (uus maja) .......... .\n2. Mina olen s\xFCndinud Eesti (Vabariik) .......... , Tartu (maakond) .......... , \xDClenurme (vald) .......... , Soinaste (k\xFCla) .......... .\n3. Mina armastan k\xE4ia (n\xE4itus) .......... ja (kontsert) .......... .\n4. Kirjuta need s\xF5nad (roheline tahvel) .......... .\n5. Sugulased saabuvad ka L\xE4ti .......... ja Venemaa .......... .\n6. Suitsup\xE4\xE4sukesed lendavad meile (soe maa) .......... , peamiselt (Aafrika) .......... .\n7. Sinu \xF5pik ei ole \xFCldse (riiul) .......... , ta on hoopis (televiisor) .......... .\n8. Tema t\xF6\xF6tab (\xF6\xF6) .......... ja magab (p\xE4ev) .......... .\n9. L\xE4hme t\xE4na (loomaaed) .......... ja (mererand) .......... . \n10. (See pilt) .......... olen ma viieaastane.\n11. V\xF5ta palun taldrikud (kapp) ..........  ja lusikad pane tagasi (karp) .......... .\n12. Minu korter asub (teine korrus) .......... .\n13. Te v\xF5ite n\xF5u k\xFCsida (direktor) .......... v\xF5i tema (aset\xE4itja) .......... . Nad oskavad seda paremini teha.\n14. Laup\xE4eviti k\xE4ib (\u0442\u0440\u0435\u0431\u0443\u0435\u0442 \u043F\u0430\u0434\u0435\u0436\u0430 \u0433\u0434\u0435!)  ema (turg) .......... ja t\xF6\xF6tab (aed) .......... .\n15. Minu \xF5e mees on (eriala) .......... elektrik.\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('task',{number:7}),source:source});}));

