var source='\n### Harjutus 1.\n\u0418\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0439\u0442\u0435 \u043C\u0435\u0441\u0442\u043E\u0438\u043C\u0435\u043D\u0438\u044F \u0432 \u043D\u0443\u0436\u043D\u043E\u043C \u043F\u0430\u0434\u0435\u0436\u0435.\nM\xF5ni inimene ei tea, mis on  .......... (tema) tervisele kasulik. 2. Vend r\xE4\xE4kis, kuidas ta ..........  (ise) koera sai. 3. Nad on ..........(oma) t\xF6\xF6ga t\xE4iesti rahul. 4. .......... (tema) juttu on raske j\xE4lgida. 5. Tulge koos .......... (mina), ma ei l\xE4he ilma ..........(teie). 6. .......... (m\xF5ni) meeldib vaikus, .......... (m\xF5ni) aga eelistab linnak\xE4ra. 7. .......... (nemad) teeb piltnik palju ilusaid fotosid. 8. .......... (mina) tundub, et Jaan on .......... (keegi) armunud. 9. Ma pole juba ammu ......... (teie) midagi head kuulnud. 10. 11. .......... (mina) m\xE4letan .......... (sina) ja .......... (sina) venda. 12. .......... (nemad) on k\xF5ik minu tuttavad, .......... (nemad) on meeldiv suhelda. 13. Pean .......... (teie) helistama ja kindlasti .......... (see) j\xE4rele k\xFCsima. 14. Ma m\xF5tlen .......... (see) tihti, mis .......... (meie) sobib paremini. 15. .......... (meie) pole aega, .......... (meie) peame .......... (nemad) asemel komandeeringusse s\xF5itma.\n\n### Harjutus 2.\n\u041F\u0435\u0440\u0435\u0432\u043E\u0434\u0438\u0442\u0435 \u043D\u0430 \u044D\u0441\u0442\u043E\u043D\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u0441\u043B\u0435\u0434\u0443\u044E\u0449\u0438\u0435 \u043F\u0440\u0435\u0434\u043B\u043E\u0436\u0435\u043D\u0438\u044F. \n\u041E\u043D\u0430 \u043A\u0443\u043F\u0438\u043B\u0430 \u0441\u0435\u0431\u0435 \u043D\u043E\u0432\u0443\u044E \u043A\u0432\u0430\u0440\u0442\u0438\u0440\u0443. 2. \u0418\u043C \u043D\u0440\u0430\u0432\u0438\u0442\u0441\u044F \u043F\u0440\u043E\u0441\u0442\u043E \u0441\u0438\u0434\u0435\u0442\u044C \u0438 \u043E \u0447\u0435\u043C-\u043D\u0438\u0431\u0443\u0434\u044C \u0440\u0430\u0437\u0433\u043E\u0432\u0430\u0440\u0438\u0432\u0430\u0442\u044C. 3. \u041D\u0435\u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0432\u0441\u0435\u0433\u0434\u0430 \u0445\u043E\u0442\u044F\u0442 \u0441\u043F\u0430\u0442\u044C, \u0430 \u043A\u0442\u043E-\u0442\u043E \u043C\u043E\u0436\u0435\u0442 \u0441\u043F\u0430\u0442\u044C \u043F\u0430\u0440\u0443 \u0447\u0430\u0441\u043E\u0432 \u0438 \u0447\u0443\u0432\u0441\u0442\u0432\u043E\u0432\u0430\u0442\u044C \u0441\u0435\u0431\u044F  \u0445\u043E\u0440\u043E\u0448\u043E. 4. \u041C\u044B \u0434\u0430\u0432\u043D\u043E \u0438\u0445 \u043D\u0435 \u0432\u0438\u0434\u0435\u043B\u0438. 5. \u042D\u0442\u0430 \u0434\u0435\u0432\u0443\u0448\u043A\u0430 \u0442\u0432\u043E\u044F \u0441\u0435\u0441\u0442\u0440\u0430? 6. \u0414\u0430\u0439 \u043C\u043D\u0435 \u0447\u0442\u043E-\u043D\u0438\u0431\u0443\u0434\u044C \u043F\u043E\u043F\u0438\u0442\u044C. 7. \u0423 \u043C\u0435\u043D\u044F \u0434\u043B\u044F \u0432\u0430\u0441 \u0435\u0441\u0442\u044C \u0445\u043E\u0440\u043E\u0448\u0430\u044F \u0438 \u043F\u043B\u043E\u0445\u0430\u044F \u043D\u043E\u0432\u043E\u0441\u0442\u044C, \u0441 \u043A\u0430\u043A\u043E\u0439 \u043D\u0430\u0447\u0430\u0442\u044C? 8. \u041C\u043D\u043E\u0433\u0438\u0435 \u0443\u0436\u0435 \u0434\u0430\u0432\u043D\u043E \u0437\u043D\u0430\u044E\u0442 \u044D\u0442\u043E\u0433\u043E \u0441\u0442\u0430\u0440\u0438\u043A\u0430. 9. \u0423 \u043C\u0435\u043D\u044F \u043D\u0435\u0442 \u0441\u0435\u0433\u043E\u0434\u043D\u044F \u0434\u043B\u044F \u044D\u0442\u043E\u0433\u043E \u0432\u0440\u0435\u043C\u0435\u043D\u0438, \u043F\u043E\u0437\u0432\u043E\u043D\u0438 \u043C\u043D\u0435 \u0437\u0430\u0432\u0442\u0440\u0430, \u0438 \u043C\u044B \u0447\u0442\u043E-\u043D\u0438\u0431\u0443\u0434\u044C \u043F\u0440\u0438\u0434\u0443\u043C\u0430\u0435\u043C. 10. \u042D\u0442\u043E \u043E\u0447\u0435\u043D\u044C \u0438\u043D\u0442\u0435\u0440\u0435\u0441\u043D\u0430\u044F \u043A\u043D\u0438\u0433\u0430, \u043C\u043D\u043E\u0433\u0438\u0435 \u0435\u0435 \u0447\u0438\u0442\u0430\u044E\u0442.\n';/* harmony default export */ __webpack_exports__["a"] = (Object(__WEBPACK_IMPORTED_MODULE_1_react_i18next__["b" /* translate */])()(function(props){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__components_MarkdownPage__["a" /* default */],{title:props.t('task',{number:17}),source:source});}));

